import { HashMap } from 'libs/base/src/lib/types.utilities';

/** Building Level data */
export class BuildingLevel {
    /** ID of the building level zone */
    readonly id: string;
    /** ID of the building zone associated with the level */
    readonly building_id: string;
    /** Name of the level */
    readonly name: string;
    /** Number or short identifier of the level */
    readonly short_name: string;
    /** Map URL for the level */
    readonly map_url: string;
    /** Usage type for the level */
    readonly type: 'staff' | 'client' | 'any';
    /** Setting for the level */
    readonly settings: HashMap;

    constructor(_data: HashMap = {}) {
        this.id = _data.id || _data.level_id || '';
        this.building_id = _data.bld_id || _data.building_id || '';
        this.name = _data.name || _data.level_name || '';
        const lower_name = this.name.toLowerCase();
        const num = lower_name.indexOf('level') >= 0 ? lower_name.replace(/ ?level ?/gi, '') : lower_name.substr(0, 1).toUpperCase();
        this.short_name = _data.short_name || num || '';
        this.map_url = _data.map_url || '';
        this.type = _data.type || _data.floor_type || 'any';
        this.settings = _data.settings;
    }
}
