import { Component, Input } from '@angular/core';
import { Booking } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { getTimezoneOffsetString } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'order-booking-details',
    templateUrl: './booking-details.component.html',
    styleUrls: ['./booking-details.component.scss'],
})
export class OrderBookingDetailsComponent {
    /** Email of the order space */
    @Input() public space: Space;
    /** Booking details to display */
    @Input() public booking: Booking;

    public get timezone() {
        return getTimezoneOffsetString(localStorage.getItem('CONCIERGE.timezone'));
    }

    /** Display startDateTZ and endDateTZ time only time */
    public get start_and_duration_time() {
        const end_ = this.booking.startDateTz.addValue({minutes: this.booking.duration});
        return `${this.start_time} - ${end_.formatDate('h:mm a')}`;
    }

    /** Display startDateTZ time only time */
    public get start_time() {
        return `${this.booking.startDateTz.formatDate('h:mm a')}`;
    }

    /** Store booking details to be used in another component */
    public storeBooking() {
        localStorage.setItem('CONCIERGE.view_booking', JSON.stringify(this.booking.toJSON()));
        localStorage.setItem('CONCIERGE.view_booking_space', JSON.stringify(this.space.toJSON()));
        localStorage.setItem('CONCIERGE.date', `${this.booking.startDateTz.ms}`);
        localStorage.setItem('CONCIERGE.timezone', `${this.booking.startDateTz.building_tz}`);
    }
}
