import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Route } from '@angular/router';

import { WeekViewComponent } from './week-view.component';
import { WeekViewTimelineComponent } from './timeline/timeline.component';
import { SharedContentModule } from 'src/app/shared/shared.module';
import { WeekViewDayDisplayComponent } from './day-display/day-display.component';
import { WeekViewItemComponent } from './day-display/item/item.component';
import { UiModule } from '@mckinsey-converge/ui';

const ROUTES: Route[] = [
    { path: '', component: WeekViewComponent },
    { path: ':level', component: WeekViewComponent },
    { path: '**', redirectTo: '' }
];

@NgModule({
    declarations: [WeekViewComponent, WeekViewTimelineComponent, WeekViewDayDisplayComponent, WeekViewItemComponent],
    imports: [CommonModule, FormsModule, ReactiveFormsModule, RouterModule.forChild(ROUTES), SharedContentModule, UiModule]
})
export class WeekViewModule {}
