import { Component, Input, Output, EventEmitter } from '@angular/core';
import {
    Booking,
    generateBookingForm
} from '@mckinsey-converge/data-common';
import { MatDialog } from '@angular/material/dialog';

import { RequirementDetailsModalComponent, RequirementDetailsModalData } from '@mckinsey-converge/legacy-overlays';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { DialogEvent } from '@mckinsey-converge/base';
import { BaseDirective } from '@mckinsey-converge/base';
import { Space } from '@mckinsey-converge/data-common';

@Component({
    selector: 'meeting-details-requirements',
    templateUrl: './requirements.component.html',
    styleUrls: ['./requirements.component.scss'],
})
export class MeetingDetailsRequirementsComponent extends BaseDirective {
    /** Booking to display details */
    @Input() public booking: Booking;
    /** Space to display details */
    @Input() public space: Space;
    /** Emitter for changes to the booking */
    @Output() public bookingChange = new EventEmitter<Booking>();
    /** Emitter for updating booking */
    @Output() public event = new EventEmitter();
    public roomNames: string;
    /** Whether to show all locations */
    public show_locations: boolean;

    /** Requirement notes */
    public get notes(): string {
        const note = this.booking.notes.find(
            (note) => note.type === 'equipment' && this.space.email === note.space
        );
        return (note ? note.message : '') || '<No Notes>';
    }

    /** Whether booking has been declined or ended */
    public get expired(): boolean {
        return this.booking.timeBasedStatusLabel === 'done' || this.booking.declined;
    }

    public get headcount(): any{
        const temp = this.booking.multi_rooms.filter(room => room.email === this.space.email)[0]['email'];
        return this.booking.headcount || this.booking.expected_attendees[temp];
    }

    constructor(private _dialog: MatDialog, private _service: ApplicationService) {
        super();
    }

    public ngOnInit(): void {
        this.roomNames = this.booking.local_room_name;
    }

    /** Open modal for adding equipment requirements to booking */
    public openEquipmentDetailsModal() {
        const form = generateBookingForm(this.booking, ['space_list'], true);
        const ref = this._dialog.open<
            RequirementDetailsModalComponent,
            RequirementDetailsModalData
        >(RequirementDetailsModalComponent, {
            maxWidth: '95vw',
            width: '32em',
            data: {
                spaces: form.controls.space_list.value,
                notes_field: form.controls.notes,
                codes_field: form.controls.equipment_codes,
                head_counts_field: form.controls.expected_attendees,
                standalone: true
            }
        });
        this.subscription(
            'modal_events',
            ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    ref.close();
                    this.event.emit(form);
                }
            })
        );
    }
}
