import {
  AfterViewInit,
    Directive,
    ElementRef,
    EventEmitter,
    Input,
    OnDestroy,
    OnInit,
    Output,
    Renderer2,
    ViewContainerRef
  } from '@angular/core';
  import { DropdownPanel } from './dropdown-panel';
  import { Overlay, OverlayRef } from '@angular/cdk/overlay';
  import { TemplatePortal } from '@angular/cdk/portal';
  import { merge, Observable, Subscription } from 'rxjs';
  
  @Directive({
    selector: '[dropdownTriggerFor]',
    host: {
      '(click)': 'toggleDropdown()'
    }
  })
  export class DropdownTriggerForDirective implements OnDestroy, AfterViewInit, OnInit {
    private isDropdownOpen = false;
    private overlayRef: OverlayRef;
    private dropdownClosingActionsSub = Subscription.EMPTY;
  
    @Input('dropdownTriggerFor') public dropdownPanel: DropdownPanel;
    @Output() isPanelOpened = new EventEmitter<boolean>();
    constructor(
      private overlay: Overlay,
      private elementRef: ElementRef<HTMLElement>,
      private viewContainerRef: ViewContainerRef,
      private renderer: Renderer2
    ) {
     
    }
    ngOnInit(): void {
      // const cngSelectEl = this.elementRef.nativeElement.nextElementSibling; 
      // const parentWidth = this.elementRef.nativeElement.offsetWidth; 
      // this.renderer.setStyle(cngSelectEl, 'width', `${parentWidth}px`);
    }
    ngAfterViewInit(): void {
      
    }

  
    toggleDropdown(): void {
      this.isDropdownOpen ? this.destroyDropdown() : this.openDropdown();
      
    }
  
    openDropdown(): void {
      let {ddWidth} = this.dropdownPanel;
      this.dropdownPanel.ddWidth = ddWidth !=='' ? ddWidth : `${this.elementRef.nativeElement.offsetWidth}px`;
      this.isDropdownOpen = true;
      this.isPanelOpened.emit(this.isDropdownOpen);
      this.overlayRef = this.overlay.create({
        hasBackdrop: true,
        backdropClass: 'cdk-overlay-transparent-backdrop',
        scrollStrategy: this.overlay.scrollStrategies.close(),
        positionStrategy: this.overlay
          .position()
          .flexibleConnectedTo(this.elementRef)
          .withPositions([
            {
              originX: 'end',
              originY: 'bottom',
              overlayX: 'end',
              overlayY: 'top',
              offsetY: 8
            }
          ])
      });
  
      const templatePortal = new TemplatePortal(
        this.dropdownPanel.templateRef,
        this.viewContainerRef
      );
      this.overlayRef.attach(templatePortal);
  
      this.dropdownClosingActionsSub = this.dropdownClosingActions().subscribe(
        () => this.destroyDropdown()
      );
    }
  
    private dropdownClosingActions(): Observable<MouseEvent | void> {
      const backdropClick$ = this.overlayRef.backdropClick();
      const detachment$ = this.overlayRef.detachments();
      const dropdownClosed = this.dropdownPanel.closed;
  
      return merge(backdropClick$, detachment$, dropdownClosed);
    }
  
    private destroyDropdown(): void {
      if (!this.overlayRef || !this.isDropdownOpen) {
        return;
      }
  
      this.dropdownClosingActionsSub.unsubscribe();
      this.isDropdownOpen = false;
      this.isPanelOpened.emit(this.isDropdownOpen);
      this.overlayRef.detach();
    }
  
    ngOnDestroy(): void {
      if (this.overlayRef) {
        this.overlayRef.dispose();
      }
    }
  }
  