import {
    Component,
    OnInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
} from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'room-details-wrapper',
    template: `
                <div class="room-admin">
                    <a-sidebar></a-sidebar>
    
                    <div class="right-panel details-view" >
                        <div class="overflow-bg">
                            <div class="white-bg">
                                <div class="backoffice">
                                    <img class="backoffice-img" src="assets/img/backoffice-converge.svg"/>
                            </div>
                            <p class="breadcrumb"><a (click)="getToRoomList()">Room Administration</a> > Room Detail</p>
                            <p class="heading">Room detail</p>
                            <div>
                                <hr class="hr"/>
                            </div>
                        </div>
                            <app-room-details [rowInfo]="rowInfo" ></app-room-details>
                        </div>
                    </div>
                </div>  
                `,
    styleUrls: ['./room-details-wrapper.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RoomDetailsWrapperComponent implements OnInit {
    public rowInfo: any;
   


    constructor(
        private cd: ChangeDetectorRef,
        private _router: Router,
    ) {
        if(this._router.getCurrentNavigation() !== null && this._router.getCurrentNavigation().extras['state']?.details) {
           this.rowInfo = this._router.getCurrentNavigation().extras['state']?.details;
        }
        else {
            this._router.navigate(['room-admin']);
        }
    }

    public ngOnInit(): void {
       this.cd.detectChanges();
    }

    public getToRoomList() {
        this._router.navigate(['room-admin'])
    }

}