export const spaceExtraFeatureToDisplayName = (feature: SpaceFeatures) => {
    switch (feature) {
        case SpaceFeatures.VideoConference:
            return 'Video conference (VC)';
        case SpaceFeatures.ConferencePhone:
            return 'Conference phone';
        case SpaceFeatures.WirelessContentSharing:
            return 'Wireless content sharing';
        case SpaceFeatures.FlipChart:
            return 'Flip chart';
        case SpaceFeatures.Glassboard:
            return 'Glassboard';
        case SpaceFeatures.ElectronicWhiteboard:
            return 'Whiteboard';
        case SpaceFeatures.ConferenceRoom:
            return 'Conference';
        case SpaceFeatures.TeamRoom:
            return 'Team';
        case SpaceFeatures.PartnerOffice:
            return 'Partner';
        case SpaceFeatures.PhoneBooth:
            return 'Phone booth';
        case SpaceFeatures.NaturalLight:
            return 'Room with window only (natural light)';
        case SpaceFeatures.BoardRoom:
            return 'Boardroom room style';
        case SpaceFeatures.CocktailRoom:
            return 'Cocktail room style';
        case SpaceFeatures.TheatreRoom:
            return 'Theatre room style';
        case SpaceFeatures.WorkshopRoom:
            return 'Workshop room style';
        case SpaceFeatures.UShapeRoom:
            return 'U Shape room style';
        case SpaceFeatures.Miscellaneous:
            return 'Miscellaneous room style'
        case SpaceFeatures.Catering:
            return 'Rooms with catering';
        // TODO: add any special display handling here.
        default:
            return feature;
    }
};


export const instantBookOption: SelectOption<any> = {
    value: 'instant-book',
    display: 'Listing you can book without waiting for a host approval',
    shortDisplay: 'Instant book'
};
export const instantBookOptions: SelectOption<any>[] = [
    instantBookOption
];

export const mapSpaceFeaturesToSelectOption = (f: SpaceFeatures): SelectOption<any> => {
    const display = spaceExtraFeatureToDisplayName(f);
    return ({
        value: f,
        display,
        // Useful for long text, like that of the instant-book
        // option.
        //
        // We do not expect this option object to be modified
        // at any further point before it appears on screen -
        // although that may change in future releases.
        shortDisplay: display
    });
};

