/**
 * Defines the reason by which spaces are filtered out / missing.
 */
export enum SpaceFilterReason {
    /**
     * Spaces were shown and not all filtered out.
     */
    None = 'None',

    /**
     * If room filters are applied to the set of rooms,
     * and rooms are available if not applied by filtering.
     */
    Filters = 'Filters',

    /**
     * We check office rules first to determine if its been filtered.
     */
    OfficeRules = 'OfficeRules',

    /**
     * Fallback as the end case if no results are found.
     */
    DateTime = 'DateTime',

    /**
     * In case there are no results for a buiding but there are other office buidings in the same city.
     */
    AlternateBuildingsFound = 'AlternateBuildingsFound',

    /**
     * If room is non-bookable in the system
     */
    NonBookable = 'NonBookable'
}

export interface FilteredSpaces {
    spaces: Space[] | undefined;
    reason: SpaceFilterReason;
    statusMap: HashMap<SpaceStatus>;
    specificReason?: SpaceFilterReason;
}

/**
 * Filter spaces by filters locally. TBD in the future for real API pagination.
 *
 * @param formFilters - the set of filters to apply on the list of spaces.
 * @param spaces - the list of spaces to filter
 * @param activeForm - the landing page set of filters.
 * @param buildings - the list of buildings loaded, used to retrieve building-specific booking rules.
 * @param currentUser - the current user
 */
export const filterSpacesByAppliedFilters = (
    formFilters: SelectOption<any>[],
    spaces: Space[] | undefined, // the resultshere are raw JSON Spaces, not an array of the Space class
    activeForm: BookingFormData,
    buildings: Building[] = [],
    currentUser: User | undefined,
    isStaffMap: boolean = false): FilteredSpaces => {
    const instantBook: boolean = formFilters.some(f => f.value === instantBookOption.value);
    const onlyCatering: boolean  = formFilters.some(f => f.value === SpaceFeatures.Catering);
    // Drop incompatible spaces. If no filters were given, just return the array unchanged.
    const featureFilters = formFilters.filter(filter => ![instantBookOption.value, SpaceFeatures.Catering].includes(filter.value));
    const formFiltersEmpty = featureFilters.length === 0;

    // R--- refactor activeForm? Only after forms can handle dateTz
    // Blocked by date time input refactor
    const options: SpaceBookingRuleOptions = {
        duration: activeForm.duration,
        host: currentUser,
        dateTz: activeForm.dateTz,
    };
    let rulesCount = 0;
    let ruleReason = '';
    let excludedByFiltersCount = 0;
    const statusMap: HashMap<SpaceStatus> = {};
    // console.group();
    const filteredSpaces = spaces?.map(space => space instanceof Space ? space : new Space(space)).filter(raw_space => {
        const space = new Space(raw_space); // Construct the full fledged space
        const building = buildings.find(b => space.zones.includes(b.id));
        const rules = rulesForSpace({
            time: options?.dateTz?.ms,
            duration: options.duration,
            user: options.host,
            rules: building?.booking_rules,
            space
        });
        ruleReason = rules.reason;
        let valid = !rules.hide;
        if (!valid) {
            rulesCount++;
            return false;
        }


        // Check for all the form filters ( except 'catering' )
        let hasAllFeatures: boolean;
        const internalRoomTypeFilters = [
            'conference',
            'meeting_room',
            'partner',
            'phone_booth',
            'team_room',
        ];

        if (featureFilters.length === 0) {
            hasAllFeatures = true;
        } else if(featureFilters.every(f => internalRoomTypeFilters.includes(f.value))) {    // If all featureFilters(selected filters) are present in internalRoomTypeFilters, make the filtering inclusive(return on first true) 
            hasAllFeatures = featureFilters.some(f => space.featuresArray.includes(f.value));
          } else {    // If any featureFilters(selected filters) are not present in internalRoomTypeFilters, make the filtering exclusive(return on first false)
            hasAllFeatures = featureFilters.every(f => space.featuresArray.includes(f.value));
          }

        const canBook = instantBook ? space.bookable : true;
        const matchesType = activeForm?.bookingType?.value === 'allRooms'
                            ? true
                            : space?.internal_or_external?.length
                            ? space.internal_or_external === activeForm?.bookingType?.value
                            : true;
        valid = valid && matchesType && (formFiltersEmpty || hasAllFeatures) && canBook;
        let hasCatering = building?.has_catering;
        if (space?.has_catering === false) {
            hasCatering = false;
        }
        // need the room catering to override.
        if (onlyCatering && !hasCatering) {
            valid = false;
            // console.log('Excluded by Catering');
        }

        // count this space if the only reason it can't be shown is because one of the filters excluded it
        if (!hasAllFeatures) {
            valid = false;
            // console.log('Excluded by Status');
        }

        const status = statusFromBookings(
            space.bookings.length ? space.bookings.map(b => new Booking(b)) : space.settings.bookings.map(b => new Booking(b)),
            space.bookable && !rules.hide,
            !rules.auto_approve,
            activeForm.dateTz,
            space
        );

        // only compute room availability here right now if instant book option is chosen and room still valid.
        if (instantBook && valid) {
            valid = status.status === SpaceStatus.Available;
            if (!valid) console.group('Excluded by Status');
            
        }


        /** 
        * If there are rooms that are recurring but not available at all during the queried times, we filter them out from the results displayed on the page.
        * The "Available" status indicates whether a room can be booked during the queried times.
        * The "isStaffMap" flag- This helps to indicate that all occurrences of this room are booked during the queried times and are currently in use.
        * "isStaffMap" flag allows including such rooms in the list, which will be displayed in red on the map.
        */
        if(activeForm.is_recurrent && !space.availableOccurrences && !isStaffMap) {
            valid = false;
        }
        /** */

        // cache space status
        if (valid) {
            if(activeForm.is_recurrent && space.availableOccurrences && space.availableOccurrences<= space.totalOccurrences) {
                // space is available and requested for recurrence booking and few occurences are available to book then let the space select/bookable from map with limited availability
                statusMap[space.id] = !rules.auto_approve ? SpaceStatus.Requestable : SpaceStatus.Available;
            }else
             {
                statusMap[space.id] = status.status;
            }
            
        } else {
            excludedByFiltersCount++;
        }
        // console.log('Valid:', valid);
        return valid;
    });

    const buildingsInSameCity = buildings.filter(
        (_) =>activeForm?.location!==undefined && activeForm?.location?.length &&  _.city === activeForm?.location[0]?.meta?.building?.city
    ) || [];
    const alternateBuildings = buildingsInSameCity.filter(building =>
        !activeForm.location.some(locationItem =>
            locationItem.meta?.building?.id === building.id
        )
    );

    // console.groupEnd();
    let reason, specificReason = SpaceFilterReason.None;
    // console.log('Spaces:', filteredSpaces?.length, excludedByFiltersCount, rulesCount, ruleReason)
    if (filteredSpaces?.length === 0) {
        if (excludedByFiltersCount > 0 && (!formFiltersEmpty || onlyCatering)) {
            reason = SpaceFilterReason.Filters;
        } else if (rulesCount > 0) {
            reason = SpaceFilterReason.OfficeRules;
        } else if(alternateBuildings){
            reason = SpaceFilterReason.AlternateBuildingsFound;
        }
        else {
            reason = SpaceFilterReason.DateTime;
        }

        if (ruleReason === SpaceFilterReason.OfficeRules) {
            specificReason = SpaceFilterReason.OfficeRules;
        }
    }

    return { spaces: filteredSpaces, reason, statusMap, specificReason };
};
