import {
    Component,
    Input,
    OnInit,
    ViewContainerRef
} from '@angular/core';
import { BaseTemplateComponent } from '../base-template.component';

@Component({
    selector: 'mck-label',
    template: `
        <ng-template>
            <mat-label>
                <ng-content></ng-content>
                <span class="required-star" *ngIf="required">*</span>
            </mat-label>
        </ng-template>
    `,
    styleUrls: ['./label.component.scss']
})
export class LabelComponent extends BaseTemplateComponent implements OnInit {

    @Input()
    required = false;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }

}
