import { ComposerService } from '@placeos/composer';
import { BehaviorSubject, Observable, of, Subject, Subscriber } from 'rxjs';

import { BaseDataClass } from './base-api.class';
import { BaseClass, HashMap } from '@mckinsey-converge/base';
import { ApplicationService } from './app.service';
import { toQueryString } from './api.utilities';
import { SettingsService } from './settings.service';
import { catchError, map } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';

export interface IEngineResponse {
  results: HashMap[];
  total: number;
}

export class BaseAPIService<T extends {} = BaseDataClass> extends BaseClass {
  /** Application service */
  public parent: ApplicationService;
  /** Display name of the service */
  protected _name: string;
  /** API Route of the service */
  protected _api_route: string;
  /** Map of state variables for Service */
  protected _subjects: { [key: string]: BehaviorSubject<any> | Subject<any> } = {};
  /** Map of observables for state variables */
  protected _observers: { [key: string]: Observable<any> } = {};
  /** Map of poll subscribers for API endpoints */
  protected _subscribers: { [key: string]: Subscriber<any> } = {};
  /** Map of promises for Service */
  protected _promises: { [key: string]: Promise<any> } = {};
  /** Comparison function for service items */
  protected _compare: (a: T, b: T) => boolean = (a, b) =>
    a === b || (a as any).id === (b as any).id;
  /** Default filter function for list method */
  protected _list_filter: (a: T) => boolean = (a) => !!a;

  /** Http Client */
  protected get http() {
    return this._composer.http;
  }
  constructor(protected _composer: ComposerService,
              protected settingsService: SettingsService) {
    super();
    this._name = 'Base';
    this._api_route = 'base';
    this.set('list', []);
  }

  /**
   * Injects concierge into form_data.
   */
  private injectConcierge(form_data: HashMap) {
      // we only send it over when concierge, since the BE may check for presence rather than
      // if its true or not.
      if (this.settingsService.concierge) {
          return { ...form_data, concierge: true }
      }
      return form_data;
  }

  /**
   * Initailise service
   */
  public init() {
    this.load().then(
      (_) => this._initialised.next(true),
      (err) => this.timeout('init', () => this.init(), 1000)
    );
  }

  /**
   * Get API route for the service
   * @param engine Whether endpoint is using the application API or engine API
   */
  public route(engine: boolean = false) {
    const endpoint = engine
      ? this._composer.auth.api_endpoint
      : '/api/staff';
    return `${endpoint}/${this._api_route}`;
  }

  /** API Route of the service */
  public get api_route() {
    return this._api_route;
  }

  /**
   * Get the current value of the named property
   * @param name Property name
   */
  public get<U = any>(name: string): U {
    if (!this._observers[name]) {
      this.set<U>(name, null);
    }
    return (this._subjects[name] as BehaviorSubject<U>).getValue();
  }

  /**
   * Listen to value change of the named property
   * @param name Property name
   * @param next Callback for value changes
   */
  public listen<U = any>(name: string): Observable<U> {
    if (!this._observers[name]) {
      this.set<U>(name, null);
    }
    return this._observers[name];
  }

  /**
   * Update the value of the named property
   * @param name Property name
   * @param value New value
   */
  protected set<U = any>(name: string, value: U): void {
    if (!this._subjects[name]) {
      this._subjects[name] = new BehaviorSubject<U>(value);
      this._observers[name] = this._subjects[name].asObservable();
    } else {
      this._subjects[name].next(value);
    }
  }

  /**
   * Get list of loaded items
   * @param predicate Function for filtering the list
   */
  public filter(predicate: (a: T) => boolean = this._list_filter): T[] {
        const list: T[] = this.get('list');
        return list.filter(predicate);
  }

  /**
   * Get item with the given id from the loaded items
   * @param id ID of the item
   */
  public find(id: string): T {
    const list = this.get('list');
    return list.find((i) => i.id === id || (i.email?.toLowerCase() === id?.toLowerCase()));
  }

  /**
   * Query the index of the API route associated with this service
   * @param query_params Map of query paramaters to add to the request URL
   */
  public query(query_params: HashMap = {}): Promise<T[]> {
    let engine = false;
    let cache = 1000;
    /* istanbul ignore else */
    if (query_params) {
      engine = !!query_params.engine;
      delete query_params.engine;
      cache = typeof query_params.cache !== 'boolean' ? query_params.cache || 1000 : 1000;
      typeof query_params.cache !== 'boolean' && delete query_params.cache;
    }
    let query = toQueryString(query_params);
    const key = `query|${query}`;
    if (!this._promises[key]) {
      // Bring back once implemented in the API
      // if (this.settingsService.concierge) {
      //   query = query + '&check_access=true';
      // }
      this._promises[key] = new Promise((resolve, reject) => {
        const url = `${this.route(engine)}${query ? '?' + query : ''}`;
        let result: T[] | HashMap[] = [];
        this.http.get(url).subscribe(
          (d: IEngineResponse | HashMap[]) => {
            result =
              d && d instanceof Array
                ? d.map((i) => this.process(i))
                : d && !(d instanceof Array) && d.results
                ? (d.results as HashMap[])
                : d && !(d instanceof Array) && !d.results 
                ? [d]
                : [];
          },
          (e) => {
            reject(e);
            this._promises[key] = null;
          },
          () => {
            resolve(result);
            this.timeout(key, () => (this._promises[key] = null), cache);
          }
        );
      });
    }
    return this._promises[key];
  }

  /**
   * query function version -2 - returns observable instead of promise...
   * Query the index of the API route associated with this service
   * @param query_params Map of query paramaters to add to the request URL
   */
   public queryObsr(query_params: HashMap = {}):Observable<any>{
    let engine = false;
    let cache = 1000;
    /* istanbul ignore else */
    if (query_params) {
      engine = !!query_params.engine;
      delete query_params.engine;
    }
    let query = toQueryString(query_params);
    const key = `query|${query}`;
    const url = `${this.route(engine)}${query ? '?' + query : ''}`;
    return this.http.get(url).pipe(
      map((d: IEngineResponse | HashMap[]) =>  this.processApiResult(d) ),
      catchError(<T>(error: any, result?: T) => {
        console.log(error);
        return of(result as T);
        })
    );;
  }


  /**
   * 
   * @param d 
   * @returns 
   */
   processApiResult(d: IEngineResponse | HashMap[]): IEngineResponse | HashMap[]{
    let result: IEngineResponse | HashMap[] =
        d && d instanceof Array
          ? d.map((i) => this.process(i))
          : d && !(d instanceof Array) && d.results
          ? (d.results as HashMap[])
          : d && !(d instanceof Array) && !d.results 
          ? [d]
          : [];
    return result;
  }

  /**
   * query function version -2 - returns observable instead of promise...
   * Query the index of the API route associated with this service
   * @param query_params Map of query paramaters to add to the request URL
   */
  public queryRoomsForQR(query_params: HashMap = {}):Observable<any>{
    let engine = false;
    let cache = 1000;
    /* istanbul ignore else */
    if (query_params) {
      engine = !!query_params.engine;
      delete query_params.engine;
    }
    let query = toQueryString(query_params);
    const key = `query|${query}`;
    const url = `${this.route(engine)}${query ? '?' + query : ''}`;
    return this.http.get(url);
  }
  
  /**
   * Query the API route for a sepecific item
   * @param id ID of the item
   * @param query_params Map of query paramaters to add to the request URL
   */
  public show(id: string, query_params: HashMap = {}): Promise<T> {
    let engine = false;
    /* istanbul ignore else */
    if (query_params) {
      engine = !!query_params.engine;
      delete query_params.engine;
    }
    const query = toQueryString(query_params);
    const key = `show|${id}|${query}`;
    /* istanbul ignore else */
    if (!this._promises[key]) {
      this._promises[key] = new Promise<T>((resolve, reject) => {
        const url = `${this.route(engine)}/${id}${query ? '?' + query : ''}`;
        let result: T = null;
        this.http.get(url).subscribe(
          (d) => (result = this.process(d)),
          (e) => {
            reject(e);
            this._promises.new_item = null;
          },
          () => {
            resolve(result);
            this.timeout(key, () => (this._promises[key] = null), 1000);
          }
        );
      });
    }
    return this._promises[key];
  }

  /**
   * Make post request for a new item to the service
   * @param form_data Data to post to the server
   * @param query_params Map of query paramaters to add to the request URL
   */
  public add(form_data: HashMap, query_params: HashMap = {}): Promise<T> {
    /* istanbul ignore else */
    if (!this._promises.new_item) {
      this._promises.new_item = new Promise<T>((resolve, reject) => {
        const query = toQueryString(query_params);
        const url = `${this.route(query_params.engine)}${query ? '?' + query : ''}`;
        let result: T = null;
        this.http.post(url, this.injectConcierge(form_data)).subscribe(
          (d) => (result = this.process(d)),
          (e) => {
            reject(e);
            this.analyticsEvent(`create-${this._name.toLowerCase()}-failed`);
            this._promises.new_item = null;
          },
          () => {
            resolve(result);
            this.set('list', this.updateList(this.get('list'), [result]));
            this.analyticsEvent(`create-${this._name.toLowerCase()}-success`);
            this._promises.new_item = null;
          }
        );
      });
    }
    return this._promises.new_item;
  }

  /**
   * Perform API task for the given item ID
   * @param id ID of the item
   * @param task_name Name of the task
   * @param form_data Map of data to pass to the API
   * @param method Verb to use for request
   */
  public 
  task<U = any>(
    id: string,
    task_name: string,
    form_data: HashMap = {},
    method: 'post' | 'get' = 'post'
  ): Promise<U> {
    const query =  toQueryString(this.injectConcierge(form_data));
    const key = `task|${id}|${task_name}|${query}`;
    /* istanbul ignore else */
    if (!this._promises[key]) {
      this._promises[key] = new Promise<U>((resolve, reject) => {
        const post_data = { ...form_data, id, _task: task_name };
        const url = `${this.route(false)}/${id}/${task_name}`;
        let result: any;
        const request =
          method === 'post'
            ? this.http.post(url, post_data)
            : this.http.get(`${url}${query ? '?' + query : ''}`);
        request.subscribe(
          (d) => (result = d),
          (e) => {
            reject(e);
            this.analyticsEvent(
              `${this._name.toLowerCase()}-task-${task_name}-failed`,
              id
            );
            delete this._promises[key];
          },
          () => {
            resolve(result as U);
            this.analyticsEvent(
              `${this._name.toLowerCase()}-task-${task_name}-success`,
              id
            );
            this.timeout(key, () => delete this._promises[key], 1000);
          }
        );
      });
    }
    return this._promises[key];
  }


  /**
   * V-2
   * Perform API task for the given item ID
   * @param id ID of the item
   * @param task_name Name of the task
   * @param form_data Map of data to pass to the API
   * @param method Verb to use for request
   */
   public taskObsr<U = any>( 
    id: string, 
    task_name: string, 
    form_data: HashMap = {}, 
    method: 'post' | 'get' = 'post' ):Observable<any>{
    const query =   toQueryString(this.injectConcierge(form_data));
    let engine = false;
    let cache = 1000;
    const post_data = { ...form_data, id, _task: task_name };
    const url = `${this.route(false)}/${id}/${task_name}`;
    let result: any;

    const request = method === 'post'
      ? this.http.post(url, post_data)
      : this.http.get(`${url}${query ? '?' + query : ''}`);
    
      return request;
  }


  /**
   * Make put request for changes to the item with the given id
   * @param id ID of the item being updated
   * @param form_data New values for the item
   * @param query_params Map of query paramaters to add to the request URL
   */
  public update(id: string, form_data: HashMap, query_params: HashMap = {}): Promise<T> {
    const key = `update|${id}`;
    /* istanbul ignore else */
    if (!this._promises[key]) {
      this._promises[key] = new Promise<T>((resolve, reject) => {
        const query = toQueryString(this.injectConcierge(query_params));
        const url = `${this.route(query_params.engine)}/${id}${query ? '?' + query : ''}`;
        let result: T = null;
        this.http.put(url, this.injectConcierge(form_data)).subscribe(
          (d) => (result = this.process(d)),
          (e) => {
            reject(e);
            this.analyticsEvent(`update-${this._name.toLowerCase()}-failed`, id);
            this._promises[key] = null;
          },
          () => {
            resolve(result);
            this.set(
              'list',
              this.updateList(this.removeItem(this.get('list'), { id } as any), [
                result
              ])
            );
            this.analyticsEvent(`update-${this._name.toLowerCase()}-success`, id);
            this._promises[key] = null;
          }
        );
      });
    }
    return this._promises[key];
  }


  /**
   * update function version -2 - returns observable instead of promise...
   * @param id url id with respect to update api
   * @param form_data data to be update 
   * @param should_inject_concierge common value to be set to add concierge: true to the query url and body
   * @param query_params Map of query paramaters to add to the request URL
   */
   public updateObsr(id: string, form_data: HashMap, should_inject_concierge: Boolean = false, query_params: HashMap = {}):Observable<any>{
    const query = should_inject_concierge ? toQueryString(this.injectConcierge(query_params)) : false;
    const url = `${this.route(query_params.engine)}/${id}${query ? '?' + query : ''}`;
    const body = should_inject_concierge ? this.injectConcierge(form_data) : form_data;
    
    return this.http.put(url, body)
      .pipe(
          map((d: IEngineResponse | HashMap[]) =>  this.processApiResult(d) 
        ),
        catchError(<T>(error: any, result?: T) => {
          console.log(error);
          this.analyticsEvent(`update-${this._name.toLowerCase()}-failed`, id);
          return of(error as T);
        })
      );

  }

  /**
   * update function version -2 - returns observable instead of promise...
   * @param form_data data to be update 
   * @param should_inject_concierge common value to be set to add concierge: true to the query url and body
   * @param query_params Map of query paramaters to add to the request URL
   */
   public uploadSpacePhotos(apiSubRoute: string, form_data: HashMap, should_inject_concierge: Boolean = false, query_params: HashMap = {}):Observable<any>{
    const query = should_inject_concierge ? toQueryString(this.injectConcierge(query_params)) : false;
    const url = `${this.route(query_params.engine)}/${apiSubRoute}${query ? '?' + query : ''}`;
    const body = should_inject_concierge ? this.injectConcierge(form_data) : form_data;

    const token = sessionStorage.length ? JSON.parse(sessionStorage.getItem('OAUTH.params')).access_token : '';

    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
    });
    
  const requestOptions = { headers: headers };

    // return this.httpClient.post(url, body, {
    //       ...requestOptions 
    //   });

    return this.http.post(url, body);

  }

  /**
   * Make delete request for the given item
   * @param id ID of item
   */
  public delete(id: string, q: HashMap = {}): Promise<void> {
    const key = `delete|${id}`;
    /* istanbul ignore else */
    if (!this._promises[key]) {
      this._promises[key] = new Promise<void>((resolve, reject) => {
        const query = toQueryString(q);
        const url = `${this.route()}/${id}${query ? '?' + query : ''}`;
        this.http.delete(url).subscribe(
          (_) => null,
          (e) => {
            reject(e);
            this._promises[key] = null;
          },
          () => {
            this.set('list', this.removeItem(this.get('list'), { id } as any));
            this._promises[key] = null;
            resolve();
          }
        );
      });
    }
    return this._promises[key];
  }

  /**
   * Load initial data for the service
   */
  protected async load(): Promise<void> {
  }

  /**
   * Post analytics event for this service
   * @param action Name of the action to post
   */
  protected analyticsEvent(action: string, label?: string) {
    // if (this.parent && this.parent.Analytics) {
    //     this.parent.Analytics.track(this._name, { desc: `${this.parent.name.toLowerCase()}-${action}`, label });
    // }
  }

  /**
   * Convert raw API data into a valid API Object
   * @param raw_item Raw API data
   */
  protected process(raw_item: HashMap): T {
    return raw_item as T;
  }

  /**
   * Update recorded list of items
   * @param old_list Old list of items
   * @param list List of updated items
   * @param compareFn Function to compare items to remove duplicates
   */
  public updateList(
    old_list: T[],
    list: T[],
    compareFn: (a: T, b: T) => boolean = this._compare
  ): T[] {
    /* istanbul ignore else */
    if (!list || list.length === 0) {
      return old_list;
    }
    const new_list: T[] = [];
    const mixed_list = [...list, ...old_list];
    /* istanbul ignore else */
    if (!compareFn) {
      compareFn = this._compare;
    }
    for (const item of mixed_list) {
      const found = new_list.find((i) => compareFn(i, item));
      /* istanbul ignore else */
      if (!found) {
        new_list.push(item);
      }
    }
    return new_list;
  }

  /**
   * Remove the given item from the given list
   * @param list List of items
   * @param item Item to remove
   * @param compareFn Function to compare items
   */
  protected removeItem(list: T[], item: T, compareFn?: (a: T, b: T) => boolean) {
    const new_list = [];
    /* istanbul ignore else */
    if (!compareFn) {
      compareFn = this._compare;
    }
    list.forEach((i) => (compareFn(item, i) ? null : new_list.push(i)));
    return new_list;
  }
}
