import { 
    createLoadingAction
  } from '../../../loading/src/lib/loading.actions';
import {
    UserState,
    UserStoreState
} from './user.types';
import { User } from '../../../data-common/src/lib/users/user.class';
import { createSelector } from '@ngrx/store';

export const loadUser = createLoadingAction<UserState, User>('CurrentUser', 'current');

export const selectUserState = ({ user }: UserStoreState) => user;

export const selectHasUserData = createSelector(createSelector(selectUserState, loadUser.selectors.model), (state) => !!state.optionalSuccess);

export const selectCurrentUser = createSelector(selectUserState, loadUser.selectors.optionalSuccess);
