import {
    Component,
    forwardRef,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import {
    BehaviorSubject,
    combineLatest
} from 'rxjs';
import { SelectOption } from '@mckinsey-converge/ui';
import { map } from 'rxjs/operators';
import { MatSelect } from '@angular/material/select';
import {
    ControlValueAccessor,
    FormControl,
    NG_VALUE_ACCESSOR
} from '@angular/forms';

@Component({
    selector: 'mck-select',
    templateUrl: './select.component.html',
    styleUrls: ['./select.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => SelectComponent),
            multi: true
        }
    ],
    encapsulation: ViewEncapsulation.None
})
export class SelectComponent implements OnInit, OnChanges, ControlValueAccessor {

    @Input()
    public label: string;

    @Input()
    public required: boolean;

    @Input()
    public options: SelectOption<any>[] = [];

    @Input()
    public selectedValue: SelectOption<any> | undefined;

    /**
     * When true, can filter by searching on the input.
     */
    @Input()
    public searchEnabled = false;

    @Input()
    public splitDisplay = false;

    /** Whether form field is disabled */
    @Input() public disabled: boolean;

    @Input()
    public noEntriesFoundLabel: string;

    @Input()
    public showDivider: boolean = false

    /** Form control on change handler */
    private _onChange: (_: SelectOption<any>) => void;
    /** Form control on touch handler */
    private _onTouch: (_: SelectOption<any>) => void;

    public filterFormControl: FormControl = new FormControl();

    public value = new BehaviorSubject<string | undefined>(this.selectedValue || this.options[0]?.value);

    /** Available time blocks for the selected date */
    public _options = new BehaviorSubject<SelectOption<any>[]>(this.options);

    /** Select field for selecting the value */
    @ViewChild('select') private select_field: MatSelect;

    public selectedOption = combineLatest([this._options, this.value]).pipe(
        map(([options, value]) => options.find(o => o.value === value))
    );

    public placeHolderOption = this._options.pipe(
        map(os => os.length > 0 && os[0])
    );

    public isSelected(option: SelectOption<any>) {
        return this.value.pipe(map(t => t === option.value));
    }

    public get isOpen() {
        return this.select_field?.panelOpen ?? false;
    }


    constructor() {
    }

    public ngOnInit(): void {
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.options) {
            this._options.next(this.options);
            this.value.next(this.selectedValue || this.options?.[0]?.value);
        }
        if (changes.selectedValue) {
            this.value.next(this.selectedValue?.value);
        }
    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: string): void {
        this.value.next(new_value);
        if (this._onChange) {
            this._onChange(this.options.find(f => f.value === new_value));
        }
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: SelectOption<any> | null) {
        if (value != null) {
            this.value.next(value.value);
        }
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: SelectOption<any>) => void): void {
        this._onChange = fn;
    }

    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: SelectOption<any>) => void): void {
        this._onTouch = fn;
    }

}
