import {
  Actions,
  ofType
} from '@ngrx/effects';

import { 
  LoadingAction,
  LoadingActionObject
} from '../../../loading/src/lib/loading.actions';

import {
  catchError,
  flatMap,
  map
} from 'rxjs/operators';
import {
  Observable,
  of
} from 'rxjs';
import { TypedAction } from '@ngrx/store/src/models';

/**
 * This creates an effect to use in Effects classes. This coordinates a simple
 * success and error payload effect.
 * @param actions - The actions object
 * @param actionObject - The created createLoadingAction object.
 * @param caller - Define what API method or asynchronous operation is needed to
 * run this. Return TSuccess.
 */
export const createLoadingEffect = <TState, TSuccess = {}, TRequest extends object | undefined = {}>(
  actions: Actions, actionObject: LoadingActionObject<TState, TRequest, TSuccess>,
  caller: (action: LoadingAction<TRequest>) => Promise<TSuccess>): Observable<TypedAction<string>> => actions.pipe(
  ofType(actionObject.request),
  flatMap((action) => caller(action as LoadingAction<TRequest>)),
  map((response: TSuccess) => actionObject.success({ payload: response })),
  catchError(err => {
    return of(actionObject.error({ payload: err }));
  })
);
