import { CateringItem } from './catering-item.class';
import { HashMap } from '@mckinsey-converge/base';

import { CateringCategory } from './catering-category.class';
import { Booking } from '../bookings';

import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

export type CateringOrderMutableProperties =
  | 'items'
  | 'location_id'
  | 'location'
  | 'delivery_time'
  | 'charge_code'
  | 'notes'
  | 'status';

export type CateringOrderStatus = 'accepted' | 'preparing' | 'ready' | 'delivered' | 'cancelled';

const now = DateNow(new Date());

// R--- refactor booking date to be dateTz
export class CateringOrder {
  /**
   * Booking fields
   * 
   * The app handles booking data inconsisently.  Ususally these booking fields are provided
   * for related booking data, but on occasion the full Booking class is used.
   */

  /** Unique Booking ID for the catering order */
  readonly booking_id: string;
  /** 
   * Timestamp based booking time of the booking associated with the order
   * 
   * Must be provide as the true Booking startDateTz or as a new DateTZ with 
   * the correct date: timestamp, local_tz = false and bulding_tz values
   */
  readonly booking_date: number;
  /**
   * Timezone of the booking associated with the order
   */
  readonly booking_timezone: string;
  /** ID of the location to deliver the items to */
  location_id: string = '';
  /** Display text for the location to deliver the items to */
  location: string = '';
  /**
   * Smart timezone aware catering datetime with timezone
   */
  readonly bookingDateTz: DateTZ;

  /** 
   * Booking associated with the order 
   * 
   * The booking attached to the CateringOrder is a special case used 
   * mainly on the Concierge catering reports because someone created 
   * a bad pattern to save catering changes to the booking.  
   * 
   * Avoid when possible.
   */
  public booking: Booking = null;

  /**
   * Catering specific fields
   * 
   * The app handles booking data inconsisently.  Ususally these booking fields are provided
   * for related booking data, but on occasion the full Booking class is used.
   */

  /** Whether the order status change is loading */
  public loading: boolean;
  /** Unique ID for the catering order */
  readonly id: string;
  /** Number of minutes after the start of the associated meeting to delivery the order */
  readonly delivery_time: number = 0;
  /** Status of the catering order */
  readonly status: CateringOrderStatus;
  /** Whether items in the order need to be prepared in the kitchen */
  readonly kitchen: boolean;
  /** Whether items in the order are in the pantry */
  readonly pantry: boolean;
  /** List of items */
  readonly items: readonly (CateringItem | CateringCategory)[] = [];
  /** Creation time of the order */
  readonly created_at: number;
  /** Charge code associated with the order */
  readonly charge_code: string;
  /** Notes associated with the order */
  readonly notes: string;
  // /** Mapping of properties to their changes */
  // private _changes: HashMap = {};
  /** Map of local property names to server ones */
  protected _server_names: HashMap<string> = {};
  /** Currency code for the location of the order */
  public symbol: string;
  /** Whether the is an error with the order */
  public error: boolean;


  constructor(data: HashMap) {
    /** 
     * Setup Defaults when raw_data values are not provided
     */
    const nowTz = DateNow(new Date());
    // now rounded to the next 5 minute increment
    const defaultStartTz = nowTz.setValue({ minute: Math.ceil(nowTz.minutes / 5) * 5 });
    const defaultLocalTimezone = Intl?.DateTimeFormat()?.resolvedOptions()?.timeZone;

    this.id = data.id || `order-${Math.floor(Math.random() * 999_999_999)}`;

    // If the booking is provided default the booking values to the true booking values
    this.booking = data.booking || null;

    
    // Has a booking so init with those values
    if (this.hasBookingAttached) {
      this.booking_date = this.booking.startDateTz.ms;
      this.booking_timezone = this.booking.timezone;
      this.bookingDateTz = this.booking.startDateTz;
      this.location_id = data.location_id || '';
      this.location = data.location || '';
    } else {
      this.booking_date = data.booking_date || defaultStartTz.ms;
      this.booking_timezone = data.booking_timezone || defaultLocalTimezone;
      this.bookingDateTz = new DateTZ({ date: this.booking_date, is_local_tz: false, building_tz: this.booking_timezone });
      this.location_id = data.location_id || '';
      this.location = data.location || '';
    }
    
    this.delivery_time = typeof data.delivery_time === 'number' ? data.delivery_time : 0;
    this.status = data.status || 'accepted';
    this.charge_code = data.charge_code || data.code;
    this.notes = data.notes;
    this.kitchen = data.kitchen instanceof Array ? !!data.kitchen.length : data.kitchen;
    this.pantry = data.pantry instanceof Array ? !!data.pantry.length : data.pantry;
    this.symbol = data.symbol || 'USD';
    this.items = (data.items || [])
      .map(item => (item.items ? new CateringCategory(item) : new CateringItem(item)))
      .filter(item => !!item.amount);
  }

  /** Special Case, check if a full booking is attached */
  public get hasBookingAttached(): boolean {
    return !!this.booking;
  }

  /** Display string for the types of items in the order */
  public get type(): string {
    let type = '';
    /* istanbul ignore else */
    if (this.kitchen) type += 'Kitchen';
    /* istanbul ignore else */
    if (this.pantry) {
      /* istanbul ignore else */
      if (type) type += ' + ';
      type += 'Pantry';
    }
    return type;
  }

  /** Total cost of the order */
  public get total(): number {
    return this.items.reduce((total, item) => total + (item.total || 0), 0);
  }

  /** Total number of items in the order */
  public get item_count(): number {
    return this.items.reduce((total, item) => total + (item.amount || 0), 0);
  }

  public get deliver_date(): DateTZ {
    return (this.bookingDateTz || this.booking?.startDateTz).addValue({ minutes: this.delivery_time })
  }

  /** Display string for the delivery time of the order (short version) */
  public get deliver_at(): string {
    return this.deliver_date.formatDate('h:mm a');
  }

  /** Display string for the delivery time of the order */
  public get deliver_at_time(): string {
    return this.deliver_date.formatDate('h:mma').toLocaleLowerCase();
  }

  /** Display string for the delivery date of the order */
  public get deliver_on_date(): string {
    return this.deliver_date.formatDate('dd MMM yyyy');
  }

  /**
   * Make a copy of this object
   */
  public clone(): CateringOrder {
      return new CateringOrder(this.toJSON());
  }

  /**
   * Convert class object into plain object
   */
  public toJSON(this: CateringOrder): HashMap {
    const obj: any = { ...this };
    // Remove local private members
    delete obj._server_names;
    // Remove local public members
    delete obj.booking;
    delete obj.loading;
    delete obj.bookingDateTz;

    // Convert remaining members to be public
    const keys = Object.keys(obj);
    for (const key of keys) {
      if (key[0] === '_') {
        const new_key = this._server_names[key.substr(1)] || key.substr(1);
        obj[new_key] = obj[key];
        delete obj[key];
      } else if (obj[key] === undefined) {
        delete obj[key];
      }
    }
    obj.items = obj.items.map((item: CateringItem) => item.toJSON());

    return obj;
  }
}
