import {
    Component,
    EventEmitter,
    Inject,
    OnInit,
    Output
} from '@angular/core';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';
import {
    FormControl,
    FormGroup
} from '@angular/forms';

import {
    ApplicationService,
    CateringItem,
    OrganisationService,
    Space,
    SpacesService
} from '@mckinsey-converge/data-common';
import {
    BaseDirective,
    DialogEvent
} from '@mckinsey-converge/base';
import { DateTZ } from '@mckinsey-converge/date-tz';


export interface CateringConfirmModalData {
    all_day: boolean;
    form: FormGroup;
    dateTz: DateTZ;
}

@Component({
    selector: 'a-catering-confirm-modal',
    templateUrl: './catering-confirm-modal.component.html',
    styleUrls: ['./catering-confirm-modal.component.scss']
})
export class BookingCateringConfirmModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user actions on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** List of selected items for the order */
    public item_list: CateringItem[] = [];

    /** Space selected for delivery */
    public get space(): Space {
        return this._spaces.find(this._data.form?.controls?.location_id?.value);
    }

    /** Delivery time for the order */
    public get delivery_time(): string {
        let date = this._data.dateTz;
        if (this._data.form && this._data.form.controls.delivery_time) {
            date = date.addValue({ minutes: this._data.form.controls.delivery_time.value });
        }
        return date.formatDate('h:mma').toLocaleLowerCase();
    }

    /** Form field for order items */
    public get item_field(): FormControl {
        return this._data.form.controls.items as any;
    }

    /** Total cost of the order */
    public get order_cost(): number {
        return this.item_list.reduce((amount, item) => {
            const price = item.price
            return amount + item.amount * price
        }, 0);
    }

    /** Currency symbol for the selected space */
    public get symbol(): string {
        /* istanbul ignore else */
        if (!this.space || !this.space.level) {
            return 'USD';
        }
        const building = this._org.buildings.find(
            (bld) => bld.id === this.space.level.building_id
        ) || { currency: 'USD' };
        return building.currency;
    }

    constructor(
        private _service: ApplicationService,
        private _org: OrganisationService,
        private _spaces: SpacesService,
        private _dialog_ref: MatDialogRef<BookingCateringConfirmModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: CateringConfirmModalData
    ) {
        super();
        if (!this._data.form) {
            this._service.notifyError('No form data for order');
            this._dialog_ref.close();
        }
    }

    public ngOnInit(): void {
        /* istanbul ignore else */
        if (this._data.form.controls.items) {
            this.subscription(
                'item_list',
                this._data.form.controls.items.valueChanges.subscribe((items) => {
                    /* istanbul ignore else */
                    if (!items || items.length <= 0) {
                        this._dialog_ref.close();
                    }
                })
            );
        }
        this.item_list = this._data.form.controls.items.value.map((item) => new CateringItem(item.toJSON()));
    }
}
