import {
    Component,
    Input,
    OnInit,
    ChangeDetectionStrategy,
    ViewEncapsulation,
    ChangeDetectorRef,
} from '@angular/core';
import { Router } from '@angular/router';
import {
    DEFAULT_BOOKING_DURATION,
    rulesForSpace,
    SpacesService,
    User,
} from '@mckinsey-converge/data-common';
import {
    OrganisationService,
    ApplicationService,
    BuildingLevel,
} from '@mckinsey-converge/data-common';
import { DateNow, DateTZ, toMiddayUTC } from '@mckinsey-converge/date-tz';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { first, take } from 'rxjs/operators';
import { Identity, BaseDirective, closestToTimeSlotTz } from '@mckinsey-converge/base';
import { Store } from '@ngrx/store';
import { selectCurrentUser, UserStoreState } from '@mckinsey-converge/user';
import { RoomAdminService } from './services/room-admin.service';
import jsPDF, { RGBAData } from 'jspdf';
import { Hash } from 'crypto';

@Component({
    selector: 'app-room-admin',
    templateUrl: './room-admin.component.html',
    styleUrls: ['./room-admin.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RoomAdminComponent extends BaseDirective implements OnInit {
    public spaces: Space[] = [];
    public newArr: any[];
    public location: string;
    public isOpen = false;

    public itemsCheckboxes = [
        {
            label: '',
            checked: false,
            id: '',
            building_id: '',
            name: '',
            email: '',
            roomname: '',
            isLevel: false,
        },
    ];

    public qrCodeUrls = [];

    public selectAll: boolean = false;

    public allLevels: any; //need this for populating levels in bulk-qr-overlay component

    /** DateTZ object of the active date */
    @Input() public dateTz: DateTZ = DateNow(new Date());

    /** ID of the active level */
    @Input() public level: string;

    /** List of levels available for the active building */
    public levels: BuildingLevel[] = [];

    /** List of space types available for the active building */
    public space_types: Identity[] = [];

    /** ID of the currently selected level */
    public active_level = '';

    /** List of legend keys to show */
    public legend_list: string[] = [];

    /** List of available legend keys */
    public readonly legend_keys: readonly Identity[] = [
        { name: 'Internal', id: 'internal', color: '#E69FC7', active: true },
        { name: 'Client', id: 'client', color: '#1F40E6', active: true },
        { name: 'External', id: 'external', color: '#8C5AC8', active: true },
        { name: 'Setup', id: 'setup', color: '#d32f2f', active: true },
        { name: 'Training', id: 'training', color: '#F0E700', active: true },
        { name: 'Interview', id: 'interview', color: '#348D02', active: true },
        { name: 'Declined', id: 'declined', color: '#617288', active: true },
    ];

    public detailsViewIndex = 0;

    public rowInfo = [];

    public has_access: boolean;
    currentUser: User;
    public enableOverlay = false;
    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _org: OrganisationService,
        private _router: Router,
        private _booking_state_service: BookingStateService,
        private store: Store,
        private cdref: ChangeDetectorRef,
        private roomAdminService: RoomAdminService
    ) {
        super();
    }

    public eventFromChild(data: number) {
        this.detailsViewIndex = data;
    }

    public passRowToDetailsView(data: []) {
        this.rowInfo = data;
    }

    public returnToRoomAdmin(data: number) {
        this.detailsViewIndex = data;
        this.updatePayload();
        this.cdref.detectChanges();
    }

    public ngOnInit(): void {
        this._service.title = 'Room Administration';

        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._org.initialised.pipe(first((_) => _)).subscribe(() => {
                // Init dateTz here once the organization data is loaded.
                this.dateTz = new DateTZ({
                    date: this.dateTz.ms,
                    is_local_tz: false,
                    building_tz: this.timezone,
                });
                this.legend_list = this.legend_keys.map((item) => `${item.id}`);
                this.subscription(
                    'building',
                    this._org.listen('active_building').subscribe(() => {
                        // Update dateTz here when the building is changed.
                        this.dateTz = new DateTZ({
                            date: this.dateTz.ms,
                            is_local_tz: false,
                            building_tz: this.timezone,
                        });
                        const building = this._org.building;
                        this.has_access = building ? true : false;
                        if (!this.has_access) {
                            return;
                        }

                        /* istanbul ignore else */
                        if (!building.levels.find((lvl) => lvl.id === this.active_level)) {
                            this.active_level = building?.levels[0]?.id;
                            this.updateLevel();
                        }
                        this.updateLevel();
                        this.levels = [{ id: '', name: 'All Levels' } as any].concat(
                            building.levels
                        );
                        this.levels.sort((a, b) =>
                            b.name.localeCompare(a.name, undefined, {
                                numeric: true,
                                ignorePunctuation: true,
                            })
                        );
                        this.space_types = [{ id: '', name: 'All Space Types' } as any].concat(
                            this._org.space_types
                        );
                    })
                );
            });
        });
    }

    public updateLevel() {
        this._booking_state_service.setZone(this.active_level || this._org.building.id);
        this.enableOverlay = true;
        this._spaces.queryRooms({zone_ids: this._org.building.id }).subscribe(
            (res) => {
                const list = res.map((i) => this._spaces.process(i));
                this._router.navigate(['/room-admin']);
                this.detailsViewIndex = 0;
                this.updatePayload([...list]);
            });
    }

    public updatePayload(spaces?) {
        const zone_id = !this.level ? this._org.building.id : this.level;
        /** TO DO : remove _space.filter use  */
        this.spaces =  spaces ? [...spaces] : this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0);
        this.location = this._org.building.name;
        const roomDataSource = [];
        for (let i = 0; i < this.spaces.length; i++) {
            if (this.spaces[i].map_id) {

                roomDataSource.push({
                    roomname: this.spaces[i].local_name,
                    roomnum: this.spaces[i].map_id,
                    level: this.spaces[i].level.short_name,
                    capacity: this.spaces[i].capacity,
                    roomtype: this.spaces[i].internal_or_external,
                    bookable: this.spaces[i].bookable,
                    settings: this.spaces[i].settings,
                    equipment: this.spaces[i]?.equipment,
                    room_style: this.spaces[i]?.room_style,
                    email: this.spaces[i].email,
                    roomobject: this.spaces[i],
                    id: this.spaces[i].id,
                    building: this.spaces[i].building,
                });
            }
        }

        this.newArr = [...roomDataSource]; // to make sure tabe is up to date

        let checkedFlag = { checked: false };
        this.itemsCheckboxes = this.newArr.map((item) => {
            return { ...item, ...checkedFlag };
        });
        this.allLevels = this._org.building.levels;
        this.enableOverlay = false;
        this.cdref.detectChanges();
    }

    public get timezone() {
        return this._org.building?.timezone || localStorage.getItem('CONCIERGE.timezone');
    }

    public bg_click_close() {
        this.isOpen = false;
        this.qrCodeUrls = [];
        this.itemsCheckboxes.forEach((checkbox) => (checkbox.checked = false));
        this.cdref.detectChanges();
    }

    public onQrCodeUrlsChange(newUrls: any[]) {
        this.qrCodeUrls = newUrls;
    }

    public downloadQrCode() {
        /**
         * Use the same array of selected rooms/levels for qr code to iterate
         */
        this.roomAdminService.doc = new jsPDF();
        let isRoom: boolean;
        this.qrCodeUrls.forEach((item, i) => {
            // Get the generated QR code img and modifiy as per design
            const qrcode = document.querySelector(`.qr-code${i} img`);
            let imageData: any = null;
            if (item.roomname) {
                isRoom = true;
                imageData = this.roomAdminService.drawCanvas(qrcode, item.roomname, false, item.level); // roomname should be room name for rooms or [Level <no>] for levels
            } else if (item.name) {
                isRoom = false;
                imageData = this.roomAdminService.drawCanvas(qrcode, item.name, true, ''); // roomname should be room name for rooms or [Level <no>] for levels
            }
            // Add generated qr code image to the pdf
            switch ((i + 1) % 4) {
                case 1:
                    this.roomAdminService.doc.addImage(imageData, 'JPEG', 3, 5, 100, 100);
                    break;
                case 2:
                    this.roomAdminService.doc.addImage(imageData, 'JPEG', 107, 5, 100, 100);
                    break;
                case 3:
                    this.roomAdminService.doc.addImage(imageData, 'JPEG', 3, 130, 100, 100);
                    break;
                case 0:
                    this.roomAdminService.doc.addImage(imageData, 'JPEG', 107, 130, 100, 100);
                    this.roomAdminService.doc.addPage();
                    break;
            }
        });
        this.roomAdminService.doc.save(`${this.location} - ${isRoom ? `rooms.pdf` : `levels.pdf`}`);
    }
}
