import {
    Pipe,
    PipeTransform
} from '@angular/core';

/**
 * Transforms input by returning a subset of items based on if the
 * any items include the search text based on passed objectKey.
 *
 * objectKey can be pipe separated keys. e.g: 'name|age'. Then it will check against all.
 */
@Pipe({
    name: 'filter'
})
export class FilterPipe implements PipeTransform {
    transform(items: any[], searchText: string, objectKey?: string): any[] {
        if (!items) return [];
        if (!searchText) return items;
        searchText = searchText.toLowerCase();
        const splitKeys = objectKey?.split('|') ?? [];
        return items.filter(it => {
            let included = false;
            splitKeys.forEach((key) => {
                if (it[key]?.toLowerCase().includes(searchText)) {
                    included = true;
                }
            });
            if (splitKeys.length === 0) {
                return it.toLowerCase().includes(searchText);
            }
            return included;
        });
    }
}
