
import { IBookingQueryOptions, PaginatedBooking } from '@mckinsey-converge/data-common';
import { 
    createLoadingAction
} from '../../../loading/src/lib/loading.actions';

import {
    MyBookingsState,
    MyBookingsStoreState,
} from './bookings.types';
import {
    createAction,
    props
} from '@ngrx/store';

export const bookingStateSelector = (state: MyBookingsStoreState) => state.bookings;

export const loadBookingsWithQuery = createAction('[Bookings] Load Bookings with query', props<IBookingQueryOptions>());

export const loadHomepageBookingsResults = createLoadingAction<MyBookingsState, PaginatedBooking, IBookingQueryOptions>('HomepageBookings', 'homepage');

export const loadUpcomingBookingsResults = createLoadingAction<MyBookingsState, PaginatedBooking, IBookingQueryOptions>('UpcomingBookings', 'upcoming');

export const loadPastBookingsResults = createLoadingAction<MyBookingsState, PaginatedBooking, IBookingQueryOptions>('PastBookings', 'past');

export const loadCancelledBookingsResults = createLoadingAction<MyBookingsState, PaginatedBooking, IBookingQueryOptions>('CancelledBookings', 'cancelled');

export const loadBookingByIdResults = createLoadingAction<MyBookingsState, PaginatedBooking, IBookingQueryOptions>('BookingById', 'bookingById');

export const clearBookingByIdResults = createAction('[BookingById] Clear Booking Data');
