export const roomCapacityToValue = (roomSize: string): number => {
    switch (roomSize) {
        case '1':
            return 1;
        case '2':
            return 3;
        case '3':
            return 10;
        case '4':
            return null;
    }
};

export const roomMaxCapacityToValue = (roomSize: string): number => {
    switch (roomSize) {
        case '1':
            return 2;
        case '2':
            return 9;
        default:
            return null;
    }
};

export interface BookingFormData {
    // R--- off the rails, define these "any's"
    id?: string;
    location: SelectOption<string>[];
    dateTz: DateTZ;
    /**
     * Duration, in minutes.
     */
    duration: number;
    bookingType: SelectOption<any>;
    roomSize: SelectOption<any>;
    selectedRoom?: any;
    organiser?: SelectOption<string>;
    title?: string;
    attendees?: any[];
    code?: string;
    notes?: string;
    head_count?: number;
    creator?: User;
    company?: any[];
    //Recurrence fields
    recurrence_period?: RecurrencePeriod;
    recurrence_interval?: number;
    recurrence_endTz?: DateTZ;
    recurrence_count?: number;
    recurrence_starts?: Array<DateTZ>;
    recurrence_exceptions?: Array<number>; //SHOULD BE DATETZ
    recurrence_days?: Array<DaysOfWeek>;
    timezone?: string;
    offset?: number;
    buildings?: Building[];
    is_recurrent?: boolean;
    is_multiroom?: boolean;
    action?: string; // helping to set staff app recurring action flag for edit/clone in the active form
    ignore?: string; // helping while editing - recurring series rooms search api call
    bookable?: boolean; // set while editing - recurring series rooms search api call
    occurrence_edits?: string[];
    opt_out?:boolean
}

/**
 * Converts form data into API query parameters.
 */
export const roomFormDataToQuery = (
    data: BookingFormData
): RoomQueryOptions => {
    // remove all buildings chip from query
    const filteredLocationIds = data?.location
        .filter((l) => l.groupChild)
        .map((l) => l.value)
        .join(',');
    return {
        dateTz: data?.dateTz,
        duration: data?.duration,
        locations: filteredLocationIds,
        capacity: roomCapacityToValue(data?.roomSize?.value),
        capacity_max: roomMaxCapacityToValue(data?.roomSize?.value),

        is_recurrent: data?.is_recurrent,
        is_multiroom: data?.is_multiroom,
        recurrence_period: data?.recurrence_period,
        recurrence_interval: data?.recurrence_interval,
        recurrence_endTz: data?.recurrence_endTz,
        recurrence_count: data?.recurrence_count,
        recurrence_starts: data?.recurrence_starts,
        recurrence_exceptions: data?.recurrence_exceptions, //SHOULD BE DATETZ
        recurrence_days: data?.recurrence_days,
        timezone: data?.timezone,
        offset: data?.offset,
        buildings: data?.buildings,
        bookable: data?.bookable,
        ignore: data?.ignore
    };
};
