import {
    Inject,
    Injectable
} from '@angular/core';
import { Title } from '@angular/platform-browser';
import {
    BehaviorSubject,
    Observable
} from 'rxjs';

import { VERSION } from '@mckinsey-converge/environment';
import {
    BaseClass,
    getItemWithKeys,
    HashMap,
    log
} from '@mckinsey-converge/base';
import { ServiceManager } from './service-manager.class';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

declare global {
    interface Window {
        debug: boolean;
    }
}

export const SETTINGS_TOKEN = 'settings_service_data';

type SettingsAppData<T> = T & {
    title: string;
    description: string;
    short_name: string;
    logo_light: {
        type: string;
        src: string;
        background: string;
    }
    logo_dark: {
        type: string;
        src: string;
        background: string;
    }
    heap_io: {
        app_id: number;
        force_ssl: boolean;
        secure_cookie: boolean;
        disable_text_capture: boolean;
        cookie_path: string;
    }
}

export interface SettingsData<TApp> {
    debug: boolean;
    mock: boolean;
    frontend: 'staff' | 'concierge' | 'booking';
    composer: {
        domain: string;
        route: string;
        protocol: string;
        port: string;
        use_domain: boolean;
        local_login: boolean;
    }
    app: SettingsAppData<TApp>;
}

export class SettingsObject {
    constructor(public data: SettingsData<any>) {
    }
}

@Injectable({
    providedIn: 'root'
})
export class SettingsService extends BaseClass {
    /** Name of the application */
    private _app_name = 'Converge';
    /** List of override settings in order of priority */
    private _overrides = new BehaviorSubject<HashMap[]>([]);
    /** Mapping of behaviour subjects */
    private _subjects: HashMap<BehaviorSubject<any>> = {};
    /** Mapping of observables */
    private _observables: HashMap<Observable<any>> = {};
    /** Which frontend we're in. staff, concierge, or bookings **/
    public readonly frontend: string;
    private _bypass_bookings: boolean = false;

    /**
     * @hidden
     */
    public set overrides(value: HashMap[]) {
        this._overrides.next(value);
    }

    /** Get observable for key */
    public listen<T = any>(name: string): Observable<T> {
        if (!this._observables[name]) {
            this._subjects[name] = new BehaviorSubject<T>(null);
            this._observables[name] = this._subjects[name].asObservable();
        }
        return this._observables[name];
    }

    /** Update observable value for key */
    public post<T>(name: string, value: T): void {
        if (!this._observables[name]) {
            this._subjects[name] = new BehaviorSubject<T>(null);
            this._observables[name] = this._subjects[name].asObservable();
        }
        this._subjects[name].next(value);
    }

    public value<T = any>(name: string): T {
        return !this._observables[name] ? null : this._subjects[name].getValue();
    }

    /** Page title */
    public get title() {
        return this._title.getTitle();
    }

    public set title(value: string) {
        this._title.setTitle(`${value} | ${this._app_name}`);
    }

    constructor(private _title: Title,
                @Inject(SETTINGS_TOKEN)
                private settings: SettingsObject) {
        super();
        ServiceManager.setService(SettingsService, this);
        const time = new DateTZ({date: VERSION.time});
        const built = DateNow(new Date()).isSameDate(time, 'day')
            ? `Today at ${time.formatDate('h:mma')}`
            : time.formatDate('do MMM yyyy, h:mma');
        const frontend = this.get('frontend') || 'Unknown';
        this.frontend = frontend;
        log('CORE', `${VERSION.semver}`, null, 'debug', true, frontend?.toUpperCase());
        log('APP', `${VERSION.hash} | Built: ${built}`, null, 'debug', true, frontend?.toUpperCase());
        this.init();
    }

    /**
     * Initialise the settings
     */
    public async init() {
        if (this.get('debug')) {
            window.debug = true;
        }
        if (this.get('app')?.name) {
            this._app_name = this.get('app').name;
        }
        log('Settings', 'Successfully loaded settings');
        this._initialised.next(true);
    }

    /** Whether settings service has initialised */
    public get app_name() {
        return this._app_name;
    }

    public get concierge() {
        return this.frontend === 'concierge';
    }

    public set bypass_bookings(value: boolean) {
        this._bypass_bookings = value;
    }

    public get bypass_bookings() {
       return this._bypass_bookings;
    }

    /**
     * Get a setting
     * @param key Name of the setting. i.e. nested items can be grabbed using `.` to seperate key names
     */
    public get(key: string): any {
        const keys = key.split('.');
        if (keys[0] !== 'app') {
            return getItemWithKeys(keys, this.settings.data);
        }
        const override_settings = this._overrides.getValue();
        for (const override of override_settings) {
            const value = getItemWithKeys(keys.slice(1), override);
            if (value != null) {
                return value;
            }
        }
        return getItemWithKeys(keys, this.settings.data);
    }
}
