import {
    Component,
    EventEmitter,
    forwardRef,
    Input,
    Output,
    ViewChild,
    OnInit
} from '@angular/core';
import {
    ControlValueAccessor,
    NG_VALUE_ACCESSOR
} from '@angular/forms';

import { BaseDirective } from '@mckinsey-converge/base';

import { MatMenuTrigger } from '@angular/material/menu';
import {
    BehaviorSubject,
    Observable
} from 'rxjs';
import { map } from 'rxjs/operators';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { AcaDatePickerComponent } from '../aca-date-picker/aca-date-picker.component';

@Component({
    selector: 'mck-date-picker',
    template: `
        <div
            [class.icon-only]="icon_only"
            [class.mck-form-input]="!icon_only"
            [class.mck-column]="!icon_only"
        >
            <mck-label [required]="true" *ngIf="!icon_only">Date</mck-label>
            <div
                [class.mck-date-display]="!icon_only"
                [class.mck-input]="!icon_only"
                [class.date-error]="invalid"
                role="button"
                form-field
                tabindex="0"
                [class.active]="is_open"
                [attr.disabled]="disabled"
                (keydown.enter)="openDatePicker()"
                [matMenuTriggerFor]="appMenu"
                
            >
                <div class="display" *ngIf="!icon_only">{{ dateString | async }}</div>
                <div class="icon">
                    <mck-calendar-icon
                        [fillClass]="is_open ? 'mck-icon-fill-open' : 'mck-icon-fill-black'"></mck-calendar-icon>
                </div>
            </div>
            
            <mat-menu #appMenu="matMenu" xPosition="before" (close)="onDatePickerClose()">
                <div mat-menu-item class="date-picker mck-column" (click)="$event.stopPropagation()">
                    <date-tz-calendar-widget
                        klass="mck-date-picker"
                        [ngModel]="date | async"
                        [options]="{ from: from, to: until, day_format: 'EEE' }"
                        (ngModelChange)="setValue($event); closeDatePicker()"
                    ></date-tz-calendar-widget>
                    <button
                        mat-stroked-button
                        color="primary"
                        class="mck-stroked-button today-button"
                        (click)="goToToday()"
                    >Today
                    </button>
                </div>
            </mat-menu>
        </div>
    `,
    styleUrls: ['./mck-date-picker.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => MckDatePickerComponent),
            multi: true
        }
    ]
})
export class MckDatePickerComponent extends BaseDirective implements OnInit, ControlValueAccessor {
    /** Earliest date available the user is allowed to pick */
    @Input('from') public _from: number = DateNow(new Date()).ms;
    /** Latest date available the user is allowed to pick */
    @Input('to') public _to: number;
    /** Position of the tooltip */
    @Input() public position: 'right' | 'left' = 'right';
    /** Offset of the tooltip */
    @Input() public offset: 'top' | 'bottom' = 'bottom';
    /** Whether form control is disabled */
    @Input() public disabled: boolean;
    /** Whether to show the calendar tooltip */
    @Input() public icon_only: boolean = false;

    @Input() public initialDateSelection?: number;

    @Input() public invalid?: boolean;

    @Output() public onSelect = new EventEmitter();

    // R--- or is this the date picker to refactor. too tired.
    public ngOnInit(): void {
        if (this.initialDateSelection) {
            this.writeValue(this.initialDateSelection)
        }
    }

    /** Whether to show the calendar tooltip */
    public show_tooltip: boolean;
    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;

    @ViewChild(MatMenuTrigger, { static: true }) private _trigger: MatMenuTrigger;
    @ViewChild(AcaDatePickerComponent, {static: true }) private _acaDatePicker: AcaDatePickerComponent;

    /** Currently selected date */
    public dateValue = new BehaviorSubject<DateTZ>(DateNow(new Date()));

    /** First allowed date on the calendar */
    public get from(): number {
        return this._from || DateNow(new Date()).startOfValue('day').ms;
    }

    /** Current date value */
    public get until(): number {
        return this._to || DateNow(new Date()).endOfValue('day').addValue({ years: 1 }).ms;
    }

    /** Display value for the current date */
    public get dateString(): Observable<string> {
        return this.dateValue.pipe(map(d => {
            const display = d.formatDate('ccc dd MMM yyyy');
            return `${display}${d.isSameDate(DateNow(new Date()), 'day') ? ' (Today)' : ''}`;
        }));
    }

    /** Whether date picker is open */
    public get is_open(): boolean {
        return this._trigger.menuOpen;
    }

    public get date(): Observable<DateTZ> {
        return this.dateValue;

    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: number) {
        // Keep hours and minutes of the old date
        const old_date = this.dateValue.value;
        const d = new DateTZ({date: new_value})
        let new_date = d
            .setValue({ hour: old_date.dateHour, minute: old_date.minutes })
            .ms;
        // Check that new date is before from
        if (new_date < this.from) {
            new_date = this.from;
        }
        const new_d = new DateTZ({ date: new_date })
        this.dateValue.next(new_d);
        if (this._onChange) {
            this._onChange(new_date);
        }
        this.onSelect.emit(new_date);
        this.show_tooltip = false;
    }

    public goToToday() {
        this.setValue(DateNow(new Date()).ms);
    }

    public resetToLastSelection = () => this._acaDatePicker.updateDate();

    public onDatePickerClose() {
        this.resetToLastSelection();
    }

    /* istanbul ignore next */
    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: number) {
        const date = new DateTZ({date: value})
        this.dateValue.next(date);
        this.show_tooltip = false;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /** Set the state of the tooltip to closed after delay */
    public closeDatePicker() {
        this._trigger.closeMenu();
    }

    /** Set the state of the tooltip to closed after delay */
    public openDatePicker() {
        this._trigger.openMenu();
    }
}
