/** Available console output streams. */
export type ConsoleStream = 'debug' | 'warn' | 'log' | 'error' | 'info';

/**
 * Log data to the browser console
 * @param type Type of message
 * @param msg Message body
 * @param args array of argments to log to the console
 * @param stream Stream to emit the console on. 'debug', 'log', 'warn' or 'error'
 * @param force Whether to force message to be emitted when debug is disabled
 */
export function log(
    type: string,
    msg: string,
    args?: any,
    stream: ConsoleStream = 'debug',
    force: boolean = false,
    app_name: string = 'STAFF'
) {
    if ((window as any).debug || force) {
        const colors: string[] = [
            'color: #E91E63',
            'color: #3F51B5',
            'color: default',
        ];
        if (args) {
            console[stream](
                `%c[${app_name}]%c[${type}] %c${msg}`,
                ...colors,
                args
            );
        } else {
            console[stream](`%c[${app_name}]%c[${type}] %c${msg}`, ...colors);
        }
    }
}

/**
 * Get item from the nested object
 * @param keys List of sub-keys to search for
 * @param map Object to search
 */
 export function getItemWithKeys(keys: string[], map: HashMap) {
    const key = keys[0];
    if (map && key in map) {
        return keys.length > 1
            ? getItemWithKeys(keys.slice(1), map[key] || {})
            : map[key];
    }
    return null;
}

/* istanbul ignore next */
/**
 * Checks whether the platform is a mobile device.
 */
export function isMobileDevice(): boolean {
    const r = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i;
    return !!navigator.userAgent.match(r);
}

/* istanbul ignore next */
/**
 * Checks whether the browser is Mobile Safari.
 */
export function isMobileSafari(): boolean {
    const agent = navigator.userAgent;
    return !!(
        agent.match(/iPhone|iPad|iPod/) &&
        agent.match(/AppleWebKit/) &&
        !agent.match('CriOS')
    );
}

/* istanbul ignore next */
/**
 * Checks whether the browser is Android Chrome.
 */
export function isAndroidChrome(): boolean {
    const agent = navigator.userAgent;
    return !!(agent.match(/Android/) && agent.match(/Chrome/));
}

/**
 * Generate string representation of a number with zeros padding the length
 * @param value Number to pad with zeros
 * @param length Length of the resulting string
 */
export function padZero(value: number, length: number): string {
    let str = value.toString();
    while (str.length < length) {
        str = '0' + str;
    }
    return str;
}

/**
 * Remove duplicates from the given array
 * @param array List of items to remove duplicates from
 * @param key Key on array objects to compare for uniqueness
 */
export function unique<T>(array: T[], key: keyof T | undefined = undefined) {
    return array.filter(
        (el, pos, arr) =>
            el &&
            arr.indexOf(
                key
                    ? arr.find((i) => i && i[key] === el[key])
                    : arr.find((i) => i === el)
            ) === pos
    );
}

/**
 * Convert duration to human readable string
 * @param duration Duration in minutes
 * @param short Whether to use short form of duration words e.g. hours as h
 */
export function humaniseDuration(durationInput: number, size: string = 'long') {
    const duration = Math.floor(durationInput);
    if (!duration || duration < 0) {
        return '';
    }
    let singular = false;
    let format = { hours: ' hour', minutes: ' minute' };
    switch (size) {
        case 'medium':
            format = { hours: 'hr', minutes: 'min' };
            break;
        case 'short':
            format = { hours: 'h', minutes: 'm' };
            singular = true;
            break;
    }
    const h = Math.floor(duration / 60);
    let d = `${
        h >= 1
            ? h +
              (singular ? format.hours : format.hours + (h === 1 ? '' : 's'))
            : ''
    }`;
    if (duration % 60 !== 0) {
        if (d) {
            d += singular ? ' ' : ', ';
        }
        const m = duration % 60;
        d += `${
            m >= 1
                ? m +
                  (singular
                      ? format.minutes
                      : format.minutes + (m === 1 ? '' : 's'))
                : ''
        }`;
    }
    return d;
}

/**
 * Get a filtered list of items
 * @param filter Value to filter on
 * @param items List of results to filter
 * @param fields Fields to check for matches on each item
 */
export function filterList<T = HashMap>(
    filter: string,
    items?: T[],
    fields: string[] = ['id']
): T[] {
    let results: any[];
    // Tokenise filter string
    const filters = (filter || '').toLowerCase().split(' ');
    const list = {};
    for (const f of filters) {
        /* istanbul ignore else */
        if (f) {
            /* istanbul ignore else */
            if (!list[f]) {
                list[f] = 0;
            }
            list[f]++;
        }
    }
    // Group similar tokens
    const parts = [];
    for (const f in list) {
        /* istanbul ignore else */
        if (list.hasOwnProperty(f)) {
            parts.push({ word: f, count: list[f], regex: new RegExp(f, 'gi') });
        }
    }
    parts.sort(
        (a, b) => b.word.length - a.word.length || a.word.localeCompare(b.word)
    );
    const item_list = JSON.parse(JSON.stringify(items || []));
    /* istanbul ignore else */
    if (filter) {
        results = item_list.filter((item) => {
            let match_count = 0;
            item.match_index = 65535;
            item.match = '';
            const field_list = {};
            // Initialise field match variables
            for (const f of fields) {
                field_list[f] = {
                    value: (item[f] || '').toLowerCase(),
                    index: 65536,
                    matched: 0,
                };
            }
            // Search for matches with the tokenised filter string
            for (const i of parts) {
                /* istanbul ignore else */
                if (i.word) {
                    // Check fields for matches
                    for (const f of fields) {
                        const field = field_list[f];
                        const index = field.value.indexOf(i.word);
                        field.index = index < field.index ? index : field.index;
                        field.matches = (
                            field.value.match(i.regex) || []
                        ).length;
                        field.value = field.value.replace(i.regex, ' ');
                    }
                    // Update token match count
                    for (const f of fields) {
                        const field = field_list[f];
                        /* istanbul ignore else */
                        if (field.matches >= i.count) {
                            match_count++;
                            // Update field matches
                            let changed = 0;
                            const tokens = (
                                item[`match_${f}`] ||
                                item[f] ||
                                ''
                            ).split(' ');
                            for (const k of tokens) {
                                /* istanbul ignore else */
                                if (changed >= i.count) {
                                    break;
                                }
                                /* istanbul ignore else */
                                if (
                                    k.toLowerCase().indexOf(i.word) >= 0 &&
                                    k.indexOf('`') < 0
                                ) {
                                    tokens[tokens.indexOf(k)] = k.replace(
                                        i.regex,
                                        '`$&`'
                                    );
                                    changed++;
                                }
                            }
                            item[`match_${f}`] = tokens.join(' ');
                            break;
                        }
                    }
                }
            }
            // Get field with the most relevent match
            for (const f of fields) {
                const field = field_list[f];
                /* istanbul ignore else */
                if (field.index < item.match_index && field.index >= 0) {
                    item.match_index = field.index;
                    item.match = f;
                }
            }
            return (
                item.match_index >= 0 &&
                item.match &&
                match_count >= parts.length
            );
        });
    } else {
        results = item_list;
    }
    // Sort by order of relevence then name
    results.sort((a, b) => {
        const diff = a.match_index - b.match_index;
        return diff === 0 ? a.name.localeCompare(b.name) : diff;
    });
    return results;
}

/**
 * Convert a match string from `filterList` to renderable HTML
 * @param str Match string to change
 */
export function matchToHighlight(str: string): string {
    /* istanbul ignore else */
    if (str) {
        str = str.replace(
            /\`[a-zA-Z0-9\@\.\_]*\`/g,
            '<span class="highlight">$&</span>'
        );
        str = str.replace(/\`/g, '');
    }
    return str;
}

/**
 * Convert time string to ms from UTC epoch for today
 * @param time Time string in the format `HH:mm`
 */
export function timeToDate(time: string): number {
    const parts = time.split(':');
    const date = DateNow(new Date())
        .setValue({
            hour: +parts[0],
            minute: +parts[1],
        })
        .startOfValue('minute');
    return date.ms;
}

/**
 * Generate a random number
 * @param ceil Biggest value to generate not inclusive
 * @param floor Smallest value to generate. Defaults to 0
 */
export function randomInt(ceil: number, floor: number = 0) {
    return Math.floor(Math.random() * (ceil - floor)) + floor;
}

/**
 * Get time format string for locale
 * @param isLowerCaseAmPm - if true, we use lowercase for am/pm.
 */
export function timeFormatString(): string {
    return is24HourTime() ? 'HH:mm' : 'h:mma';
}

export const hoursMinutes = (): string => (is24HourTime() ? 'HH:mm' : 'h:mma');

export function dayJsTimeFormatString(): string {
    return is24HourTime() ? 'HH:mm' : 'h:mma';
}

export const dayJsHoursMinutes = (): string =>
    is24HourTime() ? 'HH:mm' : 'h:mm';

/**
 * Converts a timeZone name into a date object.
 * @param timeZoneName - name of timezone
 */
export const timezoneNameToDate = (
    timeZoneName: string | undefined
): DateTZ | undefined => {
    let zone: DateTZ | undefined;
    if (timeZoneName) {
        try {
            zone = DateNow(new Date()).toZone(timeZoneName);
        } catch (e) {
            // invalid timezone. Suppress logs here.
            // console.error(e);
        }
    }
    return zone;
};

export const timezoneDisplay = (date: DateTZ) =>
    !!date ? `${date.formatDate('ZZZZ')}` : '';
export const gmtOffsetDisplay = (date: DateTZ) =>
    !!date ? `(GMT${date.formatDate('ZZ')})` : '';

/**
 * Returns a full time with timezone.
 * @param date
 */
export function timeWithZoneFormatString(date: DateTZ): string {
    return `${date
        .formatDate(timeFormatString())
        .toLocaleLowerCase()} ${timezoneDisplay(date)}`;
}

export function timeWithGmtOffsetFormatString(date: DateTZ): string {
    return `${date
        .formatDate(timeFormatString())
        .toLocaleLowerCase()} ${gmtOffsetDisplay(date)}`;
}

/**
 * Returns a full start end range.
 *
 * Start and end both use lowercase am/pm markers (c.f. timeFormatString).
 * @param startDateTz start time DateTZ object
 * @param endDateTz end time DateTZ object
 */
export function startEndTimeFormatString(
    startDateTz: DateTZ,
    endDateTz: DateTZ
): string {
    return `${startDateTz
        .formatDate(timeFormatString())
        .toLocaleLowerCase()}-${endDateTz
        .formatDate(timeFormatString())
        .toLocaleLowerCase()}`;
}

/**
 * Returns a full start end range with timezone.
 *
 * Start and end both use lowercase am/pm markers (c.f. timeFormatString).
 * @param startDateTz start time DateTZ object
 * @param endDateTz end time DateTZ object
 */
export function startEndTimeWithZoneFormatString(
    startDateTz: DateTZ,
    endDateTz: DateTZ
): string {
    return `${startDateTz
        .formatDate(timeFormatString())
        .toLocaleLowerCase()}-${endDateTz
        .formatDate(timeFormatString())
        .toLocaleLowerCase()} ${gmtOffsetDisplay(startDateTz)}`;
}

/**
 * Returns a full date with weekday day month year.
 * @param date
 * @param comma optional boolean to display a comma after the month
 */
export function dateLocalFormatString(date: DateTZ, comma?: boolean): string {
    if (comma) {
        return date.formatLocalDate('cccc d MMMM, yyyy');
    }
    return date.formatLocalDate('cccc d MMMM yyyy');
}

export function dateBuildingFormatString(
    date: DateTZ,
    comma?: boolean
): string {
    if (comma) {
        return date.formatDate('cccc d MMMM, yyyy');
    }
    return date.formatDate('cccc d MMMM yyyy');
}

export function shorterLocalDateFormatString(
    date: DateTZ,
    comma?: boolean
): string {
    if (comma) {
        return date.formatLocalDate('ccc d MMM, yyyy');
    }
    return date.formatLocalDate('ccc d MMM yyyy');
}

export function shorterBuildingDateFormatString(
    date: DateTZ,
    comma?: boolean
): string {
    if (comma) {
        return date.formatDate('ccc d MMM, yyyy');
    }
    return date.formatDate('ccc d MMM yyyy');
}

/** Whether locale string is displayed in 24 hour time */
export function is24HourTime(): boolean {
    const date = new Date();
    const localeString = date
        .toLocaleTimeString(
            document.querySelector('html').getAttribute('lang') ||
                navigator.language
        )
        .toLowerCase();
    return localeString.indexOf('am') < 0 && localeString.indexOf('pm') < 0;
}

/* istanbul ignore next */
/**
 * Downloads a file to the users computer with the given filename and contents
 * @param filename Name of the file to download
 * @param contents Contents of the file to download
 */
export function downloadFile(filename: string, contents: string) {
    const element = document.createElement('a');
    element.setAttribute(
        'href',
        'data:text/plain;charset=utf-8,' +
            encodeURIComponent('\uFEFF' + contents)
    );
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
}

/**
 * Parse raw CSV data into a JSON object
 * @param csv CSV data to parse
 */
export function csvToJson(csv: string) {
    const lines = csv.split('\n');
    let fields = lines.splice(0, 1)[0].split(',');
    fields = fields.map((v) => v.replace('\r', ''));
    const list: any[] = [];
    for (const line of lines) {
        let parts = line.split(',');
        parts = parts.map((v) => v.replace('\r', ''));
        /* istanbul ignore else */
        if (parts.length >= fields.length) {
            const item: any = {};
            for (let i = 0; i <= parts.length; i++) {
                let part = null;
                part = parts[i];
                /* istanbul ignore else */
                if (part !== undefined) {
                    item[(fields[i] || '').split(' ').join('_').toLowerCase()] =
                        part;
                }
            }
            list.push(item);
        }
    }

    return list;
}

/**
 * Convert javascript array to CSV string
 * @param json Javascript array to convert
 */
export function jsonToCsv(json: HashMap[]) {
    /* istanbul ignore else */
    if (json instanceof Array && json.length > 0) {
        const keys = Object.keys(json[0]);
        const valid_keys = keys.filter((key) => json[0].hasOwnProperty(key));
        return `${valid_keys.join(',')}\n${json
            .map((item) =>
                valid_keys
                    .map((key) => {
                        return typeof item[key] !== 'boolean'
                            ? `"${item[key] || ''}"`
                            : item[key];
                    })
                    .join(',')
            )
            .join('\n')}`;
    }
    return '';
}

/* istanbul ignore next */
/**
 * detect IE
 * returns version of IE or false, if browser is not Internet Explorer
 */
export function detectIE() {
    var ua = window.navigator.userAgent;

    var msie = ua.indexOf('MSIE ');
    if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
    }

    var trident = ua.indexOf('Trident/');
    if (trident > 0) {
        // IE 11 => return version number
        var rv = ua.indexOf('rv:');
        return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
    }

    var edge = ua.indexOf('Edge/');
    if (edge > 0) {
        // Edge (IE 12+) => return version number
        return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
    }

    // other browser
    return false;
}

/**
 * Grab point details from mouse or touch event
 * @param event Event to grab details from
 */
export function eventToPoint(event: MouseEvent | TouchEvent): Point {
    if (!event) {
        return { x: -1, y: -1 };
    }
    if (event instanceof MouseEvent) {
        return { x: event.clientX, y: event.clientY };
    } else {
        return event.touches && event.touches.length > 0
            ? { x: event.touches[0].clientX, y: event.touches[0].clientY }
            : { x: -1, y: -1 };
    }
}

/* istanbul ignore next */
/**
 * Flatten nested array
 * @param an_array Array to flatten
 */
export function flatten<T = any>(an_array: T[]) {
    const stack = [...an_array];
    const res = [];
    while (stack.length) {
        // pop value from stack
        const next = stack.pop();
        if (Array.isArray(next)) {
            // push back array items, won't modify the original input
            stack.push(...next);
        } else {
            res.push(next);
        }
    }
    // reverse to restore input order
    return res.reverse();
}

const seed = xmur3('PlaceOS');
const rand = sfc32(0x9e3779b9, 0x243f6a88, 0xb7e15162, seed());

export function predictableRandomInt(ceil: number = 100, floor: number = 0) {
    return Math.floor(rand() * (ceil - floor)) + floor;
}

// https://stackoverflow.com/questions/521295/seeding-the-random-number-generator-in-javascript
function xmur3(str) {
    for (var i = 0, h = 1779033703 ^ str.length; i < str.length; i++)
        (h = Math.imul(h ^ str.charCodeAt(i), 3432918353)),
            (h = (h << 13) | (h >>> 19));
    return function () {
        h = Math.imul(h ^ (h >>> 16), 2246822507);
        h = Math.imul(h ^ (h >>> 13), 3266489909);
        return (h ^= h >>> 16) >>> 0;
    };
}

function sfc32(a, b, c, d) {
    return function () {
        a >>>= 0;
        b >>>= 0;
        c >>>= 0;
        d >>>= 0;
        var t = (a + b) | 0;
        a = b ^ (b >>> 9);
        b = (c + (c << 3)) | 0;
        c = (c << 21) | (c >>> 11);
        d = (d + 1) | 0;
        t = (t + d) | 0;
        c = (c + t) | 0;
        return (t >>> 0) / 4294967296;
    };
}

export function isLessThanBreakpoint(breakpoint: number): boolean {
    if (window) {
        return window.innerWidth < breakpoint;
    }
    return false;
}

export function toTitleCase(str: string): string {
    return typeof str === 'string'
        ? str
              .toLowerCase()
              .split(' ')
              .map(function (word) {
                  return word.replace(word[0], word[0].toUpperCase());
              })
              .join(' ')
        : '';
}
