import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})

export class ReadLocalPicService {
  /**
   * eventErrorDecoder
   * @param event 
   * @returns errorMessage
   */
  private eventErrorDecoder(event: any): string {
    let errorMessage = null;

    switch (event.target.error.code) {
      case event.target.error.NOT_FOUND_ERR:
        errorMessage = 'NOT_FOUND_ERR';
        break;
      case event.target.error.SECURITY_ERR:
        errorMessage = 'SECURITY_ERR';
        break;
      case event.target.error.ABORT_ERR:
        errorMessage = 'ABORT_ERR';
        break;
      case event.target.error.NOT_READABLE_ERR:
        errorMessage = 'NOT_READABLE_ERR';
        break;
      case event.target.error.ENCODING_ERR:
        errorMessage = 'ENCODING_ERR';
        break;
      case event.target.error.NO_MODIFICATION_ALLOWED_ERR:
        errorMessage = 'NO_MODIFICATION_ALLOWED_ERR';
        break;
      case event.target.error.INVALID_STATE_ERR:
        errorMessage = 'INVALID_STATE_ERR';
        break;
      case event.target.error.SYNTAX_ERR:
        errorMessage = 'SYNTAX_ERR';
        break;
      case event.target.error.INVALID_MODIFICATION_ERR:
        errorMessage = 'INVALID_MODIFICATION_ERR';
        break;
      case event.target.error.QUOTA_EXCEEDED_ERR:
        errorMessage = 'QUOTA_EXCEEDED_ERR';
        break;
      case event.target.error.TYPE_MISMATCH_ERR:
        errorMessage = 'TYPE_MISMATCH_ERR';
        break;
      case event.target.error.PATH_EXISTS_ERR:
        errorMessage = 'PATH_EXISTS_ERR';
        break;
      default:
        errorMessage = 'Unknown Error: ' + event.target.error.code;
        break;
    }
    return errorMessage;
  }

  /**
   * 
   * @param inputFile 
   * @returns 
   */
  async readFileInput(inputFile: any): Promise<string> {
    return new Promise<string>((resolve, reject) => {
      if (!inputFile ) {
        reject('No file selected');
      } else {
        if (
          !(window.File && window.FileReader && window.FileList && window.Blob)
        ) {
          reject('Your browser does not support reading files');
        }

        const reader = new FileReader();
        reader.onload = (e: any) => {
          resolve(e.target.result);
        };
        reader.onabort = (e: any) => {
          reject(
            'Failed to convert file to base64img, aborted: ' +
              this.eventErrorDecoder(e)
          );
        };
        reader.onerror = (e: any) => {
          reject(
            'Failed to convert file to base64img, error: ' +
              this.eventErrorDecoder(e)
          );
        };

        reader.readAsDataURL(inputFile);
      }
    });
  }


  b64toFile(dataURI): File {
    // convert the data URL to a byte string
    const byteString = atob(dataURI.split(',')[1]);

    // pull out the mime type from the data URL
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0]

    // Convert to byte array
    const ab = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(ab);
    for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }

    // new File
    // Create a blob that looks like a file.
    const blob = new File([ab],'room-photo.png', { 'type': mimeString });
    // blob['lastModifiedDate'] = (new Date()).toISOString();
    // blob['name'] = 'file';
        
    // Figure out what extension the file should have
    // switch(blob.type) {
    //     case 'image/jpeg':
    //         blob['name'] += '.jpg';
    //         break;
    //     case 'image/png':
    //         blob['name'] += '.png';
    //         break;
    // }
    // cast to a File
    return <File>blob;
  }

}
