import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

import { CateringOrder } from '@mckinsey-converge/data-common';
import { SpacesService } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'catering-order-list-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
})
export class CateringOrderListItemComponent implements OnInit {
    /** Catering order to display */
    @Input() order: CateringOrder;
    /**  */
    @Input() public space_list: Space[];
    /** Selected datetime */
    @Input() date: number;
    /** Emitter for edit events */
    @Output() public edit = new EventEmitter<void>();
    /** Emitter for delete events */
    @Output() public delete = new EventEmitter<void>();

    public room: Space;

    public ngOnInit(): void {
        this.room = (this.space_list || []).find(space => space.email === this.order.location_id);
    }

    public get space_in_booking(): boolean {
        return !!this.room;
    }

    /** Space selected for delivery */
    public get local_space_name(): string {
        return this.room?.local_name || '<No Location>';
    }

    /** Delivery time for the order */
    public get delivery_time(): string {
        /* istanbul ignore else */
        if (!this.order) {
            return '<Meeting Start>';
        }
        return this.order.bookingDateTz.addValue({ minutes: this.order.delivery_time || 0}).formatDate('h:mma').toLocaleLowerCase();
    }


    /** Total cost of the order */
    public get order_cost(): number {
        const items = (this.order || { items: [] }).items as any;
        return items.reduce((amount, item) => amount + item.amount * item.unit_price, 0);
    }

    /** Number of items in the order */
    public get item_count(): number {
        const items = (this.order || { items: [] }).items as any;
        return items.reduce((amount, item) => amount + item.amount, 0);
    }

    /** Currency symbol for the selected space */
    public get symbol(): string {
        const space = this._spaces.find((this.order || {}).location_id);
        /* istanbul ignore else */
        if (!space || !space.level) {
            return 'USD';
        }
        const building = this._org.buildings.find((bld) => bld.id === space.level.building_id) || {
            currency: 'USD',
        };
        return building.currency;
    }

    constructor(private _spaces: SpacesService, private _org: OrganisationService) {}
}
