import {
    Component,
    OnInit,
    Input,
    Output,
    OnChanges,
    SimpleChanges,
    forwardRef,
    EventEmitter,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { BaseClass } from '@mckinsey-converge/base';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

export interface IDateTzPickerOptions {
    /** DateTZ object. First selectable date */
    fromDateTz?: DateTZ;
    /** DateTZ object. Last selectable date */
    untilDateTz?: DateTZ;
    /** Luxon format string for formatting days of the week. Defaults to `DDD` */
    day_format?: string;
    /** Luxon format string for formatting active month. Defaults to `MMMM yyyy` */
    month_format?: string;
}

interface IDateBlock {
    /** Timestamp value of the date */
    value: DateTZ;
    /** Day of the month to display */
    display: string;
    /** Whether block is the selected date */
    active: boolean;
    /** Whether block not selectable */
    disabled: boolean;
    /** Whether block outside the active month */
    non_month: boolean;
    /** Whether the block is today */
    today: boolean;
    /** Number of events on this block */
    count?: number;
}

@Component({
    selector: 'date-tz-calendar-widget',
    templateUrl: './date-tz-calendar-widget.component.html',
    styleUrls: ['./date-tz-calendar-widget.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => DateTzCalendarWidgetComponent),
            multi: true,
        },
    ],
})
export class DateTzCalendarWidgetComponent
    extends BaseClass
    implements OnInit, OnChanges, ControlValueAccessor
{
    /** CSS class to add to the root element of the component */
    @Input() public className = 'default';
    /** Counters of the events on given dates. Key format is `yyyy-MM-dd` */
    @Input() public counters: { [date: string]: number };
    /** Settings for the date picker component */
    @Input() public options: IDateTzPickerOptions;
    /** Offset of the display month from the current month */
    @Output() public month = new EventEmitter<number>();

    public readonly weeks = new Array(6).fill(0);
    public selectedDateTz: DateTZ = DateNow();

    // R--- this is the date picker that needs to be TZ aware
    /** Offset of the month displayed from the current month */
    public offset = 0;
    /** Minimum offset for changing the month */
    public min_offset = -999999;
    /** Maximum offset for changing the month */
    public max_offset = 999999;
    /** Blocks representing the rendering details of the display days of the month */
    public date_list: IDateBlock[] = [];
    /** Name of the actively displayed month */
    public month_name = DateNow(new Date()).formatDate('MMMM yyyy');
    /** Name of the days of the week to display */
    public days_of_week = [];
    /** First selectable date */
    private fromTz: DateTZ;
    /** Last selectable date */
    private toTz: DateTZ;
    /** Luxon format string for formatting days of the week. Defaults to `dd` */
    private day_format = 'EEE';
    /** Luxon format string for formatting active month. Defaults to `MMMM yyyy` */
    private month_format = 'MMMM yyyy';
    /** Form control on change handler */
    private _onChange: (_: DateTZ) => void;
    /** Form control on touch handler */
    private _onTouch: (_: DateTZ) => void;

    private _today = DateNow();

    public get timezone() {
        return this.selectedDateTz?.building_tz || '';
    }

    public ngOnInit(): void {
        this.generateMonth();
        this.generateDaysOfTheWeek();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.options) {
            this.updateOptions();
        }
        if (changes.counters) {
            this.generateMonth();
        }
    }

    /** Change date using the given block */
    public setDate(item: IDateBlock) {
        this.selectedDateTz = item.value;
        if (this._onChange) {
            this._onChange(this.selectedDateTz);
        }
        this.generateMonth();
    }

    /**
     * Update offset to show the selected date 2
     */
    public updateDate(): void {
        if (!this.selectedDateTz) {
            return;
        }
        const now = this._today.startOfValue('month');
        const startOfMonthDateTz = this.selectedDateTz.startOfValue('month');
        if (startOfMonthDateTz.valid) {
            const difference = startOfMonthDateTz.dateDiff(
                now,
                'months',
                'months'
            );
            if (this.offset !== difference) {
                this.offset = Math.round(difference);
                this.month.emit(this.offset);
            }
            this.generateMonth();
        }
    }

    /**
     * Update local properties based off changes to settings
     */
    public updateOptions(): void {
        if (this.options) {
            // Setup from date
            if (this.options.fromDateTz) {
                const today = this._today.startOfValue('month');
                this.fromTz = this.options.fromDateTz.startOfValue('day');
                this.min_offset = Math.round(
                    this.fromTz
                        .startOfValue('month')
                        .dateDiff(today, 'months', 'months')
                );
            } else if (this.fromTz) {
                this.fromTz = null; // R-- check if ok
                this.min_offset = -999999;
            }
            // Setup to date
            if (this.options.untilDateTz) {
                const today = this._today.startOfValue('month');
                this.toTz = this.options.untilDateTz.startOfValue('day');
                this.max_offset = this.toTz
                    .startOfValue('month')
                    .dateDiff(today, 'months', 'months');
            } else if (this.toTz) {
                this.toTz = null;
                this.max_offset = 999999;
            }
            if (this.options.month_format) {
                this.month_format = this.options.month_format || 'MMMM yyyy';
            }
            if (this.options.day_format) {
                this.day_format = this.options.day_format || 'EEE';
                this.generateDaysOfTheWeek();
            }
            // Update date blocks
            this.generateMonth();
        }
    }

    /**
     * Generate the display blocks for the days of the active month
     */
    private generateMonth(): void {
        this._today = new DateTZ({ date: Date.now(), is_local_tz: !this.timezone, building_tz: this.timezone });
        const startOfMonthDateTz = this._today.addValue({ months: this.offset });
        const active = this.selectedDateTz || DateNow();
        let startDate = startOfMonthDateTz
            .setValue({ day: 1 })
            .setValue({ weekday: 7 })
            .subtractValue({ weeks: 1 });
        this.date_list = [];
        for (let i = 0; i < 42; i++) {
            this.date_list.push({
                value: startDate,
                display: startDate.formatDate('d'),
                active: active.isSameDate(startDate, 'day'),
                non_month: !startOfMonthDateTz.isSameDate(startDate, 'month'),
                count: this.counters
                    ? this.counters[startDate.formatDate('yyyy-MM-dd')] || 0
                    : 0,
                disabled:
                    (this.fromTz ? startDate.ms < this.fromTz.ms : false) ||
                    (this.toTz ? startDate.ms > this.toTz.ms : false),
                today: this._today.isSameDate(startDate, 'day'),
            });
            startDate = startDate.addValue({ days: 1 });
        }
        this.month_name = startOfMonthDateTz.formatDate(this.month_format);
    }

    /**
     * Generate this weekdays to display in the header of the calendar
     */
    private generateDaysOfTheWeek() {
        let date = this._today.startOfValue('week');
        this.days_of_week = [];
        for (let i = 0; i < 7; i++) {
            this.days_of_week.push(date.formatDate(this.day_format));
            date = date.addValue({ days: 1 });
        }
    }

    /**
     * Changes the month offset by the given value.
     * Does nothing if the new month is not available to be selected
     * @param value Number of months to change the offset by
     */
    public changeMonth(value: number): void {
        const new_offset = Math.min(
            this.max_offset,
            Math.max(this.min_offset, this.offset + value)
        );
        this.offset = Math.round(new_offset);
        this.generateMonth();
        this.month.emit(this.offset);
    }

    /**
     * Reset the month offset to the month of the selected date
     */
    public reset() {
        const offset = this.offset;
        this.offset = Math.round(
            this._today.dateDiff(this.selectedDateTz, 'months', 'months')
        );
        if (offset !== this.offset) this.generateMonth();
        this.month.emit(this.offset);
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: DateTZ) {
        if (value) {
            this.selectedDateTz = value;
            this.updateDate();
            this.generateMonth();
        }
    }

    public readonly registerOnChange = (fn: (_: DateTZ) => void) => this._onChange = fn;
    public readonly registerOnTouched = (fn: (_: DateTZ) => void) => this._onTouch = fn;
}
