import {
    Component,
    forwardRef,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import {
    ControlValueAccessor,
    NG_VALUE_ACCESSOR
} from '@angular/forms';
import { MatSelect } from '@angular/material/select';

import { BaseDirective } from '@mckinsey-converge/base';
import {
    Identity,
    timeFormatString
} from '@mckinsey-converge/base';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'a-time-field',
    templateUrl: './time-field.component.html',
    styleUrls: ['./time-field.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => TimeFieldComponent),
            multi: true
        }
    ]
})
export class TimeFieldComponent extends BaseDirective implements OnInit, OnChanges, ControlValueAccessor {
    /** Time step between each allowed time option */
    @Input() public step: number = 15;
    /** Whether form field is disabled */
    @Input() public disabled: boolean;
    /** Whether past times are allowed */
    @Input() public no_past_times: boolean = true;
    /** String representing the currently set time */
    public date: number = DateNow(new Date()).ms;
    /** String representing the currently set time */
    public time: string = DateNow(new Date()).formatDate('HH:mm');
    /** Available time blocks for the selected date */
    public _time_options: Identity[];
    /** Whether select field should be shown */
    public show_select: boolean;
    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;

    /** Select field for selecting the time */
    @ViewChild('select') private select_field: MatSelect;

    // R--- This is the form input that needs to be TZ active.
    public ngOnInit(): void {
        this.show_select = true;
        this._time_options = this.generateAvailableTimes(this.date, !this.no_past_times, this.step);
        this.timeout('hide', () => this.show_select = false);
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.no_past_times || changes.step) {
            this._time_options = this.generateAvailableTimes(this.date, !this.no_past_times, this.step);
        }
    }

    public get date_(){
        return new DateTZ({date: this.date})
    }

    /** Available time blocks for the selected date */
    public get time_options() {
        const time = (this.time || '00:00').split(':');
        const date = this.date_.setValue({ hour: +time[0], minute: +time[1] });
        if (date.minutes % 15 !== 0 && !this._time_options.find(time => time.id === date.formatDate('HH:mm'))) {
            this._time_options.push({
                name: `${date.formatDate(timeFormatString())}`,
                id: date.formatDate('HH:mm')
            });
            this._time_options.sort((a, b) => `${a.id}`.localeCompare(`${b.id}`));
        }
        return this._time_options;
    }


    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: string): void {
        this.time = new_value;
        if (this._onChange) {
            const time = (this.time || '00:00').split(':');
            const date = this.date_.setValue({ hour: +time[0], minute: +time[1] }).startOfValue('minute');
            this._onChange(date.ms);
        }
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: number) {
        if (!value) {
            return;
        }
        this.date = value;
        let date = this.date_.startOfValue('minute');
        date = date.setValue({ minute: Math.ceil(date.minutes / 5) * 5 });
        this.time = date.formatDate('HH:mm');
        this._time_options = this.generateAvailableTimes(this.date, !this.no_past_times, this.step);
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }

    /**
     * Show select field for time options
     */
    public showSelect() {
        this.show_select = true;
        this.timeout('on_shown', () => {
            if (this.select_field) {
                this.select_field.focus();
                this.select_field.open();
                this.subscription('listen_close', this.select_field.openedChange.subscribe((state) => {
                    if (!state) {
                        this.show_select = false;
                    }
                }));
            }
        });
    }

    /**
     * Generate a list of time options for the given date
     * @param datestamp Date to generate options for
     * @param show_past Whether past times should be options
     */
    private generateAvailableTimes(datestamp: number, show_past: boolean, step: number = 15): Identity[] {
        let date = new DateTZ({date: datestamp});
        const blocks = [];
        if (show_past || date.isAfterDate(DateNow(new Date()))) {
            date = date.startOfValue('day');
        } else if (date.isAfterDate(DateNow(new Date()))) {
            date = DateNow(new Date());
        }
        date = date.setValue({ minute: Math.ceil(date.minutes / step) * step });
        const end = date.endOfValue('day');
        // Add options for the rest of the day
        while (date.isBeforeDate(end)) {
            blocks.push({
                name: `${date.formatDate(timeFormatString()).toLocaleLowerCase()}`,
                id: date.formatDate('HH:mm')
            });
            date = date.addValue({ minutes: step });
        }
        return blocks;
    }
}
