import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { OrganisationService, RecurrencePeriod, RecurrenceType, removeExpiredOcurrences, RepeatsOn } from "@mckinsey-converge/data-common";
import { DateTZ } from "@mckinsey-converge/date-tz";
import { first } from "rxjs/operators";
import { RecurrencePatternForm } from "../recurrence-pattern-form/recurrence-pattern-form.component";

@Component({
    selector: 'recurrence-booking-form',
    templateUrl: './recurrence-booking-form.component.html',
    styleUrls: ['./recurrence-booking-form.component.scss']
})

export class RecurringBookingForm implements OnInit {
    /** Form fields for booking */
    @Input() public form: FormGroup;

    public current_type: RecurrenceType; 
    public recurrence_types: Array<RecurrenceType> = [
        {type: RepeatsOn.PATTERN, label: 'Repeats on'},
        {type: RepeatsOn.DATE, label: 'Repeats on specific date'}
    ]

    public building_tz = "";

    @ViewChild(RecurrencePatternForm) pattern_form: RecurrencePatternForm;

    constructor(
        private _org: OrganisationService,
    ) {}

    public get recurrencePeriod(){
        return this.form.get('recurrence_period')
    }

    public ngOnInit(): void {
        this._org.initialised.pipe(first((_) => _)).subscribe(() => {
            this.building_tz = this._org?.building?.timezone || 'America/Chicago';
        })
        if(this.recurrencePeriod.value === RecurrencePeriod.LIST){
            this.current_type = this.recurrence_types[1];
            removeExpiredOcurrences(this.form)
        }else{
            this.current_type = this.recurrence_types[0];
        }
    }

    public onChangeType(value: string) {
        this.current_type = this.recurrence_types.filter((type) => type.type === value)[0]
        if(this.current_type.type === RepeatsOn.PATTERN){
            this.recurrencePeriod.setValue(this.pattern_form.recurrencePatternFormControl.value)
        }else{
            this.recurrencePeriod.setValue(RecurrencePeriod.LIST)
        }
    }

    public dateToBuildingTz(date?: number){
        return new DateTZ({date, building_tz: this.building_tz, is_local_tz: false})
    }

    public get error(){
        const recurrence_starts = this.form.controls.recurrence_starts
        const recurrence_days = this.form.controls.recurrence_days
        return recurrence_starts.invalid || recurrence_days.invalid
    }

}
