import { Component, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
    selector: 'a-counter',
    templateUrl: './counter.component.html',
    styleUrls: ['./counter.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            /* istanbul ignore next */
            useExisting: forwardRef(() => CounterComponent),
            multi: true,
        },
    ],
})
export class CounterComponent implements ControlValueAccessor {
    /** Maximum amount for the counter */
    @Input() public max: number = 10;
    /** Minimum amount for the counter */
    @Input() public min: number = 0;

    @Input() public useInput = false;

    /** Current value of the counter */
    public value: number;
    public previous_value: number = 0;
    /** Whether shift key is being held by the user */
    public shift_key: boolean;
    /** Whether control key is being held by the user */
    public ctrl_key: boolean;

    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;

    /**
     * Add the `step` to the current value
     */
    public add() {
        if (!this.value) {
            this.value = this.min || 0;
        }
        const step = this.ctrl_key ? 100 : this.shift_key ? 10 : 1;
        this.value += step;
        if (this.value > this.max) {
            this.value = this.max || 10;
        }
        this.setValue(this.value);
    }

    /** Remove the `step` from the current value */
    public remove() {
        if (!this.value) {
            this.value = this.min || 0;
        }
        const step = this.ctrl_key ? 100 : this.shift_key ? 10 : 1;
        this.value -= step;
        if (this.value < this.min) {
            this.value = this.min || 0;
        }
        this.setValue(this.value);
    }

    public handleTimeout = null;

    public onInputChange(new_value: string) {
        if (this.handleTimeout) {
            clearTimeout(this.handleTimeout);
            this.handleTimeout = null;
        }
        this.handleTimeout = setTimeout(() => this.changeInput(new_value), 500);
    }

    public changeInput(new_value: string) {
        let value = Number(new_value);
        if (isNaN(value)) {
            value = this.previous_value;
        }
        if (value < this.min) {
            value = this.min || 0;
        }
        if (value > this.max) {
            value = this.max || 10;
        }
        this.setValue(value);
    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: number): void {
        this.previous_value = new_value;
        this.value = new_value;
        /* istanbul ignore else */
        if (this._onChange) {
            this._onChange(new_value);
        }
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: number) {
        this.value = value;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }
}
