import { Component, OnInit } from '@angular/core';

import { Building } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { ApplicationLinkInternal, ApplicationIcon } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'a-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

    /** Currently active building */
    public get active_building(): Building {
        return this._org.building;
    }
    public set active_building(building: Building) {
        this._org.building = building;
    }

    /** List of available menu items */
    public get menu_items(): ApplicationLinkInternal[] {
        return this._service.setting('app.general.menu.items') || [];
    }

    /** Logo for the application organisation */
    public get logo(): ApplicationIcon {
        return this._service.setting('app.logo_light');
    }

    /** List of available buildings */
    public get buildings(): Building[] {
        return this._org.buildings || [];
    }

    constructor(private _service: ApplicationService, private _org: OrganisationService) {}

    ngOnInit(): void {}
}
