import {
    Component,
    Input,
    OnInit,
    OnDestroy,
    ViewChild,
    AfterViewInit
} from '@angular/core';
import { MatExpansionPanel } from '@angular/material/expansion';
import { BehaviorSubject, Subscription } from 'rxjs';
import { CollapseService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'mck-collapse-header',
    template: `
        <mat-expansion-panel
            #expansionPanel
            [disabled]="isLoading"
            name="expansion-panel"
            class="mck-expansion-panel {{(isExpanded | async) && 'mck-expanded'}}"
            (opened)="isExpanded.next(true)"
            (closed)="isExpanded.next(false)"
            hideToggle>
            <mat-expansion-panel-header *ngIf="!isLoading" name="expansion-panel-header">
                <mat-panel-title>{{title}}</mat-panel-title>
                <div class="mck-collapse-icon" [class.expanded]="isExpanded | async"></div>
            </mat-expansion-panel-header>
            <mat-expansion-panel-header *ngIf="isLoading">
                <div class="mck-row mck-center skeleton">
                    <ngx-skeleton-loader class="mck-skeleton-header"></ngx-skeleton-loader>
                </div>
            </mat-expansion-panel-header>
            <mat-panel-description *ngIf="description && (isExpanded | async)">{{description}}</mat-panel-description>
            <ng-template matExpansionPanelContent>
                <ng-content></ng-content>
            </ng-template>
        </mat-expansion-panel>
    `,
    styleUrls: ['./collapse-header.component.scss'],
})
export class CollapseHeaderComponent implements OnInit, OnDestroy {

    @Input() title: string;
    @Input() description: string;
    @Input() isLoading = false;

    @ViewChild('expansionPanel') expansionPanel: MatExpansionPanel;

    public isExpanded = new BehaviorSubject(false);
    private collapseSubscription: Subscription;

    constructor(private collapseService: CollapseService) {}

    ngOnInit(): void {
        this.collapseSubscription = this.collapseService.collapse$.subscribe(() => {
            if (this.expansionPanel) {
                this.expansionPanel.close(); // Close the panel
            }
        });
    }

    ngOnDestroy(): void {
        this.collapseSubscription.unsubscribe();
    }
}
