import {
    Component,
    Input,
    ViewContainerRef,
    ViewEncapsulation
} from '@angular/core';
import { BaseTemplateComponent } from '../base-template.component';

@Component(
    {
        selector: 'mck-informational-image',
        template: `
            <ng-template>
                <div class="body">
                    <div class="info-block full">
                        <div class="image-wrapper full">
                            <img src="{{src}}">
                        </div>
                    </div>
                </div>
            </ng-template>
        `,
        styleUrls: ['./informational-image.component.scss'],
        encapsulation: ViewEncapsulation.None
    }
)
export class InformationalImageComponent extends BaseTemplateComponent {

    @Input()
    public src: string;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }
}
