import { Space } from '../../spaces/space.class';
import { BuildingLevel } from '../../organisation/level.class';
import { Building } from '../../organisation/building.class';

export class RoomResultsViewModel {

    public levelDisplay: string;
    public title: string;
    public office: string;
    public capacity: string;

    constructor(public space: Space,
                public level?: BuildingLevel,
                public building?: Building) {

        this.levelDisplay = `Level ${this.space?.level?.short_name}`;
        this.title = this.space.local_name;
        this.office = this.building ? `(${this.building?.code}) ${this.building?.name}` : '';
        this.capacity = `Capacity: ${this.space.capacity} people`;
    }
}

/**
 * For each result from results, we find its level and building from the lists.
 */
export const mapResultsToViewModels = (results: Space[],
                                       flattenedLevels: BuildingLevel[],
                                       buildings: Building[]): RoomResultsViewModel[] =>
    results.map((r: Space) => {
        const level = flattenedLevels.find(l => r.zones.includes(l.id));
        const building = buildings.find(b => r.zones.includes(b.id));
        return new RoomResultsViewModel(
            r,
            level,
            building
        );
    });
