import { Building, BuildingCity } from '../organisation/building.class';
import { Booking } from '../bookings/booking.class';
import { BuildingLevel } from '../organisation/level.class';
import { Space } from '../spaces/space.class';
import { SpacesService } from '../spaces/spaces.service';
import {
    timezoneDisplay,
    timezoneNameToDate,
    unique,
    ImageDirective,
} from '@mckinsey-converge/base';
import { SelectOption } from '../../../../ui/src/lib/options/select-option';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

/**
 * If more than 3 are selected, we truncate location display.
 */
const MAX_ABBREV_LOCATIONS = 3;

export const mapBuildingToSelectOption = (
    city: string,
    building: Building
): SelectOption<any> => ({
    display: `${building.name} ${timezoneDisplay(
        timezoneNameToDate(building.timezone)
    )}`,
    dropdownOverride: `${building.code}-${building.name}, ${building.address}`,
    value: building.id,
    groupId: city,
    groupChild: true,
    shortDisplay: `${building.name} ${timezoneDisplay(
        timezoneNameToDate(building.timezone)
    )}`,
    meta: { building, city },
});

export const mapCityToAllSelectOption = (
    city: BuildingCity
): SelectOption<any> => ({
    display: `${city.name} (All Offices) ${timezoneDisplay(
        timezoneNameToDate(city.timezone)
    )}`,
    value: city.name,
    groupHeader: true,
    groupId: city.name,
    shortDisplay: `${city.name} (All Offices) ${timezoneDisplay(
        timezoneNameToDate(city.timezone)
    )}`,
    meta: { city },
});

/**
 * Flattens a map of {@link BuildingCity} to {@link Building} array into a list of {@link SelectOption}.
 * @param grouped The grouping
 */
export const flattenDisplayOffices = (
    grouped: Map<BuildingCity, Building[]>
) => {
    const options: SelectOption<any>[] = [];
    grouped.forEach((value, key) => {
        options.push(mapCityToAllSelectOption(key));
        value.forEach((b) =>
            options.push(mapBuildingToSelectOption(key.name, b))
        );
    });
    return options;
};

/**
 * This method will either add or remove a selected option from the selectedOptions list based on
 * these conditions:
 * 1. If the option EXISTS in the list AND is a groupHeader, de-select all of its children.
 * 2. If the option EXISTS in the list AND is a groupChild, remove it and its associated header from the list.
 * 3. If the option does NOT EXIST in the list AND is a groupHeader, select all other children.
 * 4. If the option does NOT EXIST in the list AND is a groupChild, add it to the list.
 *
 * Special note regarding item 4:
 * 4a. If that selection completes the children selection, select its associated header as well.
 */
export const toggleSelectedByGroup = (
    options: SelectOption<any>[],
    selectedOptions: SelectOption<any>[],
    option: SelectOption<any>
) => {
    if (selectedOptions.find((s) => s.value === option.value)) {
        return selectedOptions.filter((s) => {
            let filter = s.value !== option.value;
            // if group header, also remove any option that is the child of it.
            if (option.groupHeader) {
                filter =
                    filter &&
                    (!s.groupChild ||
                        (s.groupChild && s.groupId !== option.groupId));
            } else if (option.groupChild) {
                // if child removing, remove the associated header.
                filter =
                    filter &&
                    (!s.groupHeader ||
                        (s.groupHeader && s.groupId !== option.groupId));
            }
            return filter;
        });
    }
    // option does NOT EXIST
    // if adding header, add the other children to the selected list, ensuring no dupes.
    if (option.groupHeader) {
        const toSelect = options.filter(
            (v) =>
                v.groupChild &&
                v.groupId === option.value &&
                !selectedOptions.find((selected) => selected.value === v.value)
        );
        return [...selectedOptions, option, ...toSelect];
    }
    // add group child, add the group header if all satisfied
    const newGroup = [...selectedOptions, option];

    // check if we have selected all children from options by filtering down by city and checking if
    // they're in the selected options list.
    const remainingChildrenInGroup = options.filter(
        (o) =>
            o.groupChild &&
            o.groupId === option.groupId &&
            !newGroup.find((ng) => ng.value === o.value)
    );
    // if we dont have remaining children, add the group
    if (remainingChildrenInGroup.length === 0) {
        return [
            ...newGroup,
            options.find((o) => o.groupHeader && o.groupId === option.groupId),
        ];
    }
    return newGroup;
};

/**
 * If the list of locations are larger than {@link MAX_ABBREV_LOCATIONS}, then truncate
 * and display the remaining count.
 */
export const truncateLocationList = (
    locations: readonly SelectOption<any>[]
): string => {
    // comma separate the locations
    let truncatedLocations = [...locations];
    const shouldTruncate = locations.length > MAX_ABBREV_LOCATIONS;
    if (shouldTruncate) {
        truncatedLocations = truncatedLocations.splice(0, MAX_ABBREV_LOCATIONS);
    }
    let display = truncatedLocations
        .map((l) => l.shortDisplay || l.display)
        .join(', ');
    if (shouldTruncate) {
        display += `...(${locations.length})`;
    }
    return display;
};

export const mapBuildingLevelToOption = (
    level?: BuildingLevel
): SelectOption<any> =>
    level
        ? {
              value: level.id,
              display: level.name,
          }
        : undefined;

/**
 * Returns all levels included with the building, deduped.
 * @param buildings
 */
export const flattenBuildingsWithLevels = (buildings: Building[]) => {
    const flattenedLevels = unique(
        buildings.reduce((next: BuildingLevel[], building: Building) => {
            next.push(...building.levels);
            return next;
        }, []),
        'id'
    );
    return {
        flattenedLevels,
        buildings,
    };
};

/** Gets route to image placeholder if room image isn't found */

export const placeholderRoute = (num: number): string => {
    let index: number;
    if (num <= 3) {
        index = num;
    } else if (num % 3 === 0) {
        index = 3;
    } else {
        index = 1;
    }
    return `assets/img/rooms/placeholder-${index}.png`;
};

/**
 * Method takes the rootFolderURL and fileSlug to create an array of three images
 * that should exist.  If a room image exists, it replaces the placehoder image.
 */
export const setupRoomImages = (
    componentReference: ImageDirective,
    image_positions: number[],
    rootFolderURL: string,
    fileSlug: string,
    imagesLoaded: boolean = false
): void => {
    let foundImages: any[] = image_positions.map((i) =>
        i ? { path: placeholderRoute(i) } : false
    );
    image_positions.forEach((n) => {
        const desiredImageName = `${rootFolderURL}${fileSlug}-part-${n}.png`;
        const desiredImage = location.pathname.includes('concierge') ?  `${location.origin}/staff/${desiredImageName}` : desiredImageName;
        if (!imagesLoaded) {
            const tester = new Image();
            tester.onload = () => {
                // Will never run on unit test
                foundImages[n - 1] = { path: desiredImage };
                componentReference.foundImages = [...foundImages.slice()];
                componentReference?.loadImages && componentReference?.loadImages.next([...foundImages.slice()]);
            };
            tester.src = desiredImage;
        }

        // Force valid image output on unit test
        if (imagesLoaded) {
            foundImages[n - 1] = { path: desiredImage };
            componentReference.foundImages = foundImages.slice();
        }
    });
};

export const bookingStatusDetails = (booking: Booking) => {
    const now = DateNow(new Date());
    let image = '';
    let text = '';
    let title = '';

    if (booking?.status) {
        const status = now > booking.endDateTz ? 'expired' : booking.status;
        switch (status) {
            case 'unavailable':
                image = 'assets/icon/booking_cancelled.svg';
                title = 'Unavailable';
                text = 'Unavailable';
                break;
            case 'declined':
                image = 'assets/icon/booking_cancelled.svg';
                title = 'Cancelled';
                text = 'Cancelled';
                break;
            case 'cancelled':
                image = 'assets/icon/booking_cancelled.svg';
                title = 'Cancelled';
                text = 'Cancelled';
                break;
            case 'expired':
                image = 'assets/icon/booking_expired.svg';
                title = 'Expired';
                text = 'Expired';
                break;
            case 'tentative':
                image = 'assets/icon/booking_pending.svg';
                title = 'Requested';
                text = 'Pending';
                break;
            case 'accepted':
                image = 'assets/icon/booking_confirmed.svg';
                title = 'Confirmed';
                text = 'Confirmed';
                break;
            case 'approved':
                image = 'assets/icon/booking_confirmed.svg';
                title = 'Confirmed';
                text = 'Confirmed';
                break;
            default:
                // TODO
                image = 'assets/icon/booking_expired.svg';
                title = 'Expired';
                text = 'Expired';
                break;
        }
    }
    return { text, title, image };
};

export const roomHasCateringHours = (building: Building, space: Space) => {
    let hasCatering = building
        ? building.has_catering && building.catering_hours
        : false;
    // Room catering status as false overrides building status
    if (space?.has_catering === false) {
        hasCatering = false;
    }
    return hasCatering;
};

export const cateringAllowed = (booking: Booking, building: Building) => {
    const status = bookingStatusDetails(booking);
    const disallowedStatus = ['declined', 'cancelled', 'expired'].includes(
        status.text
    );
    const opens = building?.catering_hours?.start;
    const closed = building?.catering_hours?.end;

    if (disallowedStatus) {
        return false;
    }

    if (typeof opens === 'undefined' || typeof closed === 'undefined') {
        return false;
    }

    // Catering time could be a decimal so convert to a date
    const openMinutes = opens * 60;
    const closeMinutes = closed * 60;

    const startOfDay = booking.startDateTz.startOfValue('day');
    const cateringOpenTime = startOfDay.addValue({ minutes: openMinutes });
    const cateringCloseTime = startOfDay.addValue({ minutes: closeMinutes });

    if (
        cateringOpenTime.ms <= booking.startDateTz.ms ||
        cateringCloseTime.ms > booking.startDateTz.ms
    ) {
        // Booking ends before catering opens
        if (booking.endDateTz.ms <= cateringOpenTime.ms) {
            return false;
        }
        // Booking starts after catering closes
        if (booking.startDateTz.ms > cateringCloseTime.ms) {
            return false;
        }

        // Start time is OK
        if (booking.startDateTz.ms > DateNow(new Date()).ms) {
            // Due to COVID, no orders are available once a meeting begins.
            return true;
        }
    }

    return false;
};

export const checkCollisions = (
    service: SpacesService,
    booking: Booking
): Promise<boolean> => {
    return new Promise((resolve, reject) => {

        let availabilityParams: {
            room_ids: string;
            dateTz: DateTZ;
            duration: number;
            setup: number;
            breakdown: number;
            hide_bookings: boolean;
            [key: string]: any;
        } = {
            room_ids: booking.space.id,
            dateTz: booking.startDateTz,
            duration: booking.duration,
            setup: booking.setup[booking.space.email],
            breakdown: booking.breakdown[booking.space.email],
            hide_bookings: false,
        };

        if (booking.recurrence_type === "master" || booking.recurrence_type === null) {
            availabilityParams = {
                ...availabilityParams,
                ignore: booking?.icaluid,
                recurrence_count: booking?.recurrence_count,
                recurrence_period: booking?.recurrence_period,
                recurrence_endTz: booking?.recurrence_endTz,
                recurrence_interval: booking?.recurrence_interval,
                recurrence_starts: booking?.recurrence_starts,
                is_recurrent: booking?.is_recurrent,
                recurrence_days: booking?.recurrence_days
            } as typeof availabilityParams;
        }

        service
            .available(
                availabilityParams,
                null,
                false
            )
            .then(
                (list) => {
                    const space = list.length ? list[0] : null;
                    if (space) {
                        if (!space.bookable) return reject('Has conflict');
                        const setup = booking?.setup[space?.email] || 0;
                        const breakdown = booking?.breakdown[space?.email] || 0;
                        const start_time = booking?.startDateTz.seconds - setup;
                        const end_time = booking.endDateTz.seconds + breakdown;

                        const bookings = space.settings.bookings;
                        // console.log('Bookings:', bookings);
                        // Compare the existing bookings to see if any truely conflict.
                        if (bookings.length) {
                            const has_conflict = bookings.find(bkn => {
                                const b_setup = bkn.setup[space.email] || 0;
                                const b_breakdown = bkn.setup[space.email] || 0;
                                const start = (bkn.start_epoch || bkn.start) - b_setup;
                                const end = (bkn.end_epoch || bkn.end) + b_breakdown;
                                return (
                                    (end_time > start && end_time <= end) || // Booking ends during the meeting
                                    (start_time >= start && start_time < end) || // Booking starts during a meeting
                                    (start_time <= start && end_time >= end) // Booking overlaps entire meeting
                                ) && 
                                ( 
                                    bkn.id !== booking.id 
                                    || 
                                    ( !!booking.is_recurrent && (booking.recurrence_type === "master" || booking.recurrence_type === null ) ) 
                                        ?
                                        bkn.recurrence_master_id !== booking.id
                                        :
                                        false
                                    
                                )



                            });
                            // Conflicts found, reject
                            if (has_conflict) return reject('has booking conflict');
                        }
                        return resolve(space.bookable);
                    }
                    reject('space not found');
                },
                () => reject('available error')
            );
    });
};

export const getStatusErrorMessage = (status?: number) => {
    let msg = '';
    switch (status) {
			case 400:
				msg = 'Apologies, the booking cannot be finalized because the current time has exceeded the scheduled start time for this booking. Please try again.';
				break;
			case 403:
				msg = 'Your do not have permission to update this booking.';
				break;
			case 409:
				msg = 'Sorry, your booking time conflicts with another booking.';
				break;
			default:
				msg = 'Your booking failed to update, please try again';
    }
    return msg;
};
