import { DateNow, DateTZ } from '@mckinsey-converge/date-tz'
import { DateTime } from 'luxon';
import * as dayjs from 'dayjs';
import {
    dayJsHoursMinutes,
    dayJsTimeFormatString,
} from './general.utilities';

/**
 * Allows you to split up durations into a group.
 */
export interface DurationGroup {
    /**
     * Step amount to generate between start and max.
     */
    step: number;
    /**
     * Where to start in minutes.
     */
    start: number;
    /**
     * Where to end in minutes.
     */
    max: number;
}

/**
 * Find the multiple of `stepMinute` which is closest to the 'minutes' property of the given date.
 * @param date - Any Lexon date.
 * @param stepMinute - The number of minutes between one timeslot and the next. Integer value
 * 1 to 59, inclusive.
 */
// R--- depreciate this one
export const closestToTimeSlot = (date: DateTime, stepMinute: number, start: number = 0) : DateTime => {
    return date.set({ millisecond: 0, second: 0, minute: Math.ceil(date.minute / stepMinute) * stepMinute })
        .plus({ minutes: start % 60 }) // apply offset as well if it starts at 15.
};

export const closestToTimeSlotTz = (dateTz: DateTZ, stepMinute: number, start: number = 0) : DateTZ => {
    return dateTz.setValue({ millisecond: 0, second: 0, minute: Math.ceil(dateTz.minutes / stepMinute) * stepMinute })
        .addValue({ minutes: start % 60 }) // apply offset as well if it starts at 15.
};

/**
 * Convert duration to human readable string
 * @param duration Duration in minutes
 * @param short Whether to use short form of duration words e.g. hours as hrs, or minutes as mins
 */
export function durationHumanized(duration: number, short: boolean = false): string {
    if (!duration || duration < 0) {
        return '';
    }
    const h = Math.floor(duration / 60);
    let d = `${h >= 1 ? h + (short ? ' hr' : ' hour' + (h === 1 ? '' : 's')) : ''}`;
    if (duration % 60 !== 0) {
        if (d) {
            d += short ? ' ' : ', ';
        }
        const m = duration % 60;
        d += `${m >= 1 ? m + (short ? ' min' : ' minute' + (m === 1 ? '' : 's')) : ''}`;
    }
    return d;
}

export const weekDayMonthYearFormat = (date: DateTZ): string => date.formatDate('ccc dd MMM yyyy');

/**
 * Tries to extract best-fit input
 * @return undefined if not valid. otherwise if good
 */
export const extractDateFromInput = (input: string,
    checkHour,
    startDate: dayjs.Dayjs): dayjs.Dayjs | undefined => {
    // check if input has am/pm or normal 24 hour time.
    // let date = dayjs(input, 'HH:mm');
    // let date = dayjs(input, 'h:mma');
    let date = dayjs(input, dayJsTimeFormatString());
    if (!date.isValid()) {
        // patch if time is in format xx:x to assume you meant xx:x0
        let cleanedInput = input;
        const times = input.split(':');
        if(!input){
            return undefined
        }
        if (times.length === 2) {
            if (times[1].length === 1) {
                cleanedInput = `${times[0]}:${times[1]}0`;
            }
        }
        // may be other input, lets try next value (without am/pm)
        date = dayjs(cleanedInput, dayJsHoursMinutes());
        // might be just purely an hour
        if (!date.isValid() && checkHour) {
            date = dayjs(input, 'H');
        }
    }
    // We expect input times to be in the future. If now is in the afternoon, this
    // simple block will ensure the returned date is also in the afternoon.
    //
    // NB: dayjs parses times as morning by default.
    if (date.isValid()) {
        // Move to the selected start date
        date = date.month(startDate.month()).date(startDate.date()).year(startDate.year());
        if (startDate.hour() > date.hour() && date.date() === startDate.date()) {
            date = date.set('hour', date.hour() + 12);
        }
    }

    return date.isValid() ? date : undefined;
};


/**
 * 1. Calculates the date from input via {@link extractDateFromInput}.
 * 2. Finds the nearest time slot that it can be via {@link closestToTimeSlot}.
 * 3. Then returns the time-format string the input expects so autocomplete can suggest
 *    closest match.
 * @param step The step between time slots.
 * @param input The input text.
 */
export const nearestStepToInput = (
    step: number,
    input: string,
    currentStartDate: number,
    start: number = 0
): string => {
    let date = extractDateFromInput(input, false, dayjs(currentStartDate));
    if (date) {
        const luxonDate = DateTime.fromMillis(date.valueOf());
        const closestDate = closestToTimeSlot(luxonDate, step, start);
        return closestDate.toFormat(dayJsHoursMinutes());
    } else {
        return input;
    }
};
/**
 * Similiar to {@link nearestStepToInput} instead:
 * 1. Calculates the date from input via {@link extractDateFromInput}.
 * 2. Finds which duration group is closest to the currentStartDate. If not found
 *    return vanilla text.
 * 3. Finds the nearest time slot that it can be via {@link closestToTimeSlot}.
 * 4. Then returns the time-format string the input expects so autocomplete can suggest
 *    closest match.
 */
export const nearestDurationToInput = (durationGroups: DurationGroup[],
    currentStartDate: number,
    value: string): string => {

    let date = extractDateFromInput(value, false, dayjs(currentStartDate));

    if (!date) {
        // Check if the input is a duration 
        const duration = parseInt(value);
        if (typeof (duration) === 'number' && value.indexOf(':') === -1 && duration > 12) {
            date = dayjs(currentStartDate).add(duration, 'm');
        }
    }

    if (date) {
        const minutes = date.diff(currentStartDate, 'minute');
        const closestDurationGroup = durationGroups.find(d => {
            return (minutes <= d.max);
        });
        if (closestDurationGroup) {
            const offset = [closestDurationGroup].reduce((previousValue, currentValue) => {
                return previousValue + currentValue.start;
            }, 0);
            const luxonDate = DateTime.fromMillis(date.valueOf());
            const closestDate = closestToTimeSlot(luxonDate, closestDurationGroup.step, 0);
            return closestDate.toFormat(dayJsHoursMinutes());
        }
    }

    return value;
};

export const resetSecondsOnTimestamp = (timestamp: number) => {
    return DateTime.fromMillis(timestamp).set({ second: 0, millisecond: 0 }).toMillis();
}

export const formatDateWithSuffix = (date: string): string => {
    const dateObj = new Date(date);
    const day = dateObj.getDate();
    const month = dateObj.toLocaleString("default", { month: "short" });
    const year = dateObj.getFullYear();
    return `${day}${nthNumber(day)} ${month} ${year}`; //this.event.date_string;
}

const nthNumber = (number) => {
    return number > 0
        ? ["th", "st", "nd", "rd"][
                (number > 3 && number < 21) || number % 10 > 3 ? 0 : number % 10
            ]
        : "";
};

