import {
    Component,
    ElementRef,
    HostListener,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Renderer2,
    SimpleChanges,
    ViewChild, NgZone, DoCheck, ChangeDetectionStrategy, ChangeDetectorRef
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { filter, first } from 'rxjs/operators';

import { BaseDirective } from '@mckinsey-converge/base';
import { Space } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { Booking } from '@mckinsey-converge/data-common';
import { HashMap, Identity } from '@mckinsey-converge/base';
import { MeetingDetailsModalComponent } from 'src/app/calendar/meeting-details-modal/meeting-details-modal.component';
import { SpacesService } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ, toMiddayUTC } from '@mckinsey-converge/date-tz';
import { isToday } from 'date-fns';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';



const UPDATE_INTERVAL = 10;

@Component({
    selector: 'day-view-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DayViewTimelineComponent
    extends BaseDirective
    implements OnInit, OnChanges, OnDestroy
{
    /** DateTZ object of the active date */
    @Input() public dateTz: DateTZ = DateNow(new Date());
    /** ID of the active level */
    @Input() public level: string;
    /** ID of the room type to filter displayed spaces by */
    @Input() public space_type: string;
    /** List of shown legend items */
    @Input() public legend: HashMap<boolean> = {};
    /** Whether to show only items with a setup and/or breakdown time */
    @Input() public overflow_only: boolean;
    /** Whether space bookings are being loaded */
    @Input() public loading: boolean;

    @Input() undo_action;

    /** List of timeblocks to display */
    public time_blocks: Identity[] = this.generateTimeBlocks();
    /** Top value of the content scroll */
    public scroll_top: number = 0;
    /** Left value of the content scroll */
    public scroll_left: number = 0;
    /** Offset of the scroll value when starting a drag */
    public scroll_offset: number = 0;
    /** List of spaces to display for  */
    public spaces: Space[] = [];
    /** List of spaces to display for  */
    public filtered_spaces: Space[] = [];
    /** Mapping of space ids to whether they are in view */
    public in_view: { [id: string]: boolean } = {};
    /** Percent of the day that is now */
    public now: number;
    /** Currently active hour of the day */
    public active_hour: string;
    /** Mapping of spaces loading booking date */
    public loading_space: { [id: string]: boolean } = {};
    /** Booking ID to open modal for */
    public view_id: string;
    /** Last update time for the space bookings */
    public last_update: number = 0;
    /** Whether an event is being dragged */
    public is_dragging: boolean;
    /** Client rectangle for the scroll area */
    public box: ClientRect;
    /** Whether to show the holding bay */
    public show_holding_bay: boolean = true;

    public space_zones: string[] = [];

    public is_today: boolean = this.isToday();
    public modalInitialized = false;

    /** Scrollable area of the day view */
    @ViewChild('scroll_area', { static: true }) private scroll_area: ElementRef<HTMLDivElement>;

    @ViewChild(CdkVirtualScrollViewport) virtualScroll: CdkVirtualScrollViewport;
    isEventMoving: boolean = false;
    bookingEl: any;
    timeoutID: any;
    column: any;

    /** Update scroll box when the window is resized */
    @HostListener('window:resize')
    public onResize() {
        if (this.scroll_area['elementRef'].nativeElement) {
            this.box = this.scroll_area['elementRef'].nativeElement.getBoundingClientRect();
        }
    }

    public get is_loading() {
        return this._booking_state_service.loading;
    }

    public isToday() {
        return new DateTZ({
            date: toMiddayUTC(this.dateTz).ms,
            is_local_tz: false,
            building_tz: this._org.building?.timezone || localStorage.getItem('CONCIERGE.timezone'),
        }).isSameDate(
            new DateTZ({
                date: Date.now(),
                is_local_tz: false,
                building_tz: this._org.building?.timezone || localStorage.getItem('CONCIERGE.timezone'),
            }),
            'day'
        );
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _booking_state_service: BookingStateService,
        private _org: OrganisationService,
        private _route: ActivatedRoute,
        private _renderer: Renderer2,
        private _dialog: MatDialog,
        private _zone: NgZone,
        private cdr: ChangeDetectorRef,
    ) {
        super();
    }

    public get holding_bay(): Space {
        const bld = this._org.building;
        /* istanbul ignore else */
        if (bld && bld.holding_bay) {
            return this._spaces.find(bld.holding_bay);
        }
        return null;
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._org.initialised.pipe(first((_) => _)).subscribe(() => {
                this._booking_state_service.startPollingEveryFiveMin();
                const zone_id = !this.level ? this._org.building.id : this.level;
                this.spaces = this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0);
                this.init();
                this.initSpaces();
                // Update time
                this.updateTime();
                this._zone.runOutsideAngular(() => {
                    this.interval('time', () => this.updateTime(), 15 * 1000);
                });
                // R--- return to see if the booking service needs updating
                this.subscription(
                    'dateTz',
                    this._booking_state_service.dateTz.subscribe((dateTz) => {
                        // const dateTz = new DateTZ({date});
                        this.dateTz = dateTz;
                    })
                );
            });
        });
        this.is_today = this.isToday();

        this.subscription(
            'spaces',
            this._spaces.listen('list').subscribe((spaces) => {
                const zone_id = !this.level ? this._org.building.id : this.level;
                this.spaces = this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0);
                this.init();
                this.initSpaces();
                // Update time
                this.updateTime();
            })
        );

    }

    ngAfterViewInit() {
        const d2 = this._renderer.createElement('div');
        this._renderer.addClass(d2, 'adjustable-room-block-2');
        this.virtualScroll.elementRef && this._renderer.appendChild(this.virtualScroll.elementRef.nativeElement, d2);
      }


    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.level && this._org.building) {
            const zone_id = !this.level ? this._org.building.id : this.level;
            this.spaces = this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0);
            this.updateTime();
            this.initSpaces();
        }
        if (changes.space_type) {
            this.initSpaces();
        }
    }

    public ngOnDestroy() {
        if (this._booking_state_service?.stopPolling) this._booking_state_service?.stopPolling();
    }

    public init() {
        this._service.title = 'Day View';
        this.subscription(
            'viewing',
            this._service
                .listen('CONCIERGE.day_view.viewing')
                .subscribe((event) => this.scrollIntoView(event) )
        );
        // Subscribe to route query changes
        this.subscription(
            'route.query',
            this._route.queryParamMap.subscribe((params) => {
                /* istanbul ignore else */
                if (params.has('view') || localStorage.getItem('CONCIERGE.view_booking')) {
                    this.view_id = params.get('view');
                    const details = localStorage.getItem('CONCIERGE.view_booking');
                    const spaceJson = localStorage.getItem('CONCIERGE.view_booking_space');
                    /* istanbul ignore else */
                    if (details) {
                        const space: Space | undefined = spaceJson
                            ? new Space(JSON.parse(spaceJson))
                            : undefined;
                        const booking = new Booking({ ...JSON.parse(details), room: space });
                        if(!this.modalInitialized) {
                            this.modalInitialized = true;
                            this.view(booking, space);
                            localStorage.removeItem('CONCIERGE.view_booking');
                            localStorage.removeItem('CONCIERGE.view_booking_space');
                        }
                    }
                }
            })
        );
        // this._zone.runOutsideAngular(() => {
        // this.interval(
        //     'scroll',
        //     () => {
        //         /* istanbul ignore else */
        //         if (this.scroll_area && this.scroll_area['elementRef'].nativeElement) {
        //             this.updateScroll({ target: this.scroll_area['elementRef'].nativeElement });
        //         }
        //     },
        //     1000
        //     );
        // });
        /* istanbul ignore else */
        if (this.scroll_area['elementRef'].nativeElement) {
            this.box = this.scroll_area['elementRef'].nativeElement.getBoundingClientRect();
        }
    }

    /**
     * Update scroll parameters to match the change in the scroll position
     * @param event
     */
    public updateScroll(event) {
        this.scroll_top = event.target.scrollTop;
        this.scroll_left = event.target.scrollLeft;
    }



    /** Generate time blocks to display on the day view */
    private generateTimeBlocks(): Identity[] {
        const blocks: Identity[] = [];
        const date = DateNow(new Date()).startOfValue('hour');
        for (let i = 0; i < 24; i++) {
            blocks.push({
                id: date.setValue({ hour: i }).formatDate('HH:mm'),
                name: date.setValue({ hour: i }).formatDate('h:mma').toLocaleLowerCase(),
            });
        }
        return blocks;
    }

    /** Update the now line position */
    private updateTime() {
        const now = new DateTZ({
            date: Date.now(),
            is_local_tz: false,
            building_tz: this._org.building?.timezone || localStorage.getItem('CONCIERGE.timezone'),
        });
        this.now = (now.dateHour + now.minutes / 60) / 24;
        this.active_hour = now.startOfValue('hour').formatDate('HH:mm');
        this.is_today = this.isToday();
        this.cdr.detectChanges();
    }

    /**
     * View booking details
     * @param booking Booking to view the details of
     * @param space Space that's selected.
     */
    private view(booking: Booking, space?: Space) {
        this._dialog.open(MeetingDetailsModalComponent, {
            data: {
                space: space || booking.space,
                booking,
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.view_id = null;
    }


    /**
     * Scroll booking to the event on the day view
     * @param booking Booking to scroll into view
     */
    private scrollIntoView(searchData:  Booking | Space ) {
        if (searchData && this.scroll_area && this.scroll_area['elementRef'].nativeElement) {
            this.column && this._renderer.removeClass(this.column, 'wild');

            /** Select space column element using space id in search data - 
             * space.id if seach data is space object or data.space.id if search data is booking object
             * 
             */
            const spaceId = searchData['space'] ? searchData['space'].id : searchData.id;
            const element: HTMLDivElement = this.scroll_area['elementRef']
                .nativeElement.querySelector(`#space-col-${spaceId}`);


            this.column = element;

            this.bookingEl && this._renderer.removeClass(this.bookingEl, 'wild');
            if(searchData['space'] && searchData['space'].id) 
                this.bookingEl = element?.querySelector(`#${searchData.id}`)?.querySelector('.event-container');


            /* istanbul ignore else */
            if (element) {
                this.timeoutID && clearTimeout(this.timeoutID);
                this._renderer.addClass(element, 'wild');
                this.bookingEl && this._renderer.addClass(this.bookingEl, 'wild');
                const scroll_box = this.scroll_area['elementRef'].nativeElement.getBoundingClientRect();

                let date_percent;
                if(searchData['space'] && searchData['space'].id) {
                     date_percent = Math.abs(
                        searchData['startDateTz'].dateDiff(
                            searchData['startDateTz'].startOfValue('day'),
                            'minutes'
                        )
                    ) / (24 * 60);
                }
                

                const box = element.getBoundingClientRect();
                const left = box.left - scroll_box.left + this.scroll_area['elementRef'].nativeElement.scrollLeft - 500;
                const top = (searchData['space'] && searchData['space'].id) ? 
                    this.scroll_area['elementRef'].nativeElement.scrollHeight * date_percent - 10 :
                    0


                this.scroll_area['elementRef'].nativeElement.scrollTo({
                    left: left,
                    top: top,
                    behavior: 'smooth',
                });

                this.timeoutID =  setTimeout(()=> {
                    element && this._renderer.removeClass(element, 'wild');
                    this.bookingEl &&  this._renderer.removeClass(this.bookingEl, 'wild');
                }, 3000);
            }
        }
    }

    /**
     * Setup an auto-scroll for the display view
     * @param x
     * @param y
     */
    public scrollView(x: number, y: number) {
        /* istanbul ignore else */
        if (x || y) {
            this.scroll_area['elementRef'].nativeElement.scrollTop += y || 0;
            this.scroll_area['elementRef'].nativeElement.scrollLeft += x || 0;
            /* istanbul ignore else */
            if (
                (y &&
                    this.scroll_area['elementRef'].nativeElement.scrollTop >=
                        this.scroll_area['elementRef'].nativeElement.scrollHeight) ||
                (x &&
                    this.scroll_area['elementRef'].nativeElement.scrollLeft >=
                        this.scroll_area['elementRef'].nativeElement.scrollWidth) ||
                (y && this.scroll_area['elementRef'].nativeElement.scrollTop <= 0) ||
                (x && this.scroll_area['elementRef'].nativeElement.scrollLeft <= 0)
            ) {
                this.clearInterval('auto_scroll');
            }
        //    this.updateScroll({ target: this.scroll_area['elementRef'].nativeElement });
        }
    }

    public clearSelected() {
        this._service.set('CONCIERGE.day_view.viewing', null);
    }

    public cancelScroll() {
        this.clearInterval('auto_scroll');
    }

    public dragChange(is_dragging: boolean) {
        if (is_dragging) {
            this.scroll_offset = this.scroll_area['elementRef'].nativeElement.scrollTop; 
            this.subscription(
                'listen_move',
                this._renderer.listen('window', 'mousemove', (event: MouseEvent) => {
                    const position = { x: event.clientX, y: event.clientY };
                    const y = position.y > this.box.bottom - this.box.height * 0.05
                                ? 1
                                : position.y < this.box.top + this.box.height * 0.05
                                ? -1
                                : 0;
                    const x = position.x > this.box.right - this.box.width * 0.05
                                ? 1
                                : position.x < this.box.left + this.box.height * 0.05
                                ? -1
                                : 0;
                    this.scrollView(x * 5, y * 5);
                    this._zone.runOutsideAngular(() => {
                        this.interval('auto_scroll', () => {
                            return this.scrollView(x * 40, y * 40) 
                        }, 15);
                    });
                    this.cdr.detectChanges();
                })
            );
            this.clearInterval('bookings');
        } else {
            this.cancelScroll();
            this.unsub('listen_move');
        }
    }

    private initSpaces() {
        const holding_bay = this.holding_bay;
        this.filtered_spaces = this.spaces.filter(
            (i) =>
                (!this.space_type || i.zones.indexOf(this.space_type) >= 0) &&
                (!holding_bay || i.id !== holding_bay.id) &&
                i.bookable
        );
        this.filtered_spaces.sort((a, b) => a.local_name.localeCompare(b.local_name));
        this.space_zones = this.spaces.map((i) => `space-col-${i.id}`);
    }



		public onEventDragging(isDragging: boolean): void {
			// Check if dragging is initiated
			if (isDragging) {
				// Store the current scroll position
				this.scroll_offset = this.scroll_area['elementRef'].nativeElement.scrollTop;
				// Subscribe to mouse move events
				this.subscribeToMouseMove();
				// Clear existing interval for bookings
				this.clearInterval('bookings');
			} else {
				// Cancel scroll operation
				this.cancelScroll();
				// Unsubscribe from mouse move events
				this.unsubscribeFromMouseMove();
			}
	}

	// Subscribe to mouse move events and handle scroll logic
	private subscribeToMouseMove(): void {
		this.subscription(
			'listen_move',
			this._renderer.listen('window', 'mousemove', (event: MouseEvent) => {
				// Get mouse position
				const position = { x: event.clientX, y: event.clientY };
				// Calculate scroll direction based on mouse position
				const [x, y] = this.calculateScrollDirection(position);
				// Scroll the view based on calculated direction
				this.scrollView(x * 5, y * 5);
				// Run auto-scroll logic
				this.runAutoScroll(x, y);
				// Detect changes to update the view
				this.cdr.detectChanges();
			})
		);
	}

	// Calculate scroll direction based on mouse position
	private calculateScrollDirection(position: { x: number, y: number }): [number, number] {
		const tolerance = 0.05;
		const x = this.calculateDirection(position.x, this.box.left, this.box.right, tolerance);
		const y = this.calculateDirection(position.y, this.box.top, this.box.bottom, tolerance);
		return [x, y];
	}

	// Calculate direction based on mouse position and element boundaries
	private calculateDirection(pos: number, min: number, max: number, tolerance: number): number {
		if (pos > max - this.box.width * tolerance) {
				return 1; // Scroll right
		} else if (pos < min + this.box.width * tolerance) {
				return -1; // Scroll left
		} else {
				return 0; // Do not scroll
		}
	}

	// Run auto-scroll logic
	private runAutoScroll(x: number, y: number): void {
		this._zone.runOutsideAngular(() => {
				this.interval('auto_scroll', () => this.scrollView(x * 10, y * 10), 10);
		});
	}

	// Unsubscribe from mouse move events
	private unsubscribeFromMouseMove(): void {
		this.unsub('listen_move');
	}

    public movingEvent(booking) {
        this.isEventMoving = booking.isMoving;
        this.loading_space[booking.movingId] = booking.isMoving;
        
    }

}

