import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { FormGroup, Validators, FormControl, FormBuilder, FormArray } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog } from '@angular/material/dialog';

import { Booking } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { DialogEvent } from '@mckinsey-converge/base';
import { ApplicationService } from '@mckinsey-converge/data-common';
import {
    ConfirmModalComponent,
    CONFIRM_METADATA,
} from '@mckinsey-converge/legacy-overlays';
import { BookingsService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';

export interface VisitorsFormModalData {
    /** Details of visitor listing */
    booking: Booking;
}

@Component({
    selector: 'a-visitors-form-modal',
    templateUrl: './visitors-form-modal.component.html',
    styleUrls: ['./visitors-form-modal.component.scss'],
})
export class VisitorsFormModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form fields for visitor booking */
    public form: FormGroup;
    /** Display string for the loading state */
    public loading: string;
    attendeesStatus: boolean = true;

    /** Visitor booking data */
    public get booking(): Booking {
        return this._data.booking;
    }

    /** Whether form fields contain an attendee */
    public get has_attendees() {
        return (
            this.form &&
            this.form.controls.attendees.value &&
            this.form.controls.attendees.value.length
        );
    }

    constructor(
        private _service: ApplicationService,
        private _bookings: BookingsService,
        private _booking_state: BookingStateService,
        private _dialog_ref: MatDialogRef<VisitorsFormModalComponent>,
        private _dialog: MatDialog,
        private fb: FormBuilder,
        @Inject(MAT_DIALOG_DATA) private _data: VisitorsFormModalData
    ) {
        super();
    }

    public ngOnInit(): void {

        this.form = new FormGroup({
            title: new FormControl(this.booking.title, [Validators.required]),
            attendees: new FormControl([...this.booking.attendees], [Validators.minLength(1)]),
            visitors: this.fb.array([])
        });
        if (this.booking.id) {
            this.form.controls.title.disable();
        }

        /** 
         * Create visitor form for existing visitors
        */
        this.booking.attendees.forEach( (el) => {
            if(el.external || el.visitor) {
                const visitorFG = this.createVisitor(el.name, el.email);
                this.getVisitors.push(visitorFG);
                // (<FormArray>this.form.controls.visitors).markAsTouched();
            }
        });
    }

    createVisitor(name, email) {
        return new FormGroup({
            name: new FormControl(name, { validators: [Validators.required, Validators.minLength(2)], updateOn: 'blur' }),
            email: new FormControl(email, {validators: [Validators.required, Validators.email], updateOn: 'blur' }),
        });
    }

    public get getVisitors() {
        return this.form.get("visitors") as FormArray;
    }

    /** Delete visitor booking */
    public delete() {
        const ref = this._dialog.open(ConfirmModalComponent, {
            ...CONFIRM_METADATA,
            data: {
                title: 'Delete meeting',
                content: 'Are you sure you wish to delete this meeting?',
                icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                action: 'Ok',
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    /* istanbul ignore else */
                    if (this.booking.id) {
                        this.loading = 'Deleting visitor booking...';
                        this._dialog_ref.disableClose = true;
                        this.booking.delete().then(
                            () => {
                                this.event.emit({ reason: 'done' });
                                this._service.notifySuccess('Successfully removed visitors.');
                                this._dialog_ref.close();
                            },
                            (err) => {
                                this.loading = null;
                                this._service.notifyError(
                                    `Failed to remove visitors. Error: ${err.message || err}`
                                );
                                this._dialog_ref.disableClose = false;
                            }
                        );
                    }
                }
                ref.close();
            })
        );
    }

    /** Save changes to visitor booking */
    public save() {
        this.timeout('save', () => {
            this.loading = 'Saving visitor booking...';
            this.form.markAllAsTouched();
            this._dialog_ref.disableClose = true;
            /* istanbul ignore else */
            if (this.form.dirty && this.form.valid) {
                const attendees = this.form.controls.attendees.value;
                for (const attendee of attendees) {
                    if (!attendee.email) {
                        attendee.email = `${Math.floor(Math.random() * 99999)}@guest.mckinsey.com`;
                    }
                }
                const new_booking = new Booking({ ...this.booking.toJSON(), ...this.form.value });
                this._bookings.save(new_booking).then(
                    (bkn) => {
                        this.event.emit({ reason: 'done', metadata: bkn });
                        this._service.notifySuccess(
                            this.booking.id
                                ? 'Successfully updated visitors.'
                                : 'Successfully added visitors.'
                        );
                        this._booking_state.replace(new Booking({ ...this.booking, ...bkn.toApiJSON() }));
                        this._dialog_ref.close();
                    },
                    (err) => {
                        this.loading = null;
                        this._service.notifyError(
                            `Failed to ${this.booking.id ? 'update' : 'add'} visitors. Error: ${
                                err.message || err
                            }`
                        );
                        this._dialog_ref.disableClose = false;
                    }
                );
            }
            else {
                this.loading = null;
                this._dialog_ref.disableClose = false;
                this._dialog_ref.close();
            }
        });
    }
}
