import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { BaseDirective, closestToTimeSlotTz } from "@mckinsey-converge/base";
import { arrayWithValues, generateWeekDays, RecurrencePeriod, recurrencePeriodToDurationType, RepeatPeriod, validDateTZ, WeekDays } from "@mckinsey-converge/data-common";
import { DateNow, DateTZ } from "@mckinsey-converge/date-tz";

@Component({
    selector: 'recurrence-pattern-form',
    templateUrl: './recurrence-pattern-form.component.html',
    styleUrls: ['./recurrence-pattern-form.component.scss']
})

export class RecurrencePatternForm extends BaseDirective implements OnInit {
    /** Form fields for booking */
    @Input() public form: FormGroup;
    @Input() disabled: boolean;

    /** Range of dates for the Start Date Picker*/
    public nowTz: DateTZ = DateNow(new Date());
    public fromStartTz: DateTZ = this.nowTz.setValue({ minute: Math.ceil(this.nowTz.minutes / 5) * 5 });
    public untilTz: DateTZ = this.nowTz.addValue({ years: 1 }).endOfValue('day');
    public timezone: string = '';
    

    /** Range of dates for the End Date Picker*/
    public fromEndTz: DateTZ;

    public repeat_period_options: Array<RepeatPeriod> = [
        {label: 'Day', id: RecurrencePeriod.DAILY},
        {label: 'Week',id: RecurrencePeriod.WEEKLY},
        {label: 'Month', id: RecurrencePeriod.ABSOLUTE_MONTHLY}
    ]
    public week_days: Array<WeekDays> = []

    public repeat_every_options


    public get recurrenceDateStartTzFormControl() {
        return this.form.get('dateTz')
    }

    public get recurrenceEndFormControl(){
        return this.form.get('recurrence_endTz')
    }

    public get recurrencePeriodFormControl(){
        return this.form.get('recurrence_period')
    }

    public get recurrenceIntervalFormControl(){
        return this.form.get('recurrence_interval')
    }

    public get recurrencePatternFormControl(){
        return this.form.get('recurrence_pattern')
    }

    public get recurrenceDaysFormControl(){
        return this.form.get('recurrence_days')
    }

    public ngOnInit(): void {
        // Setup some data to only run once, the previous giters were hogs
        this.timezone = this.form.get('timezone').value;
        this.nowTz = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: this.timezone });
        // R--- The from date is curious, why isn't it based on the form dateTz?
        this.repeat_every_options = new Array(1)
        this.fromStartTz = closestToTimeSlotTz(this.nowTz, 15)
        this.untilTz = this.nowTz.addValue({ years: 1 }).endOfValue('day');
        this.fromEndTz = this.fromStartTz.addValue({days: 1})

        this.setEndDateFromStartDate(this.recurrenceDateStartTzFormControl.value) //make sure end date is after start date

        if(!this.recurrencePeriodFormControl.value){
            this.recurrencePeriodFormControl.setValue(RecurrencePeriod.WEEKLY)
        }

        if(this.recurrencePeriodFormControl.value !== RecurrencePeriod.LIST){
            this.recurrencePatternFormControl.setValue(this.recurrencePeriodFormControl.value)
        }

        this.week_days = generateWeekDays(this.recurrenceDateStartTzFormControl.value.ms, { building_tz: this.fromStartTz.building_tz })
        this.generateMaxIntervals()

        if(this.recurrenceDaysFormControl.value.length > 0){
            this.week_days = this.week_days.map((day) => 
                ({...day, active: this.recurrenceDaysFormControl.value.includes(day.full_name)})
            )
        }

        if(!this.recurrenceDaysFormControl.value.length && this.recurrencePeriodFormControl.value === RecurrencePeriod.WEEKLY){
            const current_days = this.week_days.filter((day) => day.active).map((day)=>day.full_name)
            this.recurrenceDaysFormControl.setValue(current_days)
        }

        this.generateOcurrenceList()

        this.subscription('startTimeChanges',  this.recurrenceDateStartTzFormControl.valueChanges
            .subscribe((next) => {
                this.setEndDateFromStartDate(next)               
            })
        );

        this.subscription('endTimeChanges',  this.recurrenceEndFormControl.valueChanges
            .subscribe(() => {
                this.generateMaxIntervals();
            })
        );
        
        this.subscription('recurrencePeriodFormControlChanges',  this.recurrencePeriodFormControl.valueChanges
            .subscribe(() => {
                this.generateMaxIntervals();
            })
        );  
    }

    public setEndDateFromStartDate(startDateTz: DateTZ){
        let end_date = this.recurrenceEndFormControl.value.setValue({hour: startDateTz.dateHour, minute: startDateTz.minutes})

        /**Update the End date picker range */
        this.fromEndTz = startDateTz.isSameDate(this.untilTz,'day') ? startDateTz : startDateTz.addValue({days: 1})

        if(end_date.isBeforeDate(this.fromEndTz)){
            end_date = this.fromEndTz
        }

        this.recurrenceEndFormControl.setValue(end_date)

        this.generateMaxIntervals()
        this.updateActiveWeekday() 
    }

    public setRepeatPeriod(value: RecurrencePeriod){
        this.updateValidators(value)
        this.recurrencePeriodFormControl.setValue(value);
        this.recurrencePatternFormControl.setValue(value);
        this.recurrenceIntervalFormControl.setValue(1)
        this.generateOcurrenceList();
    }

    public setRepeatEvery(value: number){
        this.recurrenceIntervalFormControl.setValue(value)
        this.generateOcurrenceList()
    }

    public repeatTimeString(value: string){
        return this.recurrenceIntervalFormControl.value !== 1 ? `${value}(s)` : value
    }

    public get displayWeekdays(){
        return this.recurrencePatternFormControl.value === RecurrencePeriod.WEEKLY
    }

    /* Check if selected interval is valid and reset to 1 if not*/
    private validateRecurrenceInterval = () => {
        if(this.repeat_every_options.length < this.recurrenceIntervalFormControl.value){
            this.recurrenceIntervalFormControl.setValue(1)
        }    
    };

    /**
     * Generate max value for dorpdown interval
     */
    public generateMaxIntervals(){
        const start = this.recurrenceDateStartTzFormControl.value;
        const date_diff = this.recurrenceEndFormControl.value.dateDiff(start, this.periodToDurationType, this.periodToDurationType)
        //If no diff default to 1
        this.repeat_every_options = new Array(Math.round(date_diff) || 1)
        this.validateRecurrenceInterval();
        if(this.recurrencePeriodFormControl.value !== RecurrencePeriod.LIST){
            this.generateOcurrenceList()
        }
    }

    /**
     * Generate ocurrence list base on the selected interval
     */
    public generateOcurrenceList() {

        /**If selected period is list then we don't want 
         * to generate a list based on the repeats on selections*/
        if(this.recurrencePeriodFormControl.value === RecurrencePeriod.LIST){
            return
        }

        const occurrence = []
        const first_date = this.recurrenceDateStartTzFormControl.value;
        let start = first_date
        const end = this.recurrenceEndFormControl.value;
        const interval = this.recurrenceIntervalFormControl.value
        const recurr_days = this.week_days.filter(day => day.active)
        /**
         * isLastDayOfMonth : Boolean
         *  flag - to generate last day of every month in case of day or month reccuring booking if start date is last day of month
         */
        //  const isLastDayOfMonth = first_date.date.day === first_date.date.daysInMonth && this.recurrencePeriodFormControl.value === RecurrencePeriod.ABSOLUTE_MONTHLY;
        const isLastDayOfMonth = first_date.dateDay === 31 && this.recurrencePeriodFormControl.value === RecurrencePeriod.ABSOLUTE_MONTHLY ;
        
        if(this.recurrencePeriodFormControl.value === RecurrencePeriod.WEEKLY){
            start = start.startOfValue('week')
        }

        while (end.dateDiff(start.startOfValue('day'), this.periodToDurationType, this.periodToDurationType) >= 0) {
            
            if(this.recurrencePeriodFormControl.value !== RecurrencePeriod.WEEKLY){
                occurrence.push(start) 
                start = start.addValue({[this.periodToDurationType]: interval}, isLastDayOfMonth);
               /**
                * IF - should only consider resetting start under mentioned condition of ticket - https://acaprojects.atlassian.net/browse/MCK-2085
                *  In summery - If user select any date except 31st, other months in the series (no matter 30 or 31 day month) should have same date select as start date.
                *  If user select 31st as an start date, other month in the seleries should have select last day of the month (30th/31st/28th/29th).
                *  Exception for February - February should select 28th or 29th  If in above both selection includes date between 28th - 31st.
                * 
                */
                if(start.dateDay !== first_date.dateDay && start.dateDay !== start.date.daysInMonth && this.recurrencePeriodFormControl.value === RecurrencePeriod.ABSOLUTE_MONTHLY) {
                    start = start.setValue({day: first_date.dateDay});
                }   
            }else {
                recurr_days.forEach((day)=>{
                    const date = start.addValue({days: day.day_index})
                    if((date.isAfterDate(first_date) || date.isSameDate(first_date, 'day')) && 
                      (date.isBeforeDate(end) || date.isSameDate(end, 'day'))){
                        occurrence.push(date)
                    }
                })
                start = start.addValue({[this.periodToDurationType]: interval})
            }
        }

        this.form.controls.recurrence_count.setValue(occurrence.length)
        this.form.controls.recurrence_starts.setValue(occurrence.sort((d1, d2) => d1.ms - d2.ms))
        
    }

    public get periodToDurationType(){
        const period = this.recurrencePeriodFormControl.value
        return recurrencePeriodToDurationType(period)
    }

    /**
     * Update active weekday in the weekdays array
     */
     private updateActiveWeekday() {
        const _week_days = this.week_days.map((day)=>{
            if(this.recurrenceDaysFormControl.value.includes(day.full_name)){
                return {...day, active: true}
            }else{
                return {...day, active: false}
            }
        })
        this.week_days = [..._week_days]         
    }

    public setWeekDays(new_day: WeekDays){
        const days = this.recurrenceDaysFormControl.value
        if(this.disabled){
            return
        }

        if(!new_day.active){
            days.push(new_day.full_name)
        }else{
            const current_index = days.indexOf(new_day.full_name)
            days.splice(current_index,1)
        }
        this.recurrenceDaysFormControl.setValue([...days])
        this.updateActiveWeekday()
        this.generateOcurrenceList()
    }

    /**Update the recurrence days validator if period changes */
    public updateValidators(value: RecurrencePeriod){
        if(value === RecurrencePeriod.WEEKLY && this.recurrencePeriodFormControl.value !== RecurrencePeriod.WEEKLY){
            const arrayValidators = [Validators.required];
            arrayValidators.push(arrayWithValues);
            this.recurrenceDaysFormControl.setValidators(arrayValidators);
            this.recurrenceDaysFormControl.updateValueAndValidity();
        }else if(value !== RecurrencePeriod.WEEKLY && this.recurrencePeriodFormControl.value === RecurrencePeriod.WEEKLY){
            this.recurrenceDaysFormControl.clearValidators();
            this.recurrenceDaysFormControl.updateValueAndValidity();
        }
    }
}
