import {
  FormControl,
  FormGroup,
  Validators
} from '@angular/forms';

import { HashMap } from '@mckinsey-converge/base';
import { User } from './user.class';

/**
 * Generate form fields for the given user
 * @param user User to generate form for
 */
export function generateUserForm(user: User): FormGroup {
  if (!user) {
    throw Error('No user passed');
  }
  const fields: HashMap<FormControl> = {
    id: new FormControl(user.id || ''),
    name: new FormControl(user.name || ''),
    first_name: new FormControl(user.first_name || '', [Validators.required, Validators.pattern(/^(?![\d\s]+$)[^\d]+$/), Validators.maxLength(50)]),
    last_name: new FormControl(user.last_name || '', [Validators.required, Validators.pattern(/^(?![\d\s]+$)[^\d]+$/), Validators.maxLength(50)]),
    email: new FormControl(user.email || '', [Validators.email])
  };
  // Generate form group for booking item
    return new FormGroup(fields);
}
