import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog } from '@angular/material/dialog';
import { UploadManager, Amazon } from '@acaprojects/ngx-uploads';

import { CateringCategory } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { DialogEvent } from '@mckinsey-converge/base';

import * as blobUtil from 'blob-util';
import { CateringCategoriesService } from '@mckinsey-converge/data-common';
import { ConfirmModalComponent, CONFIRM_METADATA } from '@mckinsey-converge/legacy-overlays';

export interface CateringCategoryModalData {
    /** Category to edit */
    category: CateringCategory;
}

@Component({
    selector: 'a-catering-category-modal',
    templateUrl: './category-modal.component.html',
    styleUrls: ['./category-modal.component.scss'],
})
export class CateringCategoryModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form controls for editing category */
    public form: FormGroup;
    /** Display string for current loading state */
    public loading: string;
    /** Whether image is being uploaded */
    public uploading: boolean;

    public get category(): CateringCategory {
        return this._data.category;
    }

    public get image(): string {
        return this.form.controls.image_path.value;
    }

    public disabled: boolean = true

    constructor(
        private _service: ApplicationService,
        private _menu_categories: CateringCategoriesService,
        private _upload_manager: UploadManager,
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<CateringCategoryModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: CateringCategoryModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        this.form = new FormGroup({
            name: new FormControl(this.category.name, [Validators.required]),
            description: new FormControl(this.category.description),
            image_path: new FormControl(this.category.image_path),
            admin_only: new FormControl(this.category.admin_only),
        });

        this.form.get('admin_only').setValue(this.form.get('admin_only').value ?? false);

        if (!this.category.id) {
            this.disabled = false
        } else {
            this.form.valueChanges.subscribe(val => {
                for(let key of Object.keys(val)){
                    if(this.category[key] !== val[key]){
                        this.disabled = false
                        return
                    }
                    this.disabled = true
                }
            });
        }

    }

    public delete() {
        const ref = this._dialog.open(ConfirmModalComponent, {
            ...CONFIRM_METADATA,
            data: {
                title: `Delete Category`,
                content: `Are you sure you want to delete this Category? Deleting it will also remove all it's children`,
                icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                action: 'Ok',
            },
            autoFocus: false,
      backdropClass: 'mck-modal-backdrop',
        });
        ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                ref.componentInstance.loading = `Deleting Category...`;
                let e = null;
                await this._menu_categories.delete(this.category.id).catch((err) => e === err);
                ref.close();
                if (!e) {
                    this._service.set('DELETED.menu', this.category.id);
                    this._dialog_ref.close();
                }
            }
        });
    }

    public submit() {
        this.form.markAllAsTouched();
        /* istanbul ignore else */
        if (this.form.valid) {
            const category = new CateringCategory(this.category);
            const data = { ...category.toJSON(), ...this.form.value };
            
            /*  Keep the "stock" status as true for main categories when adding or updating to avoid problems.
                 The problem occurs when the top-level category is marked as "out of stock" if all items within it are out of stock. 
                 Then, if the items become available again, the top-level category stays marked as "out of stock," even if subcategories and items are in stock.
            */
            data.out_of_stock = false; 
            const request = category.id
                ? this._menu_categories.update(category.id, data)
                : this._menu_categories.add(data);
            request.then(
                (item) => {
                    (item as any).items = category.items
                    this.event.emit({ reason: 'done', metadata: item });
                    this._dialog_ref.close();
                },
                (err) =>
                    this._service.notifyError(
                        `Error ${this.category.id ? 'updating' : 'creating'} category. Error: ${
                            err.message || err
                        }`
                    )
            );
        }
    }

    /** Upload the image to the cloud */
    public uploadImage(event) {
        /* istanbul ignore else */
        if (event && event.target && event.target.files) {
            const files: FileList = event.target.files;
            /* istanbul ignore else */
            if (files.length) {
                const fileReader = new FileReader();
                fileReader.addEventListener('loadend', (e: any) => {
                    this.uploading = true;
                    const arrayBuffer = e.target.result;
                    const blob = blobUtil.arrayBufferToBlob(arrayBuffer, 'image/png');
                    this._upload_manager.upload([blob], { file_name: files[0].name });
                    const upload = this._upload_manager.uploads[
                        this._upload_manager.uploads.length - 1
                    ];
                    this.interval('upload', () => {
                        /* istanbul ignore else */
                        if (upload.complete || upload.error) {
                            /* istanbul ignore else */
                            if (upload.access_url && upload.complete) {
                                this.form.controls.image_path.setValue(upload.access_url);
                            }
                            this.uploading = false;
                            this.clearInterval('upload');
                        }
                    });
                });
                fileReader.readAsArrayBuffer(files[0]);
            }
        }
    }
}
