import {
    Component,
    EventEmitter,
    Input,
    Output
} from '@angular/core';
import { SelectOption } from '@mckinsey-converge/ui';

@Component({
    selector: 'mck-toggle',
    template: `
        <div class="mck-row toggle-container">
            <button *ngFor="let option of options" [class.desktop-only]="!option?.meta?.mobile"
                name="{{option.display}}"
                class="mck-toggle-button {{isSelected(option) ? 'mck-toggle-button-selected' : ''}}"
                mat-ripple
                (click)="onTapOption(option)"
                >{{option.display}} </button>
        </div>
    `,
    styleUrls: ['./toggle.component.scss']
})
export class ToggleComponent {

    @Input()
    options: SelectOption<any>[];

    @Input()
    selected: SelectOption<any>;

    @Output()
    selectedChange = new EventEmitter<SelectOption<any>>();

    public onTapOption(option: SelectOption<any>) {
        this.selectedChange.emit(option);
    }

    isSelected(option: SelectOption<any>) {
        return this.selected === option;
    }
}
