import { Injectable } from '@angular/core';
import {
    Actions,
    Effect
} from '@ngrx/effects';
import { createLoadingEffect } from '@mckinsey-converge/loading';
import { loadUser } from './user.actions';
import { UsersService } from '../../../data-common/src/lib/users/users.service';


@Injectable()
export class UserEffects {

    constructor(private actions: Actions,
                private userService: UsersService) {
    }

    @Effect()
    loadUser = createLoadingEffect(this.actions, loadUser, () => this.userService.loadCurrentUser());
}
