import {
    Component,
    OnInit,
    Input,
    Output,
    EventEmitter,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SpacesService, noWhitespaceValidator, OrganisationService, SpaceEquipments, SpaceStyles, setupRoomImages } from '@mckinsey-converge/data-common';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DropdownOpion, CngSelectData } from '@mckinsey-converge/ui';
import { roomTypeOptions, roomSubsetOptions, roomVisibilityOptions, canteringAvailabilityOptions, room, RoomSubset } from '../util/room-admin-utility';
import jsPDF from 'jspdf';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { ManagePhotosComponent } from '../manage-photos/manage-photos.component';
import { BaseDirective, DialogEvent, SpaceImageObject } from '@mckinsey-converge/base';
import { BehaviorSubject } from 'rxjs';



@Component({
    selector: 'app-room-details',
    templateUrl: './room-details.component.html',
    styleUrls: ['./room-details.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RoomDetailsComponent extends BaseDirective  implements OnInit {
    public reactiveForm: FormGroup;
    public room: room;
    public rowInfoData: any;
    public doc: jsPDF;
    public cityname: string;

    public roomTypeDD: CngSelectData;
    public roomSubsetDD: CngSelectData;
    public roomVisibilityDD: CngSelectData;
    public canteringAvailabilityDD: CngSelectData;
		public givenRoomType: any = 'internal';
		public givenRoomSubType: any = 'conference';
		public givenCateringAvailability: any;
		public uploadedPhotos : Array<any> = [];
		public roomSubsetOptions:RoomSubset;

    @Input() set rowInfo(value: any) {
        this.rowInfoData = value;
    }
    @Input() set location(value: string) {
        this.cityname = value;
    }

    @Output('parentFun') parentFun: EventEmitter<any> = new EventEmitter<any>();
		public loadImages = new BehaviorSubject<SpaceImageObject[]>([]);

    public disableButtons = false;
    public enableOverlay = false;
    public windowLocation = location;


		// Set to a config once we know where the files are hosted.
    public rootFolderURL = 'assets/img/rooms/';
		public foundImages: SpaceImageObject[] = [];
		public roomImages: SpaceImageObject[] = [];


		public get roomImagesFileSlug(): string {
			return this.rowInfoData.email.split('@')[0];
		}

    constructor(
        private _spaces: SpacesService,
        private cd: ChangeDetectorRef,
        private _snackBar: MatSnackBar,
        private _router: Router,
				private _dialog: MatDialog,
				private cdRef: ChangeDetectorRef,
				private _org:OrganisationService
    ) {
				super();
        this.room = {} as room;
    }

    public ngOnInit(): void {
        this.doc = new jsPDF();
        history.pushState({}, '');

        this.cityname = this?.rowInfoData?.building?.city;

				const internalSubSet: DropdownOpion[] = this._org.getRoomTypeSubsetDDOptions('internal_room_types');
				const externalSubset: DropdownOpion[] = this._org.getRoomTypeSubsetDDOptions('external_room_types');
				this.roomSubsetOptions = {internal: internalSubSet, external: externalSubset};

				const internal_or_external = this.rowInfoData?.roomobject?.internal_or_external; // ( this.rowInfoData?.roomobject?.filters?.external ? 'external' : 'internal') || this.rowInfoData?.settings;

				if(internal_or_external) {
					this.givenRoomType =    typeof(internal_or_external) === 'string'
																	? internal_or_external
																	: typeof(internal_or_external.value) === 'string'
																	? internal_or_external.value
																	: internal_or_external.value.value;  // temporary fix
				}
				const roomSubType = this.rowInfoData?.roomobject?.room_type;
				this.givenRoomSubType = Array.isArray(roomSubType) && roomSubType.length
										?  roomSubType[0]
										: Array.isArray(roomSubType) && !roomSubType.length
										? 'conference'
										: roomSubType  || 'conference';
				this.givenCateringAvailability = this.rowInfoData?.roomobject?.filters?.catering ? 'yes-onsite' : 'no';


				this.rowInfoData && this.setAllDropdowns(this.rowInfoData);


        this.reactiveForm = new FormGroup({

            roomname: new FormControl(this.rowInfoData.roomname, [
                Validators.required,
                Validators.maxLength(250),
                Validators.minLength(1),
                Validators.pattern('[^=`~!#$%*{};\'"]*'),
                noWhitespaceValidator,
            ]),

            city: new FormControl({ value: this.cityname, disabled: true }, []),
            level: new FormControl({ value: this.rowInfoData.level, disabled: true }, []),
            roomnumber: new FormControl({ value: this.rowInfoData.roomnum, disabled: true }, []),




            internal_or_external: new FormControl( this.givenRoomType , []),
						room_type: new FormControl( this.givenRoomSubType, [] ),

            capacity: new FormControl(this.rowInfoData.capacity.toString(), [
                Validators.required,
                Validators.maxLength(250),
                Validators.pattern(/^(?!0+$)[0-9]{1,10}$/),
            ]),


            roomvisibility: new FormControl(this.rowInfoData.bookable),
						catering: new FormControl(this.rowInfoData?.roomobject?.filters?.catering || false),

						// equipment_code === booking charge code ! if possible change equipment_code key to booking_charge_code
						equipment_code: new FormControl( this.rowInfoData?.roomobject?.filters?.equipment_code || false), // || this.rowInfoData?.settings['equipment_code']
						charge_code: new FormControl(this.rowInfoData?.roomobject?.filters?.charge_code || false), // || this.rowInfoData.settings.charge_code

            vc: new FormControl(this.isFeatureChecked('vc')),
            conference_phone: new FormControl(this.isFeatureChecked('conference_phone')),
            wireless_content_sharing: new FormControl( this.isFeatureChecked('wireless_content_sharing')),
            flipchart: new FormControl(this.isFeatureChecked('flipchart')),
            glassboard: new FormControl(this.isFeatureChecked('glassboard')),
            whiteboard: new FormControl(this.isFeatureChecked('whiteboard')),
            natural_light: new FormControl(this.isFeatureChecked('natural_light')),
            boardroom: new FormControl(this.isFeatureChecked('boardroom')),
            cocktail: new FormControl(this.isFeatureChecked('cocktail')),
            theatre: new FormControl(this.isFeatureChecked('theatre')),
            workshop: new FormControl(this.isFeatureChecked('workshop')),
            u_shape: new FormControl(this.isFeatureChecked('u_shape')),
            miscellaneous: new FormControl(this.isFeatureChecked('miscellaneous')),
        });


					setupRoomImages(this, [1,2,3], this.rootFolderURL, this.roomImagesFileSlug);

					this.subscription(
						'found_images',
						this.loadImages.subscribe((imgArr: SpaceImageObject[]) => {
							this.roomImages = imgArr;
							this.cdRef.detectChanges();
						}));

    }



    public isFeatureChecked(param: string) {
        switch (param) {
            case 'vc': {
                return this.rowInfoData['equipment']?.includes(param) || this.rowInfoData.roomobject['filters']?.vc;
            }
            case 'natural_light': {
                return this.rowInfoData.roomobject['filters']?.natural_light; // || this.rowInfoData['settings']?.natural_light;
            }
            default: {
                // const { extra_features } = this.rowInfoData['settings'];
                return  this.rowInfoData['equipment']?.includes(param) || this.rowInfoData['room_style']?.includes(param); //extra_features && extra_features?.includes(param) ||
            }
        }
    }

    get capacity() {
			return this.reactiveForm.get('capacity');
    }

    get roomname() {
			return this.reactiveForm.get('roomname');
    }

    public onSubmit(formValue: any) {
			let roomObject = Object.assign({}, this.rowInfoData.roomobject);

			const allowed_keys = [
					'edge_id',
					'name',
					'description',
					'email',
					'equipment',
					'capacity',
					'filters',
					'features',
					'bookable',
					'installed_ui_devices',
					'zones',
					'modules',
					'room_type',
					'room_style',
					'settings',
					'created_at',
					'support_url',
					'version',
					'id',
			];

			// this is to filter unwanted keys and make the request body match room model
			roomObject = Object.keys(roomObject)
					.filter((key) => allowed_keys.includes(key))
					.reduce((obj, key) => {
							obj[key] = roomObject[key];
							return obj;
					}, {});


			// roomObject.settings.room_name = formValue.roomname;
			roomObject.simple_name = formValue.roomname;

			if(formValue.internal_or_external) {
				// roomObject.settings.internal_or_external = formValue.internal_or_external;
				roomObject.filters['external'] = formValue.internal_or_external === 'external';
			}
			roomObject.room_type = formValue.room_type;
			roomObject.capacity = parseInt(formValue.capacity);

			roomObject.bookable = formValue.roomvisibility;
			// roomObject.settings['equipment_code'] = formValue.equipment_code;
			roomObject.filters['equipment_code'] = formValue.equipment_code;

			// roomObject.settings['catering'] = formValue.catering
			roomObject.filters['catering'] = formValue.catering;
			// roomObject.settings['charge_code'] = formValue.charge_code;
			roomObject.filters['charge_code'] = formValue.charge_code;



			// roomObject.features = getExtraFeatures();
			// roomObject.settings.extra_features = getExtraFeatures();
			roomObject.filters.vc = formValue.vc;
			// roomObject.settings.vc = formValue.vc;
			// roomObject.settings.natural_light = formValue.natural_light;
			roomObject.filters['natural_light'] = formValue.natural_light;
			getExtraFeatures();

			function getExtraFeatures() {
					const excludeFromExtraFeatures = ['capacity','charge_code','internal_or_external', 'roomvisibility', 'roomname', 'equipment_code', 'catering', 'room_type'];
					// let features = '';

					Object.keys(formValue).forEach((element) => {
							const isEqAndDetail = formValue[element];
							// console.log(SpaceEquipments[element]);
							if (!excludeFromExtraFeatures.includes(element) && isEqAndDetail) {
									// features += ` ${element}`;

									if(SpaceEquipments[element]!==undefined && !roomObject?.equipment.includes(element) ) {
										roomObject?.equipment.push(element);
									}
									 else if( SpaceStyles[element]!==undefined && !roomObject?.room_style.includes(element) ){
										  roomObject.room_style.push(element);
									 }
							} else if(!isEqAndDetail) {
								// roomObject.features = roomObject.features.replace(element, '');
								roomObject.equipment = SpaceEquipments[element]!==undefined ? roomObject.equipment.filter((value)=>value!=element) : roomObject.equipment;
								roomObject.room_style = SpaceStyles[element]!==undefined ? roomObject.room_style.filter((value)=>value!=element) : roomObject.room_style;
							}
					});
					// return features;
			}

			const body = roomObject;

			if (this.reactiveForm.invalid) {
					for (const control of Object.keys(this.reactiveForm.controls)) {
							this.reactiveForm.controls[control].markAsTouched();
					}
			} else {
					this.disableButtons = true;
					this.enableOverlay = true;

					this._spaces.updateSpace(this.rowInfoData.id, body).subscribe(
							(data: any) => {
									this.updateAllRooms(); // TO DO: this call enabled to pull all updated rooms to use on day/week view, In future remove this call here and day/week view should handle it there
									this.disableButtons = false;
									this.enableOverlay = false;
									this.reactiveForm.markAsPristine();
									this.cd.markForCheck();
									this._snackBar.open('Operation Successful!', 'Close', {
										duration: 3000,
										panelClass: ['room-admin-toast'],
									});
								},
							(err: any) => {
									console.log(err);
									this._snackBar.open('There was an error', 'Close', { duration: 3000 });
									this.disableButtons = false;
									this.enableOverlay = false;
									this.cd.markForCheck();
							}
					);
			}
			this.room = this.reactiveForm.value;
    }

    public updateAllRooms(): void {
		 // Adjusted the query by incorporating the "zone_ids" option to filter rooms based on specific zone IDs instead of considering all thousands of rooms.
		 const option = {
            zone_ids: this._org.building.id
        }
        this._spaces.query(option, true).then(() => { }).catch(() => {});
    }

    public onCancel() {
			this._router.navigate(['room-admin']);
    }




		public setAllDropdowns(rowInfoData) {
			// const {internal_or_external} = rowInfoData?.settings;
			// const roomType =  typeof(internal_or_external) === 'string' ? internal_or_external : internal_or_external.value;

			this.roomTypeDD = {
				ddBoxClass: 'room-type',
				options: roomTypeOptions,
				defaultSelected: roomTypeOptions.filter(el => el.value ===  this.givenRoomType )[0],
				onChangeCallback: this.roomTypeChanged.bind(this)
			};

			this.roomSubsetDD = {
					disabled: false,
					ddBoxClass: 'room-subset',
					options: this.roomSubsetOptions[this.roomTypeDD?.defaultSelected?.value.toString()],
					defaultSelected: this.roomSubsetOptions[this.roomTypeDD?.defaultSelected?.value.toString()].filter(el => el.value === this.givenRoomSubType )[0],
					onChangeCallback: this.roomTypeSubsetChanged.bind(this)
			};

			this.roomVisibilityDD = {
					ddBoxClass: 'room-visibility',
					options: roomVisibilityOptions,
					defaultSelected: roomVisibilityOptions.filter(el => el.value === rowInfoData?.bookable.toString())[0],
					onChangeCallback: this.roomVisibilityChanged.bind(this)
			};

			this.canteringAvailabilityDD = {
					disabled: false,
					ddBoxClass: 'catering-availability',
					options: canteringAvailabilityOptions,
					defaultSelected: canteringAvailabilityOptions.filter(el => el.value === this.givenCateringAvailability )[0],
					onChangeCallback: this.roomCateringChanged.bind(this)
			};
		}

    /**
	 * store selected border by sorting method and apply to
	 * @param sortingValue
	 */
	public roomTypeChanged(type: DropdownOpion) {
			this.reactiveForm.controls.internal_or_external.setValue(type.value);
			this.reactiveForm.markAsDirty();

			if(!this.roomSubsetDD.disabled) {
				this.roomSubsetDD.options = this.roomSubsetOptions[type.value.toString()];
        this.roomSubsetDD.defaultSelected = type.value === this.givenRoomType ? this.roomSubsetDD.options.filter(el => el.value ===  this.givenRoomSubType )[0] : this.roomSubsetOptions[type.value.toString()][0];
				this.reactiveForm.controls.room_type.setValue(this.roomSubsetDD.defaultSelected.value);
			}
			this.reactiveForm.markAsDirty();
			this.cdRef.detectChanges();
	}
	public roomTypeSubsetChanged(subset: DropdownOpion ) {
			// console.log(subset);
			this.reactiveForm.controls.room_type.setValue(subset.value);
			this.reactiveForm.markAsDirty();
	}

	public roomVisibilityChanged(visibility: DropdownOpion) {
		this.reactiveForm.controls.roomvisibility.setValue(visibility.value==='true');
		this.reactiveForm.markAsDirty();
	}

	public roomCateringChanged(isCatering: DropdownOpion) {
		this.reactiveForm.controls.catering.setValue(!(isCatering.value==='no'));
		this.reactiveForm.markAsDirty();
	}

    /**
     * Function drawCanvas - Generate canvas as per required design for QR code
     * https://mckinsey.atlassian.net/browse/CNG-10
     * @param img
     * @returns canvas image
     */
    drawCanvas(img): string | HTMLCanvasElement | HTMLImageElement {
			// TO DO : move to enum/utility file, while room detail more editable field developement
			const width = 500;
			const height = 550;
			const arcY = 300;
			const arc_X_Center = width/2;
			const otrRadius = arc_X_Center - 2 ;
			const whtRadius = otrRadius - 12;
			const innrRadius = otrRadius - 15;

			const canvas: HTMLCanvasElement = document.createElement('canvas');
			canvas.setAttribute('width', width.toString());
			canvas.setAttribute('height', height.toString());
			const ctx = canvas.getContext('2d');

			ctx.strokeStyle = "transparent";
			ctx.moveTo(arc_X_Center, 0);
			ctx.lineTo(arc_X_Center, height);
			ctx.stroke();

			ctx.font = '14px McKinsey Sans, Helvetica Neue, Arial, sans-serif';
			ctx.textAlign = "center";
			ctx.fillText(this.rowInfoData?.roomname , arc_X_Center, 20);

			//oputer arc
			ctx.beginPath();
			ctx.arc(arc_X_Center, arcY, otrRadius, 0, 2 * Math.PI);
			ctx.fillStyle = '#051C2C';
			ctx.fill();
			ctx.stroke();

			//inner white arc
			ctx.beginPath();
			ctx.arc(arc_X_Center, arcY, whtRadius, 0, 2 * Math.PI);
			ctx.fillStyle = '#FFFFFF';
			ctx.fill();
			ctx.stroke();

			//second inner arc
			ctx.beginPath();
			ctx.arc(arc_X_Center, arcY, innrRadius, 0, 2 * Math.PI);
			ctx.fillStyle = '#051C2C';
			ctx.fill();
			ctx.stroke();

			// header text
			ctx.fillStyle = '#FFFFFF';
			ctx.font = '44px Bower';
			ctx.fillText('Book Me', arc_X_Center, 166);

			//centeral rectangle
			ctx.fillRect(137, 187, 225, 225);
			//fill central rectangle with color
			ctx.fillStyle = '#FFFFFF';

			// fill text at the bottom
			ctx.font = '29px McKinsey Sans, Helvetica Neue, Arial, sans-serif';
			ctx.fillText('in Converge', arc_X_Center, 470);

			// draw QR code image into center of canvas
			img !== null && ctx.drawImage(img, 137, 187);

			// create data url
			const dataURL: string | HTMLCanvasElement | HTMLImageElement =
			canvas.toDataURL('image/png');
			return dataURL;
    }

    /**
     * Function downloadQRPdf
     * download generated QR code to the local system
     */
    public downloadQRPdf(roomid?: string): void {
        // Get the generated QR code img and modifiy as per design
        const qrcode = document.querySelector('.qr-code img');
        let imageData = this.drawCanvas(qrcode);
        // Add generated qr code image to the pdf
        this.doc.addImage(imageData, 'JPEG', 3, 5, 100, 100);

				// this.doc.addImage(imageData, "JPEG", 107, 5, 100, 100);
				// this.doc.addImage(imageData, "JPEG", 3, 130, 100, 100);
				// this.doc.addImage(imageData, "JPEG", 107, 130, 100, 100);
        // this.doc.addImage(imageData, "JPEG", 10, 10);   // left commented for alignment reference later in future
        // doc.addPage();
				// this.openQRInConverge();
        this.doc.save(`${this.rowInfoData.id}.pdf`);
    }

    /**
     * Not in use but keeping for future use
     * Function openQRInConverge
     * Open QR code in seperate tab
     */
    public openQRInConverge(): void {
        const qrcode = document.querySelector('.qr-code img');
        const dataURL = this.drawCanvas(qrcode);
        const win = window.open();
        win &&
            win.document.write(
                '<div style=" height: 100%; flex-direction: row; box-sizing: border-box; display: flex; place-content: flex-start; align-items: flex-start; flex-flow: row wrap; box-sizing: border-box; display: flex;" >\
							<iframe src="' +
                    dataURL +
                    '" frameborder="0" style="border:0; width:500px; height:550px; margin:10px 10px 0 0 " allowfullscreen> </iframe>\
						</div>'
            );
    }

		/** Open modal for adding equipment requirements to booking */
		public openManagePhotosModal() {
			const equipment_ref = this._dialog.open<ManagePhotosComponent>(ManagePhotosComponent, {
				maxWidth: '95vw',
				width: '70em',
				data: {
					photos: this.roomImages,
					room: this.rowInfoData
					}
			});
			this.subscription(
				'equipments_modal_events',
				equipment_ref.componentInstance.event.subscribe((event: any) => {
					/* istanbul ignore else */

						// this.uploadedPhotos = event;
						setupRoomImages(this, [1,2,3], this.rootFolderURL, this.roomImagesFileSlug);
						//equipment_ref.close();

				})
			);
			equipment_ref.afterClosed().subscribe(() => {
				this.cdRef.detectChanges();
				this.unsub('equipments_modal_events')
			});
		}


		public get timestamp () {
			return (new Date()).getTime()
		}


}
