import { ChangeDetectionStrategy, Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface RoomAvaibilityModalData {
	headerText: string,
	message: string,
	buttonLabel: string,
	callback: Function,
    shouldClose: boolean
}

@Component({
    selector: 'conflict-alert-modal',
    templateUrl: 'conflict-alert-modal.component.html',
    styleUrls: ['./conflict-alert-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class ConflictAlertModalComponent implements OnInit {
    constructor(public dialogRef: MatDialogRef<ConflictAlertModalComponent>,
        @Inject(MAT_DIALOG_DATA) public dialogData: RoomAvaibilityModalData) {
    }
    ngOnInit(): void {}

    public done() {
        this.dialogData?.callback(); this.dialogRef.close()
    }
}
