import {
    Component,
    EventEmitter,
    Inject,
    OnInit,
    Output,
} from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import {
    BehaviorSubject,
    Observable,
    of,
    Subject
} from 'rxjs';
import {
    catchError,
    debounceTime,
    distinctUntilChanged,
    first,
    map,
    switchMap,
    take,
    withLatestFrom
} from 'rxjs/operators';
import {
    Building,
    mapBuildingToSelectOption,
    mapCityToAllSelectOption,
    OrganisationService,
    SettingsService,
    Space,
    SpaceBookingRuleOptions,
    SpacesService,
    toggleSelectedByGroup,
    User,
    UsersService
} from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import {
    DialogEvent,
    HashMap,
    Identity,
    unique
} from '@mckinsey-converge/base';
import { NoCateringModalComponent } from '../no-catering-modal/no-catering-modal.component'
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { DateTZ } from '@mckinsey-converge/date-tz';
import { FormControl, FormGroup } from '@angular/forms';
import { SelectOption } from '@mckinsey-converge/ui';

export interface SpaceSelectModalData {
    /** List of selected spaces */
    spaces: Space[];
    /** Start time for availability of spaces */
    dateTz: DateTZ;
    /** Duration of the availability block to look for */
    duration: number;
    /** Host of the booking being made */
    host: User;
    /** Whether multiple spaces can be selected */
    multiple: boolean;
    /** Force zone ids */
    zone_ids?: string;
    /** Booking needs catering */
    needsCatering?: boolean,
}

@Component({
    selector: 'a-space-select-modal',
    templateUrl: './space-select-modal.component.html',
    styleUrls: ['./space-select-modal.component.scss']
})
export class SpaceSelectModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether space list is being loaded */
    public loading: boolean;
    /** List of available spaces to select from */
    public spaces: Space[] = [];
    /** Active building to filter spaces on */
    public active_building: Building;
    /** Active building to filter spaces on */
    public active_type: Identity;
    /** List of items from an API search */
    public search_results$: Observable<Space[]>;
    /** Subject holding the value of the search */
    public readonly change$ = new Subject<string | string[]>();
    /** List of currently selected spaces */
    public selected_spaces: Space[] = [];
    /** Mapping as to whether a space is by request */
    public request_map: HashMap<boolean> = {};
    /** Whether selected list should be shown on mobile */
    public show_list: boolean;

    /** host to load (only in staff) **/
    public host: User;

    /** List of available buildings */
    public types: Identity[] = [];

    public isConcierge: boolean;
    public frontend: string;

    /** List of available buildings */
    public get buildings(): Building[] {
        return this._org.buildings;
    }

    /** Whether multiple spaces can be selected */
    public get multiple(): boolean {
        return false;
    }

    public get has_zone(): boolean {
        return !!this._data.zone_ids;
    }

    public formGroup = new FormGroup({
        location: new FormControl([]),
    });

    public options = new BehaviorSubject<SelectOption<any>[]>([]);

    public selectedOptions = new BehaviorSubject<SelectOption<any>[]>([]);


    public noCateringDialogRef: MatDialogRef<NoCateringModalComponent, any>;

    /** List of zones to filter the spaces on */
    public zone_ids: string[] = [];


    public get initialBuildingIds(): string[] {
        return this._org.building ? [this._org.building.id] : [];
    }

    public savedSelection

    constructor(
        private _org: OrganisationService,
        private _users: UsersService,
        private _spaces: SpacesService,
        private _settings: SettingsService,
        private _dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) private _data: SpaceSelectModalData
    ) {
        super();
        this.isConcierge = _settings.concierge;
        this.frontend = _settings.frontend;
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            let request_id = 0;
            // Listen for input changes
            this.search_results$ = this.change$.pipe(
                debounceTime(400),
                distinctUntilChanged(),
                switchMap((bld) => {
                    this.loading = true;
                    const zone_ids: string[] = [];
                    /* istanbul ignore else */
                    if (this.active_building) {
                        zone_ids.push(this.active_building.id);
                    }

                    if (bld?.includes('|')) {
                        bld = this.savedSelection
                    }

                    const id = request_id;
                    return this._spaces.available({
                        zone_ids: bld?.length ? bld : this._data.spaces[0].building.id || zone_ids.join(','),
                        dateTz: this._data.dateTz,
                        duration: this._data.duration
                    }).then(async (list) => ({ id, list }));
                }),
                catchError((_) => of({ id: request_id, list: [], error: _ })),
                map((resp: { id: number, list: Space[] }) => {
                    this.loading = false;
                    const filtered_list = resp.list.filter((space: Space) => {
                        // if concierge skip rules check.
                        if (this._settings.concierge) {
                            return (this.active_type && this.active_type.id ? space.zones.includes(`${this.active_type.id}`) : true) &&
                                !this.selected_spaces.find((s) => s.id === space.id);
                        }

                        const rules = space.rulesFor({
                            dateTz: this._data.dateTz,
                            duration: this._data.duration,
                            host: this.host || this._data.host
                        });
                        return (
                            rules.hide !== true &&
                            (this.active_type && this.active_type.id
                                ? space.zones.includes(`${this.active_type.id}`)
                                : true) &&
                            !this.selected_spaces.find((_space) => _space.id === space.id)
                        );
                    });
                    return resp.id === request_id ? filtered_list : this.spaces;
                })
            );
            // Process API results
            this.subscription(
                'search_results',
                this.search_results$.subscribe((list) => {
                    this.spaces = list.filter((space) => {
                        if (!space.available) {
                            return false;
                        }
                        for (const zone of this.zone_ids) {
                            if (space.zones.includes(zone)) {
                                return true;
                            }
                        }
                        return !this.zone_ids.length;
                    });
                    this.spaces.sort((a, b) => this.sort(a, b));
                    if (!this.selectedOptions.value.length) {
                        this.setDefaultBuilding()
                    }
                })
            );
            this.active_building = this._data.spaces?.length
                ? this._org.buildings.find((i) => i.id === this._data.spaces[0].level.building_id)
                : this._org.building;
            this.types = [{ id: '', name: 'Any Space Type' } as any].concat(
                this._org.space_types || []
            );
            this.active_type = this.types[0];
            this.change$.next('');
        });

        this.subscription('buildings', this._org.listen('buildings').subscribe((bld_list) => {
            const city = this._org.building.city
            const timezone = this._org.building.timezone
            const grouped_by_city = {
                key: { name: city, timezone },
                value: bld_list?.filter((bld) => bld.city === city) || []
            }
            const city_buildings = grouped_by_city.value.map(
                (bld) => mapBuildingToSelectOption(grouped_by_city.key.name, bld)
            )
            const buildings = [
                mapCityToAllSelectOption(grouped_by_city.key),
                ...city_buildings
            ]
            this.options.next(buildings)
        }));

    }

    /** Add space to selected list */
    public addSpace(space: Space) {
        this.selected_spaces.push(space);
        this.selected_spaces = unique(this.selected_spaces, 'id');
    }

    /** Remove space from selected list */
    public removeSpace(space: Space) {
        this.selected_spaces = this.selected_spaces.filter((a_space) => a_space.id !== space.id);
    }

    /** Emit current state of the selected list */
    public save(list: Space[]) {
        if (!this._data.needsCatering) {
            // No catering order to warn about
            this.event.emit({ reason: 'done', metadata: list });
            return;
        }
        // Check if the room has catering 
        if (list[0].has_catering) {
            this.event.emit({ reason: 'done', metadata: list });
            return;
        }
        // If not warn that the room doesn't have catering
        this.noCateringDialogRef = this._dialog.open(NoCateringModalComponent, {
            panelClass: 'mck-modal-mini',
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.subscription(
            'no-catering-modal',
            this.noCateringDialogRef.componentInstance.event.subscribe((event) => {
                if (event.reason === 'done') {
                    this.event.emit({ reason: 'done', metadata: list });
                }
                if (event.reason === 'close') {
                    this.event.emit({ reason: 'close' });
                }
            })
        );
    }

    private update() {
        const map = {};
        const options: SpaceBookingRuleOptions = {
            dateTz: this._data.dateTz,
            duration: this._data.duration,
            host: this._data.host
        };
        this.selected_spaces.forEach((space) => (map[space.id] = space.byRequest(options)));
        this.spaces.forEach((space) => (map[space.id] = space.byRequest(options)));
        this.request_map = map;
    }

    public setDefaultBuilding() {
        const defaultBld = this.options.value.filter(opt => opt.value === this._org.building.id)
        this.selectedOptions.next(defaultBld)
    }

    /**
     * Compare two spaces to determine order
     * @param space_a
     * @param space_b
     */
    public sort(space_a: Space, space_b: Space) {
        const bld = this._org.buildings.find((bld) => space_a.zones.includes(bld.id)) || { name: '', sort_order: [] };
        const bld_b = this._org.buildings.find((bld) => space_b.zones.includes(bld.id));
        if (bld && bld !== bld_b) {
            return bld.name.localeCompare(bld_b?.name);
        }
        const sort_order = [...bld.sort_order].reverse();
        for (const zone_id of sort_order) {
            if (zone_id === '*') {
                continue;
            }
            const a_has_zone = space_a.zones.indexOf(zone_id) >= 0;
            const b_has_zone = space_b.zones.indexOf(zone_id) >= 0;
            if (a_has_zone && !b_has_zone) {
                return 1;
            } else if (b_has_zone && !a_has_zone) {
                return -1;
            }
        }
        return space_a.name.localeCompare(space_b.name);
    }

    public toggle(option: SelectOption<any>) {
        // update the selected value
        this.selectedOptions.pipe(
            take(1),
            withLatestFrom(this.options),
            map(([selectedOptions, options]) => {
                return toggleSelectedByGroup(options, selectedOptions, option);
            })
        ).subscribe(next => {
            const formData = this.formGroup.value;
            this.formGroup.setValue({
                location: formData.location,
            });
            this.selectedOptions.next(next);
            this.savedSelection = next.map((bld) => bld.value)

        });
    }

    /**
     * Update the selected space list
     * @param space Space to add/toggle in the selected list
     */
    public selectSpace(space: Space) {
        const list = this.spaces;
        if (list.find((item) => item.id === space.id)) {
            this.spaces = list.filter((item) => item.id !== space.id);
        } else {
            this.spaces = [space];
        }
    }

    public clearList() {
        this.spaces = []
        this.savedSelection = []
    }

    public triggerSearch = () => {
        if (this.savedSelection.length) {
            this.change$.next(this.savedSelection)
        } else {
            this.spaces = []
        }
    }

}
