import {
    Pipe,
    PipeTransform
} from '@angular/core';
import { SelectOption } from '../types';

/**
 * Patch orphaned SelectOption children into acting as their own group headers.
 *
 * This intends to help in the common scenario where filters have (for whatever
 * reason) prevented the child's usual group header from appearing.
 */
@Pipe({
    name: 'groupHeaderPatch',
})
export class GroupHeaderPipe implements PipeTransform {
    transform(items: SelectOption<any>[]): SelectOption<any>[] {
        if (!items) {
            return [];
        }

        return items.map(v => {
            const hasHeader = !!items.find(i => i.groupHeader && i.groupId === v.groupId);
            return {
                ...v,
                displayAsHeader: !hasHeader,
            }
        })
    }

}
