import {
    Component,
    forwardRef,
    Input,
    OnInit,
    ViewContainerRef
} from '@angular/core';
import { SelectOption } from '../types';
import {
    ControlValueAccessor,
    NG_VALUE_ACCESSOR
} from '@angular/forms';
import { BaseTemplateComponent } from '../base-template.component';
import { BehaviorSubject } from 'rxjs';

@Component({
    selector: 'mck-radio-select',
    template: `
        <ng-template>
            <div class="mck-column mck-form-input mck-radio-select">
                <mck-label [required]="required">{{label}}</mck-label>
                <mat-radio-group [disabled]="disabled"
                                 [value]="(value | async).value"
                                 (change)="setValue($event.value)">
                    <mat-radio-button *ngFor="let option of options"
                                      [value]="option.value">{{option.display}}</mat-radio-button>
                </mat-radio-group>
            </div>
        </ng-template>
    `,
    styleUrls: ['./radio-select.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => RadioSelectComponent),
            multi: true
        }
    ]
})
export class RadioSelectComponent extends BaseTemplateComponent implements OnInit, ControlValueAccessor {

    @Input()
    label: string;

    @Input()
    required?: boolean;

    @Input()
    options: SelectOption<any>[];

    public value = new BehaviorSubject<SelectOption<any> | undefined>(undefined);

    /** Whether form field is disabled */
    @Input() public disabled: boolean;
    /** Form control on change handler */
    private _onChange: (_: SelectOption<any>) => void;
    /** Form control on touch handler */
    private _onTouch: (_: SelectOption<any>) => void;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }

    registerOnChange(fn: any): void {
        this._onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this._onTouch = fn;
    }

    setDisabledState(isDisabled: boolean): void {
        this.disabled = isDisabled;
    }

    public setValue(value: string) {
        const obj = this.options.find(o => o.value === value);
        this.writeValue(obj);
        if (this._onChange) {
            this._onChange(obj)
        }
    }

    writeValue(obj: SelectOption<any>): void {
        this.value.next(obj);
    }
}
