import { Component, Input } from '@angular/core';

import { Booking } from '@mckinsey-converge/data-common';
import { CateringOrder } from '@mckinsey-converge/data-common';
import { CateringCategory } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { BookingsService } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { BookingStateService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'order-details-items',
    templateUrl: './items.component.html',
    styleUrls: ['./items.component.scss'],
})
export class OrderDetailsItemsComponent extends BaseDirective {
    /** Booking details to display */
    @Input() public booking: Booking;
    /** Catering order to display notes for */
    @Input() public order: CateringOrder;
    /** Whether order is being edited */
    public editing: boolean;
    /** Whether order is being updated */
    public loading: boolean;

    /** List of items in the order */
    public get items(): CateringCategory[] {
        return this.order.items as any;
    }

    constructor(
        private _service: ApplicationService, 
        private _bookings: BookingsService,
        private _booking_state_service: BookingStateService,) {
        super();
    }

    public editOrder() {
        this.editing = true;
    }

    /** Handle changes to catering order */
    public async handleOrderEvent(order: CateringOrder) {
        /* istanbul ignore else */
        if (order) {
            const orders = [...this.booking.catering];
            const index = orders.findIndex((an_order) => order.id === an_order.id);
            if (index < 0) {
                orders.push(order);
            } else {
                orders.splice(index, 1, order);
            }
            this.loading = true;
            const new_booking = new Booking({ ...this.booking.toJSON(), catering: orders });
            const booking = await this._bookings
                .save(new_booking)
                .catch((err) =>
                    this._service.notifyError(
                        `Error unable to update order. Error: ${err.message || err}`
                    )
                );
            if (booking) {
                this._booking_state_service.replace(booking);
                this.booking = booking;
                this.order = order;
                this.editing = false;
            }
            this.loading = false;
        }
    }
}
