import {
    Component,
    Input,
    OnChanges,
    SimpleChanges
} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { FormControl } from '@angular/forms';

import {
    ApplicationService,
    CateringCategory,
    CateringItem,
    SettingsService
} from '@mckinsey-converge/data-common';
import { ANIMATION_SHOW_CONTRACT_EXPAND } from '@mckinsey-converge/animations';
import { BaseDirective } from '@mckinsey-converge/base';
import { PackageOptionsModalComponent } from '../package-options-modal/package-options-modal.component';

function hasSelectionRequirements(category: CateringCategory) {
    return (
        category.must_select < category.items.length ||
        (category.must_select === category.items.length &&
            category.items.find((item: any) => item.items && item.must_select < item.items.length))
    );
}

function getActiveList(category: CateringCategory) {
    const items = category.items || [];
    let list = items.filter((item) => item.amount > 0).map((item) => item.id);
    for (const item of items) {
        if ((item as any).items && (item as any).items.length) {
            list = list.concat(getActiveList(item as any));
        }
    }
    return list;
}

@Component({
    selector: 'a-catering-menu-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
    animations: [ANIMATION_SHOW_CONTRACT_EXPAND]
})
export class CateringMenuItemComponent extends BaseDirective implements OnChanges {
    /** Whether item is a child of another */
    @Input() public subitem: boolean;
    /** Item to display */
    @Input() public item: CateringCategory;
    /** Field for storing items */
    @Input() public field: FormControl;
    /** Current symbol for the selected space */
    @Input() public symbol: string;
    /** Whether item should be disabled if not available anytime */
    @Input() public disabled: boolean;
    /** Whether to show smaller version of component */
    @Input() public compact: boolean;
    /** Whether to show children items */
    public show_children: boolean;
    public initial_state = null;

    /** List of contents of the package */
    public get contents(): string {
        return this.item.items
            ? this.item.items.reduce((list, item) => {
                /* istanbul ignore else */
                if (list) {
                    list += '\n\n';
                }
                return (list += `${item.name}${
                    (item as any).items
                        ? (item as any).items.reduce(
                        (a_list, an_item) =>
                            (a_list += `\n ${an_item.amount ? '✓' : '•'} ${an_item.name} `),
                        ''
                        )
                        : ''
                } ${item.amount ? '✓' : ''}`);
            }, '')
            : '';
    }

    constructor(private _service: ApplicationService, private _dialog: MatDialog,
                public settings: SettingsService) {
        super();
        // on the item selection set the revert/close state
        this.initial_state = this.item;
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.field && this.field) {
            const list = this.field.value;
            // check the category
            let item = list.find((an_cat) => an_cat.id === this.item.id);
            if (item) {
                // update the category level amount
                this.item.setAmount(item ? item.amount : 0);
            } else {
                // find a category item
                for (var cat of list) {
                    item = cat?.items.find((an_item) => an_item.id === this.item.id);
                    if (item) {
                        this.item.setAmount(item ? item.amount : 0);
                        break;
                    }
                };                
            } 

            // On the confirm catering order modal set the revert/close state
            if (!this.initial_state && this.item.amount) {
                this.initial_state = this.item;
            }

            this.subscription(
                'field_change',
                this.field.valueChanges.subscribe((list) => {
                    const item = list.find((an_item) => an_item.id === this.item.id);
                    this.item.setAmount(item ? item.amount : 0);
                    this.updatePackageSelection();
                })
            );
        }
    }

    /**
     * Update the amount of the item in the order
     * @param value New amount
     */
    public updateAmount(value: number) {
        /* istanbul ignore else */
        if (this.field) {
            const list: CateringItem[] = this.field.value;
            const item = list.find((an_item) => an_item.id === this.item.id);
            if (item) {
                this.item.setAmount(value);
                item.setAmount(value);
                // MCK-1550 fix
                const price = this.item.price;
                item.setPrice(price)
            } else {
                const amount = this.item.amount;
                this.item.setAmount(value);
                if (
                    this.item.package &&
                    amount < value &&
                    this.item.items &&
                    this.item.items.length &&
                    hasSelectionRequirements(this.item)
                ) {
                    this.selectPackageOptions().then(
                        (confirmed_item) => {
                            list.push(new CateringCategory(confirmed_item));
                            this.field.setValue(list.filter((an_item) => an_item.amount));
                        },
                        () => {
                            this.item.setAmount(999);
                            this.timeout('amount', () => this.item.setAmount(0), 5);
                            this._service.notifyWarn(
                                'Package options not selected. Item removed from order'
                            );
                        }
                    );
                } else {
                    list.push(new CateringItem(this.item));
                }
            }
            this.field.setValue(list.filter((an_item) => an_item.amount));
        }
    }

    public updatePackageSelection() {
        const cart_item = this.field.value.find((item) => item.id === this.item.id);
        if (cart_item) {
            const active_items = getActiveList(cart_item);
            this.timeout(
                'update_package',
                () => {
                    this.item.items.forEach((item) => {
                        item.setAmount(active_items.includes(item.id) ? 1 : 0);
                        if ((item as any).items) {
                            (item as any).items.forEach((item) => {
                                item.setAmount(active_items.includes(item.id) ? 1 : 0);
                            });
                        }
                    });
                },
                5
            );
        }
    }

    /** Reselect package options */
    public updatePackageOptions(): void {
        let updatedList: CateringItem[] = [];
        this.selectPackageOptions().then(
            (confirmed_item) => {
                // Update the existing category package
                this.field.value.forEach((category: CateringCategory) => {
                    if (category.id === confirmed_item.id) {
                        updatedList.push(new CateringCategory(confirmed_item))
                    } else {
                        updatedList.push(category)
                    }
                })
                this.field.setValue(updatedList.filter((an_item) => an_item.amount));
            },
            () => {
                this.item.setAmount(999);
                this.timeout('amount', () => this.item.setAmount(0), 5);
                this._service.notifyWarn(
                    'Package options not selected. Item removed from order'
                );
            }
        );
    }

    /**
     * Open modal for user to select package options
     */
    private selectPackageOptions(): Promise<CateringCategory> {
        return new Promise((resolve, reject) => {
            const ref = this._dialog.open(PackageOptionsModalComponent, {
                maxWidth: '95vw',
                data: {
                    item: new CateringCategory(this.item)
                },
                autoFocus: false,
                backdropClass: 'mck-modal-backdrop',
            });
            let done = false;
            ref.componentInstance.event.subscribe((event) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    done = true;
                    resolve(ref.componentInstance.item);
                    this.initial_state = this.item;
                    ref.close();
                }
            });
            ref.afterClosed().subscribe(() => {
                /* istanbul ignore else */
                if (!done) {
                    if (this.initial_state) {
                        resolve(this.initial_state);
                    } else {
                        reject();
                    }
                }
            });
        });
    }
}
