import { Component, OnInit, Input, SimpleChanges, OnChanges, EventEmitter, Output } from '@angular/core';
import { first } from 'rxjs/operators';
import { Router } from '@angular/router';

import { ApplicationService } from '@mckinsey-converge/data-common';
import { Building } from '@mckinsey-converge/data-common';
import { ReportDetails } from '../reports.component';
import { Report } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { ReportsService } from '@mckinsey-converge/data-common';

import { DateNow, DateTZ, toMiddayUTC } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'a-report-display',
    templateUrl: './report-display.component.html',
    styleUrls: ['./report-display.component.scss'],
})
export class ReportDisplayComponent implements OnInit, OnChanges {
    /** Details about the report being requested */
    @Input() public report: ReportDetails;
    /** Start date for the generated report */
    public start_date: number;
    /** End date for the generated report */
    public end_date: number;
    /** List of report data entries */
    public report_data: Report;
    /** Whether report data is loading */
    public loading: boolean;
    /** List of selected locations */
    public locations: Building[] = [{} as any];
    /** Multiple Locations? */
    public multipleLocations: boolean = false;

    /** List of available buildings */
    public get buildings(): Building[] {
        return this._org.buildings;
    }

    @Output() locationChanged = new EventEmitter<Building>();


    constructor(
        private _service: ApplicationService,
        private _org: OrganisationService,
        private _reports: ReportsService,
        private _router: Router
    ) {}

    public ngOnInit(): void {
        // R--- TODO no prams present for timezone
        const now = DateNow();
        this.start_date = now.startOfValue('day').ms;
        this.end_date = now.endOfValue('day').ms;
        this._org.initialised.pipe(first((_) => _)).subscribe(() => {
            this.locations = [this._org.building];
        });
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.report) {
            this.report_data = null;
        }
        if (this.locations.length > 1) {
            this.locations = [this.locations[0]];
        }
    }

    /* function runs on location change from on-page dropdown(not from sidebar) */
    public onLocationChange(data: any) {
        this.locationChanged.emit(this.locations[0]);
    }

    /**
     * Ensure Space Time Continuum
     */
    public checkDates(new_date: number) {
        if (this.start_date > this.end_date) {
            // R--- TODO no prams present for timezone
            const date = new DateTZ({ date: new_date });
            this.start_date = date.startOfValue('day').ms;
            this.end_date = date.endOfValue('day').ms;
        }
    }

    /**
     * Generate report
     */
    public generateReport() {
        /* istanbul ignore else */
        if (this.report) {
            this.loading = true;
            this.report_data = null;
            const date = toMiddayUTC(new DateTZ({ date: this.start_date })).toZone(
                this.locations[0]?.timezone
            );
            const end_date = toMiddayUTC(new DateTZ({ date: this.end_date })).toZone(
                this.locations[0]?.timezone
            );
            this._reports
                .show(this.report.id, {
                    date: date.formatDate('yyyy-MM-dd'),
                    start_date: date.startOfValue('day').seconds,
                    end_date: end_date.endOfValue('day').seconds,
                    zone_ids: this.locations.map((i) => i.id).join(','),
                })
                .then(
                    (report) => {
                        const zone_ids = this.locations.map((i) => i.id).join(',');
                        if (this.report.id === 'day' && this.locations[0]?.city !== 'Dallas') {
                            this.loading = false;
                            this._service.notifyError(
                                `Day View is currently available for Dallas only`
                            );
                        } else {
                            this.loading = false;
                            this.report_data = new Report({ ...report, type: this.report.id });
                        }
                    },
                    (err) => {
                        this.loading = false;
                        this._service.notifyError(
                            `Error generating report. Error: ${err.message || err}`
                        );
                    }
                );
        }
    }

    public downloadReport() {
        /* istanbul ignore else */
        if (this.report_data) {
            const date = new DateTZ({ date: this.start_date });
            this.report_data.downloadCSV(
                `${this.report.name.split(' ').join('_').toLowerCase()}+${date.formatDate(
                    'yyyy-MM-dd'
                )}.csv`
            );
        }
    }
}
