import {
    AfterViewInit,
    Component,
    OnInit,
    ViewChild,
    Input,
    ViewEncapsulation,
    ChangeDetectionStrategy,
    Output,
    EventEmitter,
    ChangeDetectorRef,
    SimpleChanges,
} from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { RoomsTableDataSource, RoomsTableItem } from './rooms-table-datasource';
import { first } from 'rxjs/operators';
import { BaseDirective } from '@mckinsey-converge/base';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    selector: 'app-rooms-table',
    templateUrl: './rooms-table.component.html',
    styleUrls: ['./rooms-table.component.scss'],
})
export class RoomsTableComponent extends BaseDirective implements AfterViewInit, OnInit {
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
    @ViewChild(MatTable) table: MatTable<RoomsTableItem>;
    dataSource: RoomsTableDataSource;

    constructor(private _org: OrganisationService,
        private _router: Router,
        private route: ActivatedRoute,
        private cdref: ChangeDetectorRef,) {
        super();
    }

    public formattedInputValue: [];

    @Input() set inputValue(value: []) {
        this.formattedInputValue = value;
    }

    @Output() private onDetailsClick = new EventEmitter<number>();

    @Output() public rowClicked = new EventEmitter<[]>();

    /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
    // tslint:disable-next-line: max-line-length
    public displayedColumns = [
        'roomname',
        'roomnum',
        'level',
        'capacity',
        'roomtype',
        'bookable',
        'details',
    ];

    public detailsViewIndex = 0;

    public _onDetailsClick(index: number) {
        this.onDetailsClick.emit(index);
    }

    public ngOnInit() {
        this.dataSource = new RoomsTableDataSource(this.formattedInputValue);
    }
    public ngOnChanges(changes: SimpleChanges): void {
    //    console.log(changes);
       this.dataSource = new RoomsTableDataSource(this.formattedInputValue);
        if(!changes.inputValue.firstChange)    {
            this.dataSource.sort = this.sort;
            this.dataSource.paginator = this.paginator;
            this.table.dataSource = this.dataSource;
            this.paginator.firstPage();
            this.cdref.detectChanges();
        }

    }

    // ngAfterViewInit heavy on performance, figure out api
    // function that gives callback after html loads
    // eg, onchange, check mat table documentation

    public ngAfterViewInit() {
    //     this.dataSource.sort = this.sort;
    //     this.dataSource.paginator = this.paginator;
    //     this.table.dataSource = this.dataSource;
    //     this.paginator.firstPage();
    }

    public getRecord(row: []) {
        const navigationExtras = {
            relativeTo:this.route,
            skipLocationChange: true ,
            state: {
                details: { ...row }
            }
        }
        this._router.navigate(['room-details'], navigationExtras)
    }
}
