import { 
    createLoadingAction,
} from '../../../loading/src/lib/loading.actions';
import {
    Building,
    BuildingCity,
} from '../../../data-common/src/lib/organisation/building.class';
import {
    BuildingLevel,
} from '../../../data-common/src/lib/organisation/level.class'
import {
    Organisation,
} from '../../../data-common/src/lib/organisation/organisation.class'
import { createSelector } from '@ngrx/store';
import {
    BuildingState,
    BuildingStoreState
} from './buildings.types';

export const loadBuildings = createLoadingAction<BuildingState, Building[], Organisation>('Buildings', 'data');

export const buildingStateSelector = (state: BuildingStoreState) => state.buildings;

export const selectLoadBuildingsSuccess = createSelector(buildingStateSelector, loadBuildings.selectors.optionalSuccess);

/**
 * Groups buildings by city
 */
export const selectBuildingOptionsGroupedByCity = createSelector(loadBuildings.selectors.optionalSuccess, (success) => {
    if (success) {
        const mapped = new Map<BuildingCity, Building[]>();
        success.forEach(s => {
            const found = Array.from(mapped.keys()).find(c => c.name === s.city);
            let list = found ? mapped.get(found) : undefined;
            if (!list) {
                list = [];
                mapped.set({
                    name: s.city,
                    timezone: s.timezone
                }, list);
            }
            list.push(s);
        });
        return mapped;
    }
    return new Map<BuildingCity, Building[]>();
});

export const selectLevelByZoneId = createSelector(selectLoadBuildingsSuccess,
    (buildings, param: string) => {
        if (buildings) {
            return buildings.map(b => b.levels.find((l: BuildingLevel) => l.id === param))
                ?.[0] ?? undefined;
        }
        return undefined;
    });
