import {
    Component,
    forwardRef,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges
} from '@angular/core';
import {
    ControlValueAccessor,
    NG_VALUE_ACCESSOR
} from '@angular/forms';

import {
    humaniseDuration,
    Identity,
    timeFormatString
} from '@mckinsey-converge/base';
import { BaseDirective } from '@mckinsey-converge/base';
import { DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'a-duration-field',
    templateUrl: './duration-field.component.html',
    styleUrls: ['./duration-field.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => DurationFieldComponent),
            multi: true
        }
    ]
})
export class DurationFieldComponent extends BaseDirective
    implements OnInit, OnChanges, ControlValueAccessor {
    /** Maximum duration option available */
    @Input() public max: number = 240;
    /** Minimum duration option available */
    @Input() public min: number = 15;
    /** Step value between options */
    @Input() public step: number = 15;
    /** Reference time for displaying next to durations */
    @Input() public time: number;
    /** Whether form field is disabled */
    @Input() public disabled: boolean;
    /** Special case prepopulation i.e. out of step options */
    @Input() public specialPreprops: Array<number> = [];
    /** Whether form all_day is enabled. */
    @Input() public all_day: boolean = false;

    public duration: number = 60;
    /** List of available duration options */
    public duration_options: Identity[];

    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;

    public ngOnInit(): void {
        this.duration_options = this.generateDurationOptions(this.max, this.min, this.step, this.all_day);
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.max || changes.min || changes.step || changes.time || changes.all_day) {
            this.duration_options = this.generateDurationOptions(this.max, this.min, this.step, this.all_day);
        }
    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: number): void {
        this.duration = new_value;
        /* istanbul ignore else */
        if (this._onChange) {
            this._onChange(+new_value);
        }
    }

    /* istanbul ignore next */
    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: number) {
        this.duration = value;
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }

    private generateDurationOptions(max: number, min: number, step: number, all_day: boolean = false): Identity[] {
        const blocks: Identity[] = [];
        let time = min;
        // R--- Change input to dateTz
        let date = this.time ? new DateTZ({date: this.time}) : null;

        // Add special case for 10min duration/prepropulation
        for (const option of this.specialPreprops) {
            blocks.push({
                id: option,
                name: date
                    ? `${date.addValue({ minutes: option }).formatDate(timeFormatString()).toLocaleLowerCase()} (${humaniseDuration(option)})`
                    : `${humaniseDuration(option)}`
            });
        }


        while (time <= max) {
            blocks.push({
                id: time,
                name: date
                    ? `${date.addValue({ minutes: time }).formatDate(timeFormatString()).toLocaleLowerCase()} (${humaniseDuration(time)})`
                    : `${humaniseDuration(time)}`
            });
            time += step;
        }
        if (all_day) {
           blocks.push({ id: 24 * 60, name: 'All Day' }); 
        }
        blocks.push({ id: 7 * 24 * 60, name: '1 week' });
        return blocks;
    }
}
