import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MatMenuTrigger } from "@angular/material/menu";
import { BaseDirective, closestToTimeSlotTz } from "@mckinsey-converge/base";
import { OrganisationService } from "@mckinsey-converge/data-common";
import { DateNow, DateTZ,toMiddayUTC } from "@mckinsey-converge/date-tz";

@Component({
    selector: 'recurrence-date-form',
    templateUrl: './recurrence-date-form.component.html',
    styleUrls: ['./recurrence-date-form.component.scss']
})

export class RecurrenceDateForm extends BaseDirective implements OnInit {
    /** Form fields for booking */
    @Input() public form: FormGroup;
    @Input() disabled: boolean;
    @ViewChild(MatMenuTrigger, { static: true }) private _trigger: MatMenuTrigger;

    public selected_dates: Array<DateTZ> = []
    public timezone: string = '';
    public nowTz: DateTZ = DateNow(new Date());
    public fromDateTz: DateTZ = this.nowTz.clone();
    public untilDateTz: DateTZ = this.fromDateTz.endOfValue('day').addValue({ years: 1 });
    public dateTz: DateTZ = DateNow(new Date());

    public get recurrenceStarts(){
        return this.form.get('recurrence_starts');
    }

    public get formDateTz(){
        return this.form.get('dateTz');
    }

    constructor (private _org: OrganisationService) {
        super();
    }

    public ngOnInit(): void {
        // Setup some data to only run once, the previous giters were hogs
        this.timezone = this.form.get('timezone').value;
        this.nowTz = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: this.timezone });
        // R--- The fromDateTz is curious, would it make sense to be based on the form dateTz?
        this.fromDateTz = this.nowTz.clone()
        this.untilDateTz = this.fromDateTz.endOfValue('day').addValue({ years: 1 });

        if(this.recurrenceStarts.value){
            const tempForSort = [...this.recurrenceStarts.value]; // due to cannot assign to read only property '0' of object '[object Array]'
            this.selected_dates = tempForSort.sort((d1: DateTZ, d2: DateTZ) => d1.ms - d2.ms);
        }
        this.subscription('ocurrenceChanges',  this.recurrenceStarts.valueChanges
            .subscribe(() => {
                this.selected_dates = [...this.recurrenceStarts.value]
            })
        ); 
    }

    /**
     * Add value to the list of selected dates
     * @param newDateTz
     */
    public addDate(newDateTz: DateTZ) {
        const d = newDateTz.clone();
        const start_date = this.formDateTz.value;
        let new_date = d.setValue({hour: start_date.dateHour, minute: start_date.minutes})
        
        /**  Prevent user from selecting past dates */
        if (new_date.isBeforeDate(this.fromDateTz)) {
            new_date = this.fromDateTz;
        }

        /** Prevent user from selecting the same date more than once */
        const is_selected = this.selected_dates.filter((date: DateTZ)=> new_date.isSameDate(date, 'day'))
        if(is_selected.length){
            return
        }
       
        this.selected_dates.push(new_date);
        this.recurrenceStarts.setValue(this.selected_dates.sort((d1, d2) => d1.ms - d2.ms));
        this.setDateTzForm();
    }

    /**
     * 
     * @param dateMsValue: number
     * @returns  
     */
     public setPickerToGivenDate(dateTsValue: DateTZ) {
        this.fromDateTz = dateTsValue.clone();
    }

    /**
     * Function calls on close of date picker without date selection
     */
    public onDatePickerClose = () => {
        this.setPickerToGivenDate(this.nowTz)
    };

    public goToToday() {
        this.addDate(this.nowTz);
        this.setPickerToGivenDate(this.nowTz);
    }

    public closeDatePicker() {
        this._trigger.closeMenu();
    }

    public openDatePicker() {
        this._trigger.openMenu();
    }

    public deleteDate(date: DateTZ) {
        const index = this.selected_dates.map((d: DateTZ) => d.ms).indexOf(date.ms)
        this.selected_dates.splice(index, 1)
        this.recurrenceStarts.setValue(this.selected_dates)
        this.setDateTzForm()
    }

    public isToday(date) {
        return new DateTZ({
            date: toMiddayUTC(date).ms,
            is_local_tz: false,
            building_tz: this.timezone,
        }).isSameDate(
            new DateTZ({
                date: Date.now(),
                is_local_tz: false,
                building_tz: this.timezone,
            }),
            'day'
        );
    }

    public formatDate(date: DateTZ){
        const isToday = this.isToday(date);
        const display = date.formatDate('ccc dd MMM yyyy');

        return  `${display}${isToday ? ' (Today)' : ''}`;
    }

    private setDateTzForm(): void {
        if (this.selected_dates.length === 0)
            return;

        const tz = this._org.building.timezone;
        const date_now = DateNow(new Date()).toZone(tz);
        let dateTz = this.selected_dates[0].clone();

        dateTz = dateTz.setValue({
            hour: (this.formDateTz.value as DateTZ).hours,
            minute: (this.formDateTz.value as DateTZ).minutes
         });

        if (this.selected_dates[0].isSameDate(this.nowTz, "day") && dateTz.hours < date_now.hours) {
            const { minutes: minute, hours: hour } = closestToTimeSlotTz(this.nowTz, 15);
            dateTz = dateTz.setValue({ minute, hour, second: 0 });
        }

        this.formDateTz.setValue(dateTz);
    }
}
