import { Component, OnInit, Input, SimpleChanges } from '@angular/core';

import { CateringCategory } from '@mckinsey-converge/data-common';
import { ANIMATION_SHOW_CONTRACT_EXPAND } from '@mckinsey-converge/animations';
import { MatDialog } from '@angular/material/dialog';
import { CateringGroupModalComponent } from '../../overlays/group-modal/group-modal.component';
import { CateringItem } from '@mckinsey-converge/data-common';
import { ConfirmModalComponent } from '@mckinsey-converge/legacy-overlays';
import { CateringComponent } from '../../catering.component';
import { DialogEvent, HashMap } from '@mckinsey-converge/base';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { Building } from '@mckinsey-converge/data-common';
import { CateringItemModalComponent } from '../../overlays/item-modal/item-modal.component';
import { BaseDirective } from '@mckinsey-converge/base';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { CateringCategoriesService } from '@mckinsey-converge/data-common';
import { CateringItemsService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'a-catering-order-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
    animations: [ANIMATION_SHOW_CONTRACT_EXPAND],
})
export class CateringMenuOrderComponent extends BaseDirective {
    /** Whether item is a child of another */
    @Input() public subitem: boolean;
    /** Item to display */
    @Input() public item: CateringCategory;
    /** Parent of the item being displayed */
    @Input() public parent: CateringCategory;
    /** Whether item should be disabled if not available anytime */
    @Input() public disabled: boolean;
    /** Whether to show children items */
    public show_children: boolean;

    /** Currently active building */
    public get building(): Building {
        return this._org.building;
    }

    public get symbol(): string {
        return this.building.currency;
    }

    constructor(
        private _service: ApplicationService,
        private _dialog: MatDialog,
        private _org: OrganisationService,
        private _menu_items: CateringItemsService,
        private _menu_categories: CateringCategoriesService
    ) {
        super();
    }

    public openGroupModal(group?: CateringCategory) {
        /* istanbul ignore else */
        if (!group) {
            group = new CateringCategory({ parent_id: this.item.id, zones: [this.building.id] });
        }
        const ref = this._dialog.open(CateringGroupModalComponent, {
            data: { group, parent: group.id ? this.parent : this.item },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.subscription(
            'item-modal',
            ref.componentInstance.event.subscribe((event) => {
                if (event.reason === 'done' && event.metadata) {
                    this.updateItemList(group, event.metadata);
                }
            })
        );
    }

    public openItemModal(item?: CateringItem) {
        /* istanbul ignore else */
        if (!item) {
            item = new CateringItem({ parent_id: this.item.id });
        }
        const ref = this._dialog.open(CateringItemModalComponent, {
            data: { item, parent: item.id ? this.parent : this.item },
            autoFocus: false,
      backdropClass: 'mck-modal-backdrop',
        });
        this.subscription(
            'item-modal',
            ref.componentInstance.event.subscribe((event) => {
                if (event.reason === 'done' && event.metadata) {
                    this.updateItemList(item, event.metadata);
                }
            })
        );
    }

    private updateItemList(old_item: CateringItem | CateringCategory, new_item: HashMap) {
        const item =
            old_item instanceof CateringCategory
                ? new CateringCategory(new_item)
                : new CateringItem(new_item);
        if (old_item.id) {
            const index = this.parent.items.findIndex((itm) => itm.id === old_item.id);
            (this.parent.items as any[]).splice(index, 1, item);
            (this.parent as any).items = [...this.parent.items];
        } else {
            (this.item.items as any[]).push(item);
        }
    }

    public deleteItem() {
        const type =
            this.item.is_category
                ? this.item.package
                    ? 'Package'
                    : 'Sub-category'
                : 'Item';
        const ref = this._dialog.open(ConfirmModalComponent, {
            data: {
                title: `Delete catering ${type}`,
                content: `Are you sure you want to delete the catering ${type} "${
                    this.item.name
                }"?<br />${
                    this.item instanceof CateringCategory
                        ? 'Any children items or groups will be <strong>immediately</strong> deleted'
                        : ''
                }`,
                icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                action: 'Ok',
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                ref.componentInstance.loading = `Deleting ${type}...`;
                let e = null;
                let service =
                    this.item.is_category
                        ? this._menu_categories
                        : this._menu_items;
                await service.delete(this.item.id).catch((err) => e = err || 'Error');
                ref.close();
                if (!e) {
                    this._service.set('DELETED.menu', this.item.id);
                } else {
                    this._service.notifyError(`Error deleting ${type}.`);
                }
            }
        });
    }
}
