import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { first } from 'rxjs/operators';
import { Booking } from '@mckinsey-converge/data-common';
import {
    VisitorsFormModalComponent,
    VisitorsFormModalData,
} from './overlays/visitors-form-modal/visitors-form-modal.component';
import { SpacesService } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'app-visitors',
    templateUrl: './visitors.component.html',
    styleUrls: ['./visitors.component.scss'],
})
export class VisitorsComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    public dateTz: DateTZ = DateNow(new Date());
    /** Search filter string */
    public search_str: string;
    /** Whether all bookings should be listed */
    public show_all: boolean;

    public get has_visitor_space(): boolean {
        const building = this._org.building;
        return building && !!this._spaces.find(building.visitor_space || building.holding_bay);
    }

    public has_access: boolean;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _service: ApplicationService,
        private _dialog: MatDialog,
        private _spaces: SpacesService,
        private _org: OrganisationService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.title = 'Visitors';
        this._org.initialised.pipe(first(_ => _)).subscribe(() => {
            // Init dateTz here once the organization data is loaded.
            this.dateTz = new DateTZ({ date: this.dateTz.ms, is_local_tz: false, building_tz: this.timezone });
            this.subscription(
                'route.query_params',
                this._route.queryParamMap.subscribe((params) => {
                    if (params.has('zone') && params.get('zone') !== this._org.building.id) {
                        const id = params.get('zone');
                        const bld = this._org.buildings.find((building) => building.id === id);
                        if (bld) {
                            this._org.building = bld;
                        }
                    }
                })
            );
            this.subscription(
                'building',
                this._org.listen('active_building')?.subscribe(() => {
                    // Update dateTz here when the building is changed.
                    this.dateTz = new DateTZ({ date: this.dateTz.ms, is_local_tz: false, building_tz: this.timezone });
                    this.has_access = this._org.building ? true : false;
                    if(!this.has_access) return
                    this._router.navigate([], {
                        relativeTo: this._route,
                        queryParams: { zone: this._org.building.id },
                    });
                })
            );
        })
    }

    public newVisitorMeeting() {
        const now = DateNow(new Date());
        const date = now.setValue({ minute: Math.ceil(now.minutes / 5) * 5 });
        const building = this._org.building;
        const space = this._spaces.find(building.visitor_space || building.holding_bay);
        const booking = new Booking({ 
            start_epoch: date.seconds,
            end_epoch: date.addValue({ minutes: 60 }),
            room: space
        });
        this._dialog.open<VisitorsFormModalComponent, VisitorsFormModalData>(
            VisitorsFormModalComponent,
            {
                data: {
                    booking,
                },
            }
        );
    }

    // R--- surely these 2 methods are not DRY and all the main tabs need a base class
    // that encapsulates the common elements.
    public get timezone() {
        return this._org.building.timezone;
    }

    public updateDateTz(dateTz: DateTZ) {
        this.dateTz = dateTz;
    }
}
