import { Component, OnInit } from '@angular/core';
import { ApplicationIcon, OrganisationService } from '@mckinsey-converge/data-common';
import { ActivatedRoute, Router } from '@angular/router';
import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { first } from 'rxjs/operators';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

export interface ReportDetails {
    /** ID associated with the report */
    id: string;
    /** Name of the report */
    name: string;
    /** Shortened name of the report */
    short_name: string;
    /** More details about the report */
    description: string;
    /** Fields to hide on the UI */
    hide_fields: string[];
    /** Icon to display for report */
    icon: ApplicationIcon;
    /** Multiselect Locations */
    multi_locations: boolean;
}

@Component({
    selector: 'a-reports',
    templateUrl: './reports.component.html',
    styleUrls: ['./reports.component.scss'],
})
export class ReportsComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    public dateTz: DateTZ = DateNow(new Date());
    /** ID of the active report */
    public active_report: string;

    public has_access: boolean;

    public location: any;

    /** List of available reports to display */
    public reports: ReportDetails[] = [
        {
            id: 'bookings',
            name: 'Aggregate Bookings Report',
            short_name: 'Bookings',
            description: 'Generate comprehensive reports on various aspects of bookings',
            hide_fields: ['room_email'],
            icon: { type: 'img', src: 'assets/img/report-booking.svg' },
            multi_locations: false,
        },
        {
            id: 'catering',
            name: 'Aggregate Charge Back Report',
            short_name: 'Catering',
            description: 'View confirmed catering report or mark up unconfirmed catering',
            hide_fields: ['room_email'],
            icon: { type: 'icon', class: 'material-icons', content: 'room_service' },
            multi_locations: true,
        },
        {
            id: 'day',
            name: 'Day View Report',
            short_name: 'day-view',
            description: ' ',
            hide_fields: [' '],
            icon: { type: 'icon', class: 'material-icons', content: 'room_service' },
            multi_locations: true,
        },
        {
            id: 'audit',
            name: 'Audit',
            short_name: 'audit',
            description: '',
            hide_fields: [],
            icon: { type: 'img', src: 'assets/img/report-booking.svg' },
            multi_locations: true,
        }
    ];

    public get the_report(): ReportDetails {
        return this.reports.find((report) => report.id === this.active_report);
    }

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _service: ApplicationService,
        private _org: OrganisationService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.title = 'Reports';
        this.subscription(
            'route.params',
            this._route.paramMap.subscribe((params) => {
                if (params.has('report')) {
                    this.active_report = params.get('report');
                }
            })
        );

        this._org.initialised.pipe(first((_) => _)).subscribe(() => {
            // Init dateTz here once the organization data is loaded.
            this.dateTz = new DateTZ({
                date: this.dateTz.ms,
                is_local_tz: false,
                building_tz: this.timezone,
            });
            this.subscription(
                'building',
                this._org.listen('active_building').subscribe(() => {
                    // Update dateTz here when the building is changed.
                    this.dateTz = new DateTZ({
                        date: this.dateTz.ms,
                        is_local_tz: false,
                        building_tz: this.timezone,
                    });
                    const building = this._org.building;
                    this.location = this._org.building;
                    this.has_access = building ? true : false;

                    //check for Dallas
                    if (this.location.city != 'Dallas') {
                        // remove day view from reports array
                        this.reports = this.reports.filter(function (reports) {
                            return reports.id !== 'day';
                        });
                    }
                })
            );
        });
    }

    public updateReport() {
        this._router.navigate(['/reports', this.active_report]);
    }

    // R--- surely these 2 methods are not DRY and all the main tabs need a base class
    // that encapsulates the common elements.
    public get timezone() {
        return this._org.building.timezone;
    }

    public updateDateTz(dateTz: DateTZ) {
        this.dateTz = dateTz;
    }

    public onLocationChanged(location: any) {
        this.location = location;
        //check for Dallas
        if (this.location.city != 'Dallas') {
            // remove day view from reports array
            this.reports = this.reports.filter(function (reports) {
                return reports.id !== 'day';
            });
            this._router.navigate([`/reports/${this.active_report}`]);
        } else {
            // add day view to reports array
            this.reports = [
                ...this.reports,
                {
                    id: 'day',
                    name: 'Day View Report',
                    short_name: 'day-view',
                    description: ' ',
                    hide_fields: [' '],
                    icon: { type: 'icon', class: 'material-icons', content: 'room_service' },
                    multi_locations: true,
                },
            ];
        }
    }
}
