import { LoadingModel } from '@mckinsey-converge/loading';
import {
    createReducer,
    on
} from '@ngrx/store';
import {
    loadHomepageBookingsResults,
    loadUpcomingBookingsResults,
    loadPastBookingsResults,
    loadCancelledBookingsResults,
    loadBookingByIdResults,
    clearBookingByIdResults,
} from './bookings.actions';
import { MyBookingsState } from './bookings.types';
   
import { 
    BookingFormState,
    clearBookingFormData,
    clearRoomFilters,
    openBookingSurvey,
    storeBookingFormData,
    storeRoomFilters
} from '@mckinsey-converge/data-common';


export const initialBookingsState: MyBookingsState = {
    query: LoadingModel.empty(),
    homepage: LoadingModel.empty(),
    upcoming: LoadingModel.empty(),
    past: LoadingModel.empty(),
    cancelled: LoadingModel.empty(),
    bookingById: LoadingModel.empty(),
};

export const bookingsReducer = createReducer(initialBookingsState,
    on(clearBookingByIdResults, (state: MyBookingsState) => ({
        ...state,
        bookingById: LoadingModel.empty(),
    }) as MyBookingsState),
    ...loadHomepageBookingsResults.ons,
    ...loadUpcomingBookingsResults.ons,
    ...loadPastBookingsResults.ons,
    ...loadCancelledBookingsResults.ons,
    ...loadBookingByIdResults.ons,
    );



    export const initialBookingFormState: BookingFormState = {
        activeFormFilters: []
    };
    
    export const bookingFormReducer = createReducer(initialBookingFormState,
        on(storeBookingFormData, (state: BookingFormState, action) => {
            return {
                ...state,
                activeForm: {
                    ...state.activeForm,
                    ...action.payload
                }
            } as BookingFormState;
        }),
        on(clearBookingFormData, state => ({
            ...state,
            activeForm: undefined
        }) as BookingFormState),
        on(openBookingSurvey, state => ({
            ...state,
            bookingCompleted: new Date()
        }) as BookingFormState),
        on(storeRoomFilters, (state: BookingFormState, action) => {
            return {
                ...state,
                activeFormFilters: action.payload
            } as BookingFormState;
        }),
        on(clearRoomFilters, (state: BookingFormState) => ({
            ...state,
            activeFormFilters: initialBookingFormState.activeFormFilters
        }) as BookingFormState)
    );
    
