import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';

import { CateringItem } from './catering-item.class';
import { CateringCategory } from './catering-category.class';
import { BaseAPIService } from '../base.service';
import { HashMap } from '@mckinsey-converge/base';
import { SettingsService } from '../settings.service';

@Injectable({
    providedIn: 'root'
})
export class CateringMenuService extends BaseAPIService<CateringItem | CateringCategory> {
    constructor(protected _composer: ComposerService,
                settingsService: SettingsService) {
        super(_composer, settingsService);
        this._name = 'Catering Menu';
        this._api_route = 'menu';
    }

    /**
     * Convert user data to local format
     * @param user User data
     */
    protected processItem(item: HashMap) {
        return item.items ? new CateringCategory(item) : new CateringItem(item);
    }
}
