import { Injectable } from '@angular/core';
import {
    Actions,
    Effect,
    ofType
} from '@ngrx/effects';
import {
    IBookingQueryOptions,
    BookingsPaginatedService
} from '../../../data-common/src/lib/bookings';
import {
    createLoadingEffect,
    LoadingAction
} from '@mckinsey-converge/loading';
import {
    loadHomepageBookingsResults,
    loadPastBookingsResults,
    loadUpcomingBookingsResults,
    loadCancelledBookingsResults,
    loadBookingsWithQuery,
    loadBookingByIdResults
} from './bookings.actions';
import { mergeMap } from 'rxjs/operators';
import {

} from './bookings.utils';
import { DateTZ } from '@mckinsey-converge/date-tz';

@Injectable()
export class BookingsEffects {
    constructor(private actions: Actions,
                private bookingService: BookingsPaginatedService) {
    }

    @Effect()
    public afterBookingsRequestResultsLoadOthersEffect = this.actions.pipe(
        ofType(loadBookingsWithQuery),
        mergeMap((action: LoadingAction<IBookingQueryOptions>) => {
            const date = new DateTZ({date: action.from})
            const yourBookingsRequest = {
                email: action.email,
                pagination: true,
                limit: null,
                offset: action.offset,
                show_cancelled: false,
                sort: 'start_epoch asc',
                filters: {
                    end_epoch: `>=${date.subtractValue({ minutes: 5 }).seconds}`,
                },
                include_rooms: true
            };
            const upcomingRequest = {
                email: action.email,
                pagination: true,
                limit: null,
                offset: action.offset,
                show_cancelled: false,
                sort: 'start_epoch asc',
                filters: {
                    end_epoch: `>=${date.seconds}`,
                },
                include_rooms: true
            };
            const pastRequest = {
                email: action.email,
                pagination: true,
                limit: null,
                offset: action.offset,
                show_cancelled: false,
                sort: 'start_epoch desc',
                filters: {
                    start_epoch: `>${date.subtractValue({ hours: 48 }).seconds}`,
                    end_epoch: `<${date.seconds}`,
                },
                include_rooms: true
            };
            const cancelledRequest = {
                email: action.email,
                pagination: true,
                limit: null,
                offset: action.offset,
                show_cancelled: true,
                sort: 'start_epoch desc',
                filters: {
                    start_epoch: `>${date.subtractValue({ hours: 48 }).seconds}`,
                },
                include_rooms: true
            };
            const byIdRequest = {
                email: action.email,
                id: action.id,
                pagination: true,
                limit: 1,
                include_rooms: true
            };

            const effects = [];
            switch(action.target) {
                case 'upcoming':
                    effects.push(loadUpcomingBookingsResults.request(upcomingRequest));
                    break;
                case 'past':
                    effects.push(loadPastBookingsResults.request(pastRequest));
                    break;
                case 'cancelled':
                    effects.push(loadCancelledBookingsResults.request(cancelledRequest));
                    break;
                case 'bookingById':
                    effects.push(loadBookingByIdResults.request(byIdRequest));
                    break
                case 'homepage':
                    effects.push(loadHomepageBookingsResults.request(yourBookingsRequest));
                    effects.push(loadPastBookingsResults.request(pastRequest));
                    break
                default:
            }
            return effects;
        })
    );

    @Effect()
    public loadHomepageBookingsResultsEffect = createLoadingEffect(this.actions, loadHomepageBookingsResults,
        (action) => this.bookingService.userBookings(action, `homepage-${action.filters?.start_epoch || ''}${action.filters?.end_epoch || ''}`));

    @Effect()
    public loadUpcomingBookingsResultsEffect = createLoadingEffect(this.actions, loadUpcomingBookingsResults,
        (action) => this.bookingService.userBookings(action, `upcoming-${action.filters?.start_epoch || ''}${action.filters?.end_epoch || ''}`));
            
    @Effect()
    public loadPastBookingsResultsEffect = createLoadingEffect(this.actions, loadPastBookingsResults,
        (action) =>
            this.bookingService.userBookings(action, `past-${action.filters?.start_epoch || ''}${action.filters?.end_epoch || ''}`));

    @Effect()
    public loadCancelledBookingsResultsEffect = createLoadingEffect(this.actions, loadCancelledBookingsResults,
        (action) =>
            this.bookingService.userBookings(action, `cancelled-${action.filters?.start_epoch || ''}${action.filters?.end_epoch || ''}`));

    @Effect()
    public loadBookingByIdResultsEffect = createLoadingEffect(this.actions, loadBookingByIdResults,
        (action) => 
            this.bookingService.userBookings(action, `bookingById-${action.id}`));

}
