import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';

import { BaseDirective, DialogEvent } from '@mckinsey-converge/base';
import { generateUserForm, User } from '@mckinsey-converge/data-common';

@Component({
    selector: 'new-user-modal',
    templateUrl: './new-user-modal.component.html',
    styleUrls: ['./new-user-modal.component.scss'],
})
export class NewUserModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form fields for the new user */
    public form: FormGroup;
    /** New user data store */
    public user: User;
    /** Whether user details are being saved */
    public loading: boolean;

    public ngOnInit(): void {
        this.user = new User();
        this.form = generateUserForm(this.user);
    }

    public saveChanges() {
        this.form.markAllAsTouched();
        if (this.form.valid) {
            if (!this.form.controls.email.value) {
                this.form.controls.email.setValue(
                    `${Math.floor(Math.random() * 99999)}@guest.mckinsey.com`
                );
            }
            const firstName = this.form.value.first_name.split(' ')
            .map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
            .join(' ');

            const lastName = this.form.value.last_name.split(' ')
            .map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
            .join(' ');
 
            const new_user = new User({
                ...this.form.value,
                name: `${firstName} ${lastName}`,
                type: 'external',
                external: true,
                visitor: true,
            });
            this.event.emit({ reason: 'done', metadata: new_user });
        }
    }
}
