import { Component, Input, Output, forwardRef, SimpleChanges, OnChanges, OnInit, EventEmitter, ViewChild, OnDestroy } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { SelectOption } from '@mckinsey-converge/ui';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import {
  BehaviorSubject
} from 'rxjs';

/**
 * @title Autocomplete filtering select of a single option via text search.
 */
@Component({
  selector: 'mck-autocomplete-select-option',
  templateUrl: 'auto-complete-select-option.component.html',
  styleUrls: ['auto-complete-select-option.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AutoCompleteSelectOptionComponent),
      multi: true
    }
  ],
})
export class AutoCompleteSelectOptionComponent implements OnInit, OnChanges, ControlValueAccessor, OnDestroy{
  @Input()
  public label: string;

  @Input()
  public required: boolean;

  @Input()
  public options: SelectOption<any>[] = [];

  @Input()
  public selectedValue: SelectOption<any> | undefined;

  @Input()
  public resetInput: number | undefined;

  @Input()
  public placeholder: string;

  @Input()
  public noEntriesFoundLabel: string;

  @Output()
  public filteredOptionsChange = new EventEmitter<SelectOption<any>[]>();

  @Output()
  public inputTextChange = new EventEmitter<string>();

  @Output() emptyBoxEvent = new EventEmitter();

  /** Form control on change handler */
  private _onChange: (_: SelectOption<any>) => void;
  /** Form control on touch handler */
  private _onTouch: (_: SelectOption<any>) => void;

  filterFormControl = new FormControl();
  filteredOptions: Observable<SelectOption<any>[]>;

  /** The selected value */
  public value = new BehaviorSubject<any | undefined>('');

  /** Available options to search / select from */
  public _options = new BehaviorSubject<SelectOption<any>[]>(this.options);
  @ViewChild('trigger')
    trigger: MatAutocompleteTrigger;

  ngOnInit() {
    this.updateFilteredOptions();
    window.addEventListener('scroll', this.scrollEvent, true);
  }

  public ngOnChanges(changes: SimpleChanges): void {
    // Watch and apply changes from the parent component.
    if (changes.options || changes.selectedValue) {
      if (changes.options && !changes.selectedValue) {
        this.filterFormControl.setValue('')
      }
      this.updateFilteredOptions();
    }
    if (changes.resetInput) {
      this.filterFormControl.setValue('');
      this.updateFilteredOptions();
    }
    
  }

  private updateFilteredOptions() {
    this.filteredOptions = this.filterFormControl.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value && value?.display ? value.display : ''),
        map(name => name ? this._filter(name) : this.options.slice())
      );
  }

  displayFn(data: SelectOption<any>): string {
    return data && data?.display ? data.display : '';
  }

  private _filter(display: string): SelectOption<any>[] {
    const filterValue = display.toLowerCase();
    // return all the matches found
    const filtered = this.options.filter(option => {
      // split this input and index both, if both
      // match return true.
      if (filterValue.includes(' ')) {
        const filterValues =filterValue.split(' ');
        let matchesAll = true;
        for (let x = 0; x < filterValues.length; x++) {
          if (option.display.toLowerCase().indexOf(filterValues[x]) === -1) {
            matchesAll = false;
          }
        }
        if (matchesAll) {
          return true;
        }
      }
      // match the whole string
      return option.display.toLowerCase().indexOf(filterValue) > -1;
    });
    this.inputTextChange.emit(filterValue);
    this.filteredOptionsChange.emit(filtered);
    return filtered;
  }

  /**
   * Update the form field value
   * @param new_value New value to set on the form field
   */
  public setValue(new_value: SelectOption<any>): void {
    this.value.next(new_value);
    if (this._onChange) {
      this._onChange(this.options.find(f => f.value === new_value.value));
    }
  }

  /**
 * Update local value when form control value is changed
 * @param value The new value for the component
 */
  public writeValue(value: SelectOption<any> | null) {
    if (value != null) {
      this.value.next(value.value);
    }
  }

  /**
   * Registers a callback function that is called when the control's value changes in the UI.
   * @param fn The callback function to register
   */
  public registerOnChange(fn: (_: SelectOption<any>) => void): void {
    this._onChange = fn;
  }

  /**
   * Registers a callback function is called by the forms API on initialization to update the form model on blur.
   * @param fn The callback function to register
   */
  public registerOnTouched(fn: (_: SelectOption<any>) => void): void {
    this._onTouch = fn;
  }

  public onChangeInput() {
    if (this.filterFormControl.value === '') {
      this.emptyBoxEvent.emit(true);
    }
  }

  private scrollEvent = (event): void => {
    if (this.trigger === null) {
      return;
    }
    /**
     * IF - to confirm user not scrolling from autocomplete opened panel and close panel if scroll event triggers from outside of panel
     */
    if (this.trigger.panelOpen && event.target.closest('.mat-autocomplete-panel') === null ) {
      this.trigger.closePanel();
        this.trigger['_element'].nativeElement.blur();
    }
  }
  public ngOnDestroy(): void {
    window.removeEventListener('scroll', this.scrollEvent, true);
  }

}