import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogEvent } from '@mckinsey-converge/base';
import { DateTZ, isToday } from '@mckinsey-converge/date-tz';


@Component({
  selector: 'override-changes-modal',
  templateUrl: './override-changes-modal.component.html',
  styleUrls: ['./override-changes-modal.component.scss']
})
export class OverrideChangesModalComponent implements OnInit{

  public edits;
  public timezone;

  @Output() public event = new EventEmitter<DialogEvent>();

  constructor(@Inject(MAT_DIALOG_DATA) private _data: OverrideChangesModalComponent){

  }

  public ngOnInit(){
      // this.edits = this._data.edits.sort((a, b) =>  a - b);

      // Function to convert epoch to 'YYYY-MM-DD' format
      const getDateString = (epoch) => {
        return new Date(epoch * 1000).toISOString().split('T')[0];
      };

      // Set to store unique date strings
      const uniqueDates = new Set();

      // Filter the edits array
      const filteredEdits = this._data.edits.filter(epoch => {
        const dateString = getDateString(epoch);
        if (!uniqueDates.has(dateString)) {
          uniqueDates.add(dateString);
          return true;
        }
        return false;
      });

      this.edits = filteredEdits;
  }

  public formatDate(date: number) {
    const dateTZ = new DateTZ({date: date*1000, is_local_tz: false, building_tz: this._data.timezone });
    const dateIsToday = isToday(dateTZ, this._data.timezone);
    const display = dateTZ.formatDate('ccc dd MMM yyyy');
    return `${display}${dateIsToday ? ' (Today)' : ''}`;
  }

  public continue(): void {
    this.event.emit({ reason: 'done' });
  }

  public close(): void {
    this.event.emit({ reason: 'close' });
  }
}