import {
    AfterViewInit,
    Component,
    OnInit,
    ViewChild,
    Input,
    ViewEncapsulation,
    ChangeDetectionStrategy,
    Output,
    EventEmitter,
    ChangeDetectorRef,
    Inject,
} from '@angular/core';
import { BaseDirective, DialogEvent, HashMap } from '@mckinsey-converge/base';
import { OrganisationService, SpacesService } from '@mckinsey-converge/data-common';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import {  ResizeService,  ReadLocalPicService } from '../services/';
import { RequiredFileType, RoomPhotoDiamension } from '../util/room-admin-utility';
import { HttpEventType, HttpHeaders, HttpClient} from '@angular/common/http';
import * as cloneDeep from 'lodash/cloneDeep';
// import { UploadManager, Amazon } from '@acaprojects/ngx-uploads';
import { of, Observable, forkJoin } from "rxjs";
import { catchError, tap, toArray } from 'rxjs/operators';
import { ComposerService } from '@placeos/composer';
import { IconComponent } from '@mckinsey-converge/legacy-ui';

@Component({
    selector: 'app-manage-photos',
    templateUrl: './manage-photos.component.html',
    styleUrls: ['./manage-photos.component.scss'],
		changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ManagePhotosComponent extends BaseDirective implements OnInit {
     /** Emitter for modal events */
     @Output() public event = new EventEmitter<any>();

		 public canvas: HTMLCanvasElement;
		 public context: any;
		 public imageInitial = null;
		 public imageToModify = null;

		 public photoArray: Array<any> = []; // array hold images to display (uploaded or to be upload)
		 public photoUploadArray: Array<any> = []; // array hold image to upload

		 public uploadProgress:number;
		 public uploadError: boolean = false;
		 public uploadingStatus: string = 'Uploading...';
		 public room: any;
		 public resizedFiles = [];
		 public requiredFileType = RequiredFileType;
		 public count: any = 0;
		 public total: number = 0;
		 public failedNo: string;
		//  isImageToModify: boolean;


    constructor(
			private _composer: ComposerService,
			@Inject(MAT_DIALOG_DATA) private _data: any,
			public resizeService: ResizeService,
			private readLocalPicService: ReadLocalPicService,
			private _spaces: SpacesService,
			private cdRef: ChangeDetectorRef,
			private httpClient?: HttpClient,
			) {
			super();
    }
  

    public ngOnInit() {
       this.photoArray =  cloneDeep(this._data?.photos);
			 this.room = this._data?.room
    }

		

		public	onFileSelected (event , indexToReplace?) {
			this.uploadError = false;
			this.canvas = document.createElement('canvas');
			this.context = this.canvas.getContext('2d');
			const ext = RequiredFileType.split(',');
			const files = event instanceof Array ? event : [...new Set(event.target.files)].filter((f:File) => ext.includes(f.type));
			const unusedIdx = indexToReplace!==undefined ? 1 : (3 - this.photoArray.length);
			files.splice(0,unusedIdx).forEach( file => {

				this.readLocalPicService.readFileInput(file).then((base64Img) => {

					setTimeout(
						async function (that) {

							await that.resizeService.resizeImage(base64Img, {width: RoomPhotoDiamension.width, height: RoomPhotoDiamension.height,  outputFormat: 'image/png', })
										.then(function (imgResized) {

											that.resizeService.createImage(imgResized).then(function (image) {

												that.context.clearRect(0, 0, image.width, image.height );
												that.canvas.width = image.width;
												that.canvas.height = image.height;
												that.context.drawImage(  image,  0,  0,  image.width,  image.height );
												
												
												const dataURL: string | HTMLCanvasElement | HTMLImageElement = that.canvas.toDataURL('image/png');
												if(indexToReplace!==undefined) {
													that.photoArray.splice(indexToReplace, 1, { path : dataURL, index: indexToReplace+1 } )
													that.photoUploadArray.splice(indexToReplace, 1, { path : dataURL, index: indexToReplace+1 } )
												}
												else {
													const index = that.photoArray.length+1;
													that.photoArray.push({ path: dataURL, index: index });
													that.photoUploadArray.push({ path: dataURL, index: index });
												}
												that.cdRef.detectChanges();

												// indexToReplace!==undefined ? that.photoArray.splice(indexToReplace, 1, { path : dataURL} ) : that.photoArray.push({ path: dataURL });

											})
											.catch((error) => {
												console.log('ERROR while creating image : ', error);
											});
										})
										.catch((error) => {
											console.log('ERROR while resizing image : ', error);
										});
							
						}, 0, this );
				})
				.catch((error) => {
					console.log('ERROR while Read local pic :', error);
				});

			})

		}


		upload() {
			this.uploadError = false;
			this.uploadProgress = 1;
			let obs: Observable<any>[] = [];
			this.count = 0;
			this.total = this.photoUploadArray.length;
			
			// for (var i = 0; i < this.photoUploadArray.length; i++)
			this.photoUploadArray.forEach(( img, i) =>{ 
				const formData = new FormData();
				const file = this.readLocalPicService.b64toFile(img.path);

				formData.append( "file", file, `${this.room.email.split('@')[0]}-part-${img.index}.png`);
				
				obs.push(this.uploadSpacePhotos('photos', formData).pipe( catchError(err => of(err.status))) ) ;

			}) 

			

			this.subscription(`uploadPhotosSub$`, forkJoin(obs.map(o => o.pipe( 
					tap(item => this.updateProgressMeter(item)),  
					// toArray() 
				)))
				.subscribe(event => {

							this._data.photos = [];
							event.forEach((res ,i ) => {
								if(res?.message === 'File uploaded successfully!')  {

									this.uploadError = false;
									
								}
								else  {
									const suffixes = ['', 'st','nd','rd'];
									this.failedNo = `${i+1}${suffixes[i+1]}`;
									this.reset();
									this.uploadError = true;
								}
								if(i === (this.photoUploadArray.length-1)) {
									this.photoUploadArray = [];
									this.uploadingStatus =  'Complete';
									this.event.emit(this._data.photos);
								}
							});
							this.cdRef.detectChanges();
						
						},
						error => {
							this._data.photos = [];
							this.reset();
							this.uploadError = true;
							console.log('Error while uploadig room photos: ', error) ;
							this.cdRef.detectChanges();
						}
					)
			);
		}

		private updateProgressMeter(item) {
			if(item?.message === 'File uploaded successfully!')  {
				this.count++;
				this.uploadProgress = this.total > 0 ? Math.round((this.count / this.total) * 100) : 0;
				this.cdRef.detectChanges();
			}
		}
	
	
		reset() {
			this.uploadProgress = null;
			this.cdRef.detectChanges();
		}

		public timestamp (photo) {
			return photo?.index ? '' : `?${(new Date()).getTime()}`;
		}


		/**
   * update function version -2 - returns observable instead of promise...
   * @param form_data data to be update 
   * @param should_inject_concierge common value to be set to add concierge: true to the query url and body
   * @param query_params Map of query paramaters to add to the request URL
   */
		 public uploadSpacePhotos(apiSubRoute: string, form_data: HashMap, should_inject_concierge: Boolean = false, query_params: HashMap = {}):Observable<any>{
			const url = `/api/staff/rooms/${apiSubRoute}`;
			const body = form_data;
	
			const token = this._composer.auth.token; //sessionStorage.length ? JSON.parse(sessionStorage.getItem('OAUTH.params')).access_token : '';
	
			const headers = new HttpHeaders({
				'Authorization': `Bearer ${token}`,
			});
			
		const requestOptions = { headers: headers };
	
			return this.httpClient.post(url, body, {
						...requestOptions 
				});
	
			// return this.http.post(url, body);
	
		}

}
