
import { Directive, OnDestroy } from '@angular/core';
import { BaseClass } from './base.class';
import { SpaceImageObject } from '@mckinsey-converge/base';
import { BehaviorSubject } from 'rxjs';

@Directive({
    selector: 'a-very-basic-component-image-that-should-not-be-used'
})
export class ImageDirective extends BaseClass implements OnDestroy {
    public foundImages?: SpaceImageObject[];
    public loadImages?: BehaviorSubject<SpaceImageObject[]>
    public ngOnDestroy(): void {
        this.destroy();
    }
}