import { Component, Input, ChangeDetectionStrategy, OnInit, ChangeDetectorRef} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { Booking } from '@mckinsey-converge/data-common';
import { MeetingDetailsModalComponent } from 'src/app/calendar/meeting-details-modal/meeting-details-modal.component';

@Component({
    selector: 'week-view-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class WeekViewItemComponent implements OnInit{
    @Input() public event: Booking;

    public type: string = '';

    constructor(private _dialog: MatDialog) {}

    public ngOnInit() {
        this.type = this.event.getType();
    }

    /**
     * Open modal to view bookings details
     */
    public viewMeetingDetails() {
        this._dialog.open(MeetingDetailsModalComponent, {
            maxWidth: 'calc(100vw - 2em)',
            data: {
                space: this.event.space,
                booking: this.event
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        })
    }
}
