/**
 * Generate a list of time options for the given date
 * @param datestamp Date to generate options for
 * @param show_past Whether past times should be options
 */

import {
    closestToTimeSlot,
    closestToTimeSlotTz,
    DurationGroup,
    durationHumanized,
    timeFormatString,
    timeWithGmtOffsetFormatString,
    unique
} from '@mckinsey-converge/base';
import { SelectOption } from '../types';
import { DateTZ } from "@mckinsey-converge/date-tz";
import { DateTime } from 'luxon';

// 24 hours
export const ALL_DAY_BOOKING_VALUE = 60 * 24;

export const DEFAULT_DURATION = 30;

// R--- depreciate.
export const generateAvailableTimes = (
    datestamp: number,
    showPast: boolean,
    step: number = 15,
    timezone: string = new DateTZ({ date: Date.now() }).local_tz
): SelectOption<any>[] => {
    // R--- refactor to only used dateTz input and drop timezone param.
    // now needs the building timezone for isAfter to work correctly
    const now = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: timezone });
    // date needs the building timezone for isAfter to work correctly
    let date = new DateTZ({ date: datestamp, is_local_tz: false, building_tz: timezone });
    // Check if we're today or a future date
    const blocks: SelectOption<any>[] = [];
    if (showPast || date.isAfterDate(now, 'day')) {
        // later than today
        date = date.startOfValue('day');
    } else {
        // today
        date = now;
    }
    // Get the closest timeslot to the start time
    const inputDate = DateTime.fromMillis(date.ms);
    const closestDate = closestToTimeSlot(inputDate, step);
    // Date changed to the closest time
    date = new DateTZ({ date: closestDate.valueOf(), is_local_tz: false, building_tz: timezone });
    const end = date.endOfValue('day');
    // Add options for the rest of the day
    while (date.isBeforeDate(end)) {
        blocks.push({
            display: timeWithGmtOffsetFormatString(date),
            shortDisplay: date.formatDate('hh:mma'),
            value: date.formatDate('HH:mm')
        });
        date = date.addValue({ minutes: step });
    }
    return blocks;
};


export const generateAvailableTimesTz = (
    fromDateTz: DateTZ,
    showPast: boolean,
    step: number = 15,
    timezone: string = new DateTZ({ date: Date.now() }).local_tz,
    all_day: boolean = false,
    extras: SelectOption<DateTZ>[] = [],
    startFromDateTz: boolean = false
): SelectOption<DateTZ>[] => {
    // now needs the building timezone for isAfter to work correctly
    const now = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: timezone });
    // date needs the building timezone for isAfter to work correctly
    let dateTz = fromDateTz.clone();
    // Check if we're today or a future date
    const blocks: SelectOption<DateTZ>[] = [];
    if (showPast || dateTz.isAfterDate(now, 'day')) {
        // later than today
        dateTz = dateTz.startOfValue('day');
    } else {
        // today/fromDateTz using the flag
        dateTz = startFromDateTz ? dateTz : now;
    }
    // Get the closest timeslot to the start time
    dateTz = closestToTimeSlotTz(dateTz, step);
    // Date changed to the closest time
    const end = dateTz.endOfValue('day');
    // Add the all day option for today
    if (all_day && dateTz.isSameDate(now, 'day')) {
        const startOfDay = dateTz.startOfValue('day');
        blocks.push({
            display: timeWithGmtOffsetFormatString(startOfDay),
            shortDisplay: startOfDay.formatDate('hh:mma'),
            value: startOfDay
        })
    }
    // Add options for the rest of the day
    while (dateTz.isBeforeDate(end)) {
        blocks.push({
            display: timeWithGmtOffsetFormatString(dateTz),
            shortDisplay: dateTz.formatDate('hh:mma'),
            value: dateTz, 
        });
        
        dateTz = dateTz.addValue({ minutes: step });
    }

    return unique(extras.concat(blocks), 'shortDisplay');
};

/**
 * Generates list of duration options for new booking flow.
 * @param date - The start dateTime
 * @param max - The max number of minutes to go to from the date param.
 * @param min - The min number of minutes to display first time from.
 * @param step - The number of minutes to step up by for timeslots.
 */
export const generateDurationOptions = (
    date: DateTZ,
    max: number,
    min: number,
    step: number,
    extended: boolean): SelectOption<any>[] => {
    const blocks: SelectOption<any>[] = [];
    let value = min;
    while (value <= max) {
        let duration: string;
        if (extended) {
            const d = durationHumanized(value - min, true);
            duration = d ? `+${d}` : 'Current';
        } else {
            duration = durationHumanized(value, true);
        }

        blocks.push({
            value,
            display: `${date.addValue({ minutes: value }).formatDate(timeFormatString()).toLocaleLowerCase()} (${duration})`,
        });
        value += step;
    }
    return blocks;
};

export const allDayOption: SelectOption<any> = ({
    value: ALL_DAY_BOOKING_VALUE,
    display: 'All Day'
});

export const oneWeekOption: SelectOption<any> = ({
    value: 60 * 24 * 7, // 1 week
    display: 'One Week'
});

export const defaultDurationGroups: DurationGroup[] = [
    {
        start: 15,
        step: 15,
        max: 60 * 24
    }
    // , {
    //     start: 60,
    //     step: 30,
    //     max: (60 * 8) - 1  // 8 hours
    // }, {
    //     start: 60 * 8,
    //     step: 60,
    //     max: 60 * 24 // 24 hours
    // }
];

/**
 * Maps the {@link DurationGroup} to {@link SelectOption} and appends the default all day and week options.
 */
export const generateDurationOptionsFromGroups = (date: DateTZ, durationGroups: DurationGroup[], extended: boolean): SelectOption<any>[] => [
    ...(durationGroups.map(duration => generateDurationOptions(date, duration.max, duration.start, duration.step, extended)).reduce(((previousValue, currentValue) => {
        previousValue.push(...currentValue);
        return previousValue;
    }), [])),
        // allDayOption,
        // oneWeekOption
    ];
