import { Component, Input, SimpleChanges, Renderer2, ElementRef, OnChanges, Output, EventEmitter} from '@angular/core';

import { BookingCheckinParams, User } from '@mckinsey-converge/data-common';
import { Booking } from '@mckinsey-converge/data-common';
import { BookingsService } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'a-visitor',
    templateUrl: './visitor.component.html',
    styleUrls: ['./visitor.component.scss'],
})
export class VisitorComponent implements OnChanges {
    /** Booking that the user is assoicated with */
    @Input() public event: Booking;
    /** User details to display */
    @Input() public person: User;
    /** Search filter string */
    @Input() public search: string;
    /** Emitter for changed to the checked in status */
    @Output() public checked = new EventEmitter<void>();
    /** Status of the visitor */
    public status: string;
    /** Whether attendee is being checked in */
    public loading: boolean;

    /** Whether attendee is late */
    public get late() {
        return this.event.timeBasedStatusLabel === 'in_progress' && this.person.external;
    }

    /** Whether attendee has checked in */
    public get approved() {
        return this.event.checkInsArray.includes(this.person.email);
    }

    /** Whether user is a match for  */
    public get is_match(): boolean {
        const search = (this.search || '').toLowerCase();
        return (
            this.person.name.toLowerCase().indexOf(search) >= 0 ||
            this.person.email.toLowerCase().indexOf(search) >= 0
        );
    }

    public get invalid_email(): boolean {
        return !this.person.email || this.person.email.includes('@guest.mckinsey.com');
    }

    /** URL for mailing all attendees of the event */
    public get mail_url(): string {
        return `mailto:${this.person.email}?subject="${this.event.title}"%20at%20${
            this.event.start_time
        },%20${this.event.date_string}${
            this.event.organiser.email.toLowerCase() !== this.person.email
                ? '&cc=' + this.event.organiser.email
                : ''
        }`;
    }

    constructor(
        private _element: ElementRef<HTMLElement>,
        private _renderer: Renderer2,
        private _bookings: BookingsService,
        private _service: ApplicationService
    ) {}

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.search) {
            if (this.search && !this.is_match) {
                this._renderer.addClass(this._element.nativeElement, 'not-match');
            } else {
                this._renderer.removeClass(this._element.nativeElement, 'not-match');
            }
        }
    }

    /**
     * Checkin the attendee
     */
     public  checkin() {
        this.loading = true;
        const fields: BookingCheckinParams= {
            host_email: this.event.organiser.email,
            attendees: [{ name: this.person.name, email: this.person.email }],
            booking_id: this.event.id,
            icaluid: this.event.icaluid,
            id: this.event.id,
            notify_users: [...this.event.notify_users],
            response_type: 'accepted',
            room_id: this.event.space.id,
        };
        this._bookings.checkinIndividualVisitor(this.event.id, fields).subscribe(result => {
                const check_ins = {...result.check_ins};
                check_ins[this.person.email] = true;
                this.event.check_ins = result.check_ins;
                this.event = new Booking({
                    ...this.event.toJSON(),
                    check_ins,
                });
                this.checked.emit();
                this.loading = false;
            }); 
    }


    public get companyString(){
        return this.person.external ? this.event.company.join(',') : 'Mckinsey'
    }
}
