import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';

import { BookingFlowEvent } from '../../booking-modal.component';
import { CateringOrder } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';

@Component({
    selector: 'a-catering-order-list',
    templateUrl: './order-list.component.html',
    styleUrls: ['./order-list.component.scss']
})
export class BookingCateringOrderListComponent implements OnInit{
    /** Form field for storing catering orders */
    @Input() public catering: FormControl;
    /** Selected datetime for the booking */
    @Input() public date: number;
    /** Whether booking is all day */
    @Input() public all_day: boolean;
    /**  */
    @Input() public space_list: Space[];
    /** Whether catering is the only action in the flow */
    @Input() public only_catering: boolean;
    /** Emitter for flow update events */
    @Output() public event = new EventEmitter<BookingFlowEvent>();
    /** Emiiter for order events */
    @Output() public order = new EventEmitter<CateringOrder>();

    public ngOnInit(): void {
        if(this.space_list && this.space_list.length) {
            this.catering.value.forEach(catering => {
                catering.location = this.space_list[0].name; 
                catering.location_id = this.space_list[0].email;
                catering.bookingDateTz = this.date;
            });
        }
    }

    /** List of orders in the booking */
    public get orders(): CateringOrder[] {
        /* istanbul ignore else */
        if (!this.catering) {
            return [];
        }
        return this.catering.value;
    }

    /** Emit event for a new catering order */
    public newOrder() {
        this.order.emit();
    }

    /** Emit event for editing existing order */
    public editOrder(order: CateringOrder) {
        this.order.emit(order);
    }

    /** Remove order from the list */
    public deleteOrder(order: CateringOrder) {
        const orders = this.orders;
        const index = orders.findIndex(an_order => order.id === an_order.id);
        /* istanbul ignore else */
        if (index >= 0) {
            orders.splice(index, 1);
            this.catering.setValue(orders);
        }
    }

    /**
     * Proceed to the next step in the space booking flow
     */
    public next(): void {
        this.event.emit({ type: 'next', step: 'catering' });
    }

    /** Move flow to previous step */
    public previous(): void {
        this.event.emit({ type: 'previous', step: 'catering' });
    }
}
