import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';

// Interface to define the structure of the configuration object for the search bar
export interface SearchBarConfig {
  placeholder?: string;
  totalFoundItem?: number;
  showSrchWarning?: boolean;
}

@Component({
  selector: 'cng-search-bar',
  templateUrl: './cng-search-bar.component.html',
  styleUrls: ['./cmg-search-bar.component.scss']
})
export class CngSearchBarComponent implements OnChanges {
  // Input property to accept configuration for the search bar
  @Input() config: SearchBarConfig = {
    placeholder: 'Search ...',
    totalFoundItem: 0,
    showSrchWarning: false
  };

  // Output event emitter to emit search strings to parent components
  @Output() search = new EventEmitter<string>();

   // Local variable to store the search string entered by the user
  searchStr: string = '';

  // Lifecycle hook that is called when any data-bound property of a directive changes
  ngOnChanges(changes: SimpleChanges): void {
    // Check if the config property has changed
    if (changes.config) {
      const configChanges = changes.config.currentValue;
      // Update the config with new values or fallback to defaults
      this.config = {
        placeholder: configChanges.placeholder || 'Search ...',
        totalFoundItem: configChanges.totalFoundItem || 0,
        showSrchWarning: configChanges.showSrchWarning || false
      };
    }
  }

  // Method to handle the search action
  onSearch(value: string): void {
    // Emit the search string if it's not empty
    if (value) {
      this.search.emit(value);
    }
  }

  // Method to clear the search input and reset related configurations
  clearSearch(box: HTMLInputElement): void {
    this.searchStr = '';
    box.value = '';
    this.config.showSrchWarning = false;
    this.config.totalFoundItem = 0;
    this.search.emit('');
  }

  // Method to reset the search-related warnings and item count
  resetSearch(): void {
    this.config.showSrchWarning = false;
    this.config.totalFoundItem = 0;
  }
}
