import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Route, RouterModule } from '@angular/router';

import { ReportsComponent } from './reports.component';
import { SharedContentModule } from '../shared/shared.module';
import { ReportDisplayComponent } from './report-display/report-display.component';
import { ReportTableComponent, NewlineToBRPipe} from './report-table/report-table.component';

const ROUTES: Route[] = [
    { path: '', component: ReportsComponent },
    { path: ':report', component: ReportsComponent },
    { path: '**', redirectTo: '' }
];

@NgModule({
  declarations: [ReportsComponent, ReportDisplayComponent, ReportTableComponent, NewlineToBRPipe],
  imports: [CommonModule, FormsModule, RouterModule.forChild(ROUTES), SharedContentModule]
})
export class ReportsModule { }
