import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';

import { ApplicationService } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationIcon } from '@mckinsey-converge/data-common';
import { UsersService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
})
export class LoginComponent extends BaseDirective implements OnInit {
    /** Logo of the application organisation */
    public logo: ApplicationIcon;
    /** Whether the user credentials are being checked */
    public loading: boolean;
    /** Form fields for the login form */
    public form: FormGroup;

    /** Password field element */
    @ViewChild('pass_field', { static: true }) private pwd_field: ElementRef<HTMLInputElement>;

    constructor(private _service: ApplicationService, private _users: UsersService) {
        super();
    }

    public ngOnInit(): void {
        this.loading = true;
        this.form = new FormGroup({
            username: new FormControl('', [Validators.required]),
            password: new FormControl('', [Validators.required]),
        });
        this._service.initialised.pipe(first((_) => _)).subscribe(() => {
            this.loading = false;
            this.logo = this._service.setting('app.logo_light') || { type: 'icon' };
        });
    }

    /** Perform user login */
    public login() {
        this.loading = true;
        const form = this.form.value;
        this._users
            .login(
                {
                    username: form.username,
                    password: form.password,
                },
                { form: true }
            )
            .then(
                () => this.loading = false,
                () => {
                    this.loading = false;
                    this.form.controls.password.setErrors({ check: true });
                }
            );
    }
}
