import {
    Component,
    Output,
    EventEmitter
} from '@angular/core';
import {
    MatDialogRef,
} from '@angular/material/dialog';
import {
    BaseDirective,
    DialogEvent
} from '@mckinsey-converge/base';

@Component({
    selector: 'no-catering-modal',
    templateUrl: './no-catering-modal.component.html',
    styleUrls: ['./no-catering-modal.component.scss']
})
export class NoCateringModalComponent extends BaseDirective {
    constructor(
        private _dialog_ref: MatDialogRef<NoCateringModalComponent>,
    ) {
        super();
    }

    // /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();

    // Submit the Survey in it's current state.
    public confirm() {
        this.event.emit({ reason: 'done' });
        this._dialog_ref.close();
    }

    // Close the modal
    public cancel() {
        this.event.emit({ reason: 'close' });
        this._dialog_ref.close();
    }
}
