import {
    Component,
    OnDestroy,
    OnInit
} from '@angular/core';
import {
    ActivatedRoute,
    Router
} from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { first, take } from 'rxjs/operators';

import {
    BaseDirective,
    HashMap,
    Identity
} from '@mckinsey-converge/base';
import { BookingModalComponent } from 'src/app/bookings/booking-modal/booking-modal.component';
import {
    ApplicationService,
    BookingStateService,
    BookingStoreState,
    BuildingLevel,
    OrganisationService,
    selectBookingByIdResultsFromState,
    User,
    Booking,
    SpacesService,
    Space
} from '@mckinsey-converge/data-common';

import {
    AlertMode,
} from '@mckinsey-converge/ui';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { MeetingDetailsModalComponent } from '../meeting-details-modal/meeting-details-modal.component';
import { MatSlideToggleChange } from '@angular/material/slide-toggle';
import { selectCurrentUser, UserStoreState } from '@mckinsey-converge/user';
import { select, Store } from '@ngrx/store';
import { BuildingStoreState } from 'libs/buildings/src';
import { clearBookingByIdResults, loadBookingsWithQuery, MyBookingsStoreState } from '@mckinsey-converge/bookings';
import { Subscription } from 'rxjs';

export type RoomBookingStoreState = BookingStoreState & BuildingStoreState & MyBookingsStoreState & UserStoreState;

@Component({
    selector: 'a-day-view',
    templateUrl: './day-view.component.html',
    styleUrls: ['./day-view.component.scss']
})
export class DayViewComponent extends BaseDirective implements OnInit {
    /** Currently selected date, starts out with no timezone data, gets updated later */
    public dateTz: DateTZ = DateNow(new Date());
    /** ID of the currently selected level */
    public active_level: string = '';
    /** ID of the currently selected type */
    public active_type: string = '';
    /** List of levels available for the active building */
    public levels: BuildingLevel[] = [];
    /** List of space types available for the active building */
    public space_types: Identity[] = [];
    /** Whether to show only items with a setup and/or breakdown time */
    public only_overflow: boolean;
    /** List of legend keys to show */
    public legend: HashMap<boolean> = {};
    /** List of legend keys to show */
    public legend_list: string[] = [];
    /** List of available legend keys */
    public legend_keys: Identity[] = [
        { name: 'Internal', id: 'internal', color: '#E69FC7', active: true },
        { name: 'Client', id: 'client', color: '#1F40E6', active: true },
        { name: 'External', id: 'external', color: '#8C5AC8', active: true },
        { name: 'Setup', id: 'setup', color: '#d32f2f', active: true },
        { name: 'Training', id: 'training', color: '#F0E700', active: true },
        { name: 'Interview', id: 'interview', color: '#348D02', active: true },
        { name: 'Declined', id: 'declined', color: '#617288', active: true }
    ];

    public banner_mode = AlertMode.Warning
    public showBanner = true;
    public undo_banner_mode = AlertMode.Error;
    public has_access: boolean;
    public undo_action: 'series' | 'booking';
    private _undo_timer: any;

		private spaceTypesSubscription: Subscription;

  event_list: Booking[] = [];
	searchStr: any;
	foundSearchLength: number = 0;
	searchedCounter: any = 1;
	showSrchWarning: boolean;
	totalFoundItem: number = 0;
    spaces: Space[] = [];


    constructor(
        private _service: ApplicationService,
        private _org: OrganisationService,
        private _dialog: MatDialog,
        private _router: Router,
        private _route: ActivatedRoute,
        private _booking_state_service: BookingStateService,
        private _spaces: SpacesService,
        private store: Store<RoomBookingStoreState>,
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.title = 'Day View';

				if( this._service.get('active_legends') !== null &&  this._service.get('active_legends').hasOwnProperty('length')) {
					this.legend_keys = this._service.get('active_legends');
				}

				this._org.initialised.pipe(first((_) => _)).subscribe(() => {
            // Init dateTz here once the organization data is loaded.
            this.dateTz = new DateTZ({ date: this.dateTz.ms, is_local_tz: false, building_tz: this.timezone });
            this.legend_list = this.legend_keys.map((item) => `${item.id}`);
            this.subscription(
                'route.params',
                this._route.paramMap.subscribe((params) => {
                    /* istanbul ignore else */
                    if (params.has('level') && this.active_level !== params.get('level')) {
                        const level = this._org.levelWithID(params.get('level'));
                        /* istanbul ignore else */
                        if (level) {
                            const building = this._org.buildings.find(
                                (bld) => bld.id === level.building_id
                            );
                            /* istanbul ignore else */
                            if (building) {
                                this.active_level = level.id;
                                this._org.building = building;
                                this._booking_state_service.setZone(this.active_level || this._org.building.id);
                            }
                        }
                    }
                })
            );
            this.subscription(
                'building',
                this._org.listen('active_building').subscribe(() => {
                    this._booking_state_service.updateRoomList();
                    // Update dateTz here when the building is changed.
										this.dateTz = new DateTZ({
											date: this.dateTz.ms,
											is_local_tz: false,
											building_tz: this.timezone,
										});
                    const building = this._org.building;
                    this.has_access = building ? true : false;
                    if (!this.has_access) {
                        return;
                    }

                    /* istanbul ignore else */
                    if (!building.levels.find((lvl) => lvl.id === this.active_level)) {
                        this.active_level = building?.levels[0]?.id;
                        this.updateLevel();
                    }
                    this.initSpace();
                    this.levels = [{ id: '', name: 'All Levels' } as any].concat(building.levels);
										this.levels.sort((a, b) =>
										b.name.localeCompare(a.name, undefined, {
												numeric: true,
												ignorePunctuation: true,
											})
										);
                    this.space_types = [{ id: '', name: 'All Space Types' } as any].concat(
                        this._org.space_types
                    );
										if (this._org.spaceTypesObservable) {
											this.spaceTypesSubscription = this._org.spaceTypesObservable.subscribe(
													(spaceTypes) => {
															this.space_types = [
																	{ id: '', name: 'All Space Types' } as any,
															].concat(spaceTypes);
													}
											);
									}
                })
            );
            this.subscription(
                'day_view_undo',
                this._service.get('undo').subscribe(undo => {
                    if (!undo)
                        return;

                    if (this.undo_action) {
                        this.undo_action = null;
                        clearTimeout(this._undo_timer);
                    }

                    this._booking_state_service.attentToDelete(undo.booking, undo.action);
                    this.undo_action = undo.action;
                    this._undo_timer = setTimeout(() => {
                        this._service.get('undo').next(null);
                        this.undo_action = null;
                    }, 15000);
                })
            );
        });

        this.configureIfLinksFromEmail();

        this.subscription(
            'bookings',
            this._booking_state_service.filtered.subscribe((list) => {
                this.event_list = [...this.filterEvents(list)];

            })
        );

        
    }

    public initSpace() {
        this.subscription(
            'spaces',
            this._spaces.listen('list').subscribe((spaces) => {
                const zone_id = !this.active_level ? this._org.building.id : this.active_level;
                this.spaces = this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0 && _.bookable);

            })
        );
    }

    private filterEvents(list): Booking[] {
            const bookings = list;
            const filterd_booking = bookings.filter((event) => {
                const ignore = event.is_recurrent && event.recurrence_type === 'master';
                return ( !ignore );
            });

            return filterd_booking || [];
    }

    isIncludes = (item: any, field: string, searchVaue: string) => (item && item[field] && item[field].toLowerCase().includes(searchVaue));
    

    searchBooking(value) {

         // Resetting warning flag
        this.showSrchWarning = false;

        // Converting search value to lower case for case-insensitive comparison
        const searchValue = value.toLowerCase().trim();
        
        // Combining event list and space arrays into one arrya for searchig 
       const allItems = [...this.event_list, ...this.spaces];
        // console.log('BOOKING : ',allItems[0]['organiser'])
       // Filtering the combine array to find matching room or booking 
       const filteredItems = allItems.filter( (item: any ) => 
        this.isIncludes(item, 'title', searchValue) ||
        this.isIncludes(item.creator, 'name', searchValue) ||
        this.isIncludes(item.organiser, 'name', searchValue) ||
        this.isIncludes(item, 'simple_name', searchValue) || // search with room name 
         (item?.attendees && !!item?.attendees.filter(el => el.name.toLowerCase().includes(searchValue)).length ) 
        );

        // Updating the total number of found items in serach
        this.totalFoundItem = filteredItems.length;

        // Setting showSrchWarning flag if no result were found 
        this.showSrchWarning = !this.totalFoundItem;

        // Updating lenght of found items for later use
        this.foundSearchLength = this.totalFoundItem - 1;


        if(this.searchStr !== value) {
            // Setting the first found as the current viewing item
            this._service.set('CONCIERGE.day_view.viewing', filteredItems[0]);
            // Resetting the search counter
            this.searchedCounter = 0;
        }
        else {
            // Handling case where seached string is the same as previou one 
            // Incrementing search counter, loopoing back to 0 if it exceed the length of found items
            this.searchedCounter = ( this.searchedCounter < this.foundSearchLength  ) ? this.searchedCounter + 1 : 0
            // Setting the next found items as the current viewing item
            this._service.set('CONCIERGE.day_view.viewing', filteredItems[this.searchedCounter]);
        }

        // Storing the current search string for future camparison
        this.searchStr = value;

    }



    public configureIfLinksFromEmail() {
        /**
         * https://acaprojects.atlassian.net/browse/MCK-2055
         * Load booking with id received from email link to concierge and setup configuration to open booking detail dialog on day view
         * Will work with both formatted URL's
         * http://localhost:4200/#/day-view?view=booking-1edd3a4f-45fa-43cd-960d-5ae6ee5b1e36
         * http://localhost:4200/#/day-view/zone-1FUT0pxu12s?view=booking-1edd3a4f-45fa-43cd-960d-5ae6ee5b1e36
         */
         const bookingId = this._route.snapshot.queryParams['view'];
         if(bookingId) {
             this.subscription('currentUser', this.store.select(selectCurrentUser).pipe(take(1))
             .subscribe((user: User | undefined) => {
                 // Once we know we have the user loaded.
                this.store.dispatch(loadBookingsWithQuery({ 'id': bookingId,  'target': 'bookingById', 'offset': 0 }));
             }));

             this.store.dispatch(clearBookingByIdResults())

             this.subscription('bookingResults', this.store.pipe( select(selectBookingByIdResultsFromState) ).pipe(first(_ => !!_ && !!_.success))
             .subscribe(next => {
                 const booking = next.success ? next.success?.[0] : null;
                 if (booking && booking.room) {
                     const levelId = this._route.snapshot.params.hasOwnProperty('level');

                     localStorage.setItem('CONCIERGE.view_booking', JSON.stringify(booking.toJSON()));
                     localStorage.setItem('CONCIERGE.view_booking_space', JSON.stringify(booking.space.toJSON()));
                     localStorage.setItem('CONCIERGE.date', `${booking.startDateTz.ms}`);
                     localStorage.setItem('CONCIERGE.timezone', `${booking.startDateTz.building_tz}`);

                     this.updateDateTz(booking.startDateTz)
                     this._booking_state_service.setDate(booking.startDateTz);
                     this._booking_state_service.setZone(booking.level_zone);
                     if(!levelId ) {
                         this._router.navigate(['/day-view', booking.level_zone], { queryParams: { view: bookingId }, replaceUrl: true });
                     }
                 }
             }));
         }
    }

    public ngOnDestroy(): void {
        if (this._service.get('undo').getValue()) {
            this._service.get('undo').next(null);
        }
				if (this.spaceTypesSubscription) {
					this.spaceTypesSubscription.unsubscribe();
				}
        this._service.set('CONCIERGE.day_view.viewing', []);
    }


    // Fires for the big blue new booking button
    public newMeeting() {
        this._dialog.open(BookingModalComponent, {
            data: { timezone: this.timezone },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
    }

    // R--- surely these 2 methods are not DRY and all the main tabs need a base class
    // that encapsulates the common elements.
    public get timezone() {
        return this._org.building?.timezone || localStorage.getItem('CONCIERGE.timezone');
    }

    public updateDateTz(dateTz: DateTZ) {
        this.dateTz = dateTz;
    }

    public updateLevel() {
        this._booking_state_service.setZone(this.active_level || this._org.building.id);
        this.initSpace();
        this._router?.url?.includes('/day-view') && this._router.navigate(['/day-view', this.active_level]);
    }

    public updateLegend(mobile: boolean = false) {
        if (mobile) {
            this.legend_list = this.legend_keys.reduce((list, key) => {
                if (key.active) list.push(key.id);
                return list;
            }, []);
						this._service.set('active_legends', this.legend_keys);
        }
        this.legend = this.legend_keys.reduce((map, key) => {
            map[key.id] = this.legend_list.includes(`${key.id}`);
            return map;
        }, {});

        this._booking_state_service.setFilters({
            ...this._booking_state_service.filters,
            hide_type: Object.keys(this.legend).filter((i) => this.legend[i] === false) as any
        });
    }

    public onOnlySetupBreakdownChanged(toggle: MatSlideToggleChange): void {
        this._service.set('APP.breakdown', toggle.checked)
    }

    public async undoBooking(): Promise<void> {
        const { action, booking } = this._service.get('undo').getValue();
        if (!booking)
            return;

        await booking.undo('accept', action === 'series' ? { series: true } : {});
        this._service.get('undo').next(null);
        this._booking_state_service.attentToDelete(booking, action, true);
        this.undo_action = null;
        clearTimeout(this._undo_timer);
        this._undo_timer = null;

        this._dialog.open(MeetingDetailsModalComponent, {
            maxWidth: 'calc(100vw - 2em)',
            data: {
                space: booking.space,
                booking: booking
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
    }

    public hideBanner() {
        this.showBanner = false;
    }

    public hideUndo() {
        this.undo_action = null;
        clearTimeout(this._undo_timer);
    }
}
