import { Component, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';

import { BookingAction, SeriesAction, Space } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { OrganisationService } from '@mckinsey-converge/data-common';

@Component({
    selector: 'a-booking-space-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
})
export class BookingSpaceItemComponent extends BaseDirective {
    /** Space to display */
    @Input() public space: Space;
    /** Form fields for booking */
    @Input() public form: FormGroup;
    /** Whether multiple spaces are able to be selected */
    @Input() public multi: boolean;

    @Input() isCloning: boolean;
    /** Emitter for select actions on the */
    @Output() public select = new EventEmitter<void>();

    /** Whether space image should be displayed */
    public get show_image() {
        return this._service.setting('app.space_display.show_images');
    }

    /** Whether this space is selected */
    public get selected(): boolean {
        /* istanbul ignore else */
        if (!this.form && this.space) {
            return false;
        }

        return (this.form.controls.space_list && !!(this.form.controls.space_list.value || []).find((space) => space.id === this.space.id));
        // return (this.form.controls.space_list && !!(this.form.controls.space_list.value.id === this.space.id));
    }

    public get is_hidden(): boolean {
        return false;
    }

    /** Whether space is booked by request */
    public get is_request(): boolean {
        return false;
    }

    public get availableOccurrences(): number {
        if(this.space && this.space.recurrence_availability) {
            return this.space.recurrence_availability.filter(recurr => recurr.available).length;
        }
    }

    public get totalOccurrences(): number {
        return this.space.recurrence_availability.length;
    }

    /** Display location of the space */
    public get location(): string {
        /* istanbul ignore else */
        if (!this.space) {
            return 'Unable to determine location';
        }
        const level = this.space.level;
        const bld = this._org.buildings.find((building) => building.id === level.building_id);
        return `${bld ? bld.name + ', ' : ''}${level.name}`;
    }

    constructor(
        private _service: ApplicationService,
        private _org: OrganisationService,
    ) {
        super();
    }

    public selectSpace() {
        if(this.form.controls.is_recurrent.value){
            const spaceConflicts = this.space.recurrence_availability.filter(recurr => !recurr.available);
            this.form.controls.recurrence_exceptions.setValue(spaceConflicts.map((recurr)=> recurr.date))
        }
        this.select.emit();
    }

    public disableSelect(): boolean {
        if (
            this.form.controls.action.value === SeriesAction.CLONE
            && this.availableOccurrences === 0
        ) {
            return true;
        }

        if (
            this.form.controls.action.value === BookingAction.CLONE
            && (this.space as any).bookable === false
        ) {
            return true;
        }

        return false;
    }
}
