import {
    Component,
    EventEmitter,
    Inject,
    Output
} from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import {
    DialogEvent,
    HashMap
} from '@mckinsey-converge/base';
import {
    CateringCategory,
    CateringItem
} from '@mckinsey-converge/data-common';

export interface PackageOptionsModalData {
    item: CateringCategory;
}

export function checkItemRequirements(category: CateringCategory): boolean {
    const items = category.items || [];
    return category.must_select >= items.length
        ? items.reduce((valid, item) => valid && checkItemRequirements(item as any), true)
        : items.reduce((amount, item) => amount + (item.amount ? 1 : 0), 0) ===
        category.must_select;
}

export function getActiveItems(category: CateringCategory): HashMap<string> {
    let map = {};
    if (category.items) {
        const item = category.items.find(item => item.amount > 0);
        if (item) {
            map[category.id] = item.id;
        }
        category.items.forEach(item => map = { ...map, ...getActiveItems(item as any) });
    }
    return map;
}

@Component({
    selector: 'a-package-options-modal',
    templateUrl: './package-options-modal.component.html',
    styleUrls: ['./package-options-modal.component.scss']
})
export class PackageOptionsModalComponent {
    /** Emitter for user actions on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();

    /** Item to select options for */
    public get item(): CateringCategory {
        return this._data.item;
    }

    /** Whether package requirements have been met */
    public get requirements_met(): boolean {
        return checkItemRequirements(this.item);
    }

    public get active_items(): HashMap<string> {
        return getActiveItems(this.item);
    }

    constructor(@Inject(MAT_DIALOG_DATA) private _data: PackageOptionsModalData) {
    }

    /**
     * Update the list of selected items
     */
    public updateAmount(a_package: CateringCategory, option: CateringItem, state: boolean) {
        if (state) {
            const needed = a_package.must_select;
            const active = a_package.items.filter(an_item => an_item.amount > 0).length;
            if (active >= needed) {
                const item = a_package.items.find(an_item => an_item.amount > 0);
                item.setAmount(0);
            }
            option.setAmount(1);
        } else {
            option.setAmount(0);
        }
    }
}
