import { Component, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Booking } from '@mckinsey-converge/data-common';
import { CateringOrder } from '@mckinsey-converge/data-common';
import { BookingModalComponent } from '../../../bookings/booking-modal/booking-modal.component';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { DialogEvent, BaseDirective  } from '@mckinsey-converge/base';

@Component({
    selector: 'meeting-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss'],
})
export class MeetingCateringDetailsComponent extends BaseDirective {
    /** Booking to display details */
    @Input() public booking: Booking;
    /** Space to display details */
    @Input() public space: Space;

    @Output() updateBooking = new EventEmitter();

    /** Whether the meeting is approved or not */
    public isApproved = false;
    
    public orders = [];

    /** Total cost of all the orders */
    public get total(): number {
        let total = 0;
        this.orders.forEach((order) => (total += order.total));
        return total;
    }

    public get total_edited(): boolean {
        return this.booking.edits.some((_) => this.orders.some((o) => o.id === _));
    }

    /** Charge code for orders in the active space */
    public get charge_code(): string {
        return (this.orders.length ? this.orders[0].charge_code : '') || '<No Charge code>';
    }

    /** Currency symbol for orders in active space */
    public get symbol(): string {
        const space = this.space || { level: { building_id: '' } };
        const building = this._org.buildings.find((bld) => bld.id === space.level.building_id) || {
            currency: 'USD',
        };
        return building.currency;
    }

    /** Notes for orders in active space */
    public get notes(): string {
        if (this.orders.length) {
            const note = this.booking.notes.find(
                (note) =>
                    note.type === 'catering' &&
                    // note.space === this.space.email &&
                    this.booking.multi_rooms.some(room => room.email === note.space) &&
                    note.author === this.booking.creator.name
            );
            return note ? note.message : '<No Notes>';
        } else {
            return '<No Notes>';
        }
    }

    /** Whether booking has been declined or ended */
    public get expired(): boolean {
        return this.booking.timeBasedStatusLabel === 'done' || this.booking.declined;
    }

    constructor(
        private _org: OrganisationService,
        private _dialog: MatDialog,
        private _router: Router
    ) {
        super();
    }

    public ngOnInit() {
        this._updateOrders();
        this.isApproved = this.booking.status === 'accepted' || this.booking.status === 'approved';
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.booking) {
            this._updateOrders();
        }
    }

    /** Store the order details for viewing in another component */
    public storeOrder(order: CateringOrder) {
        const booking = this.booking || order.booking;
        localStorage.setItem('CONCIERGE.view_order', JSON.stringify(order.toJSON()));
        localStorage.setItem('CONCIERGE.view_booking', JSON.stringify(booking.toApiJSON()));
        localStorage.setItem('CONCIERGE.date', `${booking.startDateTz.ms}`);
        localStorage.setItem('CONCIERGE.timezone', `${booking.startDateTz.building_tz}`);
    }

    /** Open modal to edit catering details for booking */
    public editMeetingCatering() {
        const dialogRef = this._dialog.open(BookingModalComponent, {
            data: {
                timezone: this.booking.timezone,
                booking: this.booking,
                only_catering: true,
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.subscription(
            'modal_events',
            dialogRef.componentInstance.event.subscribe(async (event: DialogEvent) => {
                if (event.reason === 'done') {
                    dialogRef.close();
                    this.updateBooking.emit(event.metadata);
                }
            })
        );
    }

    /**
     * View catering order for the event
     */
    public viewOrder(order: CateringOrder) {
        const booking = this.booking || order.booking;
        if (!booking || !order) return;
        /* istanbul ignore else */
        if (localStorage) this.storeOrder(order);
        this._router.navigate(['/catering', 'orders'], {
            queryParams: { booking: booking.id, view: order.id },
        });
        this._dialog.closeAll();
    }

    /** List of orders for this booking and space */
    private _updateOrders() {
        let orders = [];
        this.booking.multi_rooms.forEach((room) =>{
            const _temp = this.booking.catering.filter(
                (order) => order.location_id === room.email
            );
            _temp[0] && orders.push(..._temp);
        });
        orders = orders.map((order) => {
            let ord = order.toJSON();
            ord.booking = this.booking; // R--- check this change works
            return new CateringOrder(ord);
        });
        this.orders = orders;
    }
}
