import { LocalStorageWrapper } from '@mckinsey-converge/storage';

export class CateringStorage extends LocalStorageWrapper {

    private static orderDetailsKey = 'order-details';

    constructor() {
        super('CATERING');
    }

    public set orderDetails(value: boolean) {
        this.setValue(CateringStorage.orderDetailsKey, value + '');
    }

    public get orderDetails(): boolean {
        const value = this.getValue(CateringStorage.orderDetailsKey);
        return value === 'true';
    }

    public removeOrderDetails() {
        this.removeValue(CateringStorage.orderDetailsKey);
    }
}
