import {
    Component,
    EventEmitter,
    forwardRef,
    Input,
    Output,
    ViewChild,
    OnInit
} from '@angular/core';
import {
    ControlValueAccessor,
    NG_VALUE_ACCESSOR
} from '@angular/forms';

import { BaseDirective } from '@mckinsey-converge/base';

import { MatMenuTrigger } from '@angular/material/menu';
import {
    BehaviorSubject,
    Observable
} from 'rxjs';
import { map } from 'rxjs/operators';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { DateTzCalendarWidgetComponent } from '../date-tz-calendar-widget/date-tz-calendar-widget.component';

@Component({
    selector: 'date-tz-date-picker',
    template: `
        <div
            [class.icon-only]="icon_only"
            [class.mck-form-input]="!icon_only"
            [class.mck-column]="!icon_only"
            [class.error]="error"
        >
            <mck-label [required]="true" *ngIf="!icon_only && label">{{label}}</mck-label>
                <div
                    [class.mck-date-display]="!icon_only"
                    [class.mck-input]="!icon_only"
                    role="button"
                    form-field
                    tabindex="0"
                    [class.active]="is_open"
                    [attr.disabled]="disabled"
                    (keydown.enter)="openDatePicker()"
                    [matMenuTriggerFor]="appMenu"
                    [class.date-error]="invalid || error"
                    
                >
                <div class="display" *ngIf="!icon_only">{{ dateString | async }}</div>
                <div class="icon">
                    <mck-calendar-icon
                        [fillClass]="is_open ? 'mck-icon-fill-open' : 'mck-icon-fill-black'"></mck-calendar-icon>
                </div>
            </div>
            
            <mat-menu #appMenu="matMenu" xPosition="before" (close)="onDatePickerClose()">
                <div class="date-picker" (click)="$event.stopPropagation()">
                    <date-tz-calendar-widget
                        className="mck-date-picker"
                        [counters]="counters"
                        [ngModel]="selectedDateTzValue | async"
                        [options]="{ fromDateTz: fromDateTz, untilDateTz: untilDateTz, day_format: 'EEE' }"
                        (ngModelChange)="setValue($event); closeDatePicker()"
                    ></date-tz-calendar-widget>
                    <button
                        mat-stroked-button
                        color="primary"
                        class="mck-stroked-button today-button"
                        *ngIf="can_pick_today"
                        (click)="goToToday()"
                    >Today
                    </button>
                </div>
            </mat-menu>
        </div>
    `,
    styleUrls: ['./date-tz-date-picker.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => DateTzDatePickerComponent),
            multi: true
        }
    ]
})
export class DateTzDatePickerComponent extends BaseDirective implements OnInit, ControlValueAccessor {
    @Input() public label?: string;
    /** Earliest date available the user is allowed to pick */
    @Input() public fromDateTz: DateTZ = DateNow(new Date());
    /** Latest date available the user is allowed to pick */
    @Input() public untilDateTz: DateTZ = DateNow(new Date()).endOfValue('day').addValue({ years: 1 });
    /** Position of the tooltip */
    @Input() public position: 'right' | 'left' = 'right';
    /** Offset of the tooltip */
    @Input() public offset: 'top' | 'bottom' = 'bottom';
    /** Whether form control is disabled */
    @Input() public disabled: boolean;
    // R-- possible TODO, address all_day that disables this input and displays "Add Day for the value like the time picker"
    @Input() public all_day: boolean = false;
    /** Whether to show the calendar tooltip */
    @Input() public icon_only: boolean = false;

    @Input() public counters: any;

    @Input() public initialDateSelectionTz?: DateTZ;

    @Output() public onSelect = new EventEmitter();

    @Input() public invalid?: boolean;

    @Input() public timezone?: string;

    @Input() public can_pick_today: boolean = true;

    /** Whether form control is invalid, touched and contains errors */
    @Input() public error?: boolean;

    public ngOnInit(): void {
        if (this.initialDateSelectionTz) {
            this.writeValue(this.initialDateSelectionTz)
        }
    }

    /** Whether to show the calendar tooltip */
    public show_tooltip: boolean;
    /** Form control on change handler */
    private _onChange: (_: DateTZ) => void;
    /** Form control on touch handler */
    private _onTouch: (_: DateTZ) => void;

    @ViewChild(MatMenuTrigger, { static: true }) private _trigger: MatMenuTrigger;
    @ViewChild(DateTzCalendarWidgetComponent, {static: true }) private _acaDatePicker: DateTzCalendarWidgetComponent;
    /** Currently selected date */
    public selectedDateTzValue = new BehaviorSubject<DateTZ>(DateNow(new Date()));

    /** Display value for the current date */
    public get dateString(): Observable<string> {
        return this.selectedDateTzValue.pipe(map(d => {
            const display = d.formatDate('ccc dd MMM yyyy');
            return `${display}${d.isSameDate(new DateTZ({ is_local_tz: false, building_tz: d.building_tz }), 'day') ? ' (Today)' : ''}`;
        }));
    }

    /** Whether date picker is open */
    public get is_open(): boolean {
        return this._trigger.menuOpen;
    }

    // date
    public get dateTzObservable(): Observable<DateTZ> {
        return this.selectedDateTzValue.pipe(map(d => {
            return d 
        }));

    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(newValueTz: DateTZ) {
        // Keep hours and minutes of the old date
        const old_date = this.selectedDateTzValue.value;
        let newDateTz = newValueTz.setValue({ hour: old_date.dateHour, minute: old_date.minutes, second: old_date.second });

        // Check that new date is before from
        if (newDateTz.ms < this.fromDateTz.ms) {
            newDateTz = this.fromDateTz;
        }

        this.selectedDateTzValue.next(newDateTz);
        if (this._onChange) {
            this._onChange(newDateTz);
        }

        this.onSelect.emit(newDateTz);
        this.show_tooltip = false;
    }

    public goToToday() {
        const building_tz = this.timezone || this.fromDateTz.building_tz
        const is_local_tz = DateNow(new Date()).building_tz === building_tz
        this.setValue(new DateTZ({ is_local_tz, building_tz }));
    }

    public resetToLastSelection = () => this._acaDatePicker.updateDate();

    public onDatePickerClose() {
        this.resetToLastSelection();
    }
    /* istanbul ignore next */
    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(selectedDateTz: DateTZ) {
        this.selectedDateTzValue.next(selectedDateTz);
        this.show_tooltip = false;
    }

    public readonly registerOnChange = (fn: (_: DateTZ) => void) => this._onChange = fn;
    public readonly registerOnTouched = (fn: (_: DateTZ) => void) => this._onTouch = fn;

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /** Set the state of the tooltip to closed after delay */
    public closeDatePicker() {
        this._trigger.closeMenu();
    }

    /** Set the state of the tooltip to closed after delay */
    public openDatePicker() {
        this._trigger.openMenu();
    }
}
