export const selectBookingFormsData =
    (state: BookingStoreState | RoomStoreState | BuildingStoreState | UserStoreState) =>
        (state as any).bookingForm as BookingFormState;

export const selectActiveForm = createSelector(selectBookingFormsData, state => {
    // Recreate because serialized form dateTz doesn't have DateTZ class methods
    if (!state.activeForm?.dateTz) {
        return state.activeForm
    }
    const { date, is_local_tz, building_tz} = state.activeForm?.dateTz;
    const endTzDate =   state.activeForm?.recurrence_endTz?.date;
    const recurrence_endTz = endTzDate ? {
        recurrence_endTz : new DateTZ({ date: endTzDate.valueOf(), is_local_tz, building_tz })
    } : {};

    const recurrence_starts =  state.activeForm?.recurrence_starts
			? {
					recurrence_starts: state.activeForm?.recurrence_starts.map( el =>
						{
							// at final step of booking creation process recurrence_start becomes Array<number> which need to be Array<DateTz>
							// To Do: Refactor - apps/staff/src/app/booking/booking-create/booking-form-base.component.ts lno: 186
							return new DateTZ({ date: typeof el === 'number' ? el * 1000 : el.date.valueOf(), is_local_tz, building_tz })
						}
					)
				}
			: {};


    const recEditingSearchPayload =  (state.activeForm.action === SeriesAction.EDIT && state.activeForm.is_recurrent)
			?
		  {
				id: state.activeForm.id,
				ignore: state.activeForm.ignore,
				bookable: state.activeForm.bookable
			}
			: {};




    return {
        ...state.activeForm,
        dateTz: new DateTZ({ date: date.valueOf(), is_local_tz, building_tz }),
        ...recurrence_endTz,
        ...recurrence_starts,
				...recEditingSearchPayload
    }
});

