import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ShellWrapperComponent } from './shell/shell.component';

const routes: Routes = [
    {
        path: '',
        component: ShellWrapperComponent,
        children: [
            {
                path: 'day-view',
                loadChildren: () =>
                    import('./calendar/day-view/day-view.module').then(m => m.DayViewModule)
            },
            {
                path: 'week-view',
                loadChildren: () =>
                    import('./calendar/week-view/week-view.module').then(m => m.WeekViewModule)
            },
            {
                path: 'catering',
                loadChildren: () =>
                    import('./catering/catering.module').then(m => m.CateringModule)
            },
            {
                path: 'visitors',
                loadChildren: () =>
                    import('./visitors/visitors.module').then(m => m.VisitorsModule)
            },
            {
                path: 'reports',
                loadChildren: () =>
                    import('./reports/reports.module').then(m => m.ReportsModule)
            },
            {
                path: 'room-admin',
                loadChildren: () =>
                import('./room-admin/room-admin.module').then(m => m.RoomAdminModule)
            },
            { path: '**', redirectTo: 'day-view' }
        ]
    },
    { path: '**', redirectTo: 'day-view' }
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule]
})
export class AppRoutingModule {}
