import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { Booking, generateBookingForm } from '@mckinsey-converge/data-common';
import { humaniseDuration, BaseDirective, DialogEvent } from '@mckinsey-converge/base';;
import { PrivateDetailsModalComponent } from '../private-details-modal/private-details-modal.component';
import { Space } from '@mckinsey-converge/data-common';

@Component({
    selector: 'meeting-private-details',
    templateUrl: './private-details.component.html',
    styleUrls: ['./private-details.component.scss'],
})
export class MeetingPrivateDetailsComponent extends BaseDirective implements OnInit {
    /** Booking to display details */
    @Input() public booking: Booking;
    /** Space to display details */
    @Input() public space: Space;

    @Output() updateBooking = new EventEmitter();

    /** Whether the meeting is approved or not */
    public isApproved = false;
    
    public get setup(): string {
        let setup = this.booking?.room_setup || this.booking.setup[this.space.email];
        return setup ? humaniseDuration(setup / 60) : '<No Setup time>';
    }

    public get breakdown(): string {
        let breakdown = this.booking.room_breakdown ||this.booking.breakdown[this.space.email];
        return breakdown ? humaniseDuration(breakdown / 60) : '<No Breakdown time>';
    }

    public get notes() {
        // return (this.booking.notes.find(note => note.type === 'private' && note.space === this.space.email) || {}).message;
        return (this.booking.notes.find(note => note.type === 'private' ) || {}).message;
    }

    constructor(private _dialog: MatDialog) {
        super();
    }

    ngOnInit(): void {
        this.isApproved = this.booking.status === 'accepted' || this.booking.status === 'approved'; 
    }

    public openPrivateDetailsModal() {
        const dialogRef = this._dialog.open<PrivateDetailsModalComponent>(PrivateDetailsModalComponent, {
            data: {
                space: this.space,
                booking: this.booking,
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });

        this.subscription(
            'modal_events',
            dialogRef.componentInstance.event.subscribe(async (event: DialogEvent) => {
                const form = generateBookingForm(this.booking, ['space_list'], true);
                if (event.reason === 'done') {
                    dialogRef.close();
                    this.updateBooking.emit(event.metadata);
                }
            })
        );
    }
}
