import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { MatMenuTrigger } from '@angular/material/menu';
import { BaseDirective } from '@mckinsey-converge/base';

import { BookingStateService, OrganisationService } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ, getTimezoneOffsetString, toMiddayUTC } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'a-topbar-header',
    templateUrl: './topbar-header.component.html',
    styleUrls: ['./topbar-header.component.scss'],
})
export class TopbarHeaderComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    @Input() public dateTz: DateTZ = DateNow(new Date());
    /** Currently building timezone */
    @Input() public timezone: string;
    /** Currently active zone */
    @Input() public zone: string;
    /** Whether to show new item button */
    @Input() public show_add_item = false;
    /** Whether to show the events for the active month */
    @Input() public show_events: boolean = false;
    /** Emitter for changes selected date */
    @Output() public dateChangeTz = new EventEmitter<DateTZ>();
    /** Emitter for add item button */
    @Output() public event = new EventEmitter();
    /** Counters of the events on given dates. Key format is `yyyy-MM-dd` */
    public event_map: { [date: string]: number };

    public fromDateTz = DateNow().subtractValue({ months: 6 });

    public get now() {
        return DateNow();
    }

    /** Display string for the currently selected date */
    public get tz(): string {
        return getTimezoneOffsetString(this.timezone_value);
    }

    public get timezone_value() {
        return localStorage.getItem('CONCIERGE.timezone') || this.timezone || this.now.building_tz;
    }

    public get is_today() {
        return new DateTZ({
            date: toMiddayUTC(this.dateTz).ms,
            is_local_tz: false,
            building_tz: this.timezone_value,
        }).isSameDate(
            new DateTZ({
                date: Date.now(),
                is_local_tz: false,
                building_tz: this.timezone_value,
            }),
            'day'
        );
    }

    constructor(private _bookings: BookingStateService, private _org: OrganisationService) {
        super();
    }

    public ngOnInit(): void {
        // CONCIERGE.date: Get the localstorage date and timezone when changing pages.
        const date = +localStorage.getItem('CONCIERGE.date') || this.now.ms;
        
        /* istanbul ignore else */
        if (date && (date !== this.dateTz.ms || this.timezone_value !== this.dateTz.building_tz)) {
            // CONCIERGE.date Reapply the localstorage date and timezone when changing pages.
            this.timeout(
                'init',
                () => {
                    this.dateTz = new DateTZ({
                        date,
                        is_local_tz: false,
                        building_tz: this.timezone_value,
                    });
                    this.dateChangeTz.emit(this.dateTz);
                    this.updateDate();
                },
                10
            );
        }
        this.subscription(
            'events',
            this._bookings._noOfMeetings.subscribe((list) => {
                this.event_map = {};
                this.event_map = list[0];
            })
        );
    }

    public noOfCount(event) {
       // Disabling bellow until find more reliable way to call as it is been called multiple times on hover/click
        // this._bookings.getDailyMeetingCount();
        // event.stopPropogation();
    }

    /**
     * Change the selected date with the arrow controls
     *
     */
    public changeDate(value: number) {
        let date = new DateTZ({
            date: toMiddayUTC(this.dateTz).ms,
            is_local_tz: false,
            building_tz: this.timezone_value,
        });
        date = date.addValue({ days: value });
        this.dateTz = date;
        this.dateChangeTz.emit(this.dateTz);
        localStorage.setItem('CONCIERGE.date', `${this.dateTz.ms}`);
        localStorage.setItem('CONCIERGE.timezone', `${this.timezone_value}`);
        this.updateDate();
    }

    /**
     * Change the selected date with the calendar widget
     *
     */
    public applyDatePickerValue(dateInputTz: DateTZ) {
        this.dateTz = new DateTZ({
            date: toMiddayUTC(dateInputTz).ms,
            is_local_tz: false,
            building_tz: this.timezone_value,
        });
        this.dateChangeTz.emit(this.dateTz);
        localStorage.setItem('CONCIERGE.date', `${this.dateTz.ms}`);
        localStorage.setItem('CONCIERGE.timezone', `${this.timezone_value}`);
        this.updateDate();
        this.closeDatePicker();
    }

    public updateDate() {
        this.timeout('update_date', () => this._bookings.setDate(this.dateTz));
    }

    /** Reset date to the current date */
    public resetDate() {
        this.dateTz = new DateTZ({
            date: toMiddayUTC(this.now).ms,
            is_local_tz: false,
            building_tz: this.timezone_value,
        });
        this.dateChangeTz.emit(this.dateTz);
        localStorage.setItem('CONCIERGE.date', `${this.dateTz.ms}`);
        localStorage.setItem('CONCIERGE.timezone', `${this.timezone_value}`);
        this.updateDate();
    }

    public closeDatePicker() {
        this.updateDate();
    }

    /** New Item button click */
    public newItem(): void {
        this.event.emit('new_item');
    }
}
