
import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';
import { BaseAPIService } from '../base.service';
import { CateringItem } from './catering-item.class';
import { SettingsService } from '../settings.service';
import { ServiceManager } from '../service-manager.class';

@Injectable({
    providedIn: 'root'
})
export class CateringItemsService extends BaseAPIService<CateringItem> {
    constructor(protected _composer: ComposerService,
                settingsService: SettingsService) {
        super(_composer, settingsService);
        ServiceManager.setService(CateringItem, this);
        this._name = 'Catering Menu';
        this._api_route = 'catering/item';
    }

    public query(): never {
        throw Error('No index endpoint for catering items. Use menu service.');
    }

    public show(): never {
        throw Error('No show endpoint for catering items. Use menu service.');
    }

    public processItem(raw_item: any) {
        return new CateringItem(raw_item);
    }

    public format(item: CateringItem) {
        return item instanceof CateringItem ? item.toJSON() : item;
    }
}
