import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';

import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { CateringItem } from '@mckinsey-converge/data-common';
import { CateringCategory } from '@mckinsey-converge/data-common';
import { DialogEvent, Identity } from '@mckinsey-converge/base';
import { CateringItemsService } from '@mckinsey-converge/data-common';
import {
    CONFIRM_METADATA, ConfirmModalComponent,
} from '@mckinsey-converge/legacy-overlays';
import { mergeCateringItemWithFormData } from '@mckinsey-converge/data-common';

export interface CateringItemModalData {
    item: CateringItem;
    parent: CateringCategory;
}

@Component({
    selector: 'a-catering-item-modal',
    templateUrl: './item-modal.component.html',
    styleUrls: ['./item-modal.component.scss'],
})
export class CateringItemModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form controls for editing category */
    public form: FormGroup;
    /** Display string for current loading state */
    public loading: string;
    /** Whether image is being uploaded */
    public uploading: boolean;
    /** List of available catering types */
    public readonly types: Identity[] = [
        { id: 'kitchen', name: 'Kitchen' },
        { id: 'pantry', name: 'Pantry' },
    ];

    /** Category being edit/created */
    public get item(): CateringItem {
        return this._data.item;
    }

    /** Parent category of the catergory being created/edited */
    public get parent(): CateringCategory {
        return this._data.parent;
    }

    constructor(
        private _service: ApplicationService,
        private _menu_items: CateringItemsService,
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<CateringItemModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: CateringItemModalData,
    ) {
        super();
    }

    public ngOnInit(): void {
        const price = this.item.price
        this.form = new FormGroup({
            name: new FormControl(this.item.name, [Validators.required]),
            description: new FormControl(this.item.description),
            catering_type: new FormControl(this.item.catering_type, [Validators.required]),
            supplier_cost: new FormControl(this.item.supplier_cost),
            unit_price: new FormControl(price),
            minimum_quantity: new FormControl(this.item.minimum_quantity),
            maximum_quantity: new FormControl(this.item.maximum_quantity),
            order_anytime: new FormControl(this.item.order_anytime),
            out_of_stock: new FormControl(this.item.out_of_stock),
            allergy: new FormControl(this.item.allergy),
            parent_categories: new FormControl([this._data.parent.id]),
        });
    }

    public delete() {
        const ref = this._dialog.open(ConfirmModalComponent, {
            ...CONFIRM_METADATA,
            data: {
                title: `Delete Item`,
                content: `Are you sure you want to delete this item?`,
                icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                action: 'Ok',
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                ref.componentInstance.loading = `Deleting item...`;
                let e = null;
                await this._menu_items.delete(this.item.id).catch((err) => e === err);
                ref.close();
                if (!e) {
                    this._service.set('DELETED.menu', this.item.id);
                    this._dialog_ref.close();
                }
            }
        });
    }

    public submit() {
        this.form.markAllAsTouched();
        /* istanbul ignore else */
        if (this.form.valid) {
            const data = mergeCateringItemWithFormData(this.item, this.form.value).toJSON();
            const request = this.item.id
                ? this._menu_items.update(this.item.id, data)
                : this._menu_items.add(data);
            request.then(
                (item) => {
                    this.event.emit({ reason: 'done', metadata: item });
                    this._dialog_ref.close();
                },
                (err) =>
                    this._service.notifyError(
                        `Error ${this.item.id ? 'updating' : 'creating'} item. Error: ${
                            err.message || err
                        }`,
                    ),
            );
        }
    }
}
