import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { first } from 'rxjs/operators';

import { Booking } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { SpacesService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ, convertTimezonedTimestampToLocalDateTz} from '@mckinsey-converge/date-tz';


@Component({
    selector: 'visitor-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class VisitorTimelineComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    @Input() public dateTz: DateTZ = DateNow(new Date());
    /** Filter string */
    @Input() public search: string;
    /** Whether all bookings should be shown */
    @Input() public show_all: boolean;
    /** List of visitor bookings for the current date */
    public bookings: Booking[] = [];
    /** List of filtered bookings */
    public filtered_bookings: Booking[] = [];
    /** Whether visitor list is loading */
    public loading: boolean;
    /** List of items from an API search */
    public search_results$: Observable<Booking[]>;
    /** Subject holding the value of the search */
    public readonly search$ = new Subject<string>();

    public get nowTz(){
        return DateNow(new Date())
    }

    constructor(
        private _org: OrganisationService,
        private _spaces: SpacesService,
        private _bookings: BookingStateService
    ) {
        super();
    }

    ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._bookings.startPollingEveryFiveMin();
            this.subscription(
                'building',
                this._org
                    .listen('active_building')
                    .subscribe((_) => {
                        this.search$.next(`${this.dateTz.ms}|${_.id}`);
                        this._bookings.setZone(_.id);
                    })
            );
            this.subscription(
                'bookings',
                this._bookings.filtered.subscribe((list) => this.updateEvents(list))
            );
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.dateTz) {
            this.search$.next(`${this.dateTz.ms}`);
        }
        /* istanbul ignore else */
        if (changes.search || changes.show_all) {
            this.filtered_bookings = this.filter(this.search);
        }
    }

    /* istanbul ignore next */
    public trackByFn(index: number, booking: Booking) {
        return booking.id;
    }

    public resetPolling() {
        this.interval(
            'update_bookings',
            () => this.search$.next(`${this.dateTz.ms}|${this.nowTz.ms}`),
            30 * 1000
        );
    }

    /** Filter the visitor bookings */
    public filter(str: string = '') {
        const search = str.toLowerCase();
        return this.bookings
            .filter((booking) => {
                return booking.attendees.find(
                    (user) =>
                        user.name.toLowerCase().indexOf(search) >= 0 ||
                        user.email.toLowerCase().indexOf(search) >= 0
                );
            })
            .filter((bkn) => (this.show_all ? true : bkn.has_visitors));
    }

    private updateEvents(list: Booking[]) {
        const unsorted_bookings = list.filter(
            (bkn) =>
                !bkn.declined &&
                bkn.room.zones.includes(this._org.building.id)
        );
        const bookings = unsorted_bookings.sort((a, b) => {
            if (a.date === b.date) {
                if (a.creator.name === b.creator.name) {
                    return a.space.name > b.space.name ? 1 : -1;
                }
                return a.creator.name > b.creator.name ? 1 : -1;
            }
            return a.date > b.date ? 1 : -1;
        })
        this.bookings = bookings;
        this.filtered_bookings = this.filter(this.search);
    }
}
