import { Directive, HostListener, HostBinding, Output, EventEmitter, Input } from '@angular/core';
import { RequiredFileType } from './room-admin-utility';

@Directive({
  selector: '[fileDragDrop]'
})

export class FileDragNDropDirective {
  //@Input() private allowed_extensions : Array<string> = ['png', 'jpg', 'bmp'];
  @Output() private filesChangeEmiter : EventEmitter<any[]> = new EventEmitter();
  //@Output() private filesInvalidEmiter : EventEmitter<File[]> = new EventEmitter();
  @HostBinding('style.background') private background = ''; //'#eee';
  @HostBinding('style.border') private borderStyle = '2px solid'; //'2px dashed';
  @HostBinding('style.border-color') private borderColor = '#E6E6E6';
  @HostBinding('style.border-radius') private borderRadius = '1px';

  constructor() { }

  @HostListener('dragover', ['$event']) public onDragOver(evt){
    evt.preventDefault();
    evt.stopPropagation();
    this.background = 'lightgray';
    this.borderColor = 'cadetblue';
    this.borderStyle = '1px dashed';
  }

  @HostListener('dragleave', ['$event']) public onDragLeave(evt){
    evt.preventDefault();
    evt.stopPropagation();
    this.background = '#eee';
    this.borderColor = '#696D7D';
    this.borderStyle = '1px dashed';
  }

  @HostListener('drop', ['$event']) public onDrop(evt){
    evt.preventDefault();
    evt.stopPropagation();
    const ext = RequiredFileType.split(',');
    this.background = '#eee';
    this.borderColor = '#696D7D';
    this.borderStyle = '1px dashed';
    // debugger;
    let files = [...new Set(evt.dataTransfer.files)].filter((f:File) => ext.includes(f.type));  //evt.dataTransfer.files;
    // let valid_files : Array<File> = files;
    this.filesChangeEmiter.emit(files);
  }
}