import {
    Component,
    Input,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import { MatSelect } from '@angular/material/select';
import { SelectOption } from '../options/select-option';

@Component({
    selector: 'mck-legend-dropdown',
    template: `
        <div class="mck-column mck-form-input legend-container">
            <mat-form-field
                appearance="outline"
                class="mck-center-suffix mck-select-dropdown-button">
                <mat-select
                    class="legend-select"
                    #select
                    panelClass="mck-select-panel select-panel-position"
                    disableOptionCentering>
                    <mat-option
                        [disabled]="true"
                        *ngFor="let option of options"
                        [value]="option.value">
                        <mck-select-option
                            [ignoreDisabled]="true"
                            [option]="option"
                            [selected]="false">
                            <div class="legend-color-{{option.value}}"></div>
                        </mck-select-option>
                    </mat-option>
                </mat-select>          
                <span class="legend-placeholder">{{ buildPlaceholder() }}</span>
                <mck-spacer
                    size="1px"
                    [mode]="'vertical'"
                    spacerClass="legend-divider"></mck-spacer>
                <div class="mck-column chevron-container">
                    <mck-chevron-icon [up]="isOpen" matSuffix fillClass="mck-icon-fill-black"></mck-chevron-icon>
                </div>              
            </mat-form-field>
        </div>
    `,
    styleUrls: ['./legend-dropdown.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class LegendDropdownComponent {

    @Input()
    public placeholder: string;

    @Input()
    public options: SelectOption<any>[] = [];

    @Input()
    public placeholderOpen?: string

    /** Select field for selecting the value */
    @ViewChild('select') private select_field: MatSelect;

    public get isOpen() {
        return this.select_field?.panelOpen ?? false;
    }

    public buildPlaceholder(){
        return this.isOpen && this.placeholderOpen ? this.placeholderOpen : this.placeholder
    }
}
