import { FormGroup } from "@angular/forms";
import { HashMap } from "@mckinsey-converge/base"
import { DateNow, DateTZ } from "@mckinsey-converge/date-tz"

export enum RecurrencePeriod {
    LIST = 'list',
    DAILY = 'daily',
    WEEKLY = 'weekly',
    //RELATIVE_MONTHLY = 'relativeMonthly', for the moment we will only support absoluteMonthly
    ABSOLUTE_MONTHLY = 'monthly'
}

export enum DaysOfWeek {
    SUNDAY = 'sunday',
    MONDAY = 'monday',
    TUESDAY = 'tuesday',
    WEDNESDAY = 'wednesday',
    THURSDAY = 'thursday',
    FRIDAY = 'friday',
    SATURDAY = 'saturday'

}

export enum SeriesAction {
    EDIT = 'edit',
    CLONE = 'clone'
}

export enum BookingAction {
    CLONE = 'booking_clone',
    EDIT = 'booking_edit'
}

export interface WeekDays {
    id: string;
    active: boolean;
    day_index: number;
    full_name: string;
}

export interface RepeatPeriod {
    id: RecurrencePeriod;
    label: string
}

export enum RepeatsOn {
    PATTERN = 'pattern',
    DATE = 'date'
}

export interface RecurrenceType {
    type: RepeatsOn;
    label: string;
}

export interface RecurrenceDetails {
    period: RecurrencePeriod,
    end: DateTZ,
    list: Array<DateTZ>
    interval: number
}

export interface RecurrenceRecurringDetails extends RecurrenceDetails {
    start: number
}

export const recurrencePeriodToDurationType = (period: RecurrencePeriod) =>{
    switch (period) {
        case RecurrencePeriod.DAILY:
            return 'days'
        case RecurrencePeriod.WEEKLY:
            return 'weeks' 
        case RecurrencePeriod.ABSOLUTE_MONTHLY:
            return 'months' 
        default:
            return 'days'
    }
}

/**
 * Generate weekdays from monday to saturday
 */
export const generateWeekDays = (date: number, params: { building_tz?: string } = {}): Array<WeekDays>=> {
    const { building_tz } = params;
    let start_week = DateNow(new Date()).startOfValue('week').addValue({days : 1}); //set monday as start of week
    const week_days: Array<WeekDays> = []
    for (let i = 1; i <= 7; i++) {
        const activeDate = new DateTZ({date, building_tz, is_local_tz: false })
        const active = activeDate.dateWeekday === i
        const full_name = start_week.formatDate('EEEE')
        const day_index = i === 7 ? 0 : i //Sunday must be indexed as day 0
        week_days.push({id: full_name.substring(0, 1), active, day_index, full_name: full_name.toLocaleLowerCase()});
        start_week = start_week.addValue({ days : 1 });
    }
    return week_days
}

export const getFirstDateFromList = (list: Array<DateTZ>) => {
    return list.reduce((a, b) => { return a.isBeforeDate(b) ? a : b; });
}

export const getLastDateFromList = (list: Array<DateTZ>) => {
    return list.reduce((a, b) => { return a.isAfterDate(b) ? a : b; });
}

export const recurrenceDetails = (recurr: RecurrenceDetails): string => {
    const end = getLastDateFromList(recurr.list).formatDate('ccc dd MMM yyyy')
    const getPeriod = (period: string) => {
        if(recurr.interval === 1){
            return `Occurs Every ${period} until ${end}`
        }else{
            return `Occurs Every ${recurr.interval} ${period}s until ${end}`
        }
    } 
    
    switch (recurr.period) {
        case RecurrencePeriod.DAILY:
            return getPeriod('Day')
        case RecurrencePeriod.WEEKLY:
            return getPeriod('Week')
        case RecurrencePeriod.ABSOLUTE_MONTHLY:
            return getPeriod('Month')
        case RecurrencePeriod.LIST:
            return `Occurs on Specific Dates until ${getLastDateFromList(recurr.list).formatDate('ccc dd MMM yyyy')}`
        default:
            return ''
    }
}

export const recurrenceRecurringDetails = (recurr: RecurrenceRecurringDetails): string => {
    const end = recurr.end
    const start = new DateTZ({ date: recurr.start })

    const getPeriod = (period: string) => {
        if(recurr.interval === 1){
            return `occurs every ${period} effective ${start.formatDate('dd MMM yyyy')} until ${end.formatDate('dd MMM yyyy')}`
        }else{
            return `occurs every ${recurr.interval} ${period}s effective ${start.formatDate('dd MMM yyyy')} until ${end.formatDate('dd MMM yyyy')}`
        }
    }

    switch (recurr.period) {
        case RecurrencePeriod.DAILY:
            return getPeriod('Day')
        case RecurrencePeriod.WEEKLY:
            return getPeriod('Week')
        case RecurrencePeriod.ABSOLUTE_MONTHLY:
            return getPeriod('Month')
        case RecurrencePeriod.LIST:
            return `occurs on Specific Dates`
        default:
            return ''
    }
}

export const handleRecurrenceFields = (data: HashMap) => {
    if(data.is_recurrent){
        if(data.recurrence_period !== RecurrencePeriod.LIST){
            delete data.recurrence_starts
            delete data.recurrence_endTz
            if(data.recurrence_period !== RecurrencePeriod.WEEKLY){
                delete data.recurrence_days
            }
        } else if(data.recurrence_period){
            data.recurrence_list = [...(data.recurrence_starts || []).map((date: DateTZ) => date.startOfValue('minute').seconds)]
            delete data.recurrence_interval;
            delete data.recurrence_endTz;
            delete data.recurrence_count;
            delete data.recurrence_starts
            delete data.recurrence_days
        }
        delete data.is_recurrent
    }else{
        delete data.recurrence_period;
        delete data.recurrence_interval;
        delete data.recurrence_endTz;
        delete data.recurrence_count;
        delete data.recurrence_starts
        delete data.is_recurrent
        delete data.recurrence_exceptions
        delete data.recurrence_days
    }

    return data
}

/** Removes expired occurrences and updates start and end dates when cloning a series */
export const removeExpiredOcurrences = (form: FormGroup) =>{
    if(form.controls.action?.value === SeriesAction.CLONE ){
        const tz = form.controls.space_list.value[0].timezone
        const date_now = new DateTZ({ is_local_tz: false, building_tz: tz });
        const occurrences = form.controls.recurrence_starts.value.filter((date: DateTZ) =>
            date_now.isBeforeDate(date, 'day') || date_now.isSameDate(date, 'day')
        )
        form.controls.recurrence_starts.setValue(occurrences)
        const { start, end } = setStartAndEndDateFromArray(occurrences, tz)
        form.controls.dateTz.setValue(start)
        form.controls.recurrence_endTz.setValue(end)
    }
}

/** 
 * Returns starts and end dates from an array of dates in milliseconds 
 **/
 export const setStartAndEndDateFromArray = (dates: Array<DateTZ>, tz: string): {start: DateTZ, end: DateTZ} =>{
    if(dates.length !== 0){
        return {
            start: dates.sort((d1, d2) => d1.ms - d2.ms)[0] ,
            end: dates.sort((d1, d2) => d2.ms - d1.ms)[0]
        }
    }
    //if the dates array is empty set the start and end dates as the current and next day
    const now = DateNow(new Date()).toZone(tz)
    return {start: now, end: now.addValue({days: 1})}
}

export const setRecurrenceStartsTime = (form: FormGroup) => {
    const { recurrence_starts, dateTz } = form.value
    const list = (recurrence_starts || []).map((date: DateTZ) => date.setValue({hour: dateTz.dateHour, minute: dateTz.minutes}))
    form.controls.recurrence_starts.setValue(list)
}