import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class ResizeService {
  private mainCanvas: HTMLCanvasElement;

  private isCanvasSupported(): boolean {
    const canvas = document.createElement('canvas');
    return !!(canvas.getContext && canvas.getContext('2d'));
  }

  /**
   * Create image on the fly using Image class 
   * @param src 
   * @param crossOrigin 
   * @returns image
   */
  async createImage(
    src: string,
    crossOrigin?: string
  ): Promise<HTMLImageElement> {
    return new Promise<HTMLImageElement>((resolve, reject) => {
      const img = new Image();
      if (crossOrigin) {
        img.crossOrigin = crossOrigin;
      }

      img.onload = () => {
        resolve(img);
      };

      img.onerror = (err) => {
        reject(err);
      };

      img.src = src;
    });
  }

  /**
   * Resize canvas - create canvas on the fly draw image with given diamension
   * @param cnv 
   * @param width 
   * @param height 
   * @returns temp canvas instance
   */
  resizeCanvas(
    cnv: HTMLCanvasElement,
    width: number,
    height: number
  ): HTMLCanvasElement {
    if (!width || !height) {
      return cnv;
    }

    const tmpCanvas = document.createElement('canvas');
    tmpCanvas.width = width;
    tmpCanvas.height = height;
    const cnx = tmpCanvas.getContext('2d');
    cnx.drawImage(cnv, 0, 0, tmpCanvas.width, tmpCanvas.height);
    return tmpCanvas;
  }

  /**
   * Resize image 
   * @param src <base64Img or blob>
   * @param options <e.g. width, height, sizeScale, step, crossOrigin>
   * @returns 
   */
  async resizeImage(src: string, options: any): Promise<string> {
    if (!this.isCanvasSupported()) {
      throw new Error('Canvas is not supported on your browser');
    }
    if (!options || !src) {
      throw new Error('Missing argument when calling resizeImage function');
    }
    //Config options
    options = {
      height: options.height
        ? options.height
        : options.width
        ? null
        : options.size
        ? null
        : 1024,
      width: options.width
        ? options.width
        : options.height
        ? null
        : options.size
        ? null
        : 1024,
      size: options.size ? options.size : 500,
      sizeScale: options.sizeScale ? options.sizeScale : 'ko',
      step: options.step ? options.step : 3,
      outputFormat: options.outputFormat ? options.outputFormat : 'image/jpeg',
      crossOrigin: options.crossOrigin ? options.crossOrigin : null,
    };

    try {
      const img = await this.createImage(src, options.crossOrigin); // read image first by given base64 or blob image

      if (options.height || options.width) {
        // resize image as per height and width
        return this.resizeImageWidthHeight(
          img,
          options.width,
          options.height,
          options.step,
          options.outputFormat
        );
      } else if (options.size) {
        if (typeof options.sizeScale === 'string') {
          switch (options.sizeScale.toLowerCase()) {
            case 'ko':
              options.size *= 1024;
              break;
            case 'mo':
              options.size *= 1024 * 1024;
              break;
            case 'go':
              options.size *= 1024 * 1024 * 1024;
              break;
          }
        }
        return this.resizeImageBySize(img, options.size, options.outputFormat); //resize by size in diamension ration
      } else {
        throw new Error('Missing option to resize the image');
      }
    } catch (err) {
      throw new Error(err);
    }
  }

  /**
   * ResizeImageWidthHeight - Create canvas - draw image if given height and with OR use image original diamensions
   * and draw image data url
   * @param image 
   * @param width 
   * @param height 
   * @param step 
   * @param outputFormat 
   * @returns 
   */
  resizeImageWidthHeight(
    image: HTMLImageElement,
    width: number,
    height: number,
    step: number,
    outputFormat: string
  ): string {
    if (!image) {
      return null;
    }
    if (!outputFormat) {
      outputFormat = 'image/jpeg';
    }

    this.mainCanvas = document.createElement('canvas');

    if (!width && !height) {
      width = image.width;
      height = image.height;
    } else if (!width && height) {
      width = (height / image.height) * image.width;
    } else if (width && !height) {
      height = (width / image.width) * image.height;
    }

    const pixelStepWidth = image.width === width || !step ? 0 : (image.width - width) / step;
    const pixelStepHeight = image.height === height || !step ? 0 : (image.height - height) / step;
    this.mainCanvas.width = image.width;
    this.mainCanvas.height = image.height;

    this.mainCanvas
      .getContext('2d')
      .drawImage(image, 0, 0, this.mainCanvas.width, this.mainCanvas.height);
    for (let i = 1; i < step; i++) {
      const newWidth = image.width - pixelStepWidth * i;
      const newHeight = image.height - pixelStepHeight * i;
      this.mainCanvas = this.resizeCanvas(this.mainCanvas, newWidth, newHeight);
    }
    this.mainCanvas = this.resizeCanvas(this.mainCanvas, width, height);

    return this.mainCanvas.toDataURL(outputFormat);
  }

  /**
   * resizeImageBySize - resize image by given size in ration 
   *  
   * @param image 
   * @param targetSize 
   * @param outputFormat 
   * @returns 
   */
  resizeImageBySize(
    image: HTMLImageElement,
    targetSize: number,
    outputFormat: string
  ): string {
    if (!image) {
      return null;
    }
    if (!outputFormat) {
      outputFormat = 'image/jpeg';
    }

    this.mainCanvas = document.createElement('canvas');
    this.mainCanvas.width = image.width;
    this.mainCanvas.height = image.height;
    this.mainCanvas
      .getContext('2d')
      .drawImage(image, 0, 0, this.mainCanvas.width, this.mainCanvas.height);

    let tmpResult = this.mainCanvas.toDataURL(outputFormat);
    let result = tmpResult;

    const sizeOfTheImage = this.calulateImageSize(tmpResult, outputFormat);
    let divideStrategy = Math.max(
      1,
      Math.min(sizeOfTheImage / targetSize, 200)
    );

    let iteratorLimit = 20;
    while (sizeOfTheImage > targetSize && iteratorLimit !== 0) {
      iteratorLimit--;

      const newImageSize = {
        width: this.mainCanvas.width / divideStrategy,
        height: this.mainCanvas.height / divideStrategy,
      };
      const canvas = this.resizeCanvas(
        this.mainCanvas,
        newImageSize.width,
        newImageSize.height
      );

      tmpResult = canvas.toDataURL(outputFormat);
      const sizeOfTheImageTmp = this.calulateImageSize(tmpResult, outputFormat);

      if (sizeOfTheImageTmp / targetSize < 0.5 || sizeOfTheImageTmp === 0) {
        divideStrategy = divideStrategy / 2;
        if (divideStrategy < 1) {
          iteratorLimit = 0;
        }
      } else {
        this.mainCanvas = canvas;
        result = tmpResult;
      }

      this.mainCanvas = canvas;
    }

    return result;
  }

  calulateImageSize(imgString: string, outputFormat: string): number {
    switch (outputFormat) {
      case 'image/jpeg':
        outputFormat = 'image/jpg';
        break;
      default:
        outputFormat = 'image/jpg';
        break;
    }
    return Math.max(
      0,
      Math.round(
         ((imgString.length - ('data:' + outputFormat + ';base64,').length) *3) / 4  
        )
    );
  }
}
