import {
    Component,
    Directive,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Optional,
    Output,
    Self,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import {
    ControlValueAccessor,
    FormControl,
    NgControl
} from '@angular/forms';
import { SelectOption } from '../types';
import {
    BehaviorSubject,
    combineLatest,
    from,
    Subject
} from 'rxjs';
import {
    COMMA,
    ENTER
} from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material/chips';
import {
    distinctUntilChanged,
    map,
    take
} from 'rxjs/operators';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { BaseDirective } from '@mckinsey-converge/base';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';

@Component({
    selector: 'mck-autocomplete-chip-select-item',
    template: `
        <div class="mck-column mck-form-input" [ngClass]="mobileFullScreen ? 'mobile-select' : ''">
            <button *ngIf="mobileFullScreen" name="CloseButton" class="close-button" mat-icon-button (click)="loseFocus()">
                <div class="close-image"></div>
            </button>
            <mck-label [required]="required">{{label}}</mck-label>
            <mat-form-field
                appearance="outline"
                floatLabel="never"
                [ngClass]="showError ? 'mck-error' : ''"
                matAutocompleteOrigin
                #origin="matAutocompleteOrigin">
                <div class="mck-row input-container">
                    <input
                        matInput
                        #componentInput="matInput"
                        #trigger="matAutocompleteTrigger"
                        [style.width]="'100%'"
                        [style.margin]="'0'"
                        [formControl]="filterFormControl"
                        placeholder="{{placeHolder}}"
                        type="text"
                        (focusin)="onFocus(true)"
                        (input)="keyedInput($event.target.value)"
                        [matAutocomplete]="auto"
                        [matAutocompleteConnectedTo]="origin"
                        [matChipInputFor]="chipList"
                        (matChipInputTokenEnd)="add($event)"
                    >
                    <div class="mck-row">
                        <div class="suffix" matSuffix *ngIf="loading">
                            <mat-spinner diameter="16"></mat-spinner>
                        </div>
                        <mck-selected-chip-count
                            (close)="clearSelections()"
                            *ngIf="hasChips"
                            [count]="(chipOptions | async).length"></mck-selected-chip-count>
                        <div class="mck-column icon-container {{focused ? '' : 'not-clickable'}}"
                             (click)="onClickArrow()" *ngIf="!hideDropdownCarret">
                            <mck-chevron-icon [up]="focused"
                                              fillClass="mck-icon-fill-black"></mck-chevron-icon>
                        </div>
                    </div>
                </div>
                <mck-spacer size="4px" *ngIf="hasChips"></mck-spacer>
                <mat-chip-list #chipList>
                    <!-- empty chip useful for padding -->
                    <mat-chip *ngIf="focused && !hasChips"
                              [style.visibility]="'collapse'"></mat-chip>
                    <mat-chip *ngFor="let selected of (chipOptions | async)" removable
                              (removed)="removeOption(selected)" class="{{selected.meta?.color ? selected.meta?.color : ''}}">{{selected.shortDisplay}}
                        <mck-close-icon matChipRemove [fillClass]="iconFillClass(selected.meta?.color)">close</mck-close-icon>
                    </mat-chip>
                </mat-chip-list>
                <mck-spacer size="4px" *ngIf="hasChips"></mck-spacer>
                <mat-autocomplete
                    class="{{!!mobileFullScreen && 'mobile-select'}} mck-checkbox-panel"
                    #auto="matAutocomplete"
                    (opened)="onPanelFocusChange(true)"
                    (closed)="onPanelFocusChange(false)"
                    (optionSelected)="selectOption($event.option.value)"
                    multiple>
                    <div class='checkbox-container'>
                        <mat-option
                            [disabled]="!option.enabled"
                            class="mck-truncated {{(option.groupHeader || option.displayAsHeader) && 'mck-checkbox-group-header'}} {{option.groupChild && !option.displayAsHeader && 'mck-checkbox-group-child'}}"
                            (click)="$event.stopPropagation()"
                            *ngFor="let option of filteredAutocompleteOptions | async | groupHeaderPatch"
                            [value]="option.value">
                            <mat-checkbox
                                [ngClass]="!option.enabled && 'mck-checkbox-disabled'"
                                [disabled]="!option.enabled"
                                [style.display]="'block'"
                                [checked]="option.selected"
                                (change)="onChanged(option, $event)"
                                (click)="$event.stopPropagation()">
                                {{option.dropdownOverride || option.display}}
                            </mat-checkbox>
                        </mat-option>
                    </div>
                    <div class='checkbox-cta-container' *ngIf='displayCTA'>
                        <button
                            mat-stroked-button class="mck-secondary-button"
                            (click)="clearSelections()"
                            [disabled]="!enableCTAs"
                        >
                            Clear
                        </button>
                        <button
                            mat-button class="mck-primary-button"
                            (click)="onFocus(false)"
                            [disabled]="!enableCTAs"
                        >
                            Confirm
                        </button>
                    </div>
                </mat-autocomplete>
            </mat-form-field>
            <mck-error-message *ngIf="showError">{{errorMessage}}</mck-error-message>
        </div>
    `,
    styleUrls: ['./auto-complete-chip-select-item.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class AutoCompleteChipSelectItemComponent extends BaseDirective implements OnInit, OnChanges, ControlValueAccessor, OnDestroy {

    @ViewChild('trigger')
    trigger: MatAutocompleteTrigger;

    @ViewChild('componentInput')
    componentInput: HTMLInputElement;

    @Input()
    label: string;

    @Input()
    required?: boolean;

    @Input()
    feedbackInput?: boolean;

    @Input()
    loading?: boolean;

    @Input()
    placeHolder: string;

    @Input()
    options: Subject<SelectOption<any>[]>;

    @Input()
    selectedOptions: Subject<SelectOption<any>[]>;

    @Input()
    useCTAs?: boolean;

    @Output()
    optionSelected = new EventEmitter<SelectOption<any>>();

    @Output()
    feedbackOutput = new EventEmitter<any>();

    @Output()
    chipsCleared: EventEmitter<void> = new EventEmitter<void>();

    @Output()
    onClickConfirm: EventEmitter<void> = new EventEmitter<void>();

    /**
     * If true, only allow 1 group at a time to get selected. Show others as disabled.
     */
    @Input()
    selectOnlyOneGroup = false;

    /** Whether form field is disabled */
    @Input() public disabled: boolean;

    @Input() public localFilter: boolean = true;

    /**
     * If error exists, a message.
     */
    @Input()
    errorMessage?: string;

    @Input()
    hideDropdownCarret?: boolean;

    @Input()
    displayCTA?: boolean;

    private selectOnlyOneGroupChange = new BehaviorSubject(false);

    separatorKeysCodes = [ENTER, COMMA];

    public filterFormControl: FormControl = new FormControl();

    public focused = false;
    public mobileFullScreen = false;

    public autocompleteOptions = new BehaviorSubject<SelectOption<any>[]>([]);
    public filteredAutocompleteOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    /** List shown to user, filters children if a group header is added **/
    public chipOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    /** Form control on change handler */
    private _onChange?: (_: SelectOption<any>[]) => void;
    /** Form control on touch handler */
    private _onTouch?: (_: string) => void;

    private value = new BehaviorSubject<SelectOption<any>[]>([]);

    public enableCTAs: boolean = false

    public hasChips: boolean = false;

    constructor(@Self() @Optional() private control: NgControl) {
        super();
        if (this.control) {
            this.control.valueAccessor = this;
        }
    }

    public get invalid(): boolean {
        return this.control ? this.control.invalid : false;
    }

    public get showError(): boolean {
        if (!this.control) {
            return false;
        }

        const { dirty, touched } = this.control;

        return this.invalid ? (dirty && touched) : false;
    }

    public iconFillClass(color?: string): string {
        return color === 'external' ? 'mck-icon-fill-gray' : 'mck-icon-fill-white'
    }

    ngOnInit() {
        this.subscription('autoCompleteOptions', combineLatest([
            this.options,
            this.selectedOptions,
            this.selectOnlyOneGroupChange
        ])
            .pipe(
                map(([options, selectedOptions, selectOnlyOneGroup]) => {
                    this.hasChips = !!selectedOptions.length;
                    return options.map(option => {
                        const selected = !!selectedOptions.find(s => s.value === option.value);
                        let enabled = true;
                        if (selectOnlyOneGroup) {
                            // find first group item in selection
                            const selectedGroupHeader = selectedOptions.find(s => s.groupHeader || s.groupId === option.groupId);

                            // if no options chosen, enable it, otherwise only allow selections from other options.
                            enabled =
                                selectedOptions.length === 0 ||
                                (selectedGroupHeader ? selectedGroupHeader.groupId === option.groupId : false);
                            this.enableCTAs = selectedOptions.length !== 0
                        }

                        return {
                            ...option,
                            enabled,
                            selected
                        } as SelectOption<any>;
                    });
                })
            ).subscribe(next => {
                const options = next.sort((a, b) => {
                    if (a.groupId === b.groupId) {
                        return b.display > a.display ? -1 : 1;
                    }
                    return b.groupId > a.groupId ? -1 : 1;
                });
                this.autocompleteOptions.next(options);
                this.filteredAutocompleteOptions.next(this.getFilteredAutocompleteOptions(this.filterFormControl.value?.trim()));
            }));
        this.subscription('filterFormControlChanges',
            this.filterFormControl.valueChanges.subscribe(value => {
                if (this.localFilter && value.length) {
                    this.filteredAutocompleteOptions.next(this.getFilteredAutocompleteOptions(value));
                } else {
                    this.filteredAutocompleteOptions.next(this.getFilteredAutocompleteOptions());
                }
            }));
        this.subscription('chipOptions', this.selectedOptions
            .pipe(map(selectedOptions => {
                // find group headers and check if children are part of that group.
                // if part of group, filter them out as redundant.
                const groupHeaders = selectedOptions.filter(s => s.groupHeader) || [];
                return selectedOptions.filter(s => s.groupHeader || s.groupChild && !(groupHeaders.find(g => g.groupId === s.groupId)));
            }))
            .subscribe(next => {
                this.chipOptions.next(next);
            }));

        this.subscription('formData', this.selectedOptions
            .pipe(map(selected => {
                // group headers always contain children, so only pick out children for data
                return selected;
            }))
            .subscribe(next => {
                this.value.next(next);
            }));

        // tie value changes to change.
        this.subscription('valueChanges', this.value
            .pipe(distinctUntilChanged())
            .subscribe(next => {
                if (this._onChange) {
                    // rewrote this to avoid read only error
                    const values = [];
                    next.forEach((v, i) => {
                        const value = { ...v };
                        if (v.meta?.user) {
                            value.display = v.meta.user.name;
                        }
                        values.push(value);
                    });
                    this._onChange(values);
                }
            }));

        window.addEventListener('scroll', this.scrollEvent, true);
    }

    getFilteredAutocompleteOptions(searchString: string = '') {
        // Exact matches of the groupId or display value
        const exactMatches = this.autocompleteOptions.value.filter(o => {
            if (searchString && searchString.length) {
                return o.groupId.toLowerCase().indexOf(searchString.toLowerCase()) === 0 || o.display.toLowerCase().indexOf(searchString.toLowerCase()) === 0;
            }
            return o.groupId && o.display;
        });
        // Fuzzier search of the dropdownOverride field
        const moreOptions = this.autocompleteOptions.value.filter(o => {
            if (o?.dropdownOverride?.length && searchString) {
                const match = o.dropdownOverride?.toLowerCase().indexOf(searchString.toLowerCase()) > -1;
                // only return found locations that don't exist in exactMatches.
                return match && !exactMatches.find(e => {
                    return e.value === o.value
                });
            }
            return false;
        });

        // Merge the two result sets
        let joinedOptions = exactMatches.concat(moreOptions).sort((a, b) => {
            if (a.groupId === b.groupId) {
                return b.display > a.display ? -1 : 1;
            }
            return b.groupId > a.groupId ? -1 : 1;
        });

        // Create a 1D array of the groupId
        const groupIds = joinedOptions.map(o => o.groupId);
        // Get a deduped list of the group parents
        const parents = this.autocompleteOptions.value.filter(o => {
            return o?.groupHeader && groupIds.indexOf(o.groupId) !== -1;
        });

        // Remove the parents from the joinedOptions and merge with the reduced parents set, and then sort by groupId.
        // Resulting array sets the parents first in order so the widget renders properly.
        const filteredAutocompleteOptions = parents.concat(joinedOptions.filter(jo => !!jo.dropdownOverride)).sort((a, b) => {
            return b.groupId > a.groupId ? -1 : 1;
        });
        return filteredAutocompleteOptions;
    }

    loseFocus(): void {
        this.mobileFullScreen = false;
        this.focused = false;
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.selectOnlyOneGroup) {
            this.selectOnlyOneGroupChange.next(this.selectOnlyOneGroup);
        }
    }

    keyedInput(matInput: string) {
        if (this.feedbackInput) {
            this.feedbackOutput.emit(matInput);
        }
    }

    selectOption(value: string) {
        this.options.pipe(
            take(1),
            map((options: SelectOption<any>[]) => options.find(o => o.value === value))
        ).subscribe(next => {
            if (next) {
                this.optionSelected.emit(next);
            }
        });
    }

    removeOption(value: SelectOption<any>) {
        this.optionSelected.emit(value);
        if (this.useCTAs) {
            this.onClickConfirm.emit();
        }

    }

    clearSelections() {
        this.selectedOptions.next([]);
        this.chipsCleared.emit();
    }

    onClickArrow() {
        // only close events we allow arrow to close panel.
        if (this.focused) {
            this.onFocus(false);
        }
    }

    onPanelFocusChange(focusIn: boolean) {
        this.onFocus(focusIn, true);
    }

    onFocus(focusIn: boolean, fromPanel?: boolean) {
        if (!focusIn && this.useCTAs) {
            this.onClickConfirm.emit();
        }
        // Only for location input???
        if (this.localFilter && fromPanel && focusIn) {
            // open on focus, mobile needs a refocus
            this.mobileFullScreen = true;
            this.timeout('showDefaults', () => {
                // All results do not show as expected unless I focus again.
                // Something about the mobile styles.
                this.onFocus(true);
            }, 100)
        }

        if (this.feedbackInput && !fromPanel) {
            // clear input when input creates options
            this.componentInput.value = '';
            this.filterFormControl.setValue('');
            this.feedbackOutput.emit('');
        }

        if (this.focused !== focusIn) {
            this.focused = focusIn;
            // clear on close
            if (!focusIn) {
                this.componentInput.value = '';
                this.filterFormControl.setValue('');
            }
            if (!fromPanel) {
                if (focusIn) {
                    this.trigger.openPanel();
                } else {
                    this.trigger.closePanel();
                    // close the mobile full screen
                    this.loseFocus();
                }
            }
        }
    }

    onChanged(value: SelectOption<any>, event: MatCheckboxChange) {
        this.optionSelected.emit(value);
    }

    add(event: MatChipInputEvent) {
        // reset
        if (event.input) {
            event.input.value = '';
        }
        this.filterFormControl.setValue('');
    }

    registerOnChange(fn: any): void {
        this._onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this._onTouch = fn;
    }

    setDisabledState(isDisabled: boolean): void {
        // TODO: disabled not implemented yet.
        this.disabled = isDisabled;
    }

    writeValue(obj: SelectOption<any>[] | undefined): void {
        // update selected options when this value is written
        this.options?.pipe(
            take(1),
            map((options: SelectOption<any>[]) => {
                return options.filter(o => obj ? !!obj.find(spl => o.value === spl.value) : false );
            })
        ).subscribe(next => {
            this.selectedOptions.next(next);
        });
    }

    private scrollEvent = (event): void => {
        if (this.trigger === null) {
          return;
        }
        /**
         * IF - to confirm user not scrolling from autocomplete opened panel and close panel if scroll event triggers from outside of panel
         */
        if (this.trigger.panelOpen && event.target.closest('.mat-autocomplete-panel') === null && !event.srcElement.classList.contains('mat-form-field-autofill-control')) {
            this.onFocus(false);
            this.trigger['_element'].nativeElement.blur();
        }
      }
      public ngOnDestroy(): void {
        window.removeEventListener('scroll', this.scrollEvent, true);
      }
}


