import {
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    OnInit,
    Output,
    ViewChild
} from '@angular/core';
import {
    AbstractControl,
    FormControl,
    FormGroup,
    Validators
} from '@angular/forms';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import {
    OrganisationService,
    BookingNote,
    CateringOrder,
    Space,
    UsersService,
    CateringNotesService,
    Booking
} from '@mckinsey-converge/data-common';
import {
    DialogEvent,
    HashMap
} from '@mckinsey-converge/base';

import { DateNow } from '@mckinsey-converge/date-tz';


export interface CateringDetailsModalData {
    spaces: Space[];
    notes_field: AbstractControl;
    catering: AbstractControl;
    booking: Booking
}

@Component({
    selector: 'a-catering-details-modal',
    templateUrl: './catering-details-modal.component.html',
    styleUrls: ['./catering-details-modal.component.scss']
})
export class CateringDetailsModalComponent implements OnInit {
    /** Emitter for modal events */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Mapping of spaces to notes */
    public notes: HashMap<string> = {};
    /** Mapping of spaces to cost codes */
    public codes: HashMap<string> = {};
    /** Mapping of spaces to estimated head count */
    public head_counts: HashMap<string> = {};
    /** Mapping of spaces to form fields */
    public form: HashMap<FormGroup> = {};
    /** Map of spaces to whether they need a charge code */
    public needs_charge_code: HashMap<boolean> = {};
    /** Index of the active space */
    public active_space: number = 0;

    @ViewChild('content', { static: true }) private _content_el: ElementRef<HTMLElement>;

    /** List of selected spaces */
    public get space_list(): Space[] {
        return (this._data.spaces || []).filter((space) =>
            this._data.catering.value.find((order) => order.location_id === space.email)
        );
    }

    public get active_form(): FormGroup {
        return this.form[this.space_list[this.active_space].email];
    }

    public get invalid_charge_code(): boolean {
        return this.active_form.controls.code.errors?.pattern || this.active_form.controls.code.errors?.minlength;
    }

    /** Whether form fields are valid */
    public get is_valid(): boolean {
        return this.space_list.reduce(
            (valid, space) => valid && this.form[space.email].valid,
            true
        );
    }

    constructor(
        private _org: OrganisationService,
        private _users: UsersService,
        private _cateringNotesService: CateringNotesService,
        @Inject(MAT_DIALOG_DATA) private _data: CateringDetailsModalData
    ) {
    }

    public ngOnInit(): void {
        const note_list = this._data.notes_field.value.filter(
            (note: BookingNote) => note.type === 'catering'
        );
        this.notes = note_list.reduce((map, note) => {
            map[note.space] = note.message;
            return map;
        }, {});
        this.codes = this._data.catering.value.reduce((map, order: CateringOrder) => {
            map[order.location_id] = order.charge_code;
            return map;
        }, {});
        this.generateFormFields();
    }

    public submit(): void {
        this.space_list.forEach((space) => this.form[space.email].markAllAsTouched());
        /* istanbul ignore else */
        if (this.is_valid) {
            // Update notes
            const notes = this._data.notes_field.value.filter(
                (note: BookingNote) => note.type !== 'catering'
            );
            const new_notes = this.space_list.map(
                (space) =>
                    ({
                        author: this._data.booking.booked_by ? this._data.booking.booked_by['name'] : this._users.current.name,
                        type: 'catering',
                        message: this.form[space.email].controls.notes.value,
                        space: space.email,
                        date: DateNow(new Date()).ms
                    } as BookingNote)
            );
            this._cateringNotesService.setNotes(new_notes);
            this._data.notes_field.setValue(notes.concat(new_notes).filter((note) => note.message));
            // Save the notes
            const orders = this._data.catering.value.map(
                (order) =>
                    new CateringOrder({
                        ...order,
                        charge_code: this.form[order.location_id]
                            ? this.form[order.location_id].controls.code.value
                            : ''
                    })
            );

            this._data.catering.setValue(orders);
            // This should go to the next
            this.event.emit({ reason: 'done' });
        } else {
            this._scrollToBottom();
        }
    }

    /** Generate form fields for each space */
    private generateFormFields(): void {
        for (const space of this.space_list) {
            const building = this._org.buildings.find(
                bld => bld.id === space.level.building_id
            );
            
            this.needs_charge_code[space.email] = space?.filters?.charge_code || space?.settings?.charge_code || false;
            this.form[space.email] = new FormGroup({
                notes: new FormControl(this.notes[space.email] || ''),
                code: new FormControl(this.codes[space.email] || '',
                    this.needs_charge_code[space.email] ?
                        [Validators.pattern(/^[a-zA-Z0-9]{4,}$/), Validators.minLength(4),  Validators.required ] : [Validators.pattern(/^[a-zA-Z0-9]{4,}$/), Validators.minLength(4)])
            });
        }
    }

    /** Scroll the content container to the bottom */
    private _scrollToBottom(): void {
        this._content_el?.nativeElement.scrollTo({
            top: this._content_el.nativeElement.scrollHeight,
            left: 0,
            behavior: 'smooth'
        });
    }
}
