import {
    Directive,
    Input,
    Renderer2,
    SimpleChanges,
    OnChanges,
    ContentChildren,
    QueryList,
    AfterContentInit
} from '@angular/core';
import { CdkDropList, CdkDrag } from '@angular/cdk/drag-drop';
import { BaseDirective } from './base.directive';

export enum ScrollDirection {
    NONE,
    X,
    Y,
    BOTH
}

@Directive({
    selector: '[cdkDropList][scrollContainer]'
})
export class CdkDropListScrollContainer extends BaseDirective
    implements OnChanges, AfterContentInit {
    /** Direction of scroll to determine updating the position of the drop list */
    @Input() direction: ScrollDirection = ScrollDirection.X;
    /** Name of the scroll container for the list */
    @Input() scrollContainer: string;
    /** Scroll container element */
    public element: HTMLElement;
    /** Last scroll position */
    public last_scroll: { x: number; y: number } = { x: 0, y: 0 };

    /** Draggable children elements */
    @ContentChildren(CdkDrag) private items: QueryList<CdkDrag>;

    constructor(private _cdkDropList: CdkDropList, private _renderer: Renderer2) {
        super();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.scrollContainer && this.scrollContainer) {
            this.element = this._cdkDropList.element.nativeElement.closest(
                this.scrollContainer
            ) as HTMLElement;
        }
    }

    public ngAfterContentInit(): void {
        this.subscription(
            'drag_items',
            this.items.changes.subscribe((items: QueryList<CdkDrag>) => {
                const list = items.toArray();
                list.forEach((i, index) => {
                    this.subscription(
                        `list-item-${index}`,
                        this._renderer.listen(i.element.nativeElement, 'mousedown', () => {
                            this.subscription(
                                'item-dragged',
                                this._renderer.listen('window', 'mouseup', () => this.onDrop())
                            );
                            this.onDrag();
                        })
                    );
                    this.subscription(
                        `list-item-touch-${index}`,
                        this._renderer.listen(i.element.nativeElement, 'touchstart', () => {
                            this.subscription(
                                'item-dragged',
                                this._renderer.listen('window', 'touchend', () => this.onDrop())
                            );
                            this.onDrag();
                        })
                    );
                });
            })
        );
    }

    /** Start listing for scroll events on the container */
    public onDrag() {
        if (this.element) {
            this.subscription(
                'scroll',
                this._renderer.listen(this.element, 'scroll', () => this.updateListPosition())
            );
        }
    }

    /** Stop listening for scroll events on the container */
    public onDrop() {
        this.unsub('scroll');
    }

    /**
     * Forcefully update the position data of the drop list
     */
    private updateListPosition() {
        this.timeout(
            'update_positions',
            () => {
                const scroll = { x: this.element.scrollLeft, y: this.element.scrollTop };
                if (
                    ((this.direction === ScrollDirection.BOTH ||
                        this.direction === ScrollDirection.Y) &&
                        scroll.y !== this.last_scroll.y) ||
                    ((this.direction === ScrollDirection.BOTH ||
                        this.direction === ScrollDirection.X) &&
                        scroll.x !== this.last_scroll.x)
                ) {
                    (this._cdkDropList._dropListRef as any)._cacheOwnPosition();
                    (this._cdkDropList._dropListRef as any)._siblings.forEach(i =>
                        i.isReceiving() ? i._cacheOwnPosition() : null
                    );
                }
                this.last_scroll = scroll;
            },
            50
        );
    }
}
