import { HashMap } from '@mckinsey-converge/base';

import { Organisation } from '../organisation/organisation.class';
import {
    ServiceLike,
    ServiceManager
} from '../service-manager.class';
import { BuildingLevel } from '../organisation/level.class';

export class MapLocation {
  /** ID of the element on the associated map */
  public readonly id?: string;
  /** Name of the location */
  public readonly name?: string;
  /** X coordinate of the location */
  public readonly x?: number;
  /** Y coordinate of the location */
  public readonly y?: number;
  /** Level details for the location */
  public readonly level: BuildingLevel;
  /** Whether the position is fixed */
  public readonly fixed: boolean;
  /** Accuracy of the location when not fixed */
  public readonly confidence?: number;
  /** Whether location is at a desk */
  public readonly at_desk?: boolean;
  /** Whether location is set */
  public readonly empty: boolean;

  /** Service for managing model on the server */
  protected get _service(): ServiceLike {
    return ServiceManager.serviceFor(MapLocation);
  }

  /** Display string for the building and level of the location */
  public get display(): string {
    const service = ServiceManager.serviceFor(Organisation);
    if (!service) {
      return this.level.name;
    }
    const bld = service.buildings.find((bld) => bld.id === this.level.building_id);
    return bld ? `${bld.name}, ${this.level.name}` : this.level.name;
  }

  /** Whether location is in a different building from the active one */
  public get in_another_building(): boolean {
    const service = ServiceManager.serviceFor(Organisation);
    return service && service.building.id !== this.level.building_id;
  }

  constructor(raw_data: HashMap) {
    this.id = raw_data.id || raw_data.map_id || raw_data.desk_id;
    this.name = raw_data.name || '';
    this.x = raw_data.x
      ? Math.floor(this.normalise(raw_data.x, raw_data.x_max || 10000) * 10000)
      : null;
    this.y = raw_data.y
      ? Math.floor(this.normalise(raw_data.y, raw_data.x_max || 10000) * 10000)
      : null;
    const service = ServiceManager.serviceFor(Organisation);
    this.level =
      raw_data.level instanceof BuildingLevel
        ? raw_data.level
        : service
        ? service.levelWithID(raw_data.level)
        : new BuildingLevel(raw_data.level);
    this.fixed = this.x === null && this.y === null;
    this.confidence = Math.max(5, Math.min(15, raw_data.confidence || 0));
    this.at_desk = this.id && this.id.indexOf('area-') === 0;
    this.empty = !(this.name && this.level && (this.id || this.x || this.y));
  }

  /** Normalise the given value within the max */
  private normalise(value: number, max: number): number {
    return value / (max * 1.0);
  }
}
