import { BaseDataClass } from '../base-api.class';
import {
    HashMap,
} from '@mckinsey-converge/base';
import {
    Building,
    BuildingLevel,
    Organisation
} from '../organisation';
import { User } from '../users';
import { ServiceManager } from '../service-manager.class';
import { SettingsService } from '../settings.service';
import { SpaceRules } from '../bookings/booking.types';
import { rulesForSpace } from '../bookings/space.utilities';
import { DateTZ } from '@mckinsey-converge/date-tz';
import { DaysOfWeek, RecurrencePeriod } from '../recurrence/recurrence.utils';
import { DateTime } from 'luxon';

export interface ISpaceAvailabilityOptions {
    /** Start date and time of the availability block */
    dateTz?: DateTZ;
    /** Length of the availability block */
    duration: number;
    /**  */
    id?: string;
    /** List of spaces to look at the availability for */
    room_ids?: string;
    /** Whether the spaces looked at should be bookable */
    bookable?: boolean;
    /** List of spaces or zones to ignore */
    ignore?: string;
    /** List of zones to look at the availability for */
    zone_ids?: string | string[];
    /** Whether space bookings should not be returned */
    hide_bookings?: boolean;
    /** Min capacity on the room **/
    capacity?: number
    clear?: boolean;
    /** Length of the setup */
    setup?: number;
    /** Length of the breakdown */
    breakdown?: number;
    /** Hide declined bookings */
    hide_declined?: boolean;
    /** Max capacity on the room **/
    capacity_max?: number

    diff_capacity?: boolean

    /** Fields to include recurrence in the availability search */
    recurrence_period?: RecurrencePeriod;
    recurrence_interval?: number;
    recurrence_endTz?: DateTZ;
    recurrence_count?: number;
    recurrence_starts?: Array<DateTZ>
    is_recurrent?: boolean
    is_multiroom?: boolean
    recurrence_days?: Array<DaysOfWeek>
}

export interface SpaceBookingRuleOptions {
    dateTz: DateTZ;
    duration: number;
    host: User;
}

export const OPTION_DEFAULTS: ISpaceAvailabilityOptions = {
    duration: 60
};

export interface RecurrenceAvailability {
    /** Unix epoch in seconds */
    readonly date: number;
    /** Whetehr space is available at this time */
    readonly available: boolean;
}

/**
 * Room Settings Type
 *
 * Exmaple:
 * available: true
 * available_until: 1644344999
 * bookings: []
 * catering: false
 * charge_code: false
 * extra_features: "meeting_room boardroom"
 * internal_or_external: "internal"
 * map_id: "08.8.15"
 * natural_light: false
 * room_booking_screen: true
 * room_name: "8.15 Meeting"
 * vc: false
 * recurrence_availability: {
 *    date: "1642596300",
 *    available: false,
 * }
 */
export interface RoomSettings {
    // Local room name is required.
    room_name?: string
    // TBD if anything else should be required
    available?: boolean
    available_until?: number
    bookings?: HashMap[]
    catering?: boolean
    charge_code?: boolean
    extra_features?: string
    internal_or_external?: string
    map_id?: string
    natural_light?: boolean
    room_booking_screen?: boolean
    vc?: boolean
    recurrence_availability?: RecurrenceAvailability[]
    equipment_code?: boolean
    external?: boolean
}

/**
 * List of space features that come from settings.
 *
 * Im sure this list will grow. It is not the most comprehensive list.
 */
export enum SpaceFeatures {
    BoardRoom = 'boardroom',
    Catering = 'catering',
    CocktailRoom = 'cocktail',
    ConferencePhone = 'conference_phone',
    ConferenceRoom = 'conference',
    ElectronicWhiteboard = 'whiteboard',
    FlipChart = 'flipchart',
    Glassboard = 'glassboard',
    PartnerOffice = 'partner',
    PhoneBooth = 'phone_booth',
    TheatreRoom = 'theatre',
    TeamRoom = 'team_room',
    VideoConference = 'vc',
    NaturalLight = 'natural_light',
    WirelessContentSharing = 'wireless_content_sharing',
    WorkshopRoom = 'workshop',
    UShapeRoom = 'u_shape',
    Miscellaneous = 'miscellaneous'
}

export enum SpaceEquipments {
    conference_phone,
    whiteboard,
    vc,
    flipchart,
    glassboard,
    wireless_content_sharing
}
export enum SpaceStyles {
    boardroom,
    cocktail,
    conference,
    partner,
    theatre,
    team_room,
    // natural_light,
    workshop,
    u_shape,
    miscellaneous
}

const EMPTY_ARRAY = [];

export class Space extends BaseDataClass {
    /** Whether space can be booked by users */
    public readonly bookable: boolean;
    /** People capacity of the space */
    public readonly capacity: number;
    /** Index to force order when sorting multiple spaces */
    public readonly sort_priority: number;
    /** Settings has extensive info displayed in the app. */
    public readonly settings: RoomSettings;
    /** URL for the control interface of the space */
    public readonly support_url: string;
    /** Engine zones associated with the space */
    public readonly zones: readonly string[];
    /** Is the Application concierge */
    public is_concierge: boolean = false;
    /** To allow bookings with rooms for staff calendar view only */
    public allowStaffRoomWithBookings: boolean = false;

    /** Simple name == local name being using in seeting.room_name
		 *
		*/
    public simple_name: string;

    /** Room style set of all room style one room has
    * ["boardroom", "cocktail", "miscellaneous", "natural_light", "theatre", "u-shape", "workshop"]   */
    public room_style: string[] = [];

    /** Equipment set of all room equipment one room has
    * ["conference_phone", "whiteboard", "vc", "flipchart", "glassboard", "whiteboard", "wireless_content_sharing"] */
    public equipment: string[] = [];

    /** Filters has charge code settings for booking and catering */
    public filters: RoomSettings;

    /** room_type subset of internal or external rooms */
    public room_type: string[] | string;
    /**
     * Features used in identifying room types.
     *
     * Both features and extra_features are space delimited strings.
     *
     * There is a getter featuresArray that returns the
     * combination of the two as an array.
     */
    public readonly features: string;
    public readonly extra_features: string;
    public map_id: string;


    /** Service for managing spaces */
    protected get _service() {
        return ServiceManager.serviceFor(Building);
    }

    /** Return the rooms' building */
    public get building() {
        return this._service?.buildings.find((bld) => {
            return this.zones.includes(bld.id);
        });
    }

    /** Return building timezone */
    public get timezone() {
        return this.building?.timezone;
    }

    constructor(raw_data: HashMap = {}) {
        super(raw_data);

        // Needed to check if the current app is Concierge or Staff
        const settingsService = ServiceManager.serviceFor(SettingsService) as unknown as SettingsService;
        this.is_concierge = !!settingsService?.concierge;
        this.allowStaffRoomWithBookings = settingsService?.bypass_bookings || false;

        const defaultGlobalName = raw_data.name || raw_data.email || 'Meeting Room';
        const defaultSettings = {
            room_name: defaultGlobalName
        }
        /**
         * Duplicate of base-api.class just for readability
         * .name is the full, mckinsey standardised name of the room.
         * You can think of it was what people would call the room when
         * considering every office and room globally.
         *
         * this.name = raw_data.name;
         */
        /**
         * Settings has extensive info displayed in the app.
         * setting.room_name is always used when displaying the room name in the applications
         * setting.room_name is the more office-specific room name, you can think of this as
         * the one people in that office would refer to the room as.
         *
         */
        this.settings = raw_data.settings || defaultSettings;
        this.filters = raw_data?.filters;
        // Room fields
        this.bookable = raw_data.bookable || false;
        this.capacity = raw_data.capacity || 0;
        this.support_url = raw_data.support_url;
        this.zones = raw_data.zones instanceof Array ? raw_data.zones : [];
        // Feature fields
        this.features = raw_data.features || '';
        this.extra_features = raw_data.settings?.extra_features || '';

		this.room_type = raw_data?.room_type;
        this.simple_name = raw_data?.simple_name;
        this.map_id = raw_data?.map_id || raw_data.settings?.map_id || '';
        this.room_style = raw_data?.room_style || [];
        this.equipment = raw_data?.equipment || [];

    }

    /**
     * Return the global room name
     * IE LDN-4-408-06
     */
    public get global_name(): string {
        return this.name || ''
    }

    /**
     * Return the local room name
     * i.e. 4.08
     * TO DO: ROOM-DATA-STRUCTURE-REFACTOR - stop using/remove seetings.room_name
     */
    public get local_name(): string {
        return this.simple_name || this.name || ''; // this.settings?.room_name
    }

    public get street_and_city(): string {
        return this.building ? `${this.building.address}, ${this.building.city}` : '';
    }

    /**
     * Return the map_id from settings
     * TO DO: ROOM-DATA-STRUCTURE-REFACTOR - stop using/remove seetings.map_id and use map_id from space modal( after testing over prod)
     */
    // public get map_id(): string {
    //     return this?.map_id || this.settings?.map_id || '';
    // }

    /** Internal / External status */
    public get internal_or_external(): string {
        const  external  = this.filters?.external;
        return external ? 'external' : 'internal'; // ( external ? 'external' : this.settings?.internal_or_external ) || 'internal';
    }

    /**
     * Works in conjunction with available_until.
     *
     * If a room is bookable that only means you can make a booking but not when.
     * Available indicates that is is available to be booked durring the queried times.
     */
    public get available(): boolean {
        return this.settings?.available ?? false;
    }

    public get currently_in_use(): boolean {
        const nowMs = new DateTZ({ date: DateTime.now(), is_local_tz: false, building_tz: this.timezone }).ms;
        const runningBookings = this.settings?.bookings?.filter( bks => {
            const startEpoch = new DateTZ({ date: bks.start_epoch * 1000, is_local_tz: false, building_tz: bks.timezone}).ms;
            const endEpoch = new DateTZ({ date: bks.end_epoch * 1000, is_local_tz: false, building_tz: bks.timezone}).ms;
             return startEpoch <= nowMs && endEpoch >= nowMs;
        })
        // console.log(raw_data.settings.room_name+' : '+ nowMs.ms);
        // console.log('IS IN USER CURRENTLY : ', isinUsernow);
        return !!runningBookings.length;
    }

    /** Last returned availability time */
    public get available_until(): number {
        return this.settings?.available_until;
    }

    /** Bookings associated with the space */
    public get bookings(): HashMap[] {
        // We should need to care if a room as bookings in Staff
        if (!this.is_concierge && !this.allowStaffRoomWithBookings) {
            return EMPTY_ARRAY;
        }

        if (this.settings?.bookings?.length) {
            /**
             * When we're dealing with Concierge the API data arriving is rooms, and those rooms contain booking,
             * but those bookings don't contain a room since the room is the root.
             * In order for Concierge to filter bookings by room we need to reattach this room
             * to each of the bookings, with it's own bookings.
             *
             * It's loopy and kooky but correcting for this pattern is beyond the scope of this refactor.
             */
            const spaceWithBookings =  new Space(this.toJSON());
            return this.settings?.bookings.map(b => {
                b.room = spaceWithBookings;
                return b;
            });
        }
        return EMPTY_ARRAY;
    }

    /**
		 * Whether space has catering
     * TO DO: ROOM-DATA-STRUCTURE-REFACTOR - stop using/remove seetings.catering and use filters?.catering instead
		 */
    public get has_catering(): boolean {
        return this.filters?.catering || this.settings?.catering || false;
    }

     /** Returns an array of the space delimited feature and extra_feature strings.
     * TO DO: ROOM-DATA-STRUCTURE-REFACTOR - stop using/remove features, extra_features
     */
    public get featuresArray(): (SpaceFeatures | string)[] {
        // (this.simple_name === "Entresol M-06" ) && console.log('raw_data : ', this)
        const roomType = Array.isArray(this.room_type) ?  this.room_type : [this.room_type]
        const features = [
                ...this.features.split(' '),
                ...this.extra_features?.split(' '),

                ...roomType, // required to work with staff>result>filters
                ...this?.room_style,
                ...this?.equipment,

                (this.filters?.natural_light) && SpaceFeatures.NaturalLight || undefined,       // || this.settings?.natural_light
                // this.settings?.natural_light && SpaceFeatures.NaturalLight || undefined,
                // this.settings?.vc && SpaceFeatures.VideoConference || undefined,
            ].filter(f => !!f).map(f => f.toLowerCase());
        //will be an array... new Set() turns it into a set, but [... ] turns it back into an array again
        return [ ...new Set(features) ];
    }

    /** Level in which the space is associated */
    public get level(): BuildingLevel {
        const service = ServiceManager.serviceFor(Organisation);
        return (service ? service.levelWithID(this.zones as any) : null) || new BuildingLevel({});
    }

    /**
     * Return the new recurrence_availability from settings
     */
    public get recurrence_availability(): RecurrenceAvailability[] {
        return this.settings?.recurrence_availability || [];
    }

    public get availableOccurrences(): number {
        if(this?.recurrence_availability) {
            return this?.recurrence_availability?.filter(recurr => recurr.available).length;
        }
    }

    public get totalOccurrences(): number {
        return this?.recurrence_availability.length;
    }

    /**
     * Make a copy of this object
     */
    public clone(): Space {
        return new Space(this);
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(): Space {
        const space = { ...this };
        space.settings.bookings = []; // clear out bookings
        return new Space({ ...space, id: null, email: null });
    }

    /**
     * Generate the booking rules for space with given options
     * @param options Conditions for generating the space rules
     */
    public rulesFor(options: SpaceBookingRuleOptions): SpaceRules {
        if (!this._service || !this.level) {
            return { auto_approve: true, hide: false };
        }

        const building = this.is_concierge ? this.level.building_id : this.building

        if (!building) {
            return { auto_approve: true, hide: false };
        }
        const { dateTz, duration, host } = options;
        const rules: SpaceRules = rulesForSpace({
            time: dateTz.ms,
            duration,
            space: this,
            user: host,
            rules: building.booking_rules
        });
        return rules;
    }

    /**
     * Whether space can only be booked by request
     * @param options Conditions for checking the space rules
     */
    public byRequest(options: SpaceBookingRuleOptions) {
        const rules = this.rulesFor(options);
        return !rules.auto_approve;
    }

    /**
     * Convert object into plain object
     */
    public toJSON(this: Space): HashMap {
        return { ...super.toJSON(), settings: { ...this.settings, bookings: [] }, filters: { ...this.filters }, equipment: [ ...this.equipment] };
    }
}
