import { Component, OnInit, Input, Output, OnChanges, SimpleChanges, forwardRef, EventEmitter } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

export interface IDatePickerOptions {
    /** Unix timestamp with millisecond. First selectable date */
    from?: number;
    /** Unix timestamp with millisecond. Last selectable date */
    to?: number;
    /** Luxon format string for formatting days of the week. Defaults to `DDD` */
    day_format?: string;
    /** Luxon format string for formatting active month. Defaults to `MMMM yyyy` */
    month_format?: string;
}

interface IDateBlock {
    /** Timestamp value of the date */
    value: number;
    /** Day of the month to display */
    display: string;
    /** Whether block is the selected date */
    active: boolean;
    /** Whether block not selectable */
    disabled: boolean;
    /** Whether block outside the active month */
    non_month: boolean;
    /** Whether the block is today */
    today: boolean;
    /** Number of events on this block */
    count?: number;
}

@Component({
    selector: 'aca-date-picker',
    templateUrl: './aca-date-picker.component.html',
    styleUrls: ['./aca-date-picker.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => AcaDatePickerComponent),
            multi: true
        }
    ]
})
export class AcaDatePickerComponent implements OnInit, OnChanges, ControlValueAccessor {
    /** CSS class to add to the root element of the component */
    @Input() public klass = 'default';
    /** Counters of the events on given dates. Key format is `yyyy-MM-dd` */
    @Input() public counters: { [date: string]: number };
    /** Settings for the date picker component */
    @Input() public options: IDatePickerOptions;
    /** Offset of the display month from the current month */
    @Output() public month = new EventEmitter<number>();
    /** Display the last selected day as active */
    @Input() public show_last_selection?: boolean = true

    public date: number = DateNow(new Date()).ms;

    // R--- this is the date picker that needs to be TZ aware
    /** Offset of the month displayed from the current month */
    public offset = 0;
    /** Minimum offset for changing the month */
    public min_offset = -999999;
    /** Maximum offset for changing the month */
    public max_offset = 999999;
    /** Blocks representing the rendering details of the display days of the month */
    public date_list: IDateBlock[] = [];
    /** Name of the actively displayed month */
    public month_name = DateNow(new Date()).formatDate('MMMM yyyy');
    /** Name of the days of the week to display */
    public days_of_week = [];
    /** First selectable date */
    private from: DateTZ;
    /** Last selectable date */
    private to: DateTZ;
    /** Luxon format string for formatting days of the week. Defaults to `dd` */
    private day_format = 'EEE';
    /** Luxon format string for formatting active month. Defaults to `MMMM yyyy` */
    private month_format = 'MMMM yyyy';
    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;
    /** ID of the timer used for changing the month */
    private _change_timer: number;

    public ngOnInit(): void {
        this.generateMonth();
        this.generateDaysOfTheWeek();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.options) {
            this.updateOptions();
        }
        if (changes.counters) {
            this.generateMonth();
        }
    }

    /** Change date using the given block */
    public setDate(item: IDateBlock) {
        this.date = item.value;
        if (this._onChange) {
            this._onChange(this.date);
        }
        this.generateMonth();
    }

    /**
     * Update offset to show the selected date
     */
    public updateDate(): void {
        
        if (!this.date) {
            return;
        }
        const now = DateNow(new Date()).startOfValue('month');
        const date_ = new DateTZ({date: this.date})
        const date = date_.startOfValue('month');
        if (date.valid) {
            const difference = date.dateDiff(now, 'months', 'months');
            if (this.offset !== difference) {
                this.offset = difference;
                this.month.emit(this.offset);
            }
            this.generateMonth();
        }
    }

    /**
     * Update local properties based off changes to settings
     */
    public updateOptions(): void {
        if (this.options) {
            // Setup from date
            if (this.options.from) {
                const date_ = new DateTZ({date: this.options.from})
                this.from = date_.startOfValue('day');;
                const today = DateNow(new Date()).startOfValue('month');
                const date = this.from.startOfValue('month');
                this.min_offset = date.dateDiff(today, 'months', 'months');
            } else if (this.from) {
                this.from = null;
                this.min_offset = -999999;
            }
            // Setup to date
            if (this.options.to) {
                const date_ = new DateTZ({date: this.options.to})
                this.to = date_.startOfValue('day');
                const today = DateNow(new Date()).startOfValue('month');
                const date = this.to.startOfValue('month');
                this.max_offset = date.dateDiff(today, 'months', 'months');
            } else if (this.to) {
                this.to = null;
                this.max_offset = 999999;
            }
            if (this.options.month_format) {
                this.month_format = this.options.month_format || 'MMMM yyyy';
            }
            if (this.options.day_format) {
                this.day_format = this.options.day_format || 'EEE';
                this.generateDaysOfTheWeek();
            }
            // Update date blocks
            this.generateMonth();
        }
    }

    /**
     * Generate the display blocks for the days of the active month
     */
    private generateMonth(): void {
        if (!this.date) {
            return;
        }
        const date = DateNow(new Date()).addValue({ months : this.offset });
        const active = new DateTZ({date: this.date});
        let start = date.setValue({ day: 1 }).setValue({ weekday: 7 }).subtractValue({ weeks: 1 });
        const end_ = new DateTZ({date: start.ms})
        const end = end_.addValue({ weeks: 1 }).endOfValue('month').addValue({ days: 2 }).endOfValue('week');
        this.date_list = [];
        while (start.isBeforeDate(end) || start.isSameDate(end, 'day')) {
            this.date_list.push({
                value: start.ms,
                display: start.formatDate('d'),
                active:  this.show_last_selection && active.isSameDate(start, 'day'),
                non_month: !date.isSameDate(start, 'month'),
                count: this.counters ? this.counters[start.formatDate('yyyy-MM-dd')] || 0 : 0,
                disabled: (this.from ? start.isBeforeDate(this.from, 'day') : false) || (this.to ? start.isAfterDate(this.to, 'day') : false),
                today: DateNow(new Date()).isSameDate(start, 'day')
            });
            start = start.addValue({ days : 1 });
        }
        this.month_name = date.formatDate(this.month_format);
    }

    /**
     * Generate this weekdays to display in the header of the calendar
     */
    private generateDaysOfTheWeek() {
        let date = DateNow(new Date()).startOfValue('week');
        this.days_of_week = [];
        for (let i = 0; i < 7; i++) {
            this.days_of_week.push(date.formatDate(this.day_format));
            date = date.addValue({ days : 1 });
        }
    }

    /**
     * Changes the month offset by the given value.
     * Does nothing if the new month is not available to be selected
     * @param value Number of months to change the offset by
     */
    public changeMonth(value: number): void {
        if (this._change_timer) {
            clearTimeout(this._change_timer);
        }
        this._change_timer = <any>setTimeout(() => {
            const new_offset = this.offset + value;
            const now = DateNow(new Date())
            const date = now.addValue({ months : new_offset, days: now.dateMonth === 2 ? 2 : 0 }); //add 2 days if month is february to avoid calc errors
            if (this.from && date.isBeforeDate(this.from, 'month')) { return; }
            if (this.to && date.isAfterDate(this.to, 'month')) { return; }
            this.offset = new_offset;
            this.month.emit(this.offset);
            this.generateMonth();
            this._change_timer = null;
        }, 100);
    }

    /**
     * Reset the month offset to the month of the selected date
     */
    public reset() {
        const date = new DateTZ({date: this.date});
        const offset = this.offset;
        this.offset = DateNow(new Date()).dateDiff(date, 'months', 'months');
        if (offset !== this.offset) {
            this.generateMonth();
        }
        this.month.emit(this.offset);
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: number) {
        this.date = value;
        this.updateDate();
        this.generateMonth();
    }

    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }
}
