import {
    Component,
    EventEmitter,
    Input,
    Output
} from '@angular/core';
import { ApplicationIcon } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';

export interface PopoutAction extends ApplicationIcon {
    /** ID of the action */
    id: string;
}

@Component({
    selector: 'a-popout-menu',
    templateUrl: './popout-menu.component.html',
    styleUrls: ['./popout-menu.component.scss']
})
export class PopoutMenuComponent extends BaseDirective {
    /** List of available actions */
    @Input() public actions: PopoutAction[] = [];
    /** Emitter for user actions */
    @Output() public action = new EventEmitter<string>();
    /** Whether actions should show */
    public show: boolean;

    public close() {
        this.timeout('close', () => {
            this.show = false;
        });
    }
}
