import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'app-bulk-qr-overlay',
    templateUrl: './bulk-qr-overlay.component.html',
    styleUrls: ['./bulk-qr-overlay.component.scss'],
})
export class BulkQrOverlayComponent implements OnInit {
    @Input() itemsCheckboxes: any[];
    @Input() qrCodeUrls: any[];
    @Output() selectItemChange: EventEmitter<any> = new EventEmitter<any>();
    @Output() selectAllChange: EventEmitter<boolean> = new EventEmitter<boolean>();
    @Input() isOpen: boolean;
    @Input() selectAll: boolean;
    @Output() downloadQrCodeRequested: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancelClick: EventEmitter<void> = new EventEmitter<void>();
    @Input() newArr: any[];
    @Input() allLevels: any;
    @Output() qrCodeUrlsChange: EventEmitter<any[]> = new EventEmitter<any[]>();

    constructor() {}

    ngOnInit(): void {}

    public onSubmit(str: string) {
        this.qrCodeUrlsChange.emit(this.qrCodeUrls); // Emit the updated qrCodeUrls array
        this.downloadQrCodeRequested.emit();
        if (str === 'room') {
            this.qrTabClick({ index: 0 });
        } else if (str === 'level') {
            this.qrTabClick({ index: 1 });
        }
    }

    public onCancel(str: string) {
        // Handle cancel button logic for rooms here (reset the form)
        if (str === 'level') {
            this.itemsCheckboxes = this.newArr.map((item) => {
                return { ...item, ...{ checked: false }, ...{ isLevel: false } };
            });
        }
        this.itemsCheckboxes.forEach((checkbox) => (checkbox.checked = false));
        this.selectAll = false;
        this.qrCodeUrls = [];
        this.qrCodeUrlsChange.emit(this.qrCodeUrls);
        this.cancelClick.emit();
    }

    public onSelectAllChange(str: string) {
        for (const checkbox of this.itemsCheckboxes) {
            checkbox.checked = this.selectAll;
        }
        if (str === 'room') {
            this.qrCodeUrls = this.itemsCheckboxes
                .filter((checkbox) => checkbox.checked)
                .map(({ id, roomname, level }) => ({
                    url: window.location.origin + '/staff/#/home?id=' + id,
                    roomname: roomname,
                    level: level
                }));
        } else if (str === 'level') {
            this.qrCodeUrls = this.itemsCheckboxes
                .filter((checkbox) => checkbox.checked)
                .map(({ id, building_id, name }) => ({
                    url: window.location.origin + '/staff/#/home?zone_id=' + building_id + '&floor=' + id,
                    name: name,
                }));
        }
        this.qrCodeUrlsChange.emit(this.qrCodeUrls);
    }

    public onSelectItemChange(e: any, item: any) {
        if (e.checked && !item.isLevel) {
            //If room selected, push it to qrCodeUrls array
            let checkedRoomObj = {
                url: window.location.origin + '/staff/#/home?id=' + item.id,
                roomname: item.roomname,
                level: item.level,
            };
            this.qrCodeUrls.push(checkedRoomObj);
            this.qrCodeUrlsChange.emit(this.qrCodeUrls);
        } else if (e.checked && item.isLevel) {
            //If levels selected, push it to qrCodeUrls array
            let checkedLevelObj = {
                url:
                    window.location.origin +
                    '/staff/#/home?zone_id=' +
                    item.building_id +
                    '&floor=' +
                    item.id,
                name: item.name,
            };
            this.qrCodeUrls.push(checkedLevelObj);
            this.qrCodeUrlsChange.emit(this.qrCodeUrls);
        } else {
            // If unselected, find the index of the checked Obj in qrCodeUrls array based on room/level name
            const index = item.isLevel
                ? this.qrCodeUrls.findIndex((obj) => obj.name === item.name)
                : this.qrCodeUrls.findIndex((obj) => obj.roomname === item.roomname);
            // If the object is found, remove it from the array using splice
            if (index !== -1) {
                this.qrCodeUrls.splice(index, 1);
                this.qrCodeUrlsChange.emit(this.qrCodeUrls);
            }
        }
    }

    isAnyItemSelected(): boolean {
        return this.itemsCheckboxes.some((checkbox) => checkbox.checked);
    }

    public qrTabClick(e: any) {
        let checkedFlag = { checked: false };
        this.qrCodeUrls = [];
        this.qrCodeUrlsChange.emit(this.qrCodeUrls);
        this.itemsCheckboxes = [];
        this.selectAll = false;

        if (e.index === 0) {
            //rooms tab
            this.itemsCheckboxes = this.newArr.map((item) => {
                return { ...item, ...checkedFlag, ...{ isLevel: false } };
            });
        } else if (e.index === 1) {
            //levels tab
            Object.assign(this.itemsCheckboxes, this.allLevels);
            this.itemsCheckboxes = this.itemsCheckboxes.map((item) => {
                return { ...item, ...checkedFlag, ...{ isLevel: true } };
            });
        }
    }
}
