import {
    createAction,
    createSelector,
    on,
    On,
    props
} from '@ngrx/store';
import {
    LoadingModel,
    wrapLoading
} from './loading.model';
import {
    selectIsLoading,
    selectOptionalError,
    selectOptionalSuccess,
    selectSuccess
} from './loading.selectors';
import { MemoizedSelector } from '@ngrx/store/src/selector';
import {
    ActionCreator,
    NotAllowedCheck,
    TypedAction
} from '@ngrx/store/src/models';
import { HashMap } from '@mckinsey-converge/base';

export interface Payload<T> {
    payload: T
}

export type LoadingAction<T> = T & TypedAction<string>
export type LoadingActionCreator<T extends null | undefined | object> = T extends null | undefined ?
    ActionCreator<string, () => TypedAction<string>>
    : ActionCreator<string, (props: T & NotAllowedCheck<T>) => LoadingAction<T>>

type ClearAction = ActionCreator<string, () => TypedAction<string>>

interface Selectors<TState, TRequest, TSuccess> {
    success: MemoizedSelector<TState, TSuccess>
    optionalSuccess: MemoizedSelector<TState, TSuccess | undefined>
    error: MemoizedSelector<TState, Error | undefined>
    loading: MemoizedSelector<TState, boolean>
    model: (TState) => LoadingModel<TSuccess>
}

export interface LoadingActionObject<TState, TRequest extends object | undefined, TSuccess> {
    request: LoadingActionCreator<TRequest>
    success: LoadingActionCreator<Payload<TSuccess>>
    error: LoadingActionCreator<Payload<Error>>
    clear: ClearAction,
    ons: On<TState>[],
    selectors: Selectors<TState, TRequest, TSuccess>
}

/**
 * Creates a {@link LoadingActionObject} that contains a set of ActionCreators
 * for request, success, error, loading, and clear data. Also, it contains
 * the necessary On reducers and selectors in order to properly connect them in
 * a standardized way to data in the application.
 *
 * `TState` is the type of data on your reducer, not global store.
 * `TRequest` is an object type to use in request methods.
 * `TSuccess` is the object type of the T parameter on `LoadingModel`.
 *
 * This assumes you have state represented by:
 * ```typescript
 * interface SomeState {
 *   stateKey: LoadingModel<TSuccess>
 * }
 * ```
 *
 * To dispatch an action:
 * ```typescript
 *
 * // request parameters depend on the object type used as second type parameter
 * // in createLoadingAction
 * this.store.dispatch(someLoadingAction.request())
 *
 * // action created with createLoadingAction<SomeState, { name: string, age: int }>
 * this.store.dispatch(someLoadingAction.request({
 *   name: 'Andrew G',
 *   age: 24
 * }))
 *
 * ```
 *
 * The generated selectors only select data from its fractured state, not the global
 * store. In order to use them, make sure you {@link select} the global state
 * before using them:
 *
 * ```typescript
 *
 * this.store.pipe(
 *   select(selectSomeState),
 *   select(someLoadingAction.selectors.success)
 * ).subscribe(next => {
 *
 * })
 *
 * ```
 *
 * @param actionPrefix - Prefixes actions to uniquely identify them.
 * @param stateKey -  The field within the reducer state to set data on.
 */
export const createLoadingAction = <TState, TSuccess extends object = {}, TRequest extends object | undefined = undefined>(
    actionPrefix: string,
    stateKey: keyof TState
): LoadingActionObject<TState, TRequest, TSuccess> => {
    const buildAction = (postfix: string) => `[${actionPrefix}] ${postfix}`;
    // @ts-ignore
    const request = createAction(buildAction('request'), props<TRequest>());
    const success = createAction(buildAction('success'), props<Payload<TSuccess>>());
    const error = createAction(buildAction('error'), props<Payload<Error>>());
    const clear = createAction(buildAction('clear'));
    const ons: On<TState>[] = [
        on(request, (state: TState & HashMap<LoadingModel<TSuccess>>, action: LoadingAction<TRequest>) =>
            ({
                ...state,
                [stateKey]: wrapLoading<TSuccess>(state[stateKey]).loading()
            })),
        on(success, (state: TState & HashMap<LoadingModel<TSuccess>>, action: LoadingAction<Payload<TSuccess>>) => ({
            ...state,
            [stateKey]: LoadingModel.success(action.payload)
        })),
        // some reason specifying state like in success and request does not work.
        on(error, (state: any, action: LoadingAction<Payload<Error>>) => ({
            ...state,
            [stateKey]: LoadingModel.error<TSuccess>(action.payload)
        })),
        on(clear, () => ({
            ...this.state,
            [stateKey]: LoadingModel.empty()
        }))
    ];
    const selectData = (state: TState) => {
        if (!state) {
            console.warn('[LoadingActions] State is empty for key', stateKey);
        }
        return wrapLoading<TSuccess>(state?.[stateKey] as unknown as LoadingModel<TSuccess>);
    };
    const selSuccess = createSelector<TState, LoadingModel<TSuccess>, TSuccess>(
        selectData,
        selectSuccess
    );
    const selOptionalSuccess = createSelector(
        selectData,
        selectOptionalSuccess
    );
    const selError = createSelector(
        selectData,
        selectOptionalError
    );
    const selLoading = createSelector(
        selectData,
        selectIsLoading
    );
    return {
        request,
        success,
        error,
        clear,
        ons,
        selectors: {
            success: selSuccess,
            optionalSuccess: selOptionalSuccess,
            error: selError,
            loading: selLoading,
            model: selectData
        }
    };
};
