import {
    Component,
    OnInit,
    Input,
    OnChanges,
    SimpleChanges,
    Output,
    EventEmitter,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
} from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';

import { BaseDirective } from '@mckinsey-converge/base';
import { Space, BOOKING_STATUS, CloseDialogService, DialogIds } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { CateringOrder } from '@mckinsey-converge/data-common';
import { MeetingDetailsModalComponent } from 'src/app/calendar/meeting-details-modal/meeting-details-modal.component';

import { Booking } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

/** Details about the associated event overlapping with other events */
export interface IEventOverlap {
    /** Index of this item with the overlapping items */
    index: number;
    /** Total number of items overlapping with this one */
    total: number;
}
export interface BookingDetailInfo {
    bookedBy: {
        display:boolean,
        value: string
    }
}
@Component({
    selector: 'day-view-space-event',
    templateUrl: './event.component.html',
    styleUrls: ['./event.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DayViewSpaceEventComponent extends BaseDirective implements OnInit, OnChanges {
    /** Event to display */
    @Input() public booking: Booking;
    /** Date to display bookings for */
    @Input() public dateTz: DateTZ = DateNow(new Date());
    /** Space associated with the booking */
    @Input() public space: Space;
    /** Whether the item is a fixed size */
    @Input() public fixed: boolean;
    /** Overlap details for the booking */
    @Input() public overlap: IEventOverlap;
    /** Whether an action is being processed for this booking */
    @Input() public loading: boolean;
    /** Emitter for the current position of the booking within the timeline */
    @Output() public position = new EventEmitter<{ top: number; height: number }>();

    /** Position of the booking start time in the day */
    public top: number;
    /** Position of the booking relative to overlapping bookings */
    public left: number;
    /** Height based of the duration of the booking */
    public height: number;
    /** Size of the booking when overlapping with other bookings */
    public width: number;
    /** Offset from the booking's top that the setup time starts */
    public overflow_top: number;
    /** Offset from the booking's bottom that the breakdown time ends */
    public overflow_bottom: number;

    public approved: boolean;
    public declined: boolean;
    public bookingDetails: BookingDetailInfo;
    constructor(
        private _service: ApplicationService,
        private _dialog: MatDialog,
        private _router: Router,
        private cdRef: ChangeDetectorRef,
        private closeDialogService: CloseDialogService
    ) {
        super();
    }

    ngOnInit(): void {
        this.approved = this.booking.status === BOOKING_STATUS.approved;
        this.declined = this.booking.status === BOOKING_STATUS.declined;
        // test comment
        const {creator, organiser, booker_concierge} = this.booking;
        this.bookingDetails = {
            bookedBy: {
                display: creator && ( creator.email !== organiser.email || (creator.email === organiser.email && booker_concierge !== null && organiser.email !==booker_concierge['email']) ),
                value: (creator.email === organiser.email && booker_concierge !== null && organiser.email !==booker_concierge['email']) ?  booker_concierge['name'] || booker_concierge['email']  :  creator?.name || creator.email
            }
        }
    }

    public get local_id(): string {
        return this.booking.icaluid.substr(0, 6);
    }

    /** Type of booking */
    public get type(): string {
        return this.booking.getType();
    }

    /** Period that the booking will occur during the day */
    public get period(): string {
        // the booking time is being pass in as the local timezone version.
        return this.booking.time_period;
    }

    /** Number attendees expected to turn up to the booking */
    public get expected(): number {
        const expected = this.booking.expected_attendees;
        return this.space ? expected[this.space.email] || 0 : 0;
    }

    /** Whether the meeting has catering attached */
    public get has_catering(): boolean {
        return !!this.order;
    }

    public get order(): CateringOrder {
        let orders = [];
        this.booking.multi_rooms.forEach((room) =>{
            const _temp = this.booking.catering.filter(
                (order) => order.location_id === room.email
            );
            _temp.length && orders.push(_temp[0]);
        });
        return orders[0];
        // this.booking.catering.find((order) => order.location_id === this.space.email);
    }

    /** View booking details */
    public view(event: Event) {
       
        this.cdRef.detach(); // Detach change detection before the dialog opens. 

       const dialogRef =  this._dialog.open(MeetingDetailsModalComponent, {
            maxHeight: '95vh',
            maxWidth: '95vw',
            data: {
                space: this.space,
                booking: this.booking,
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.closeDialogService.registerDialog(DialogIds.MeetingDetails, dialogRef);
        dialogRef.afterClosed().subscribe(_ => {
            this.cdRef.reattach(); // Reattach change detection after the dialog closes.

            this.calculatePosition();
            this.cdRef.detectChanges();
        });
    }

    /** Whether booking is active */
    public get active(): boolean {
        const booking = this._service.get('CONCIERGE.day_view.viewing');
        return !booking || booking.id === this.booking.id;
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.booking || changes.overlap || changes.dateTz) {
            this.calculatePosition();
            this.approved = this.booking.status === BOOKING_STATUS.approved;
            this.declined = this.booking.status === BOOKING_STATUS.declined;
        }
    }

    /** Store the order details for viewing in another component */
    public storeOrder(order: CateringOrder) {
        const booking_details = this.booking.toApiJSON();
        localStorage.setItem('CONCIERGE.view_order', JSON.stringify(order.toJSON()));
        localStorage.setItem('CONCIERGE.view_booking', JSON.stringify(booking_details));
        localStorage.setItem('CONCIERGE.date', `${this.booking.startDateTz.ms}`);
        localStorage.setItem('CONCIERGE.timezone', `${this.booking.startDateTz.building_tz}`);
    }

    /**
     * View catering order for the booking
     */
    public viewOrder(order: CateringOrder) {
        /* istanbul ignore else */
        if (this.booking && this.booking.catering) {
            /* istanbul ignore else */
            if (localStorage) {
                this.storeOrder(order);
            }
            this._router.navigate(['/catering', 'orders'], {
                queryParams: { booking: this.booking.id, view: order.id },
            });
        }
    }

    /** Calculate the position */
    private calculatePosition() {
        /* istanbul ignore else */
        if (this.booking) {
            const overlap = this.overlap || { index: 0, total: 1 };
            const shown_date = this.dateTz.startOfValue('day');
            const date = this.booking.startDateTz.startOfValue('minute');
            const end = this.booking.endDateTz.startOfValue('minute');
            const from_previous_day = !date.isSameDate(shown_date, 'day');
            if (end.isBeforeDate(shown_date, 'day')) { // Hide booking if not on shown date
                this.top = -100;
                this.height = 1;
                return;
            }

            //R-- Stragest thing, but one time this was logging bookings form different timezones?
            // this.dateTz.logDateTz('calculatePosition this.dateTz')
            // this.booking.startDateTz.logDateTz('calculatePosition this.booking.startDateTz')

            const start = from_previous_day ? 0 : Math.abs(+date.dateDiff(shown_date, 'hours', 'hours').toFixed(3));
            const duration = Math.min(
                this.booking.duration,
                Math.abs(shown_date.dateDiff(date.addValue({ minutes: this.booking.duration }), 'minutes'))
            );
            this.top = Math.max(0, (start / 24) * 100);
            this.height = Math.min(
                100 - this.top,
                this.fixed ? duration / 60 : (duration / 60 / 24) * 100
            );
            this.position.emit({ top: this.top, height: (duration / 60 / 24) * 100 });
            this.width = Math.min(100, 100 / overlap.total + 5);
            this.left = Math.min(100 - this.width, this.width * overlap.index - 5 * overlap.index);
            this.overflow_top =
                ( (this.booking.room_setup || this.booking.setup[this.space.email])
                    ? ( ( this.booking.room_setup || this.booking.setup[this.space.email]) / 60) / duration
                    : -0.1) * 100;
            this.overflow_bottom =
                ( ( this.booking?.room_breakdown || this.booking.breakdown[this.space.email])
                    ? ( (this.booking?.room_breakdown || this.booking.breakdown[this.space.email]) / 60) / duration
                    : -0.1) * 100;
            if (this.booking.all_day) {
                this.top = 0;
                this.height = 100;
            }
        }
    }
}
