import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';

import { BaseDirective } from '@mckinsey-converge/base';

@Component({
    selector: 'feedback',
    templateUrl: './feedback.component.html',
    styleUrls: ['./feedback.component.scss'],
})
export class FeedbackComponent extends BaseDirective implements OnInit {
    /** Name of the currently active page */
    public current_page: string;

    constructor(private _router: Router) {
        super();
    }

    public ngOnInit(): void {
        this.timeout('Getting Title', () => this.updateRoute(document.title), 1000);
        this._router.events.subscribe((event: NavigationEnd) => {
            /* istanbul ignore else */
            if (event instanceof NavigationEnd) {
                this.timeout('Getting Title', () => this.updateRoute(document.title));
            }
        });
    }

    public updateRoute(location) {
        location = location.substr(0, location.indexOf(' |'));
        this.current_page = location;
    }
}
