import {
    Component,
    Input,
    OnInit, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter 
} from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { distinctUntilChanged, first, skip } from 'rxjs/operators';

import {
    BaseDirective,
    closestToTimeSlotTz,
    Identity,
    unique
} from '@mckinsey-converge/base';
import {
    ApplicationService,
    arrayWithValues,
    BookingAction,
    CateringOrder,
    SeriesAction,
    SettingsService,
    User,
    UsersService
} from '@mckinsey-converge/data-common';

import { NewUserModalComponent } from '../new-user-modal/new-user-modal.component';
import {
    SpaceSelectModalComponent,
    SpaceSelectModalData
} from '../space-select-modal/space-select-modal.component';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { DateTime } from 'luxon';
import { MatChipsModule} from '@angular/material/chips';


@Component({
    selector: 'a-booking-form',
    templateUrl: './booking-form.component.html',
    styleUrls: ['./booking-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class BookingFormComponent extends BaseDirective implements OnInit {
    /** Form fields for booking */
    @Input() public form: FormGroup;
    /** List of available users to host bookings */
    public user_list: User[] = [];
    public nowTz: DateTZ = DateNow(new Date());
    /** From date input for date field */
    public fromDateTz: DateTZ = this.nowTz.setValue({ minute: Math.ceil(this.nowTz.minutes / 5) * 5 });
    /** Last bookable date, concierge only */
    public readonly untilDateTz = DateNow(new Date()).addValue({ days: 365 }).endOfValue('day');
    public isConcierge: boolean;
    public localTimezone = DateNow(new Date()).building_tz;
    public localStart = "";
    public localEnd = "";
    // Define an array to store selected company options
    public chipOptions: any[] = [];

    constructor(
        private _service: ApplicationService,
        private _users: UsersService,
        private _dialog: MatDialog,
        private settingsService: SettingsService,
        private cdRef: ChangeDetectorRef
    ) {
        super();
        this.isConcierge = this.settingsService.concierge;
    }

    public ngOnInit(): void {
        // Trim title 
        // this.form.get('title').setValue(this.form.get('title').value.trim());

        this.chipOptions = [...this.form.controls.company.value];

        this.localTimezone = DateNow(new Date()).building_tz;
        const building_tz = (
            this.form.controls.action &&
            (
                this.form.controls.action.value === 'booking_clone' ||
                this.form.controls.action.value === 'clone' ||
                this.form.controls.action.value === 'edit' ||
                this.form.controls.action.value === 'booking_edit'
            ) &&
            this.form.controls.dateTz &&
            this.form.controls.dateTz.value
        ) ? this.form.controls.dateTz.value.building_tz : this.localTimezone;

        this.nowTz = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz });
        this.fromDateTz = closestToTimeSlotTz(this.nowTz, 15);

        this._users.initialised.pipe(first((_) => _)).subscribe(() => {
            const user: User = this._users.current;
            const delegates = user.delegates.map(
                (del) => this._users.find(del.email) || new User({ email: del.email, name: del.email.split('@')[0] })
            );
            this.user_list = [user].concat(delegates);
        });

        this.updateCoachmarkTimes(this.form.controls.timezone.value);
        this.subscription('formChanges',  this.form.valueChanges
            .pipe(
                skip(1), // initial form changes
                distinctUntilChanged())
            .subscribe(() => {
                this.timeout('update_coachmark', () => {
                    this.updateCoachmarkTimes(this.form.controls.timezone.value);
                }, 500);
            })
        );

        this.subscription('isRecurrentChanges',  this.form.controls.is_recurrent.valueChanges
            .subscribe(() => {
                const recurrenceStarts = this.form.controls.recurrence_starts
                if(!this.form.controls.is_recurrent.value){
                    recurrenceStarts.clearValidators()     
                }else{
                    const arrayValidators = [Validators.required];
                    arrayValidators.push(arrayWithValues);
                    recurrenceStarts.setValidators(arrayValidators);
                }
                recurrenceStarts.updateValueAndValidity()
            })
        );     

        /**Disable Inputs */
        this.disableInputs();

        /**turn is_recurrent to false if editing or cloning a stand-alone/occurrence */
        if(this.form.controls.id?.value && !this.form.controls.action?.value){
            this.form.controls.is_recurrent.setValue(false)
        }
    }

    /**
     * 
     * @param key : formcontrol name 
     * Function trim value being submitted with only spaces/beginning/end 
     */
    public trim(key: string): void {
        this.form.get(key).setValue(this.form.get(key).value.trim());
    }

    /** Whether notes and description should use the HTML editor */
    public get use_html(): boolean {
        return this._service.setting('app.booking.html_body') || false;
    }

    /** List of available types for bookings */
    public get booking_types(): Identity[] {
        return this._service.setting('app.booking.booking_types') || [];
    }

    /** Display string for the list of active spaces */
    public get spaces(): string {
        const spaces = this.form.controls.space_list.value;
        return spaces.length === 1
            ? spaces[0].local_name
            : spaces.length > 1
                ? `${spaces.length} spaces selected`
                : 'No Spaces selected';
    }

    public disableInputs() {
        const { id, action, needs_space, is_recurrent } = this.form.value;
        const has_id = !!id;
        const is_edit = action === BookingAction.EDIT;
        if (has_id) {
            this.form.get('is_recurrent')?.disable();
        }

        if(is_edit){
            this.form.get('is_multiroom')?.disable();
        }

        if (!this.isConcierge && !needs_space) {
            this.form.get('has_catering')?.disable();
        }
        if(has_id && is_edit && is_recurrent){
            this.form.get('dateTz')?.disable()
            this.form.patchValue({ action: null });
        }
    }

    /**
     * Open modal to change the space details for the booking
     */
    public openSpacesModal(): void {
        const ref = this._dialog.open<SpaceSelectModalComponent, SpaceSelectModalData>(
            SpaceSelectModalComponent,
            {
                width: 'auto',
                height: 'auto',
                maxWidth: '95vw',
                data: {
                    spaces: this.form.controls.space_list.value,
                    dateTz: this.form.controls.dateTz.value,
                    duration: this.form.controls.duration.value,
                    host: this.form.controls.organiser.value,
                    multiple: true,
                    needsCatering: !!this.form.controls.catering?.value?.length,
                }
            }
        );
        this.subscription(
            'new_spaces',
            ref.componentInstance.event.subscribe((event) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    let space_list = this.form.controls.space_list.value;
                    space_list = event.metadata;
                    this.form.controls.space_list.setValue(unique(space_list, 'id'));
                    const catering = this.form.controls?.catering?.value.map((order: CateringOrder) => {
                        order.location = space_list[0].name;
                        order.location_id = space_list[0].email;
                        return order;
                    })
                    this.form.controls.catering?.setValue(catering || [])
                    ref.close();
                    this.unsub('new_spaces');
                }
                if (event.reason === 'close') {
                    ref.close();
                    this.unsub('new_spaces');
                }
            })
        );
    }

    /**
     * Open modal to change the recurrence details for the booking
     */
    public openNewUserModal() {
        const ref = this._dialog.open<NewUserModalComponent>(NewUserModalComponent, {
            width: 'auto',
            height: 'auto'
        });
        this.subscription(
            'new_user',
            ref.componentInstance.event.subscribe((event) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    const attendees = this.form.controls.attendees?.value;
                    attendees.push(event.metadata);
                    this.form.controls.attendees.setValue(unique(attendees, 'email'));
                    ref.close();
                    this.unsub('new_user');
                    this.cdRef.detectChanges();
                }
            })
        );
    }

    // Add & Remove chip and set the value of the form control accordingly
    addCompany(event, company): void {
        if (company) {
            this.chipOptions.push(company);
            this.form.controls.company.setValue(this.chipOptions);
        }
        event.preventDefault();
    }

    removeOption(option: any): void {
        const index = this.chipOptions.indexOf(option);
        if (index >= 0) {
            this.chipOptions.splice(index, 1);
        }
        this.form.controls.company.setValue(this.chipOptions);
    }

    private updateCoachmarkTimes(timezone: string) {
        if (!timezone) timezone = this.localTimezone;
        // Resolve the problem that locally I might have the same TZ offset but the names don't match.
        const localTzOffset = DateTime.now().setZone(this.localTimezone).offset;
        const buildingTzOffset = DateTime.now().setZone(timezone).offset;
        const dateTz = this.form.controls.dateTz.value;
        if (dateTz && localTzOffset !== buildingTzOffset) {
            const startDate = dateTz.clone();
            this.localStart = startDate.formatLocalDate("h:mma");
            this.localEnd = startDate.addValue({ minutes: this.form.value.duration }).formatLocalDate("h:mma");
        } else {
            // No location is selected so coachmark is hidden
            this.localStart = "";
            this.localEnd = "";
        }
        this.cdRef.detectChanges();
    }

    public get isSeriesAction(){
        return this.form.value.action === SeriesAction.CLONE || this.form.value.action === SeriesAction.EDIT
    }

    onToggleChange(toggledControlName: string): void {
        if (toggledControlName === 'is_recurrent') {
          // If the is_recurrent toggle is being activated, deactivate is_multiroom
          if (this.form.controls.is_recurrent?.value) {
            this.form.controls.is_multiroom?.setValue(false);
          }
        } else if (toggledControlName === 'is_multiroom') {
          // If the is_multiroom toggle is being activated, deactivate is_recurrent
          if (this.form.controls.is_multiroom?.value) {
            this.form.controls.is_recurrent?.setValue(false);
          }
        }
      }
      
}
