import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output
} from '@angular/core';
import { SelectOption } from '@mckinsey-converge/ui';

@Component({
    selector: 'mck-switch-group',
    template: `
        <div class="mck-column mck-content-expand-width mck-stretch switch-container">
            <span
                id="{{title}}"
                class="switch-title">{{title}}</span>
            <mat-slide-toggle
                class="mck-slide-toggle"
                labelPosition="before"
                name="{{title}} - toggle - {{option.display}}"
                *ngFor="let option of options"
                (change)="onCheckedChange(option)"
                [checked]="isSelected(option)">{{option.display}}</mat-slide-toggle>
        </div>
    `,
    styleUrls: ['./switch-group.component.scss']
})
export class SwitchGroupComponent implements OnInit {

    @Input()
    title: string;

    @Input()
    options: SelectOption<any>[];

    @Input()
    selectedOptions: SelectOption<any>[] = [];

    @Output()
    optionChecked = new EventEmitter<SelectOption<any>>();

    constructor() {
    }

    ngOnInit(): void {
    }

    public onCheckedChange(option: SelectOption<any>) {
        this.optionChecked.emit(option);
    }

    public isSelected(option: SelectOption<any>) {
        return !!this.selectedOptions.find(s => s.value === option.value);
    }
}
