import { Component, OnInit, Inject, EventEmitter, Output } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { UploadManager } from '@acaprojects/ngx-uploads';

import { ApplicationService } from '@mckinsey-converge/data-common';
import { CateringCategory } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { DialogEvent, Identity } from '@mckinsey-converge/base';

import * as blobUtil from 'blob-util';
import { CateringCategoriesService } from '@mckinsey-converge/data-common';
import {
    ConfirmModalComponent,
    CONFIRM_METADATA,
} from '@mckinsey-converge/legacy-overlays';

export interface CateringGroupModalData {
    group: CateringCategory;
    parent: CateringCategory;
}

@Component({
    selector: 'a-catering-group-modal',
    templateUrl: './group-modal.component.html',
    styleUrls: ['./group-modal.component.scss'],
})
export class CateringGroupModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form controls for editing category */
    public form: FormGroup;
    /** Display string for current loading state */
    public loading: string;
    /** Whether image is being uploaded */
    public uploading: boolean;
    /** List of available catering types */
    public readonly types: Identity[] = [
        { id: 'kitchen', name: 'Kitchen' },
        { id: 'pantry', name: 'Pantry' },
        { id: 'none', name: 'Other' },
    ];

    /** Category being edit/created */
    public get category(): CateringCategory {
        return this._data.group;
    }

    /** Parent category of the catergory being created/edited */
    public get parent(): CateringCategory {
        return this._data.parent;
    }

    /** Image URL for the category */
    public get image(): string {
        return this.form.controls.image_path.value;
    }

    /** Whether category is a package */
    public get package(): boolean {
        return this.form.controls.package.value;
    }

    constructor(
        private _service: ApplicationService,
        private _menu_categories: CateringCategoriesService,
        private _upload_manager: UploadManager,
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<CateringGroupModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: CateringGroupModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        const price = this.category.price
        this.form = new FormGroup({
            name: new FormControl(this.category.name, [Validators.required]),
            description: new FormControl(this.category.description),
            image_path: new FormControl(this.category.image_path),
            catering_type: new FormControl(this.category.catering_type, [Validators.required]),
            package: new FormControl(this.category.package || false),
            must_select: new FormControl(this.category.must_select),
            unit_price: new FormControl(price),
            minimum_quantity: new FormControl(this.category.minimum_quantity),
            maximum_quantity: new FormControl(this.category.maximum_quantity),
            order_anytime: new FormControl(this.category.order_anytime),
            out_of_stock: new FormControl(this.category.out_of_stock),
        });
    }

    public delete() {
        const ref = this._dialog.open(ConfirmModalComponent, {
            ...CONFIRM_METADATA,
            data: {
                title: `Delete ${this.category.package ? 'Package' : 'Sub-Category'}`,
                content: `Are you sure you want to delete this ${
                    this.category.package ? 'Package' : 'Sub-Category'
                }? Deleting it will also remove all it's children`,
                icon: { type: 'icon', class: 'material-icons', content: 'delete' },
                action: 'Ok',
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                ref.componentInstance.loading = `Deleting ${
                    this.category.package ? 'Package' : 'Sub-Category'
                }...`;
                let e = null;
                await this._menu_categories.delete(this.category.id).catch((err) => e === err);
                ref.close();
                if (!e) {
                    this._service.set('DELETED.menu', this.category.id);
                    this._dialog_ref.close();
                }
            }
        });
    }

    public submit() {
        this.form.markAllAsTouched();
        /* istanbul ignore else */
        if (this.form.valid) {
            const data = {
                ...(this.category instanceof CateringCategory
                    ? this.category.toJSON()
                    : this.category),
                ...this.form.value,
                parent_categories: [this.parent.id],
            };
            //if sub-category/item becomes non-package then mark it in stock 
            data.out_of_stock = !data.package ? false : data.out_of_stock;
            data.price = data.unit_price;
            const request = this.category.id
                ? this._menu_categories.update(this.category.id, data)
                : this._menu_categories.add(data);
            request.then(
                (item) => {
                    (item as any).items = this.category.items
                    this.event.emit({ reason: 'done', metadata: new CateringCategory(item) });
                    this._dialog_ref.close();
                },
                (err) =>
                    this._service.notifyError(
                        `Error ${this.category.id ? 'updating' : 'creating'} category. Error: ${
                            err.message || err
                        }`
                    )
            );
        }
    }

    /** Upload the image to the cloud */
    public uploadImage(event) {
        /* istanbul ignore else */
        if (event && event.target && event.target.files) {
            const files: FileList = event.target.files;
            /* istanbul ignore else */
            if (files.length) {
                const fileReader = new FileReader();
                fileReader.addEventListener('loadend', (e: any) => {
                    this.uploading = true;
                    const arrayBuffer = e.target.result;
                    const blob = blobUtil.arrayBufferToBlob(arrayBuffer, 'image/png');
                    this._upload_manager.upload([blob], { file_name: files[0].name });
                    const upload = this._upload_manager.uploads[
                        this._upload_manager.uploads.length - 1
                    ];
                    this.interval('upload', () => {
                        /* istanbul ignore else */
                        if (upload.complete || upload.error) {
                            /* istanbul ignore else */
                            if (upload.access_url && upload.complete) {
                                this.form.controls.image_path.setValue(upload.access_url);
                            }
                            this.uploading = false;
                            this.clearInterval('upload');
                        }
                    });
                });
                fileReader.readAsArrayBuffer(files[0]);
            }
        }
    }
}
