import {
    Component,
    EventEmitter,
    Input,
    Output
} from '@angular/core';

@Component({
    selector: 'mck-selected-chip-count',
    template: `
        <div
            (click)="close.emit()"
            class="mck-row mck-center selected-count-chip">
            <span>{{count}}</span>
            <mck-close-icon fillClass="mck-icon-fill-black"></mck-close-icon>
        </div>
    `,
    styleUrls: ['./selected-chip-count.component.scss']
})
export class SelectedChipCountComponent {

    @Input()
    count: number;

    @Output()
    close = new EventEmitter<void>();

}
