import { Injectable } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Injectable({
  providedIn: 'root'
})
export class CloseDialogService {
  private dialogRefMap = new Map<string, MatDialogRef<any>>();

  constructor() { }

  // Register a dialog with its ID
  registerDialog(id: string, dialogRef: MatDialogRef<any>): void {
    this.dialogRefMap.set(id, dialogRef);
  }

  // Close a specific dialog by ID
  closeDialog(id: string): void {
    const dialogRef = this.dialogRefMap.get(id);
    if (dialogRef) {
      dialogRef.close();
      this.dialogRefMap.delete(id);
    }
  }

  // Optional: Close all dialogs
  closeAllDialogs(): void {
    this.dialogRefMap.forEach((dialogRef, id) => {
      dialogRef.close();
      this.dialogRefMap.delete(id);
    });
  }
}

export enum DialogIds {
  MeetingDetails = 'meetingDetailsDialogId',
  BookingConfirmation = 'bookingConfirmationDialogId',
}
