import {
    ChangeDetectorRef,
    Component,
    EventEmitter,
    forwardRef,
    Input,
    OnInit,
    Output,
    ViewChild
} from '@angular/core';
import {
    ControlValueAccessor,
    FormControl,
    NG_VALUE_ACCESSOR
} from '@angular/forms';
import {
    Observable,
    of,
    Subject
} from 'rxjs';
import {
    catchError,
    debounceTime,
    distinctUntilChanged,
    tap,
    map,
    switchMap,
    finalize
} from 'rxjs/operators';

import { BaseDirective } from '@mckinsey-converge/base';
import {
    ApplicationService,
    User,
    UsersService
} from '@mckinsey-converge/data-common';
import {
    csvToJson,
    downloadFile,
    filterList,
    matchToHighlight
} from '@mckinsey-converge/base';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';

@Component({
    selector: 'a-user-list-field',
    templateUrl: './user-list-field.component.html',
    styleUrls: ['./user-list-field.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => UserListFieldComponent),
            multi: true
        }
    ]
})
export class UserListFieldComponent extends BaseDirective implements OnInit, ControlValueAccessor {
    /** Whether form field is disabled */
    @Input() public disabled: boolean;
    /** Number of characters needed before a search will start */
    @Input() public limit: number = 3;
    /** Emitter for action to make a new user */
    @Output('newUser') public new_user = new EventEmitter<void>();

    /** User list to display */
    public user_list: User[];
    /** List of active selected users on the list */
    public active_list: User[];
    /** Whether user list is loading */
    public loading: boolean;
    /** String  */
    public search_str: string;
    /** List of users from an API search */
    public search_results$: Observable<User[]>;
    /** Subject holding the value of the search */
    public search$ = new Subject<string>();

    /** Form control on change handler */
    private _onChange: (_: User[]) => void;
    /** Form control on touch handler */
    private _onTouch: (_: User[]) => void;

    @ViewChild(MatAutocompleteTrigger, {read: MatAutocompleteTrigger}) inputAutoComplete: MatAutocompleteTrigger;

    constructor(private _service: ApplicationService, private _users: UsersService,  private cdRef: ChangeDetectorRef) {
        super();
    }

    ngOnInit() {
        // Listen for input changes
        this.search_results$ = this.search$.pipe(
            debounceTime(100),
            distinctUntilChanged(),
            switchMap((query) => {
                this.loading = true;
                return query.length >= this.limit
                    ? this._users.query({
                        q: query.slice(0, Math.max(this.limit, 3)),
                        cache: 60 * 1000
                    })
                    : Promise.resolve([]);
            }),
            catchError((_) => of([])),
            map((list: User[]) => {
                this.loading = false;
                return filterList(this.search_str, list, ['name', 'email']);
            })
        );
        // Process API results
        this.subscription(
            'search_results',
            this.search_results$.subscribe((list) => {
                this.user_list = list;
                this.user_list.forEach((i: any) => {
                    i.match_name = matchToHighlight(i.match_name);
                    i.match_email = matchToHighlight(i.match_email);
                    return i;
                });
                this.user_list.length ? this.inputAutoComplete.openPanel() : this.inputAutoComplete.closePanel();
            })
        );
    }

    /**
     * Add user to the user list
     * @param user
     */
    public addUser(user: User) {
        /* istanbul ignore else */
        if (!this.active_list) {
            this.active_list = [];
        }
        const index = this.active_list.findIndex((a_user) => a_user.id === user.id);
        /* istanbul ignore else */
        if (index < 0) {
            this.active_list = [...this.active_list, user];
        }
        this.setValue(this.active_list);
        this.search_str = '';
    }

    /**
     * Remove user from the user list
     * @param user
     */
    public removeUser(user: User) {
        this.active_list = this.active_list.filter((a_user) => a_user.id !== user.id);
        this.setValue(this.active_list);
    }

    /**
     * Load CSV file and populate the user list with the contents
     * @param event File input field event
     */
    public addUsersFromFile(event) {
        /* istanbul ignore else */
        if (event.target) {
            const file = event.target.files[0];
            /* istanbul ignore else */
            if (file) {
                const reader = new FileReader();
                reader.readAsText(file, 'UTF-8');
                reader.addEventListener('load', (evt) => {
                    this.processCsvData((evt.srcElement as any).result);
                    event.target.value = '';
                    this.cdRef.detectChanges();
                });
                reader.addEventListener('error', (_) =>
                    this._service.notifyError('Error reading file.')
                );
            }
        }
    }

    /**
     * Process raw CSV data and save user data to attendee list
     * @param data CSV data
     */
    private processCsvData(data: string) {
        const list = csvToJson(data) || [];
        const id = this._users.current.staff_code;        
        list.forEach((el) => {
            const firstName = el.first_name.split(' ').map((word: string) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()).join(' ');
            const lastName = el.last_name.split(' ').map((word: string) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()).join(' ');

            el.name = el.name || `${firstName} ${lastName}`;
            const display = (el.name || `${Math.floor(Math.random() * 9999_9999)}`)
                .split(' ')
                .join('_')
                .toLowerCase();
            /* istanbul ignore else */
            if (!el.email) {
                el.email = `${display}+${id}@guest.mckinsey.com`;
            }
            el.type = 'external';
            el.external = !el.email.endsWith("@mckinsey.com") && !el.email.endsWith("@external.mckinsey.com");
            this.addUser(new User(el));
        });
    }

    /* istanbul ignore next */
    /** Download template CSV file */
    public downloadCSVTemplate() {
        const template = `Organisation Name,First Name,Last Name,Email,Phone\nFake Org,John,Smith,john.smith@example.com,01234567898`;
        downloadFile('template.csv', template);
    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: User[]): void {
        this.active_list = new_value;
        if (this._onChange) {
            this._onChange(new_value);
        }
    }

    /* istanbul ignore next */
    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: User[]) {
        this.active_list = value;
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: User[]) => void): void {
        this._onChange = fn;
    }

    /* istanbul ignore next */
    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: User[]) => void): void {
        this._onTouch = fn;
    }
}
