import {
    Component,
    Input,
    ViewContainerRef
} from '@angular/core';
import { BaseTemplateComponent } from '../base-template.component';
import { SelectOption } from '../types';

@Component({
    selector: 'mck-select-option',
    template: `
        <ng-template>
            <div
                *ngIf="option"
                [ngClass]="{ 'mck-ignore-disabled': ignoreDisabled }"
                class="mck-row mck-select-option-{{selected ? 'selected' : 'unselected'}} mck-select-option mck-center-align">
                <mck-spacer
                    [autoSize]="true"
                    size="5px"
                    class="mck-select-option-bar"
                    [mode]="'vertical'"></mck-spacer>
                <ng-content></ng-content>
                {{ option.display }}
            </div>
        </ng-template>
    `,
    styleUrls: ['./select-option.component.css']
})
export class SelectOptionComponent extends BaseTemplateComponent {

    @Input()
    option: SelectOption<any>;

    @Input()
    selected: boolean;

    /**
     * If true, we still display as if it wasn't disabled.
     */
    @Input()
    ignoreDisabled = false;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }

}
