import {
    Component,
    Input,
    OnInit,
    ViewContainerRef
} from '@angular/core';
import { BaseTemplateComponent } from '../base-template.component';

export enum SpacerMode {
    /**
     * If vertical, it expands in the vertical direction. Size is width.
     */
    Vertical = 'vertical',
    /**
     * If horizontal, it expands in the horizontal direction. Size is height.
     */
    Horizontal = 'horizontal',
}

@Component({
    selector: 'mck-spacer',
    template: `
        <ng-template>
            <div class="mck-spacer-content {{spacerClass}}"
                 *ngIf="mode === SpacerMode.Vertical"
                 [ngStyle]="{ width: size, height: autoSize ? 'auto' : '100%'}">
            </div>
            <div class="mck-spacer-content {{spacerClass}}"
                 *ngIf="mode === SpacerMode.Horizontal"
                 [ngStyle]="{ height: size, width: autoSize ? 'auto' : '100%'}">
            </div>
        </ng-template>
    `,
    styleUrls: ['./spacer.component.css'],
})
export class SpacerComponent extends BaseTemplateComponent implements OnInit {
    SpacerMode = SpacerMode;

    @Input()
    size: string;

    @Input()
    mode: SpacerMode = SpacerMode.Horizontal;

    @Input()
    autoSize?: boolean;

    @Input()
    spacerClass?: string;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }


}
