import { Component, Input, SimpleChanges, ChangeDetectionStrategy } from '@angular/core';
import { map } from 'rxjs/operators';

import { Booking, Space } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { BookingStateService } from '@mckinsey-converge/data-common';

import { timePeriodsIntersect } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'week-view-day-display',
    templateUrl: './day-display.component.html',
    styleUrls: ['./day-display.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class WeekViewDayDisplayComponent extends BaseDirective {
    /** Date to display bookings for */
    @Input() public dateTz: DateTZ = DateNow(new Date());
    /** List of spaces to display bookings for */
    @Input() public spaces: Space[];
    /** Whether the date set is today */
    public isToday: boolean = false;
    /** List of bookings for this date */
    public readonly booking_list = this._booking_state_service.filtered_week.pipe(
        map((bookings) => {
            const date = this.dateTz.startOfValue('day');
            /**
             * Before the dateTz refactor timePeriodsIntersect would show bookings on the wrong day
             * because this.dateTz did not have the building timezone
             */
            return bookings.filter((booking) => {
                const ignore = booking.is_recurrent && booking.recurrence_type === 'master';
                return (
                    !ignore &&
                    // this.spaces.find((space: Space) => space.email === booking.room.email) &&
                    // Check if any room in booking.multi_rooms satisfies the condition with any space in this.spaces
                    booking.multi_rooms.some(room => 
                        this.spaces.find((space: Space) => space.email === room.email)
                    ) &&
                    timePeriodsIntersect(
                        date.ms,
                        date.endOfValue('day').ms,
                        booking.startDateTz.ms,
                        booking.endDateTz.ms
                    )
                );
            });
        })
    );

    /** Display string for the set date */
    public get date_display(): string {
        return this.dateTz.formatDate('EEE, dd MMM yyyy');
    }

    constructor(private _booking_state_service: BookingStateService) {
        super();
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.dateTz) {
            const now = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: this.dateTz.building_tz })
            this.isToday = this.dateTz.isSameDate(now, 'day');
        }
        if (changes.spaces) {
            this.timeout('changed', () => this._booking_state_service.setFilters(this._booking_state_service.filters), 100);
        }
    }

     /**
     * Track by function for ngFor optimisation
     * @param index Index of the item
     * @param item Data associated with the item at the given index
     */
      public trackByFn(index: number, item: Booking) {
        return item.id || index;
    }
}
