import {
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges
} from '@angular/core';

export enum AlertMode {
    Warning = 'warning',
    Coachmark = 'coachmark',
    BlueCoachmark = 'blue-coachmark',
    Error = 'error'
}

export enum AlertBannerDisplay {
    /**
     * New UI
     */
    V2 = 'v2',

    /**
     * Default, legacy ui.
     */
    V1 = 'v1',
}

@Component({
    selector: 'mck-alert-banner',
    template: `
        <div class="alert-container {{mode}}">
            <div class="alert-icon-container {{mode}}">
                <img [src]="iconPath">
            </div>
            <div class="content-wrap">
                <div name="message" class="message {{mode}} {{display}}" [innerHTML]="message | sanitize"></div>
                <div class="actions-button-container {{display}}" *ngIf="buttonText">
                    <button name="action-button"
                            color="{{materialColor}}"
                            class="{{materialColor}} {{display}}"
                            [class.mck-stroked-button]="display === 'v2'"
                            [class.mck-short]="display === 'v2'"
                            type="button" mat-stroked-button
                            (click)="action.emit($event)">{{buttonText}}</button>
                </div>
            </div>
            <button
                *ngIf="showClose"
                mat-icon-button name="close-button" class="close-button"
                aria-label="Close"
                color="{{materialColor}}"
                (click)="close.emit($event)">
                <img [attr.src]="'assets/img/' + closeIcon">
            </button>
        </div>
    `,
    styleUrls: ['./alert-banner.component.scss']
})
export class AlertBannerComponent implements OnInit, OnChanges {

    @Input()
    public mode: AlertMode = AlertMode.Warning;

    @Output()
    public close = new EventEmitter<MouseEvent>();

    @Output()
    public action = new EventEmitter<MouseEvent>();

    @Input()
    public message: string;

    @Input()
    public buttonText: string;

    @Input()
    public showClose = true;

    @Input()
    public display: AlertBannerDisplay = AlertBannerDisplay.V1;

    public iconPath: string = 'assets/icon/warning-yellow.svg';
    public materialColor: string = 'black';
    public closeIcon: string = 'close_outline.svg';

    public ngOnInit(): void {
        this._onModeChange();
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.mode) {
            this._onModeChange();
        }
    }

    private _onModeChange() {
        this.iconPath = 'assets/icon/warning-yellow.svg';
        this.closeIcon = 'close_outline.svg';
        switch (this.mode) {
            case AlertMode.Warning:
                this.materialColor = 'black';
                break;
            case AlertMode.Coachmark:
                this.materialColor = 'coachmark';
                this.closeIcon = 'close_outline_coachmark.svg';
                break;
            case AlertMode.BlueCoachmark:
                this.iconPath = 'assets/icon/info-bg-dark.svg'
                this.materialColor = 'blue-coachmark'
                break;
            case AlertMode.Error:
                this.iconPath = 'assets/img/error-icon.svg'
                this.materialColor = 'white'
                break;
            default:
                this.materialColor = 'black';
        }
    }

}
