import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { VisitorsComponent } from './visitors.component';
import { SharedContentModule } from '../shared/shared.module';
import { VisitorTimelineComponent } from './timeline/timeline.component';
import { VisitorBookingItemComponent } from './timeline/item/item.component';
import { VisitorComponent } from './timeline/visitor/visitor.component';
import { VisitorsFormModalComponent } from './overlays/visitors-form-modal/visitors-form-modal.component';
import { EventFormComponent } from './overlays/visitors-form-modal/event-form/event-form.component';
import { VisitorFormComponent } from './overlays/visitors-form-modal/visitor-form/visitor-form.component';

const ROUTES: Route[] = [
    { path: '', component: VisitorsComponent },
    { path: ':level', component: VisitorsComponent },
    { path: '**', redirectTo: '' },
];

@NgModule({
    declarations: [
        VisitorsComponent,
        VisitorTimelineComponent,
        VisitorBookingItemComponent,
        VisitorComponent,
        VisitorsFormModalComponent,
        EventFormComponent,
        VisitorFormComponent,
    ],
    imports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,
        RouterModule.forChild(ROUTES),
        SharedContentModule,
    ],
})
export class VisitorsModule {}
