import { CateringGroupModalComponent } from './overlays/group-modal/group-modal.component';
import { CateringItemModalComponent } from './overlays/item-modal/item-modal.component';
import { CateringOrdersItemComponent } from './orders/item/item.component';
import { OrderDetailsModalComponent } from './overlays/order-details-modal/order-details-modal.component';
import { OrderDetailsItemsComponent } from './overlays/order-details-modal/items/items.component';
import { OrderBookingDetailsComponent } from './overlays/order-details-modal/booking-details/booking-details.component';
import { OrderDetailsNotesComponent } from './overlays/order-details-modal/messages/messages.component';
import { CateringStorage } from './catering.storage';
