import { DateObjectUnits, DateTime, DateTimeUnit, DurationLike, DurationUnits } from 'luxon';
import { getTimezoneOffset } from 'date-fns-tz';

function padZero(value: number, length: number): string {
    let str = `${value}`;
    while (str.length < length) str = `0${str}`;
    return str;
}

interface DateTZParams {
    date?: number | DateTime
    building_tz?: string
    is_local_tz?: boolean
    all_day?: boolean
}

const now = new Date()

export class DateTZ {
    date: DateTime
    date_org: DateTime
    building_tz: string
    is_local_tz: boolean = true
    all_day: boolean
    local_tz: string
    valid: boolean

    constructor(data?: DateTZParams) { 
        this.date_org = typeof data?.date === 'number' ? DateTime.fromMillis(data?.date) : data?.date ? data?.date : DateTime.fromMillis(now.valueOf())
        this.is_local_tz = data?.is_local_tz === false ? data?.is_local_tz : true
        this.all_day = data?.all_day || false
        this.local_tz = DateTime.now().zoneName
        this.building_tz = data?.building_tz || this.local_tz
        this.date = !this.is_local_tz ? this.date_org.setZone(this.building_tz) : this.date_org
        this.valid = this.date.isValid
    }

    //get epoch milliseconds
    public get ms(): number {
        return this.date.valueOf()
    }

    //get epoch seconds
    public get seconds(): number {
        return Math.trunc(this.date.toSeconds())
    }

    //get the second of the minute
    public get second(): number {
        return this.date.second
    }

    //get the minutes of the hour
    public get minutes(): number {
        return this.date.minute
    }

    //get the minutes of the hour
    public get hours(): number {
        return this.date.hour
    }

    public get utc(): number {
        const newDate = new DateTZ({...this, date: this.date.toUTC()})
        return newDate.seconds
    }

    public get dateDay(): number {
        return this.date.day
    }

    public get dateHour(): number {
        return this.date.hour
    }

    public get dateYear(): number {
        return this.date.year
    }

    public get dateWeekday(): number {
        try {
            return this.date.weekday;
        } catch (e) {
            return this.date.toJSDate().getDay();
        }
    }

    public get dateMonth(): number {
        return this.date.month
    }

    public dateDiff (compare_to: DateTZ, unit: DurationUnits, ans_unit = 'minutes'): number {
        return this.date.diff(compare_to.date, unit)[ans_unit]
    }

    //set the date to the begining of the given unit
    public startOfValue (unit: DateTimeUnit) {
        // All the luxon start of values work as needed except for week
        let newDate = new DateTZ({...this, date: this.date.startOf(unit)})
        // Special week logic
        if (unit === 'week') {
            if (this.dateWeekday === 7) {
                // Don't change, we want sunday as the start of the week even though luxon thinks it is Monday
                newDate =  new DateTZ({ ...this, date: this.date })
            } else if (this.dateWeekday === 1) {
                // If this.date is monday, just subtract one more day to make it Sunday
                const monday =  new DateTZ({ ...this, date: this.date })
                newDate = monday.subtractValue({ days: 1 });
            } else {
                // If later than monday, startOfWeek back to monday and then subtact another day to get to Sunday.
                const monday = new DateTZ({...this, date: this.date.startOf(unit)})
                newDate = monday.subtractValue({ days: 1 })
            }
        }

        return newDate
    }

    //set the date to the end of the given unit
    public endOfValue (unit: DateTimeUnit) : DateTZ {
        // All the luxon start of values work as needed except for week
        let newDate = new DateTZ({...this, date: this.date.startOf(unit)})
        // Special week logic
        if (unit === 'week') {
            if (this.dateWeekday === 6) {
                // Don't change, we want saturday as the end of the week even though luxon thinks it is Sunday
                newDate =  new DateTZ({ ...this, date: this.date })
            } else if (this.dateWeekday === 7) {
                // If this.date is Sunday, this alone is the luxon end of week so add a day
                // to get to monday, then endOfWeek to Sunday, and subtract 1 day to Saturday
                const sunday =  new DateTZ({ ...this, date: this.date }).addValue({ days: 1 })
                newDate = sunday.endOfValue(unit).subtractValue({ days: 1 }); // return next saturday
            } else {
                // If not saturday or sunday
                // endOfWeek to Sunday and then subtact a day to get to Saturday.
                const later = new DateTZ({...this, date: this.date.endOf(unit)})
                newDate = later.subtractValue({ days: 1 })
            }
        } else {
            newDate =  new DateTZ({...this, date: this.date.endOf(unit)})
        }

        return newDate
    }
    
    public startEpoch (): number {
        const org = new DateTZ({date: this.date_org})
        return this.all_day
        ? this.startOfValue('day').ms - this.dateDiff(org, 'minutes')
        : this.ms
    }

    //clone the given value and unit to the date
    public clone (): DateTZ {
        // return new DateTZ({...this})
        return this.addValue({ seconds: 0 })
    }

    //adds the given value and unit to the date
    public addValue (duration: DurationLike, isLastDayOfMonth?: boolean): DateTZ {
        return new DateTZ({...this, date: isLastDayOfMonth ? this.date.plus(duration).endOf('month') : this.date.plus(duration)});
    }

    //subtracts the given value and unit to the date
    public subtractValue (duration: DurationLike): DateTZ {
        const newDate = new DateTZ({...this, date: this.date.minus(duration)})
        return newDate
    }

    public setValue (obj: DateObjectUnits): DateTZ {
        const newDate = new DateTZ({...this, date: this.date.set(obj)})
        return newDate
    }

    public formatDate (format: string): string {
        // Return new immuatable via "plus" to prevent 
        // TypeError: Cannot assign to read only property 'weekData' of object
        // https://github.com/moment/luxon/issues/601
        return this.date.plus({ seconds: 0 }).toFormat(format)
    }

    public formatLocalDate (format: string): string {
        // setZone already returns new immutable so it doesn't have the formatDate issue.
        return this.date.plus({ seconds: 0 }).setZone(this.local_tz).toFormat(format)
    }

    public isSameDate(compare_to: DateTZ, value: DateTimeUnit): boolean {
        return this.date.hasSame(compare_to.date, value)
    }

    public isAfterDate(compare_to: DateTZ, unit?: DateTimeUnit): boolean {
        switch (unit) {
            case 'year':
                return this.dateYear > compare_to.dateYear
            case 'month':
                if(this.isAfterDate(compare_to, 'year')){
                    return true
                }else if(!this.isSameDate(compare_to, 'year')){
                    return false
                }else{
                    return this.dateMonth > compare_to.dateMonth
                }
            case 'day':
                if(this.isAfterDate(compare_to, 'month')){
                    return true
                }else if(!this.isSameDate(compare_to, 'month')){
                    return false
                }else{
                    return this.dateDay > compare_to.dateDay
                }
            case 'hour':
                if(this.isAfterDate(compare_to, 'day')){
                    return true
                }else if(!this.isSameDate(compare_to, 'day')){
                    return false
                }else{
                    return this.dateHour > compare_to.dateHour
                }
            case 'minute':
                if(this.isAfterDate(compare_to, 'hour')){
                    return true
                }else if(!this.isSameDate(compare_to, 'hour')){
                    return false
                }else{
                    return this.minutes > compare_to.minutes
                }
            default:
                return this.ms > compare_to.ms;
        }
    }

    public isBeforeDate(compare_to: DateTZ, unit?: DateTimeUnit): boolean {
        switch (unit) {
            case 'year':
                return this.dateYear < compare_to.dateYear
            case 'month':
                if(this.isBeforeDate(compare_to, 'year')){
                    return true
                }else if(!this.isSameDate(compare_to, 'year')){
                    return false
                }else{
                    return this.dateMonth < compare_to.dateMonth
                }
            case 'day':
                if(this.isBeforeDate(compare_to, 'month')){
                    return true
                }else if(!this.isSameDate(compare_to, 'month')){
                    return false
                }else{
                    return this.dateDay < compare_to.dateDay
                }
            case 'hour':
                if(this.isBeforeDate(compare_to, 'day')){
                    return true
                }else if(!this.isSameDate(compare_to, 'day')){
                    return false
                }else{
                    return this.dateHour < compare_to.dateHour
                }
            case 'minute':
                if(this.isBeforeDate(compare_to, 'hour')){
                    return true
                }else if(!this.isSameDate(compare_to, 'hour')){
                    return false
                }else{
                    return this.minutes < compare_to.minutes
                }
            default:
                return this.ms < compare_to.ms;
        }
    }

    public toZone(tz: string): DateTZ {
        const newDate = new DateTZ({
            ...this, 
            date: this.date.setZone(tz), 
            building_tz: tz, 
            is_local_tz: false
        })
        return newDate
    }

    public setTimestamp(timestamp: number): DateTZ {
        const newDate = new DateTZ({
            ...this, 
            date: timestamp,
        })
        return newDate
    }

    public get JSDate(): Date {
        return this.date.toJSDate()
    }

    // helper to log out the dateTZ values
    public logDateTz (label: string = ''): void {
        console.log(`${label} local tz/date/time`, this.addValue({ seconds: 0 }).formatLocalDate("cccc d MMMM,yyyy h:mma"), this.local_tz)
        console.log(`${label} building tz/date/time`, this.addValue({ seconds: 0 }).formatDate("cccc d MMMM,yyyy h:mma"), this.building_tz)
    }
}

export const DateNow = (date: Date = new Date()) => {
    return new DateTZ({date: date.valueOf()})
} 

// input the local time and the building timezone
export const convertLocalTimestampToTimezonedDateTz = (timestamp: number, buildingTimezone: string): DateTZ => {
    const date = new Date(timestamp);
    return new DateTZ({ date: timestamp, building_tz: buildingTimezone, is_local_tz: false }).setValue({
        day: date.getDate(),
        hour: date.getHours(),
        minute: date.getMinutes()
    });
}

// input the building time and the building timezone
export const convertTimezonedTimestampToLocalDateTz = (timestamp: number, buildingTimezone: string): DateTZ => {
    const local_tz = DateTime.now().zoneName;
    const localOffset = getTimezoneOffset(local_tz, new Date(timestamp));
    const desiredOffset = getTimezoneOffset(buildingTimezone, new Date(timestamp));
    if (localOffset > desiredOffset) {
        const eleA = timestamp + (localOffset - desiredOffset);
        return  new DateTZ({date: eleA, building_tz: buildingTimezone, is_local_tz: false});
    } else {
        const eleA = timestamp + (desiredOffset - localOffset);
        return  new DateTZ({date: eleA, building_tz: buildingTimezone, is_local_tz: false});
    }
}

export const localTimezone = () => {
    return Intl.DateTimeFormat().resolvedOptions().timeZone;
}

export const getTimezoneOffsetString = (tz: string) => {
    const offset_minutes = Math.floor(getTimezoneOffset(tz) / 1000 / 60);
    const hours = Math.floor(offset_minutes/60);
    const minutes = offset_minutes % 60;
    return `${offset_minutes > 0 ? '+' : '-'}${padZero(Math.abs(hours), 2)}${padZero(minutes, 2)}`;
}

export function toMiddayUTC(value: DateTZ = DateNow()) {
    // exclude Auckland from rouding up timezone as it has 12+ hours UTC timezone difference
    return value.building_tz.includes('Auckland') ? value :  new DateTZ({
        date: value.setValue({ hour: 12, minute: 0 }).ms,
        is_local_tz: false,
        building_tz: 'UTC',
    }).setValue({ hour: 12, minute: 0 });
}

/**returns the offset between two timezones in minutes and hours */
export function timezoneOffset(tz: string){
    const nowTz = DateNow(new Date())
    const offset = nowTz.date.offset - nowTz.toZone(tz).date.offset
    return {minutes: offset, hours: offset/60}
}

export function isToday (date, timezone) {

    return new DateTZ({
        date: toMiddayUTC(date).ms,
        is_local_tz: false,
        building_tz: timezone
    }).isSameDate(
        new DateTZ({
            date: Date.now(),
            is_local_tz: false,
            building_tz: timezone,
        }),
        'day'
    );
}
