    /** List of timeblocks to display */
    public time_blocks: Identity[] = this.generateTimeBlocks();

    /** Generate time blocks to display on the day view */
    private generateTimeBlocks(): Identity[] {
        const blocks: Identity[] = [];
        const date = DateNow(new Date()).startOfValue('hour');
        for (let i = 0; i < 24; i++) {
            blocks.push({
                id: date.setValue({ hour: i }).formatDate('HH:mm'),
                name: date.setValue({ hour: i }).formatDate('h:mma').toLocaleLowerCase(),
            });
        }
        return blocks;
    }
    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _booking_state_service: BookingStateService,
        private _bookings: BookingsService,
        private _dialog: MatDialog,
        private cdr: ChangeDetectorRef,
        private _org: OrganisationService,
    ) {
        super();
    }

    /** Events to display for the active date */
    public  events: Booking[]  = []

    public ngOnInit(): void {
        this.events = this.filterEvents();
        this._service.initialised.pipe(first((_) => _)).subscribe(() => {
            this.settings = this._service.setting('app.day_view') || {};
            this.subscription(
                'breakdown',
                this._service
                    .listen('APP.breakdown')
                    .subscribe((state) => {
                        this.overflow_only = state;
                        this.events = [...this.filterEvents()];
                    })
            );
            this.subscription(
                'legend',
                this._service
                    .listen('CONCIERGE.legend')
                    .subscribe((state) => (this.legend = state || {}))
            );
            this.subscription(
                'bookings',
                this._booking_state_service.filtered.subscribe((list) => {
                    this.event_list = [...list];
                    this.events = [...this.filterEvents()];
                    this.cdr.detectChanges();
                })
            );
            this.checkInView();
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.scroll) {
            this.checkInView();
        }
        if (changes.space) {
            this.event_list = [...this.event_list];
            this.timeout('in_view', () => this.checkInView(), 100);
        }
        /* istanbul ignore else */
        if (changes.last_update) {
            this.timeout('update_overlaps', () => this.calculateOverlaps(), 1000);
        }
    }

