import {Component, OnInit, Inject, Output, EventEmitter} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {MatDialog, MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {first} from 'rxjs/operators';
import {BaseDirective, closestToTimeSlotTz} from '@mckinsey-converge/base';
import {
  Booking,
  BookingAction,
  generateBookingForm, OrganisationService, RecurrencePeriod, removeExpiredOcurrences, SeriesAction, validDateTZ
} from '@mckinsey-converge/data-common';
import {AvailableBookingFields} from '@mckinsey-converge/data-common';
import {ApplicationService} from '@mckinsey-converge/data-common';
import {HashMap, DialogEvent} from '@mckinsey-converge/base';
import {CateringDetailsModalComponent, CateringDetailsModalData} from '@mckinsey-converge/legacy-overlays';
import {BookingConfirmComponent} from '@mckinsey-converge/legacy-overlays';
import {RequirementDetailsModalComponent, RequirementDetailsModalData} from '@mckinsey-converge/legacy-overlays';
import {SpacesService} from '@mckinsey-converge/data-common';
import { ConflictsModalComponent, ConflictsModalComponentData, OverrideChangesModalComponent } from '@mckinsey-converge/ui';
import { DateNow, DateTZ } from "@mckinsey-converge/date-tz";
import { I } from '@angular/cdk/keycodes';


export interface BookingFlowEvent {
  /** Type of flow event */
  type: 'previous' | 'next' | 'reset' | 'close' | 'error';
  /** Step which the event was emitted from */
  step: string;
  /** Additional metadata for the event */
  metadata?: HashMap;
}

export interface BookingModalData {
  /** Booking to create/edit */
  booking: Booking;
  /** Whether to edit only catering */
  only_catering?: boolean;
  /** Pass in timezone for new bookings */
  timezone?: string;
  /**Type of action on a series */
  action?: SeriesAction | BookingAction;
  //** If Booking request from day view grid click */
  isFromGrid?: boolean;
}

@Component({
  selector: 'a-booking-modal',
  templateUrl: './booking-modal.component.html',
  styleUrls: ['./booking-modal.component.scss'],
})
export class BookingModalComponent extends BaseDirective implements OnInit {

  /** Range of dates for the Start Date Picker*/
  public from_start: DateTZ;
  public to: DateTZ;

  /** Range of dates for the Start Date Picker*/
  public from_end: DateTZ;

  /** Form fields */
  public form: FormGroup;
  /** Form data store */
  public booking: Booking;
  /** Step of the booking flow that is currently active */
  public flow_step: string;
  public modalTitle: string;

  public iscloning: SeriesAction | BookingAction | null = null;

  public edit_recurrence: boolean

  /** Whether to edit only catering */
  public get only_catering(): boolean {
    return !!this._data.only_catering;
  }

  @Output() public event = new EventEmitter<DialogEvent>();

  /** List of available booking fields for the form */
  public get available_fields(): AvailableBookingFields[] {
    return this._service.setting('app.booking.show_fields') || [];
  }

  constructor(
    private _service: ApplicationService,
    private _spaces: SpacesService,
    private _org: OrganisationService,
    private _dialog: MatDialog,
    private _dialog_ref: MatDialogRef<BookingModalComponent>,
    @Inject(MAT_DIALOG_DATA) private _data: BookingModalData
  ) {
    super();
  }

  public ngOnInit(): void {
    this.getModalTitle();
    this._dialog_ref.disableClose = true;
    this._spaces.initialised.pipe(first(_ => _)).subscribe(() => {
      this.flow_step = this._data.only_catering ? 'catering' : 'form';
      this.loadFormData();
    });
  }

  public getModalTitle() {
    switch (this._data.action) {
      case BookingAction.EDIT:
        this.modalTitle = 'Edit Booking';
        break;
      case SeriesAction.CLONE:
        this.modalTitle = 'Clone Recurring Series';
        break;
      case BookingAction.CLONE:
        this.modalTitle = 'Clone Booking';
        break;
      case SeriesAction.EDIT:
        this.modalTitle = 'Edit Series';
        break;
      default:
        this.modalTitle = 'New Booking';
        break;
    }
  }

  /**
   * Handle events from child components
   * @param event Booking flow event
   */
  public handleFlowEvent(event: BookingFlowEvent) {
    /* istanbul ignore else */
    if (event.type === 'next') {
      this.toNextFlowStep(event);
    } else if (event.type === 'previous') {
      this.toPreviousFlowStep(event);
    } else if (event.type === 'reset') {
      this.resetForm();
    }
  }

  public close() {
    this._dialog_ref.close();
  }


  private toNextFlowStep(event: BookingFlowEvent) {
    const space_list = this.form.controls.space_list.value;
    switch (event.step) {
      case 'recurrence':
        this.openRecurrenceConflictsModal();
        break
      case 'search':
        if (
          space_list &&
          space_list.length &&
          (!event.metadata || !event.metadata.ref || event.metadata.ref !== 'equipment')
        ) {
          this.openEquipmentDetailsModal();
        } else {
          if (this.form.controls.has_catering && this.form.controls.has_catering.value) {
            this.navigate('catering');
          } else {
            this.navigate('form');
            this.confirmBooking();
          }
        }
        break;
      case 'catering':
        const order_list = this.form.controls.catering.value;
        if (
          order_list.find(order => this.form.controls.space_list.value.find(space => space.email === order.location_id)) &&
          order_list &&
          order_list.length &&
          (!event.metadata || !event.metadata.ref || event.metadata.ref !== 'details')
        ) {
          this.openCateringDetailsModal();
        } else {
          this.navigate('form');
          this.confirmBooking();
        }
        break;
      case 'changes':
        this.overrideChanges();
      break;
      default:
        this.form.markAsDirty();
        //const is_recurr_series_edit = this.booking.id && this.form.controls.is_recurrent.value && this._data.action
        const is_recurr_series_edit = this.booking.id && (
          this._data.action === SeriesAction.CLONE || this._data.action === SeriesAction.EDIT || this._data.action === BookingAction.CLONE
        )
        if (!space_list || !space_list.length || !this.booking.id || is_recurr_series_edit || this.booking.is_multiroom) {
          this.navigate('search');
        } else if (this.booking.id && space_list && space_list.length) {
          this.openEquipmentDetailsModal();
        } else {
          this.confirmBooking();
        }
        break;
    }
  }

  private toPreviousFlowStep(event: BookingFlowEvent) {
    switch (event.step) {
      case 'search':
        this.navigate('form');
        break;
      case 'catering':
        /* istanbul ignore else */
        if (!this._data.only_catering) {
          this.navigate('search');
        }
        else {
          this.close();
        }
        break;
    }
  }

  private navigate(route: string) {
    this.flow_step = route;
  }

  /** Clear user changes to the form data */
  private resetForm() {
    delete this.form;
    // Pass in the desired location timezone
    this.form = generateBookingForm(
      this.booking,
      this.available_fields,
      true,
      this._data?.timezone,
      this._data.action
    );

    this.form.addControl('action', new FormControl(this._data.action))
  }

  /**
   * Load existing form data or generate a new booking form
   *
   * It fires when the modal first opens.
   * this._data.booking is an empty {} when creating a booking but set when editing
   */
  private loadFormData(): void {
    const form_data: HashMap = this._data.booking ? this._data.booking.toJSON() : {};
    form_data.duration = form_data.duration || form_data?.startDateTz?.dateDiff(form_data?.endDateTz, 'minutes') || 0
    //
    /**
     * Set the desired timezone in the booking creation.
     * this._data?.timezone is when creating a new booking
     *  this._data.booking?.timezone is when editing a booking
     *
     * R-- refactor to make consistent atm _data.timezone is defined everywhere
     * but generateBookingForm could use form_data.timezone
     */
    form_data.timezone = this._data.booking?.timezone || this._data?.timezone;
    // start a new booking out with a start end time.

    const is_cloning_or_edit = this._data.action === SeriesAction.CLONE ||
      this._data.action === BookingAction.CLONE ||
      this._data.action === BookingAction.EDIT ||
      this._data.action === SeriesAction.EDIT;

    if (
        ( !this._data?.isFromGrid && ( !form_data.startDateTz || form_data.startDateTz.isBeforeDate(DateNow()) ) )
        && !this._data.only_catering && !is_cloning_or_edit
      ) {
      form_data.startDateTz = closestToTimeSlotTz(new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: form_data.timezone }), 15);
      form_data.start_epoch = form_data.startDateTz.seconds;
      form_data.endDateTz = form_data.startDateTz.addValue({ minutes: form_data.duration });
      form_data.end_epoch = form_data.endDateTz.seconds;
    }
    this.booking = new Booking(form_data);

    if(this.booking.is_recurrent && (
        this._data.action === SeriesAction.CLONE || this._data.action === SeriesAction.EDIT
    )){ //if it's series get the recurrence_end from the occurences
      this.booking.recurrence_endTz = this.booking.recurrenEndFromOcurrences(this.booking.occurrence_details)
    }


    // An empty booking here is when creating a new booking, edit booking will populate all the values
    this.form = generateBookingForm(
      this.booking,
      this.available_fields,
      true,
      this._data?.timezone, // MUST be defined in dialog.open(BookingModalComponent or booking will revert to a local timezone
      this._data.action
    );

    this.form.addControl('action', new FormControl(this._data.action))

    // localize the booking form start time
    /* istanbul ignore else */
    if (form_data && form_data.room_ids && this.form.controls.space_list) {
      this.form.controls.space_list.setValue(
        form_data.room_ids.map(id => this._spaces.find(id) || {id, name: id})
      );
    }

    this.edit_recurrence = this.booking?.id
      && (this.form.controls.action?.value === SeriesAction.EDIT || this.form.controls.action?.value === SeriesAction.CLONE);

    if (this._data.action === SeriesAction.CLONE || this._data.action === BookingAction.CLONE) {

      this.iscloning = this._data.action === SeriesAction.CLONE ? SeriesAction.CLONE : BookingAction.CLONE;

      this._data.action === BookingAction.CLONE
        ? this.form.get('title').setValue(`Clone ${this.booking.title}`)
        : this.form.get('title').setValue(`Series Clone ${this.booking.title}`);

      this.form.get('id').setValue('');
      this.form.get('organiser').enable();

      if (this._data.action === BookingAction.CLONE) {
        this.form.get('is_recurrent').setValue(false);
        this.form.get('is_recurrent').disable();
      }
      const tz = this._org.building.timezone;
      this.form.patchValue({ timezone: tz });

      // Check if the booking is a list, if so then updates the value for start and end date fields.
      if (form_data.recurrence_period === RecurrencePeriod.LIST) {
        removeExpiredOcurrences(this.form)
      }

      // cloning individual occurrence set tup
      const start_time = form_data.recurrence_period === RecurrencePeriod.LIST ? this.form.controls.dateTz.value : new DateTZ({ date: this.booking.start_epoch * 1000, is_local_tz: false, building_tz: tz });
      const date_now = DateNow(new Date()).toZone(tz);

      // case 1: past booking
      if (start_time.isBeforeDate(date_now, "day")) {
        if (form_data.recurrence_period !== RecurrencePeriod.LIST) {
          this.form.controls.dateTz.setValidators([
            Validators.required,
            validDateTZ
          ]);
          this.form.controls.dateTz.updateValueAndValidity();
          this.form.controls.dateTz.markAsTouched();
          this.form.controls.is_before_date.setValue(true);

          if(this._data.action === BookingAction.CLONE && this.form.controls.all_day.value === true) {
            this.form.get('dateTz').enable();
          }
        }
      }

      // case 2: booking is in the same day but but the start time has already passed
      if (
        start_time.isSameDate(date_now, "day") && start_time.startEpoch() < date_now.startEpoch()
      ) {
        this.form.controls.dateTz.setValue(closestToTimeSlotTz(date_now, 15));
      }

      // case 3: future booking nothing to do
      //Nothing to do
    }

    if (
      this.form.controls.action?.value === SeriesAction.EDIT || this.form.controls.action?.value === BookingAction.EDIT
    ) {
      const tz = this._org.building.timezone;
      const start_time = form_data.recurrence_period === RecurrencePeriod.LIST ? this.form.controls.dateTz.value : new DateTZ({ date: this.booking.start_epoch * 1000, is_local_tz: false, building_tz: tz });
      const date_now = DateNow(new Date()).toZone(tz);

      if (
        start_time.isSameDate(date_now, "day") && (
          start_time.startEpoch() <= date_now.startEpoch() &&
          start_time.addValue({ minutes: this.booking.duration}).startEpoch() >= date_now.startEpoch())
       ) {
        this.form.controls.is_before_date.setValue(true);
      }

      if (start_time.isSameDate(date_now, "day") && start_time.addValue({ minutes: this.booking.duration}).startEpoch() < date_now.startEpoch()) {
        this.form.controls.dateTz.setValue(closestToTimeSlotTz(date_now, 15));
      }
    }

    if (!this.booking.catering.length) {
      const notes = this.booking.notes.filter(note => note.type !== 'catering');

      this.form.get('notes').setValue(notes);
    }
  }

  /** Open modal for adding equipment requirements to booking */
  private openEquipmentDetailsModal() {
    const equipment_ref = this._dialog.open<RequirementDetailsModalComponent,
      RequirementDetailsModalData>(RequirementDetailsModalComponent, {
      maxWidth: '95vw',
      width: '32em',
      data: {
        spaces: this.form.controls.space_list.value,
        notes_field: this.form.controls.notes,
        codes_field: this.form.controls.equipment_codes,
        head_counts_field: this.form.controls.expected_attendees
      }
    });
    this.subscription(
      'equipments_modal_events',
      equipment_ref.componentInstance.event.subscribe((event: DialogEvent) => {
        /* istanbul ignore else */
        if (event.reason === 'done') {
          this.toNextFlowStep({
            type: 'next',
            step: 'search',
            metadata: {ref: 'equipment'}
          });
          equipment_ref.close();
        }
      })
    );
    equipment_ref.afterClosed().subscribe(() => this.unsub('equipments_modal_events'));
  }

  /** Open modal for adding equipment requirements to booking */
  private openCateringDetailsModal() {
    const catering_ref = this._dialog.open<CateringDetailsModalComponent, CateringDetailsModalData>(
      CateringDetailsModalComponent,
      {
        maxWidth: '95vw',
        width: '32em',
        data: {
          spaces: this.form.controls.space_list.value,
          notes_field: this.form.controls.notes,
          catering: this.form.controls.catering,
          booking: this.booking
        }
      }
    );
    this.subscription(
      'catering_modal_events',
      catering_ref.componentInstance.event.subscribe((event: DialogEvent) => {
        /* istanbul ignore else */
        if (event.reason === 'done') {
          this.toNextFlowStep({
            type: 'next',
            step: 'catering',
            metadata: { ref: 'details' }
          });
          catering_ref.close();
        }
      })
    );
    catering_ref.afterClosed().subscribe(() => this.unsub('catering_modal_events'));
  }

  /*
    Open overriding changes modal
  */
  private overrideChanges() {
    const ref = this._dialog.open<OverrideChangesModalComponent>(OverrideChangesModalComponent, {
        width: '576px',
        data: {
          edits: this.booking.occurrence_edits,
          timezone: this.booking.timezone
        }
    });
    ref.componentInstance.event.subscribe(event => {
      if (event.reason === 'done') {
        this.toNextFlowStep({
          type: 'next',
          step: 'search',
        });
        ref.close();
      } else if (event.reason === 'close') {
        ref.close();
      }
    });
  }

  /**
   * Open modal to confirm the booking details
   */
  private confirmBooking() {
    /* Clear recurrence validator if only catering is being edited */
    if(this.only_catering){
      this.clearRecurrenceValidators()
    }

    /* istanbul ignore else */
    if (this.form.valid || this.only_catering) {
      const formValues = this.form.value;
      // this.booking is the booking being edited, not much to it on a new booking
      // having an empty booking is used in determining changes I assume.
      const startDateTz = this.form.controls.dateTz.value
      const endDateTz = startDateTz.addValue({ minutes: formValues.duration })
      const booking = {
        ...(
          this._data.action === BookingAction.CLONE || this._data.action === BookingAction.EDIT || this._data.only_catering ? this.sanitizeBookingFromCloning(this.booking) : this.booking.toJSON()),
        ...(
          this._data.action === BookingAction.CLONE || this._data.action === BookingAction.EDIT  || this._data.only_catering ? this.sanitizeBookingFromCloning(formValues) : formValues),
        startDateTz,
        endDateTz,
        start_epoch: startDateTz.seconds,
        end_epoch: endDateTz.seconds,
        room: formValues.space_list[0],
        multi_rooms: formValues.space_list.map(room => room),
      };

      const dialog_ref = this._dialog.open(BookingConfirmComponent, {
        width: '32em',
        maxWidth: '95vw',
        maxHeight: '95vh',
        data: {
          old_booking: this.booking,
          booking: new Booking(booking),
          action: this._data.action
        },
        autoFocus: false,
        backdropClass: 'mck-modal-backdrop',
      });
      dialog_ref.componentInstance.event.subscribe(event => {
        /* istanbul ignore else */
        if (event.reason === 'done') {
          this.close();
          this.event.emit({ reason: 'done', metadata: this.form});
        } else if (event.reason === 'action') {
          this.resetForm();
        }
        else if(event.reason === 'last-moment-conflict') {
          this.form.patchValue({space_list: []}, {emitEvent: true});
          // this.handleFlowEvent({ type: 'reset', step: 'search' })
          dialog_ref.close();
        }
      });
    }
  }

  private sanitizeBookingFromCloning(booking: Booking | Object): Object {
    const obj = booking instanceof Booking ? booking.toJSON() : booking;
    return {...Object.keys(obj).reduce((acc, key) => {
          if(
            key.search(/^recurrence_/) === -1
            && key.search(/^occurrence_/) === -1
          ) {
            acc[key] = obj[key]
          };

          return acc;
      }, {}),
      is_recurrent: false
    }
  }

  /** Open modal for adding equipment requirements to booking */
  private openRecurrenceConflictsModal() {
    const recurr_ref = this._dialog.open<ConflictsModalComponent,
      ConflictsModalComponentData>(ConflictsModalComponent, {
      maxWidth: '95vw',
      width: '32em',
      data: {
        room_name: this.form.controls.space_list.value[0].name,
        conflicts: this.form.controls.recurrence_exceptions.value,
        timezone: this.booking.timezone
      }
    });
    this.subscription(
      'conflicts_modal_events',
      recurr_ref.componentInstance.event.subscribe((event: DialogEvent) => {
        if(this.booking.occurrence_edits && this.booking.occurrence_edits.length && event.reason === 'done') {
          this.toNextFlowStep({
            type: 'next',
            step: 'changes',
          });
        } else {
          /* istanbul ignore else */
          if (event.reason === 'done') {
            this.toNextFlowStep({
              type: 'next',
              step: 'search',
            });
          }
        }
        recurr_ref.close();
      })
    );
    recurr_ref.afterClosed().subscribe(() => this.unsub('conflicts_modal_events'));
  }

  public get title(){
    const type = this.booking?.id ? 'Edit' : 'New'
    return `${type} ${this.edit_recurrence ? 'recurring' : ''} booking`
  }

   private clearRecurrenceValidators() {
      const recurrenceStarts = this.form.controls.recurrence_starts
      recurrenceStarts.clearValidators()
      recurrenceStarts.updateValueAndValidity()
  }
}
