import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Route } from '@angular/router';

import { DayViewComponent } from './day-view.component';
import { DayViewTimelineComponent } from './timeline/timeline.component';
import { DayViewApprovalsComponent } from './approvals-view/approvals-view.component';
import { DayViewApprovalsEventComponent } from './approvals-view/event/event.component';
import { DayViewSpaceComponent } from './space/space.component';
import { DayViewSpaceEventComponent } from './space/event/event.component';

import { SharedContentModule } from 'src/app/shared/shared.module';
import { FormsModule } from '@angular/forms';
import { UiModule } from '@mckinsey-converge/ui';

const ROUTES: Route[] = [
    { path: '', component: DayViewComponent },
    { path: ':level', component: DayViewComponent },
    { path: '**', redirectTo: '' }
];

@NgModule({
    declarations: [
        DayViewComponent,
        DayViewTimelineComponent,
        DayViewApprovalsComponent,
        DayViewApprovalsEventComponent,
        DayViewSpaceComponent,
        DayViewSpaceEventComponent
    ],
    imports: [CommonModule, FormsModule, RouterModule.forChild(ROUTES), SharedContentModule, UiModule]
})
export class DayViewModule {}
