import {
    Component,
    Input
} from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'mck-modal',
    template: `
        <mat-dialog-content class="mck-column modal-container">
            <div class="mck-column modal-top mck-content-expand-width mck-stretch">
                <div class="button-row">
                    <button *ngIf="shouldClose" name="CloseButton" mat-icon-button (click)="close()">
                        <div class="close-image"></div>
                    </button>
                </div>
                <div class="mck-column mck-center">
                    <h1 name="ModalHeader" class="modal-header" *ngIf="header">{{header}}</h1>
                </div>
            </div>
            <ng-content></ng-content>
        </mat-dialog-content>
    `,
    styleUrls: ['./modal.component.scss']
})
export class ModalComponent {

    @Input()
    header?: string;
    @Input() shouldClose?: boolean = true;

    constructor(private dialogRef: MatDialogRef<ModalComponent>) {
    }

    public close() {
        this.dialogRef.close();
    }
}
