import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { BaseDirective } from '@mckinsey-converge/base';

import { Booking, BookingStateService, SeriesAction, RecurrencePeriod, BookingsService } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ } from "@mckinsey-converge/date-tz"
import { BookingModalComponent } from 'src/app/bookings/booking-modal/booking-modal.component';

@Component({
    selector: 'meeting-recurring-details',
    templateUrl: './recurring-details.component.html',
    styleUrls: ['./recurring-details.component.scss'],
})
export class MeetingRecurringDetailsDisplayComponent extends BaseDirective implements OnInit {
    /** Booking to display details */
    @Input() public booking: Booking;
    /** helper for setLoading state of the parent modal */
    @Output() public confirmDelete: EventEmitter<{ series?: boolean}> = new EventEmitter<{}>();

    /** Whether the meeting is approved or not */
    public isApproved = false;
    
    private _recurrence_endTz: DateTZ;
    private _recurrence_startTz: DateTZ;
    private _recurrence_period: RecurrencePeriod;
    private _recurrence_interval: number;
    private _occurrence_details: { id: string, start_epoch: number, dateTz?: DateTZ }[];
    private _specific_dates: boolean = false;
    private _master_booking: Booking;
    public expired_ocurrences: Array<Booking> = [];

    constructor(
        private _booking_state: BookingStateService,
        private _dialog: MatDialog,
        private _bookings: BookingsService
    ) {
        super()
    }

    public ngOnInit(): void {
        this.subscription(
            'detail_bookings',
            this._booking_state.bookings.subscribe(async (bookings) => {

                const booking = bookings.find(bkn => { 
                    if(this.booking.recurrence_type === 'master')
                     return bkn.recurrence_master_id === this.booking.icaluid;
                    else 
                        return bkn.icaluid === this.booking.icaluid ;
                }) || this.booking;
                // if (!booking && this.booking.recurrence_type !== 'master') {
                //     return;
                // }
                

                this.master_booking = ( this.booking.recurrence_type === 'occurrence' && booking
                    ? bookings.filter(bkg => bkg.id === booking.recurrence_master_id)
                    : this.booking.recurrence_type === 'master' ? [this.booking] 
                    : booking 
                    ? [booking]
                    : []
                )?.shift();

                if (!this.master_booking) {
                    this.master_booking = await this._bookings.show(booking.recurrence_master_id);
                    this._booking_state.add(this.master_booking);
                }

                this.recurrence_endTz = this.master_booking.recurrenEndFromOcurrences(this.master_booking.occurrence_details);
                this.recurrence_startTz = new DateTZ({date: this.master_booking.start_epoch * 1000, is_local_tz: false, building_tz: this.master_booking.timezone});
                this.recurrence_period = this.master_booking.recurrence_period;
                this.recurrence_interval = this.master_booking.recurrence_interval;
                this.occurrence_details = this.master_booking.occurrence_details && this.master_booking.occurrence_details
                    .filter(ocurrence => ocurrence.id !== this.master_booking.id)
                    .map(ocurrence => ({ ...ocurrence, start_epoch:  ocurrence.start_epoch * 1000, dateTz: new DateTZ({ date: ocurrence.start_epoch * 1000, is_local_tz: false, building_tz: this.master_booking.timezone }) }));
                const occurrence_ids = this.master_booking?.occurrence_details.map(ocurr => ocurr.id) || []
                this.expired_ocurrences = bookings.filter(booking => {
                    if(occurrence_ids.includes(booking.id)){
                        const building_tz = this.master_booking.timezone
                        const date = (booking.start_epoch * 1000) + (booking.duration * 60000)
                        const start = new DateTZ({date, is_local_tz: false, building_tz})
                        const now = DateNow(new Date()).toZone(building_tz)
                        return now.isAfterDate(start, 'minute')
                    }
                    return false
                })
            })
        );
        this.isApproved = this.booking.status === 'accepted' || this.booking.status === 'approved';
    }

    public set recurrence_startTz(date: DateTZ)  {
        this._recurrence_startTz = date
    }

    public get recurrence_startTz():DateTZ  {
        return this._recurrence_startTz
    }

    public set recurrence_endTz(date: DateTZ)  {
        this._recurrence_endTz = date
    }

    public get recurrence_endTz(): DateTZ  {
        return this._recurrence_endTz
    }

    public set recurrence_period(period: RecurrencePeriod)  {
        this._recurrence_period = period;
    }

    public get recurrence_period():RecurrencePeriod  {
        return this._recurrence_period;
    }

    public set recurrence_interval(interval: number)  {
        this._recurrence_interval = interval;
    }

    public get recurrence_interval():number  {
        return this._recurrence_interval;
    }

    public set occurrence_details(occurrences: { id: string, start_epoch: number }[]) {
        this._occurrence_details = occurrences;
    }

    public get occurrence_details(): { id: string, start_epoch: number }[]  {
        return this._occurrence_details;
    }

    public set specific_dates(val: boolean) {
        this._specific_dates = val;
    }

    public get specific_dates(): boolean {
        return this._specific_dates;
    }

    public set master_booking(bkng: Booking)  {
        this._master_booking = bkng;
    }

    public get master_booking():Booking  {
        return this._master_booking;
    }

    public toggleSpecificDates() {
        this.specific_dates = !this.specific_dates;
    }

    public async editSeries() {
        this._dialog.open(BookingModalComponent, {
            data: {
                booking: this.master_booking.duplicate(true), //we want to send the master booking since it's the one with all of the series data
                action: SeriesAction.EDIT,
                timezone: localStorage.getItem('CONCIERGE.timezone')
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });  
    }

    public async cloneSeries() {
        this._dialog.open(BookingModalComponent, {
            data: {
                booking: this.master_booking.duplicate(),
                action: SeriesAction.CLONE
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
    }

    public async cancelSeries() {
        this.confirmDelete.emit({ series: true });
    }
}
