import {
    Component,
    EventEmitter,
    Inject,
    Output
} from '@angular/core';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';

import { ApplicationIcon } from '@mckinsey-converge/data-common';
import {
    BaseDirective,
    DialogEvent
} from '@mckinsey-converge/base';

export interface ConfirmModalData {
    /** Title of the modal */
    title: string;
    /** Contents of the modal */
    content: string;
    /** Text displaed on the confirmation button */
    action?: string;
    /** Icon to display on the modal */
    icon: ApplicationIcon;
}

export const CONFIRM_METADATA = {
    height: 'auto',
    width: '24em',
    maxHeight: 'calc(100vh - 2em)',
    maxWidth: 'calc(100vw - 2em)'
};

@Component({
    selector: 'confirm-modal',
    templateUrl: './confirm-modal.component.html',
    styleUrls: ['./confirm-modal.component.scss']
})
export class ConfirmModalComponent extends BaseDirective {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Loading state */
    public loading: string;

    /** Title of the confirm modal */
    public get title(): string {
        return (this._data ? this._data.title : null) || 'Confirm';
    }

    /** Body of the confirm modal */
    public get content(): string {
        return (this._data ? this._data.content : null) || 'Are you sure?';
    }

    /** Display text on the confirm button */
    public get action(): string {
        return (this._data ? this._data.action : null) || 'Ok';
    }

    /** Display icon properties */
    public get icon(): ApplicationIcon {
        return (this._data ? this._data.icon : null) || null;
    }

    constructor(
        private _dialog: MatDialogRef<ConfirmModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: ConfirmModalData
    ) {
        super();
    }

    /**
     * User confirmation of the content of the modal
     */
    public accept() {
        this.event.emit({ reason: 'done' });
    }
}
