import {
    Component,
    EventEmitter,
    Input,
    Output,
    ViewContainerRef
} from '@angular/core';
import { BaseTemplateComponent } from '../base-template.component';

@Component({
    selector: 'mck-modal-button-row',
    template: `
        <ng-template>
            <div
                class="button-wrapper"
                [class.has-fixed]="!disableFixed">
                <mck-spacer
                    [autoSize]="true"
                    spacerClass="mck-spacer-content-expand-mobile mck-spacer-content-divider"
                    size="1px">
                </mck-spacer>
                <div class="mck-row mck-center button-row" [class.small-buttons]="smallButtons">
                    <button
                        *ngIf="secondaryLabel"
                        mat-stroked-button class="mck-secondary-button mck-modal-secondary-button"
                        type="submit"
                        (click)="secondarySubmit.emit()"
                        [disabled]="secondaryDisabled"
                    >
                        {{secondaryLabel}}
                    </button>
                    <button
                        *ngIf="primaryLabel"
                        mat-button class="mck-primary-button mck-modal-primary-button"
                        type="submit"
                        (click)="primarySubmit.emit()"
                        [disabled]="primaryDisabled"
                    >
                        {{primaryLabel}}

                    </button>
                </div>
            </div>
        </ng-template>
    `,
    styleUrls: ['./modal-button-row.component.scss']
})
export class ModalButtonRowComponent extends BaseTemplateComponent {

    @Input()
    primaryLabel?: string;

    /**
     * Specify if you want to disable the primary button
     */
    @Input()
    primaryDisabled?: boolean = false;

    /**
     * Used only if primaryLabel is specified.
     */
    @Output()
    public primarySubmit = new EventEmitter();

    /**
     * Specify if you want to show a secondary label button
     */
    @Input()
    secondaryLabel?: string;

    @Input()
    disableFixed: boolean = false;

    /**
     * Specify if you want to disable the secondary button
     */
    @Input()
    secondaryDisabled?: boolean = false;

    /**
     * Used only if secondaryLabel is specified.
     */
    @Output()
    public secondarySubmit = new EventEmitter();

    @Input()
    smallButtons?: boolean = false;

    constructor(vcRef: ViewContainerRef) {
        super(vcRef);
    }

}
