import { HashMap } from '@mckinsey-converge/base';
import {
    ISpaceAvailabilityOptions,
    Space
} from './space.class';
import { Building } from '../organisation/building.class';
import { DateTZ } from '@mckinsey-converge/date-tz';
import { handleRecurrenceFields, RecurrencePeriod } from '../recurrence/recurrence.utils';

export function availabilityOptionsToQuery(options: ISpaceAvailabilityOptions): HashMap {
    let query: HashMap = {};
    if (options) {
        query = { ...options };
        if (options.dateTz) {
            const date = options.dateTz.startOfValue('minute');
            query.available_from = date.seconds; // Add one second or API will not allow end to end booking
            query.available_to = date.addValue({ minutes: options.duration || 60 }).subtractValue({ seconds: 1 }).seconds;

            if (options.setup) query.setup = options.setup ;
            if (options.breakdown) query.breakdown = options.breakdown;
 
            delete query.dateTz;
            delete query.duration;
        }

        if (!options.capacity_max) delete query.capacity_max

        if (options.hide_declined) query.hide_declined = options.hide_declined;
        query = handleRecurrenceFields(query)
    }
    return query;
}

/**
 * Compare two spaces to determine order
 * @param first
 * @param second
 */
export function sort(first: Space, second: Space, blds: Building[] = []) {
    const bld = blds.find(bld => first.zones.includes(bld.id));
    const bld_b = blds.find(bld => second.zones.includes(bld.id));
    if (bld) {
        if (bld !== bld_b) {
            return (bld.name).localeCompare(bld_b?.name);
        }
        const sort_order = [...bld.sort_order].reverse();
        for (const zone_id of sort_order) {
            if (zone_id === '*') {
                continue;
            }
            const a_has_zone = first.zones.indexOf(zone_id) >= 0;
            const b_has_zone = second.zones.indexOf(zone_id) >= 0;
            if (a_has_zone && !b_has_zone) {
                return 1;
            } else if (b_has_zone && !a_has_zone) {
                return -1;
            }
        }
    }
    return first.name.localeCompare(second.name);
}
