import { Component, Input, SimpleChanges, OnChanges, EventEmitter, Output } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';

import { Booking } from '@mckinsey-converge/data-common';
import { User } from '@mckinsey-converge/data-common';
import { BaseClass, Identity } from '@mckinsey-converge/base';

@Component({
    selector: 'a-event-form',
    templateUrl: './event-form.component.html',
    styleUrls: ['./event-form.component.scss'],
})
export class EventFormComponent extends BaseClass implements OnChanges {
    /** Booking details to display */
    @Input() public event: Booking;
    /** Booking details to display */
    @Input() public form: FormGroup;

    /** List of attendees in the booking */
    public attendees: User[] = [];

    constructor() {
        super();
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.event || changes.form) {
            this.attendees = this.form?.value.attendees || [];
            this.subscription(
                'attendees',
                this.form
                    .get('attendees')
                    ?.valueChanges.subscribe((l) => (this.attendees = l || []))
            );
        }
    }

    public addVisitor() {
        const list = this.form.controls.attendees.value;
        const visitor = new User({ visitor: true, external: true, email: '' })
        list.push(visitor);
        //** Create visitor form group */
        const visitorFG = this.createVisitor(visitor);
        //** Push to visitor form array */
        this.getVisitors.push(visitorFG);
        this.form.controls.attendees.setValue(list);
        this.form.markAsDirty();
    }

    /**
     * Create visitor form group
     */
    createVisitor(visitor) {
        return new FormGroup({
            name: new FormControl(visitor.name, {validators: [Validators.required, Validators.minLength(2)], updateOn: 'blur' }),
            email: new FormControl(visitor.email, { validators: [Validators.required, Validators.email], updateOn: 'blur' }),
        });
    }

    /** Replace visitor with updated values */
    public replaceVisitor(item: User, index: number) {
        const attendees = this.attendees;
        attendees.splice(index, 1, item);
        this.form.controls.attendees.setValue(attendees);
        this.form.markAsDirty();
    }


    /** Remove visitor from attendee list */
    public removeVisitor(item: User, index: number) {
        const attendees = [...this.attendees];
        attendees.splice(index, 1);
        this.form.controls.attendees.setValue(attendees);
        // (<FormArray>this.form.controls.visitors).removeAt(index);
        this.getVisitors.removeAt(index-1);
        this.form.markAsDirty();
    }

    /**
     * Get the visitor form array 
     */
    public get getVisitors() {
        return this.form.get("visitors") as FormArray;
    }

    public trackByFn(index: number, item: Identity) {
        return index;
    }
}
