import {
    Component,
    forwardRef,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import {
    ControlValueAccessor,
    FormControl,
    NG_VALUE_ACCESSOR
} from '@angular/forms';
import { MatSelect } from '@angular/material/select';

import {
    BaseDirective,
    closestToTimeSlot,
    nearestStepToInput,
    timeWithGmtOffsetFormatString
} from '@mckinsey-converge/base';

import { SelectOption } from '@mckinsey-converge/ui';
import { generateAvailableTimes } from '../utils/time.utils';
import {
    BehaviorSubject,
    combineLatest
} from 'rxjs';
import {
    filter,
    map,
    withLatestFrom
} from 'rxjs/operators';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { DateTime } from 'luxon';

@Component({
    selector: 'mck-time-field',
    template: `
        <div class="mck-column mck-form-input">
            <mck-label [required]="required">{{label}}</mck-label>
            <mat-form-field
                appearance="outline"
                class="mck-center-suffix">
                <mat-select
                    #select
                    [placeholder]="(placeHolderOption | async)?.display"
                    panelClass="mck-select-panel"
                    [value]="value | async"
                    [disabled]="disabled"
                    (valueChange)="setValue($event)"
                    disableOptionCentering>
                    <mat-option>
                        <ngx-mat-select-search
                            noEntriesFoundLabel="No timeslots found for current search. Try selecting one in the dropdown."
                            [placeholderLabel]="(selectedOption | async)?.display"
                            [formControl]="filterFormControl"></ngx-mat-select-search>
                    </mat-option>
                    <mat-option
                        *ngFor="let option of options | async | filter: (nearestStep | async): 'shortDisplay'"
                        [value]="option.value">
                        <mck-select-option
                            [option]="option"
                            [selected]="isSelected(option) | async"
                        ></mck-select-option>
                    </mat-option>
                </mat-select>
                <div class="mck-column">
                    <mck-chevron-icon [up]="isOpen" matSuffix fillClass="mck-icon-fill-black"></mck-chevron-icon>
                </div>
            </mat-form-field>
        </div>
    `,
    styleUrls: ['./time-field.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => TimeFieldComponent),
            multi: true
        }
    ],
    encapsulation: ViewEncapsulation.None
})
export class TimeFieldComponent extends BaseDirective implements OnInit, OnChanges, ControlValueAccessor {
    @Input()
    public label: string;
    @Input()
    public required: boolean;
    /** Time step between each allowed time option */
    @Input() public step = 15;
    /** Whether form field is disabled */
    @Input() public disabled: boolean;
    /** Whether past times are allowed */
    @Input() public noPastTimes = true;
    /** Representing the date from start **/
    @Input() public from: number;
    /** Representing desired timezone **/
    @Input() public timezone?: string = new DateTZ({ date: Date.now() }).local_tz;

    // R-- Which time field is going to be used, there's too many
    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;

    /** Select field for selecting the time */
    @ViewChild('select') private select_field: MatSelect;

    /** String representing the currently set date */
    public date = new BehaviorSubject(DateNow(new Date()).ms);

    public filterFormControl: FormControl = new FormControl();

    public nearestStep = combineLatest([
        this.filterFormControl.valueChanges,
        this.date]).pipe(
        map(([value, date]) => nearestStepToInput(this.step, value, DateTime.now().valueOf()))
    );

    /** String representing the currently set time */
    public value = new BehaviorSubject<string>(closestToTimeSlot(DateTime.now(), this.step).toFormat('HH:mm'));

    /** Available time blocks for the selected date */
    public options = new BehaviorSubject<SelectOption<any>[]>(generateAvailableTimes(DateNow(new Date()).ms, !this.noPastTimes, this.step, this.timezone));

    public selectedOption = combineLatest([this.options, this.value]).pipe(
        map(([options, value]) => options.find(o => o.value === value))
    );

    public placeHolderOption = this.options.pipe(
        map(os => os.length > 0 && os[0])
    );

    public isSelected(option: SelectOption<any>) {
        return this.value.pipe(map(t => t === option.value));
    }

    public get isOpen() {
        return this.select_field?.panelOpen ?? false;
    }

    public ngOnInit(): void {
        // Generate the time input options for the booking date
        this.options.next(generateAvailableTimes(this.date.value, !this.noPastTimes, this.step, this.timezone));

        // Subscribe to selected option changes
        this.subscription('dateTimeChanges',
            combineLatest([
                this.date
            ]).pipe(
                map(([date]) => {
                    // R--- The time input needing to be TZ aware.
                    const closestTimeSlot = closestToTimeSlot(DateTime.fromMillis(date.valueOf()), this.step);
                    // Need to return the date with building timezone or the HH:mm representation will no match the option values
                    return new DateTZ({date: closestTimeSlot.valueOf(), is_local_tz:false, building_tz: this.timezone});
                }),
                withLatestFrom(this.options),
                filter(([date, timeOptions]: [DateTZ, SelectOption<any>[]]) => {
                    // if new time slot opened up when date / time changes, add it here.
                    return !timeOptions.find(t => t.value === date.formatDate('HH:mm'));
                }),
                map(([date, timeOptions]) => {
                    // TODO probably an issue here with going from a future date and time earlier than now back to day showing null
                    // Push the current date option in
                    timeOptions.push({
                        display: timeWithGmtOffsetFormatString(date),
                        value: date.formatDate('HH:mm')
                    });
                    // Sort the time options
                    timeOptions.sort((a, b) => `${a.value}`.localeCompare(`${b.value}`));
                    return timeOptions;
                })
            ).subscribe(next => this.options.next(next)));
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.no_past_times || changes.step || changes.timezone) {
            // If there's a new date value then update the time options
            this.options.next(generateAvailableTimes(this.date.value, !this.noPastTimes, this.step, this.timezone));
            const buildingDate = new DateTZ({date: this.from, is_local_tz: false, building_tz: this.timezone}).startOfValue('minute');
            this.value.next(buildingDate.formatDate('HH:mm'));
        }
        if (changes.from) {
            // When the from input param changes update the date value
            this.date.next(this.from);
        }
    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: string): void {
        // value comes in as a HH:mm string
        const newTime = (new_value.indexOf(':') > 0 ? new_value : '00:00').split(':');
        // need the date with the building timezone so the hour set is relative to there and not local.
        const d = new DateTZ({ date: this.date.value, is_local_tz: false, building_tz: this.timezone });
        const date = d.setValue({ hour: parseInt(newTime[0]), minute: parseInt(newTime[1]) });
        if (date) {
            if (this._onChange) {
                this._onChange(date.ms);
            }
        }
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    public writeValue(value: number | null) {
        if (value != null) {
            // Time input value in building time milliseconds
            this.date.next(value);
            // Get the closest time to slot in case the browser was idle and elapse the valid times
            const date = closestToTimeSlot(DateTime.fromMillis(value).startOf('minute'), this.step);
            // Need the building time so HH:mm is correct
            const buildingDate = new DateTZ({date: date.valueOf(), is_local_tz: false, building_tz: this.timezone}).startOfValue('minute');
            this.options.next(generateAvailableTimes(buildingDate.ms, !this.noPastTimes, this.step, this.timezone));
        }
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }
}
