import {
    Component,
    EventEmitter,
    Inject,
    OnInit,
    Output
} from '@angular/core';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';
import { Router } from '@angular/router';

import {
    ApplicationService,
    Booking,
    BookingsService,
    BookingStateService,
    CateringOrder,
    getFirstDateFromList,
    OrganisationService,
    rulesForSpace,
    recurrenceDetails,
    SeriesAction,
    SettingsService,
    Space,
    SpacesService,
    User,
    UsersService,
    formatWhen,
    BOOKING_STATUS,
    CloseDialogService,
    DialogIds
} from '@mckinsey-converge/data-common';
import {
    BaseDirective,
    DialogEvent,
    HashMap,
    humaniseDuration
} from '@mckinsey-converge/base';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { MatDialog } from '@angular/material/dialog';
import { ConflictsModalComponent, ConflictsModalComponentData } from '@mckinsey-converge/ui';


export interface BookingConfirmModalData {
    old_booking: Booking;
    booking: Booking;
    action?: SeriesAction
}

export interface BookingDetailInfo {
    bookedBy: {
        display:boolean,
        value: string
    }
}

@Component({
    selector: 'a-booking-confirm-modal',
    templateUrl: './booking-confirm.component.html',
    styleUrls: ['./booking-confirm.component.scss']
})
export class BookingConfirmComponent extends BaseDirective implements OnInit{
    /** Emitter for user actions on the modal */
    @Output() public event = new EventEmitter();
    /** Display string for changes are being processed */
    public loading: string;
    /** Whether space availability is being checked */
    public checking_available: boolean;
    /** Whether to show all attendees */
    public show_attendees: boolean;
    /** Whether to show all locations */
    public show_locations: boolean;
    /** Whether to show all catering orders */
    public show_orders: boolean;
    /** Whether booking has succeeded */
    public success: boolean;
    /** Whether booking needs to be accepted */
    public readonly is_request: boolean;

    public approved: boolean;
    public declined: boolean;
    public bookingDetails: BookingDetailInfo;
    lastMomentConflict: boolean = false;
    
    /** Booking to confirm changes to */
    public get old_booking(): Booking {
        return this._data.old_booking;
    }

    /** Booking to confirm changes to */
    public get booking(): Booking {
        return this._data.booking;
    }

    /** Active user */
    public get user(): User {
        return this._users.current;
    }

    /** Title of the displayed event */
    public get title(): string {
        return this.booking.title;
    }

    /** Comapny names */
    public get company(): string[] {
        return this.booking.company;
    }
    

    /** Display string of when the event will occur */
    public get when(): string {
        const duration_value = this.booking.duration;
        const all_day = this.booking.all_day && duration_value < 25 * 60;
        if(this.booking.is_recurrent && this.booking.recurrence_starts && this.booking.recurrence_starts.length){
            return formatWhen({all_day, ...this.recurrenceDates })
        }
        return formatWhen({
            all_day,
            startTZ: this.booking.startDateTz,
            endTZ: this.booking.endDateTz,
            displayEndTZ: this.booking.displayEndDateTz})
    }

    public get recurrenceDates(){
        const startTZ = getFirstDateFromList(this.booking.recurrence_starts)
        const endTZ = startTZ.addValue({minutes: this.booking.duration})
        return {startTZ, endTZ, displayEndTZ: endTZ}
    }

    /** Display string for the recurrence value of the booking */
    public get recurrence(): string {
        return recurrenceDetails({
            period: this.booking.recurrence_period,
            end: this.booking.recurrence_endTz,
            list: this.booking.recurrence_starts,
            interval: this.booking.recurrence_interval
        });
    }

    /** Display string for the event's duration */
    public get duration(): string {
        const duration_value = this.booking.duration;
        return this.booking.all_day ? 'All Day' : humaniseDuration(duration_value);
    }

    /** List of spaces associated with the event */
    public get spaces(): Space[] {
        return this.booking.multi_rooms;
    }

    /** List of attendees associated with the event not including the host */
    public get attendees(): User[] {
        const attendees = this.booking.attendees;
        return attendees.filter((i) => i.email !== this.organiser.email);
    }

    /** Host of the event */
    public get organiser(): User {
        return this.booking.organiser || this._users.current;
    }

    /** Host of the event */
    public get equipment_codes(): HashMap<string> {
        return this.booking.equipment_codes || {};
    }

    /** Host of the event */
    public get catering_codes(): HashMap<string> {
        const catering = this.catering;
        const codes = {};
        for (const order of this.catering) {
            codes[order.location_id] = order.charge_code;
        }
        return codes;
    }

    /** Host of the event */
    public get equipment_notes(): HashMap<string> {
        return this.booking.notes
            .filter((note) => note.type === 'equipment')
            .reduce((map, notes) => {
                map[notes.space] = notes.message;
                return map;
            }, {});
    }

    /** Details about the event */
    public get notes(): string {
        return this.booking.body;
    }

    /** List of catering orders in the booking */
    public get catering(): CateringOrder[] {
        return (this.booking.catering || []).map((order) => {
            const space: any = this.spaces.find(
                (a_space) => a_space.email === order.location_id
            ) || {
                level: {}
            };
            const building: any =
                this._org.buildings.find((bld) => bld.id === space.level.building_id) || {};
            return new CateringOrder({
                ...order,
                location: space.local_name,
                symbol: building.currency,
                booking_date: this.booking.startDateTz.ms,
                booking_timezone: this.booking.timezone,
            });
        });
    }

    /** Map of changes to the booking */
    public get changes(): HashMap<boolean> {
        return {};
    }

    constructor(
        private _service: ApplicationService,
        private _users: UsersService,
        private _org: OrganisationService,
        private _bookings: BookingsService,
        private _booking_state_service: BookingStateService,
        private _spaces: SpacesService,
        private _router: Router,
        private _dialog: MatDialog,
        private _dialog_ref: MatDialogRef<BookingConfirmComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: BookingConfirmModalData,
        public settings: SettingsService,
        private closeDialogService: CloseDialogService, 
    ) {
        super();
        this.booking.time_changed =
            this.old_booking &&
            (this.old_booking.startDateTz.ms !== this.booking.startDateTz.ms ||
                this.old_booking.duration !== this.booking.duration);
        // in concierge, all bookings are acceptable without explicit approval.

        const rules = rulesForSpace({ user: this.user, space: this.booking.room, duration: 60 });
        this.is_request = this.settings.concierge ? false : rules.auto_approve;
    }

    ngOnInit(): void {
        this.approved = this.booking.status === BOOKING_STATUS.approved;
        this.declined = this.booking.status === BOOKING_STATUS.declined; 
        // test comment
        const {creator, organiser, booker_concierge} = this.booking;
        this.bookingDetails = {
            bookedBy: {
                display: creator && ( creator.email !== organiser.email || (creator.email === organiser.email && (booker_concierge !== null && booker_concierge !==undefined ) && organiser.email !==booker_concierge['email']) ),
                value: (creator.email === organiser.email && booker_concierge !== null && (booker_concierge['email'] && organiser.email !==booker_concierge['email']) ) ?  booker_concierge['name'] || booker_concierge['email']  :  creator?.name || creator.email
            }
        }
    }

    /**
     * Save booking changes
     */
    public saveChanges() {
        this.loading = 'Checking availability of selected spaces';
        this._dialog_ref.disableClose = true;
        this.checkSpaceAvailability().then(
            () => {
                this.loading = 'Processing booking request...';
                // Change the unchanged settings to the selected room
                const newEmail = this.booking.space.email;
                const replaceObjects = ['setup', 'breakdown', 'approval_status']
                replaceObjects.forEach(key => {
                    const obj = this.booking[key];
                    if (obj && obj.constructor === Object) {
                        const keys = Object.keys(obj);
                        if (keys.length > 0) {
                            const replacement = {};
                            keys.forEach(nk => {
                                replacement[newEmail] = obj[nk];
                            })
                            this.booking[key] = replacement;
                        }
                    }
                });

                const buffer = {};
                this.booking.multi_rooms.map(room => room.email).forEach(elem => buffer[elem] = 900);

                // if notes or catering exist? add 15 min buffer setup and breakdown to booking
                const has_note_buffer = (this.booking.notes && this.booking.notes?.length) || (this.booking.catering && this.booking.catering?.length) ? 
                buffer :  null; // 900 seconds = 15 min
                if (has_note_buffer) {
                  this.booking.setup = has_note_buffer;
                  this.booking.breakdown = has_note_buffer; 
                  this.booking.room_setup = has_note_buffer; 
                  this.booking.room_breakdown = has_note_buffer; 
                }

                this._bookings.save(this.booking, this._data.action === SeriesAction.EDIT).then(
                    async (bkn) => {
                        this.loading = null;
                        this.success = true;
                        this._dialog_ref.disableClose = false;
                        const updated = new Booking({ ...this.booking, ...bkn.toApiJSON() });
                        this._booking_state_service.replace(updated);
                        
                        /**
                         * update the master booking in case of recurring to have latest updated in the system
                         */
                        if(updated.recurrence_master_id !== null)
                        { 
                            const master_booking = await this._bookings.show(updated.recurrence_master_id);
                            this._booking_state_service.replace(master_booking);
                        }

                        if (bkn.is_recurrent && bkn.recurrence_type === 'master') {
                            this._booking_state_service.pollOnce();
                            this.closeDialogService.closeDialog(DialogIds.MeetingDetails);
                        }

                        localStorage.removeItem('ACA_STAFF.booking_form_data');
                        this.event.emit({ reason: 'done' });
                        this._service.notifySuccess(
                            `Successfully ${this.booking.id ? 'updated' : 'created'} booking`
                        );
                    },
                    (err) => {

                        this._dialog_ref.disableClose = false;
                        let erroMessage: string = '';
                        switch(err.status){
													case 400:
														erroMessage = 'Apologies, the booking cannot be finalized because the current time has exceeded the scheduled start time for this booking. Please try again.';
														break;
													case 409:
														erroMessage = 'Apologies, this room has been booked recently by another user, please try another one.';
														this.lastMomentConflict = true;
														break;
												}
                        this.loading = erroMessage;
                        this._service.notifyError(
                            `Error ${this.booking.id ? 'updating' : 'creating'} booking: ${erroMessage}`
                        );
                       setTimeout(()=> {
                        this.loading = null;
                        this.event.emit({ reason: 'last-moment-conflict' });
                       }, 4000) ;
                    }
                );
            },
            (err) => {

                this.lastMomentConflict = true;
                this._dialog_ref.disableClose = false;
                const erroMessage = 'Apologies, this room has been booked recently by another user, please try another one.';
                this.loading = erroMessage;
                this._service.notifyError(
                    `Error ${this.booking.id ? 'updating' : 'creating'} booking: ${erroMessage}`
                );
               setTimeout(()=> {
                this.loading = null;
                this.event.emit({ reason: 'last-moment-conflict' });
               }, 4000) ;
            }
        );
    }

    /**
     * Staff only
     */
    public newBooking() {
        this._router.navigate(['/book', 'spaces']);
        this._dialog_ref.close();
    }

    /**
     * Staff only
     */
    public toSchedule() {
        this._router.navigate(['/schedule'], {
            queryParams: { email: this.booking.organiser.email }
        });
        this._dialog_ref.close();
    }

    /**
     * Check whether the selected spaces are available for the selected time period
     */
    public checkSpaceAvailability(): Promise<void> {
        const spaces: Space[] = this.spaces;
        if (
            (this.booking.id &&
                (this.booking.startDateTz.ms === this.old_booking.startDateTz.ms &&
                    this.booking.duration === this.old_booking.duration)) ||
            (spaces && spaces.length <= 0)
        ) {
            return Promise.resolve();
        }
        return new Promise((resolve, reject) => {
            // Use of this.booking.timezone is unreliable, use booking space instead.
            const all_day = this.booking.all_day;
            // R--- should be nothing to do with date as a timestamp getter
            this._spaces
                .available({
                    room_ids: spaces.map((space) => space.id).join(','),
                    dateTz: this.booking.startDateTz,
                    duration: all_day ? 24 * 60 : this.booking.duration,
                    ignore: this.booking.icaluid,
                    recurrence_count: this.booking.recurrence_count,
                    recurrence_period: this.booking.recurrence_period,
                    recurrence_endTz: this.booking.recurrence_endTz,
                    recurrence_interval: this.booking.recurrence_interval,
                    recurrence_starts: this.booking.recurrence_starts,
                    is_recurrent: this.booking.is_recurrent,
                    recurrence_days: this.booking.recurrence_days
                })
                .then(
                    (space_list) => {
                        for (const space of space_list) {
                            if (!space.bookable) {
                                this.lastMomentConflict = true;
                                return reject(
                                    `${space.name} is not available at the select time period.`
                                );
                            }
                        }
                        if (space_list.length < spaces.length) {
                            this.lastMomentConflict = true;
                            return reject(
                                `${spaces[0].name} is not available at the select time period.`
                            );
                        }
                        resolve();
                    },
                    (err) => reject(err)
                );
        });
    }

    public openConflictsModal(){
        const ref = this._dialog.open<ConflictsModalComponent,
        ConflictsModalComponentData>(ConflictsModalComponent, {
        maxWidth: '95vw',
        width: '32em',
        data: {
            room_name: this.booking.local_room_name,
            conflicts: this.booking.recurrence_exceptions,
            copy_conflicts: true,
            timezone: this.booking.timezone
        }
        });

        this.subscription(
        'modal_events',
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            if(event.reason === 'close'){
                ref.close();
            }else if(event.reason  === 'action'){
                navigator.clipboard.writeText(this.conflictsText)
            }
            
        })
        );
        ref.afterClosed().subscribe(() => this.unsub('modal_events'));
    }

    public get conflictsText(){
        let text = ''
        this.booking.recurrence_exceptions.map((date)=>{
            const dateTZ = new DateTZ({date: date*1000});
            const display = dateTZ.formatDate('ccc dd MMM yyyy');
            text = `${text}\n${display}${dateTZ.isSameDate(DateNow(new Date()), 'day') ? ' (Today)' : ''}`;
        })
        return text
    }
}
