import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RoomAdminComponent } from './room-admin.component';
import { RoomDetailsWrapperComponent } from './room-details/room-details-wrapper.component';

const routes: Routes = [
  { path: '',
    children: [
      { path: '', component: RoomAdminComponent },
      {
        path: 'room-details', 
        component: RoomDetailsWrapperComponent
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RoomAdminRoutingModule { }


