import {
  Observable,
  Subject
} from 'rxjs';
import { Action } from '@ngrx/store';
import { Actions } from '@ngrx/effects';
import { TypedAction } from '@ngrx/store/src/models';
import { take } from 'rxjs/operators';
import {
  LoadingActionObject
} from '../src/lib/loading.actions';

export const generateMockActions = () => {
  const dispatcher = new Subject<Action>();
  return {
    dispatcher,
    actions: new Actions(dispatcher)
  };
};

/**
 * Simple verifier that checks if the request action results in a success returned.
 */
export const verifyActionSuccess = <TState, TSuccess, TRequest extends object | undefined = {}>(
  action: LoadingActionObject<TState, TRequest, TSuccess>,
  effect: Observable<TypedAction<string>>,
  dispatcher: Subject<Action>,
  options: {
    data: TSuccess,
    payload?: TRequest,
  }) => {
  let result: TypedAction<string>;
  effect
    .pipe(take(1))
    .subscribe(next => {
      result = next;
    });
  // payload has type issues, just ignore them since we specify TRequest as type param already.
  dispatcher.next(action.request((options.payload || {}) as never));

  expect(result).toBeDefined();
  expect(result).toStrictEqual(action.success({ payload: options.data }));
};

/**
 * Simple verifier that checks if the request action results in a failure returned.
 */
export const verifyActionError = <TState, TRequest extends object, TSuccess>(
  action: LoadingActionObject<TState, TRequest, TSuccess>,
  effect: Observable<TypedAction<string>>,
  dispatcher: Subject<Action>,
  options: {
    error: Error,
    payload?: TRequest
  }) => {
  let result: TypedAction<string>;
  effect
    .pipe(take(1))
    .subscribe(next => {
      result = next;
    });
  // payload has type issues, just ignore them since we specify TRequest as type param already.
  dispatcher.next(action.request((options.payload || {}) as never));

  expect(result).toBeDefined();
  expect(result).toStrictEqual(action.error({ payload: options.error }));
};
