export interface IBookingSlot {
    start: number;
    end: number;
}


export function bookingOptionsToQuery(options: IBookingQueryOptions): HashMap {
    let query: HashMap = {};
    if (options) {
        query = { ...options };
        delete query.type;
        if (options.filters) {
            delete query.filters;
            for (const property in options.filters) {
                if (!query[property]) {
                    query[property] = options.filters[property]
                }
            }
        }

        // id?: string;
        // cancelled?: boolean;
        if (options.until) {
            query.to = options.until;
            delete query.until;
        }
        
    }
    return query;
}


/**
 * Generate a list of free time slots between the given bookings
 * @param list List of bookings to find slots between
 * @param min_size Minimum length of a free slot in minutes
 */
export function getFreeBookingSlots(list: Booking[]=[], min_size: number = 30, dateTzMs: number = DateNow(new Date()).ms,): IBookingSlot[] {
    /* istanbul ignore else */
    if (!list.length) {
        return [
            {
                start: 0,
                end: DateNow(new Date()).startOfValue('minute').ms * 10
            }
        ];
    }
    const slots: IBookingSlot[] = [];
    let start = new DateTZ({ date: dateTzMs });
    list.sort((a, b) => a.date - b.date);
    for (const booking of list) {
        const bkn_start = booking.startDateTz;
        const bkn_end = booking.endDateTz;
        if (bkn_start.isAfterDate(start)) {
            const diff = Math.abs(bkn_start.dateDiff(start, 'minutes'));
            /* istanbul ignore else */
            if (diff >= min_size) {
                slots.push({ start: start.ms, end: bkn_start.ms });
            }
            start = bkn_end;
        } else if (start.startOfValue('minute').ms === bkn_start.startOfValue('minute').ms) {
            start = bkn_end;
        }
    }
    slots.push({
        start: start.ms,
         // R--- these X 10's make no sense. why not + 2 years?
        end: DateNow(new Date()).startOfValue('minute').ms * 10
    });

    return slots;
}

/**
 * Get the next free time slot from the given bookings
 * @param list List of bookings to find the next slot
 * @param date Date to find next slot after in ms since UTC epoch
 * @param min_size Minimum length of the free slot in minutes
 * 
 * R--- Possibly depreciate, I don't see it used anywhere except for its own test
 */
export function getNextFreeBookingSlot(
    list: Booking[],
    date: number = DateNow(new Date()).ms,
    min_size: number = 15
): IBookingSlot {
    const slots = getFreeBookingSlots(list, min_size, date);
    const date_ = new DateTZ({date})
    const time = date_.startOfValue('minute').setValue({ second: 1 });
    for (const block of slots) {
        const start = new DateTZ({date: block.start});
        const end = new DateTZ({date: block.end});
        if (start.startOfValue('minute').isAfterDate(time)) {
            return block;
        } else if (time.isBeforeDate(end.startOfValue('minute'))) {
            const duration = end.startOfValue('minute').dateDiff(time, 'minutes');
            /* istanbul ignore else */
            if (duration >= min_size) {
                return block;
            }
        }
    }
    return slots[slots.length - 1];
}

/**
 * Generate form fields for the given booking
 * @param booking Booking to generate form for
 * @param current_user Current user of the system to default as the host
 */
export function generateBookingForm(
    booking: Booking,
    use_fields: AvailableBookingFields[],
    isConcierge: boolean = false,
    manualTimezone?: string,
    action?: SeriesAction | BookingAction | null
    ): FormGroup {
    if (!booking) {
        throw Error('No booking passed');
    }

    const user_service = ServiceManager.serviceFor(User);
    const current_user =
        user_service.current ||
        new User({ id: 'local-user', name: 'Local User', email: 'local@place.tech' });

    const fields: HashMap<FormControl> = {
        id: new FormControl(booking.id || ''),
        space_list: new FormControl( booking.is_multiroom? booking.multi_rooms : [booking.room], []),
        room: new FormControl(booking.room, {}),
        dateTz: new FormControl({ value: booking.startDateTz || DateNow(), disabled: false }, [Validators.required]),
        duration: new FormControl({ value: booking.duration, disabled: false }),
        organiser: new FormControl(booking.organiser || current_user, [Validators.required]),
        attendees: new FormControl(booking.attendees, []),
        title: new FormControl(booking.title || '', { validators: [ Validators.required, Validators.minLength(1) ], updateOn: 'blur' }), //Validators.pattern("[^=`~!@#$%*{};:'\",_.<>]*")
        booking_type: new FormControl(booking.booking_type),
        body: new FormControl(booking.body),
        notes: new FormControl(booking.notes),
        equipment_codes: new FormControl(booking.equipment_codes),
        expected_attendees: new FormControl(booking.expected_attendees),
        company: new FormControl(booking.company),
        is_multiroom: new FormControl(booking.is_multiroom),
        catering: new FormControl(
            booking.cateringOrders.map(
                (order) => new CateringOrder({
                    ...order,
                    booking_date: booking.startDateTz.ms,
                    booking_timezone: booking.timezone,
                })
            )
        ),
        all_day: new FormControl(!!booking.all_day),
        has_catering: new FormControl(!!booking.has_catering),
        needs_space: new FormControl(true),
        action: new FormControl(''),
        timezone: new FormControl(manualTimezone || ""),
        is_before_date: new FormControl(false)
    };
    if (!isConcierge) {
        fields.duration.setValidators([Validators.required, validateEndTime(fields.dateTz)]);
    }
    /* istanbul ignore else */
    if (booking.id && booking.id !== 'ad-hoc') {
        fields.organiser.disable();
        /* istanbul ignore else */
        if (booking.endDateTz.ms < DateNow(new Date()).ms) {
            fields.dateTz.disable();
        }
    } else {
        const dateValidators = [Validators.required];
        if (!isConcierge) {
            dateValidators.push(isFuture);
        }
        dateValidators.push(validDateTZ)
        fields.dateTz.setValidators(dateValidators);
        fields.dateTz.updateValueAndValidity();
    }
    let list_length = -1;
    fields.space_list.valueChanges.subscribe((list) => {
        const expected = fields.expected_attendees.value || {};
        const matches = Object.keys(expected).filter((key) =>
            list?.find((space) => space.email === key)
        ).length;
        if (list && list.length && matches === 0) {
            const codes = fields.equipment_codes.value || {};
            if (Object.keys(expected).length >= 0 || Object.keys(codes).length >= 0) {
                const key = Object.keys(expected)[0] || Object.keys(codes)[0];
                const new_expected = {};
                const new_codes = {};
                const notes = fields.notes.value;
                notes.forEach((note) => (note.space === key ? (note.space = list[0].email) : ''));
                new_expected[list[0].email] = expected[key];
                new_codes[list[0].email] = codes[key];
                fields.expected_attendees.setValue(new_expected);
                fields.equipment_codes.setValue(new_codes);
            }
        }
        list_length = list?.length;
    });
    fields.dateTz.valueChanges.subscribe((_) => {
        fields.duration.updateValueAndValidity();
    });
    fields.needs_space.valueChanges.subscribe((space_needed) => {
        if (!space_needed) {
            fields.space_list.setValue([]);
        }
    });
    fields.has_catering.valueChanges.subscribe((has_catering) => {
        if (!has_catering) {
            fields.catering.setValue([]);
        }
    });
    const simplified_fields: HashMap<FormControl> = [
        'id',
        'space_list',
        'space_ids',
        'notes',
        'dateTz',
        'booking_type',
        'equipment_codes',
        'expected_attendees',
        'timezone',
        'is_before_date',
        ...use_fields
    ].reduce((map, key) => {
        /* istanbul ignore else */
        if (fields[key]) {
            map[key] = fields[key];
        }
        return map;
    }, {});
    
    if (simplified_fields.all_day) {
        const handleAllDay = (value) => {
            if (value) {
                let startOfDay = simplified_fields.dateTz.value.clone();
                // When the booking space isn't set we still need a way to determine midnight
                // and here we use the current concierge building selection's timezone.
                if (manualTimezone) {
                    startOfDay = startOfDay.toZone(manualTimezone);
                }

                simplified_fields.dateTz.setValidators([Validators.required]);
                simplified_fields.dateTz.setValue(simplified_fields.all_day.value
                            ? startOfDay.startOfValue('day')
                            : simplified_fields.dateTz.value);
                simplified_fields.duration.setValue(simplified_fields.all_day.value
                    ? 24 * 60
                    : simplified_fields.duration.value);
                simplified_fields.dateTz.updateValueAndValidity();
                simplified_fields.duration.disable();
                // simplified_fields.dateTz.disable();
            } else {
                const dateValidators = [Validators.required];
                if (!isConcierge) {
                    dateValidators.push(isFuture);
                }
                dateValidators.push(validDateTZ)
                simplified_fields.dateTz.setValidators(dateValidators);
                simplified_fields.duration.setValue(booking.duration || 30);
                simplified_fields.dateTz.updateValueAndValidity();
                simplified_fields.duration.enable();
                simplified_fields.dateTz.enable();
            }
        };
        simplified_fields.all_day.valueChanges.subscribe(handleAllDay);
        handleAllDay(simplified_fields.all_day.value);
    }

    const occurrences = booking.occurrence_details || [];

    //Add recurrence fields
    if(action && booking.recurrence_period  === RecurrencePeriod.LIST){
        booking.recurrence_starts = occurrences
            .filter(occurrence => occurrence.id !== booking.recurrence_master_id)
            .map((occurrence) => new DateTZ({date: occurrence.start_epoch * 1000, is_local_tz: false, building_tz: booking.timezone})
        ) 
    }
    /**If series exists then set the start and end dates from the first and last bookings in the occurrences array
     * this prevents prefilled form errors due to individual edits
     */
    if(action && action !== SeriesAction.CLONE && action !== BookingAction.CLONE && occurrences && occurrences.length && !booking.id){
        const start_dateTz = new DateTZ({
            date: occurrences.sort((d1, d2) => d1.start_epoch - d2.start_epoch)[0].start_epoch * 1000, 
            is_local_tz: false, building_tz: booking.timezone
        })
        simplified_fields.dateTz.setValue(start_dateTz)
        const end = occurrences.sort((d1, d2) => d2.start_epoch - d1.start_epoch)[0].start_epoch * 1000
        booking.recurrence_endTz =  new DateTZ({date: end, is_local_tz: false, building_tz: booking.timezone})  
    }

    // R-- needs TZ aware fields
    const _recurr_end = simplified_fields.dateTz?.value || new DateTZ({ date: 1 }); // new DateTZ({date: simplified_fields.date?.value}) 
    const fields_with_recurrence = {
        ...simplified_fields,
        recurrence_period: new FormControl(booking.recurrence_period || RecurrencePeriod.WEEKLY),
        recurrence_interval: new FormControl(booking.recurrence_interval || 1),
        recurrence_endTz: new FormControl(booking.recurrence_endTz || _recurr_end.addValue({days: 1})),
        recurrence_count: new FormControl(booking.recurrence_count || 0),
        is_recurrent: new FormControl(booking.is_recurrent),
        recurrence_starts: new FormControl(booking.recurrence_starts || []),
        recurrence_exceptions: new FormControl([]),
        recurrence_pattern: new FormControl(RecurrencePeriod.WEEKLY), //Helper value to store the last selected pattern (daily, weekly, monthly),
        recurrence_days: new FormControl(booking.recurrence_days || [])
    }

    if(booking.is_recurrent && action !== BookingAction.CLONE){
        fields_with_recurrence.recurrence_starts.setValidators([arrayWithValues])
        fields_with_recurrence.recurrence_starts.updateValueAndValidity()
        fields_with_recurrence.recurrence_endTz.setValidators([Validators.required, validDateTZ]);
    }
    // Generate form group for booking item
    const form = new FormGroup(fields_with_recurrence);
    return form;
}

/**
 * Validate whether date is in the future
 * @param control Control to check value
 */
export function isFuture(control: AbstractControl) {
    const dateTz = control.value; // new DateTZ({date: control.value});
    return dateTz.isBeforeDate(DateNow(new Date()).subtractValue({ minutes: 5 }))
        ? { dateTz: 'Date needs to be in the future' }
        : null;
}

/**
 * Validate whether an array has values
 * @param control Control to check value
 */
export function arrayWithValues (control: AbstractControl) {
    return control.value.length !== 0 ? null : { field: 'Needs at least one value'}
}

/**
 * Validate whether a date has a valid value (future or current date)
 * @param control Control to check value
 */
 export function validDate (control: AbstractControl) {
     if(!control.value){
         return { date: 'Needs a valid date'}
     }
    const date = new DateTZ({date: control.value})
    const now = DateNow(new Date())
    const isValid = now.isBeforeDate(date, 'day') || now.isSameDate(date, 'day')
    return isValid ? null : { date: 'Needs a valid date'}
}

/**
 * Validate whether a date has a valid DateTZ value (future or current date)
 * @param control Control to check value
 */
 export function validDateTZ (control: AbstractControl) {
    if(!control.value){
        return { date: 'Needs a valid date'}
    }
   const date = DateNow(control.value.JSDate);
   const now = DateNow(new Date());
   const isValid = now.isBeforeDate(date, 'day') || now.isSameDate(date, 'day')
   return isValid ? null : { date: 'Needs a valid date'}
}

/**
 * Get minimum duration from ruleset in minutes
 * Default to 5min
 */
export function getMinLength(rule_list: HashMap<BookingRule[]>): number {
    return Object.values(rule_list).reduce((min, block) => {
        const min_block = block.reduce((min_length, el) => {
            if (el.conditions.min_length && stringToMinutes(el.conditions.min_length) > min) {
                return stringToMinutes(el.conditions.min_length);
            } else {
                return min_length;
            }
        }, 5);
        if (min_block > min) {
            return min_block;
        } else {
            return min;
        }
    }, 5) as number;
}

/**
 * Whether the first input is greater than the last. Converts duration strings into minutes
 * @param duration_1 First input can be a number in minutes or a duration string e.g. `1 hour`
 * @param duration_2 Second input can be a number in minutes or a duration string e.g. `30 minutes`
 */
export function durationGreaterThanOrEqual(
    duration_1: string | number,
    duration_2: string | number
) {
    const first: number = typeof duration_1 === 'string' ? stringToMinutes(duration_1) : duration_1;
    const second: number =
        typeof duration_2 === 'string' ? stringToMinutes(duration_2) : duration_2;
    return first >= second;
}

/**
 * Conver time string into minutes
 * @param str timestring e.g. `'1 day'`, `'15 minutes'`, `'2 weeks'`
 */
export function stringToMinutes(str: string): number {
    const parts = str.split(' ');
    return +parts[0] * DURATION_MAP[parts[1]];
}

/**
 * Get current status within bookings
 * @param bookings List of bookings
 * @param host Host of the new event
 * @param date Datetime of the new event
 */
export function statusFromBookings(
    bookings: Booking[] = [],
    bookable: boolean = true,
    requestable: boolean = false,
    dateTz: DateTZ = DateNow(),
    space?: Space
) {
    const free_slots = getFreeBookingSlots(bookings.filter((bkn) => !bkn.declined));
    const now = DateNow() // new DateTZ({date}); // now should be now
    // commeted bellow piece as correct way to get next free booking slot is at line- 488
    // const next_free_slot = free_slots.find((slot) => {
    //     return slot && (slot.start > now.ms || now.ms > slot.start - slot.start % 1000 || now.ms < slot.end - slot.end % 60 * 1000);
    //     // const start = new DateTZ({date: slot?.start || 0 });
    //     // const end = new DateTZ({date: slot?.end || 0}); // slot end isn't late enough? or after now/
    //     // return start.isAfterDate(now) || (now.isAfterDate(start.startOfValue('second')) && now.isBeforeDate(end.startOfValue('minute')))
    // });

    const next_free_slot = getNextFreeBookingSlot(bookings.filter((bkn) => !bkn.declined), dateTz.ms);
    
    const start = new DateTZ({date: next_free_slot?.start || 0});
    const end = new DateTZ({date: next_free_slot?.end || 0});
    const currently_free = dateTz.isAfterDate(start.startOfValue('second')) && dateTz.isBeforeDate(end.startOfValue('minute'));
    const time_until_next_block = humaniseDuration(
        currently_free ? end.dateDiff(dateTz, 'minutes') : start.dateDiff(dateTz, 'minutes'),
        'short'
    );

    const free_tomorrow = !currently_free && !start.isSameDate(dateTz, 'day');
    const free_today = currently_free && !end.isSameDate(dateTz, 'day');
    
    return {
        status: (!bookable
            ? SpaceStatus.NotBookable
            : currently_free
                ? requestable
                    ? SpaceStatus.Requestable
                    : SpaceStatus.Available
                : SpaceStatus.InProgress) as SpaceStatus,
        available_until: free_today
            ? 'No meetings today'
            : currently_free
                ? `Free until ${end.formatDate(timeFormatString())}(${time_until_next_block})`
                : free_tomorrow
                    ? 'Unavailable today'
                    : `Free at ${start.formatDate(timeFormatString())}(${time_until_next_block})`
    };
}

/**
 * V1 version display.
 */
export const spaceStatusToDisplay = (status: SpaceStatus): string => {
    switch (status) {
        case SpaceStatus.NotBookable:
            return 'Not Bookable';
        case SpaceStatus.Requestable:
            return 'Available by Request';
        case SpaceStatus.Unavailable:
            return 'Unavailable';
        case SpaceStatus.InProgress:
            return 'Meeting in Progress';
        case SpaceStatus.Available:
        default:
            return 'Available';
    }
};

export function replaceBookings(
    list: Booking[],
    new_bookings: Booking[],
    filter_options: { room_email: string; fromTz: DateTZ; toTz: DateTZ }
) {
    const filtered_list = list.filter((booking) => {
        return (
            !(booking.multi_rooms.some(room => room?.email === filter_options.room_email)) ||
            !timePeriodsIntersect(filter_options.fromTz.ms, filter_options.toTz.ms, booking.startDateTz.ms, booking.endDateTz.ms)
        );
    });
    const updated_list = filtered_list.concat(new_bookings);
    updated_list.sort((a, b) => a.date - b.date);
    return unique(updated_list, 'icaluid');
}

export function timePeriodsIntersect(
    start1: number,
    end1: number,
    start2: number,
    end2: number,
    type = ''
) {
    return (
        (start1 >= start2 && start1 < end2) ||
        (end1 > start2 && end1 <= end2) ||
        (start2 >= start1 && start2 < end1) ||
        (end2 > start1 && end2 <= end1)
    );
}

export function formatWhen({
    all_day,
    startTZ,
    endTZ,
    displayEndTZ
}): string {
    if (all_day) {
        return `${startTZ.formatDate('dd MMM yyyy')} - All Day`;
    } else {
        if (startTZ.isSameDate(endTZ, 'day')) {
            return `${startTZ.formatDate('dd MMM yyyy, h:mma')} - ${displayEndTZ.formatDate('h:mma')}`;
        } else {
            return `${startTZ.formatDate('dd MMM yyyy, h:mma')} - ${displayEndTZ.formatDate('dd MMM yyyy, h:mma')}`;
        }
    }
}
