import {
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    OnInit,
    Output,
    ViewChild
} from '@angular/core';
import {
    AbstractControl,
    FormControl,
    FormGroup,
    Validators
} from '@angular/forms';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import {
    BookingNote,
    OrganisationService,
    SettingsService,
    Space,
    UsersService
} from '@mckinsey-converge/data-common';
import {
    DialogEvent,
    HashMap
} from '@mckinsey-converge/base';

import { DateNow } from '@mckinsey-converge/date-tz';


export interface RequirementDetailsModalData {
    spaces: Space[];
    notes_field: AbstractControl;
    codes_field: AbstractControl;
    head_counts_field: AbstractControl;
    standalone?: boolean;
}

@Component({
    selector: 'a-requirement-details-modal',
    templateUrl: './requirement-details-modal.component.html',
    styleUrls: ['./requirement-details-modal.component.scss']
})
export class RequirementDetailsModalComponent implements OnInit {
    /** Emitter for modal events */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Mapping of spaces to notes */
    public notes: HashMap<string> = {};
    /** Mapping of spaces to cost codes */
    public codes: HashMap<string> = {};
    /** Mapping of spaces to estimated head count */
    public head_counts: HashMap<string> = {};
    /** Mapping of spaces to form fields */
    public form: HashMap<FormGroup> = {};
    /** Map of spaces to whether they need a charge code */
    public needs_charge_code: boolean;
    /** Index of the active space */
    public active_space: number = 0;

    @ViewChild('content', { static: true }) private _content_el: ElementRef<HTMLElement>;

    /** List of selected spaces */
    public get space_list(): Space[] {
        return this._data.spaces;
    }

    public get has_recurrence(): boolean {
        return false
    }

    public get active_form(): FormGroup {
        return this.form[this.space_list[this.active_space].email];
    }

    public get invalid_charge_code(): boolean {
        return this.active_form.controls.code.errors?.minlength || this.active_form.controls.code.errors?.pattern;
    }

    /** Whether form fields are valid */
    public get is_valid(): boolean {
        // return this.space_list.reduce((valid, space) => valid && this.form[space.email].valid, true);
        return this.space_list.length > 0 && this.space_list.reduce((valid, space) => valid && this.form[space.email].valid, true);
    }

    public isConcierge: boolean;
    public frontend: string;

    constructor(
        private _org: OrganisationService,
        private _users: UsersService,
        @Inject(MAT_DIALOG_DATA) private _data: RequirementDetailsModalData,
        private _settings: SettingsService,
    ) {
        this.isConcierge = _settings.concierge;
        this.frontend = _settings.frontend;
    }

    public ngOnInit(): void {
        const note_list = this._data.notes_field.value
            .filter((note: BookingNote) => note.type === 'equipment');
        this.notes = note_list.reduce((map, note) => {
            map[note.space] = note.message;
            return map;
        }, {});
        this.codes = { ...this._data.codes_field.value };
        this.head_counts = { ...this._data.head_counts_field.value };
        this.generateFormFields();
    }

    public submit(): void {
        // Get the email of the first space
        const firstSpaceEmail = this.space_list[0].email;

        // Copy values from the first space's form controls to all other spaces
        this.space_list.forEach(space => {
            if (space.email !== firstSpaceEmail) {
                this.form[space.email].controls.notes.setValue(this.form[firstSpaceEmail].controls.notes.value);
                this.form[space.email].controls.code.setValue(this.form[firstSpaceEmail].controls.code.value);
                this.form[space.email].controls.head_count.setValue(this.form[firstSpaceEmail].controls.head_count.value);
            }
        });

        this.space_list.forEach(space => this.form[space.email].markAllAsTouched());
        /* istanbul ignore else */
        if (this.is_valid) {
            // Update notes 
            const notes = this._data.notes_field.value
                .filter((note: BookingNote) => note.type !== 'equipment');
            const new_notes = this.space_list.map(space => ({
                author: this._users.current.name,
                type: 'equipment',
                message: this.form[space.email].controls.notes.value.trim(),
                space: space.email,
                date: DateNow(new Date()).ms
            } as BookingNote));
            this._data.notes_field.setValue(notes.concat(new_notes).filter(note => note.message));
            this._data.codes_field.setValue(this.space_list.reduce((map, space) => {
                map[space.email] = this.form[space.email].controls.code.value;
                return map;
            }, {}));
            this._data.head_counts_field.setValue(this.space_list.reduce((map, space) => {
                map[space.email] = this.form[space.email].controls.head_count.value;
                return map;
            }, {}));
            this.event.emit({ reason: 'done' });
        } else {
            this._content_el.nativeElement.scrollTo({
                top: this._content_el.nativeElement.scrollHeight,
                left: 0,
                behavior: 'smooth'
            });
        }
    }

    /** Generate form fields for each space */
    // private generateFormFields(): void {
    //     for (const space of this.space_list) {
    //         const building = this._org.buildings.find(
    //             bld => bld.id === space[0]?.level.building_id
    //         );
    //         // this.needs_charge_code[space.email] = space?.settings?.equipment_code || building?.required?.equipment_code || false;
    //         const { filters, settings } = space;
    //         const roomBasedEqCode = filters?.equipment_code || settings?.equipment_code;
    //         const officeBasedEqCode = building?.required?.equipment_code;
    //         this.needs_charge_code[space.email] = roomBasedEqCode !== undefined ? roomBasedEqCode : officeBasedEqCode !== undefined ? officeBasedEqCode: false;

    //         this.form[space.email] = new FormGroup({
    //             notes: new FormControl(this.notes[space.email] || ''),
    //             code: new FormControl(
    //                 this.codes[space.email] || '',
    //                 this.needs_charge_code[space.email] ?
    //                     [Validators.pattern(/^[a-zA-Z0-9]*$/), Validators.minLength(4),  Validators.required] : [Validators.minLength(4), Validators.pattern(/^[a-zA-Z0-9]*$/)]
    //             ),
    //             head_count: new FormControl(this.head_counts[space.email] || '', [Validators.required, Validators.pattern(/^(?!0+$)/)])
    //         });
    //     }
    // }
    
    private generateFormFields(): void {
        // Initialize a variable to determine if any space needs a charge code
        let anySpaceNeedsChargeCode = false;
    
        // First pass to determine if any space needs a charge code
        for (const space of this.space_list) {
            const building = this._org.buildings.find(
                bld => bld.id === space?.level.building_id
            );
            const { filters, settings } = space;
            const roomBasedEqCode = filters?.equipment_code;
            const officeBasedEqCode = building?.required?.equipment_code;
    
            // this.needs_charge_code[space.email] = space?.settings?.equipment_code || building?.required?.equipment_code || false;
            if (roomBasedEqCode || officeBasedEqCode) {
                anySpaceNeedsChargeCode = true;
                this.needs_charge_code = true;
                break;
            } else{
                this.needs_charge_code = false;
            }
        }
    
        // Second pass to create form groups with the determined charge code requirement.
        // We show only the first space, but if one of the selected spaces needs a mandatory charge code,
        // then all spaces need a mandatory charge code
        for (const space of this.space_list) {    
            this.form[space.email] = new FormGroup({
                notes: new FormControl(this.notes[space.email] || ''),
                code: new FormControl(
                    this.codes[space.email] || '',
                    anySpaceNeedsChargeCode ?
                        [Validators.pattern(/^[a-zA-Z0-9]*$/), Validators.minLength(4), Validators.required] :
                        [Validators.minLength(4), Validators.pattern(/^[a-zA-Z0-9]*$/)]
                ),
                head_count: new FormControl(this.head_counts[space.email] || '', [Validators.required, Validators.pattern(/^(?!0+$)/)])
            });
        }
    }
}
