import { Component, Input, SimpleChanges, OnChanges } from '@angular/core';
import { HashMap } from '@mckinsey-converge/base';
import { ReportDetails } from '../reports.component';



import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'newlineToBR',
  pure: true // Set pure to true to ensure it's a stateless pipe
})
export class NewlineToBRPipe implements PipeTransform {
  transform(value: string): string {
    if (typeof value !== 'string' || !value.includes('\n')) {
        // If the value is not a string or doesn't contain newline characters, return it as is
        return value;
      }
      return value.replace(/\n/g, '<br/>').replace(/\s/g, "\u00A0");
  }
}

@Component({
    selector: 'a-report-table',
    templateUrl: './report-table.component.html',
    styleUrls: ['./report-table.component.scss'],
})
export class ReportTableComponent implements OnChanges {
    /** List of report table data */
    @Input() public list: HashMap[] = [];
    /** Map of details for report */
    @Input() public details: ReportDetails;

    public fields: string[] = [];

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.list && this.list && this.list.length) {
            this.fields = Object.keys(this.list[0]).filter(i => this.details.hide_fields.indexOf(i) < 0);
        }
    }
}