import { BaseDataClass } from '../base-api.class';
import { CateringOrder } from '../catering';
import { User } from '../users';
import {
    flatten,
    HashMap,
    humaniseDuration,
    shorterBuildingDateFormatString,
    shorterLocalDateFormatString,
    toTitleCase,
    unique
} from '@mckinsey-converge/base';
import { Space } from '../spaces/space.class';
import { ServiceManager } from '../service-manager.class';
import { SettingsService } from '../settings.service';
import {
    BookingNote,
} from './booking.types';
import { convertLocalTimestampToTimezonedDateTz, DateNow, DateTZ, getTimezoneOffsetString } from '@mckinsey-converge/date-tz';
import { DaysOfWeek, getLastDateFromList, handleRecurrenceFields, RecurrencePeriod } from '../recurrence/recurrence.utils';
import { findSpace } from '../spaces';
import { DateTime } from 'luxon';


export interface IBookingQueryOptions {
    /** booking ID */
    id?: string;
    email?: string;
    target?: string;
    from?: number; // R--- depreciate
    until?: number; // R--- depreciate
    fromTz?: DateTZ;
    untilTz?: DateTZ;
    show_cancelled?: boolean;
    pagination?: boolean;
    limit?: number;
    offset?: number;
    sort?: string,
    filters?: any;
    include_rooms?: boolean;
    building_zone?: string;
}

export class Booking extends BaseDataClass {
    /** Unique calendar event ID */
    // public readonly icaluid: string;
    /** Subject or title of the booking */
    public title: string;
    /** Luxon based date class of the booking start time */
    public startDateTz: DateTZ;
    /** Luxon based date class of the booking start time */
    public endDateTz: DateTZ;
    /** Description or details of the booking */
    public readonly body: string;
    /** Type of booking */
    public readonly booking_type: string;
    /** List of catering orders for the booking */
    public catering: readonly CateringOrder[];
    /** Whether booking's duration covers all day */
    public readonly all_day: boolean;
    /** Mapping of emails to approval statuses */
    public approval_status: HashMap<string>;
    /**
     * New booking approval status set in constructor
     *
     * timeBasedStatusLabel is a getter calculated via current time, some parts of the application
     * may use status when it should use timeBasedStatusLabel
     */
    public status: 'tentative' | 'accepted' | 'approved' | 'declined' | 'cancelled' | 'expired' | 'unavailable';
    /** Host/Organiser of the booking */
    public organiser: User;
    /** List of people invited to attend the booking */
    public attendees: User[];
    /** Author of the booking */
    public readonly creator: User;

    /** List of notes associated with the booking */
    public notes: readonly BookingNote[];
    /** Mapping of spaces to equipment charge codes */
    public equipment_codes: HashMap<string>;
    /** Mapping of spaces to expected number of attendees */
    public expected_attendees: HashMap<number>;
    /** Map of space emails to the setup time before the meeting in minutes */
    public setup: HashMap<number>;
    /** List of checked in attendees */
    public check_ins: object;
    /** Map of space emails to the breakdown time before the meeting in minutes */
    public breakdown: HashMap<number>;
    /** List of fields edited since creation */
    public readonly edits: string[];
    /** List of users to be notified on visitor arrivals */
    public readonly notify_users: readonly string[];
    /** Whether the time or duration has changed */
    public time_changed = false;
    /**
     * Booking Space
     * Bookings only have one room on MCK
     */
    public room: Space;
    /** building_zone */
    public building_zone: string;
    /** Initialized Timezone */
    public timezone: string;
    /** Array of company names */
    public company?: string[];
    /** Flag for multiroom booking */
    public is_multiroom: boolean;
    /** Multiroom booking ID */
    public multiroom_master_id: string;    
    /** Type of recurrence,  Shows whether this is a master or an occurence in the series. */
    public readonly recurrence_type: string;
    /** The frequency of the recurring booking. */
    public readonly recurrence_period: RecurrencePeriod;
    /** The interval time between each period. Defaults to 1. For example, with a period of "weekly" and an interval of 2, the recurrencd happen every 2 weeks. */
    public readonly recurrence_interval: number;
    /** Unix epoch in seconds of the recurrence end date */
    private _recurrence_endTz: DateTZ;
    /** The number of times to repeat the recurring booking. */
    public readonly recurrence_count: number;
    /** ID of the booking considered the master */
    public readonly recurrence_master_id: string;
    /** An array of booking IDs which belong to this recurring series. This INCLUDES the master booking ID. */
    public readonly occurrence_ids: string[];
    /** A list of INDIVIDUAL edits to any of the bookings in the series. This is so we can prompt the user if they are going to override previously updated bookings with a whole-series update. */
    public readonly occurrence_edits: string[];
    /** A list of Ocurrences */
    public readonly occurrence_details: { id: string, start_epoch: number }[];
    /** When the booking was created */
    public created_epoch: number;
    /** Is the Application concierge */
    public is_concierge: boolean;
    /**helper to know if recurring toggle is on */
    public is_recurrent: boolean;
    /**array of start dates of each ocurrence in the series */
    public recurrence_starts: Array<DateTZ>
    /**array of conflicting dates not to be included in the recurrence series */
    public recurrence_exceptions: Array<number>

    public recurrence_days?: Array<DaysOfWeek>

    /** Master recurrence start */
    public recurrence_start: number;

    /** Start Epoch */
    public start_epoch: number;
    public end_epoch: number;
    public level_zone: string;
    public opt_out: boolean;
    public multi_rooms: Space[];
    public room_setup: any;
    public room_breakdown: any;
    public headcount: number;
    public equipment_code: string;

    /** Currently back-end is not properly setting the recurrence_end property, this is a workaround */
    public get recurrence_endTz(): DateTZ {
        return this._recurrence_endTz;
    }

    public set recurrence_endTz(date: DateTZ) {
        this._recurrence_endTz = date;
    }

    /** Mark a booking for deleting */
    public attempted_to_delete?: 'series' | 'booking' | null;

    // No specific reason to set readonly but canm be change if needed to update in future
    public readonly booked_by : string | { name: string };
    public readonly booker: Object;
    public readonly booker_concierge: Object

    constructor(raw_data: HashMap = {}) {
        super(raw_data);
        // Needed to check if the current app is Concierge or Staff
        const settingsService = ServiceManager.serviceFor(SettingsService) as unknown as SettingsService;
        this.is_concierge = settingsService.concierge;

        /**
         * Setup Defaults when raw_data values are not provided
         */
        const nowTz = new DateTZ();

        const defaultTitle = ''; // Blank since the create booking form will init with test values.
        const defaultLocalTimezone = Intl?.DateTimeFormat()?.resolvedOptions()?.timeZone;
        const defaultBuildingZone = null // R --- no idea, should be a building.id but which and how
        const defaultOrgainiser = User.active_user || new User(); // meh, non American use.
        const defaultBookingType = 'internal';

        /**
        * Process the simple raw_data with defaults mixed in
        */
        this.title = raw_data.title || defaultTitle;
        this.timezone = raw_data.timezone || defaultLocalTimezone;
        // Used by components to get the building details
        this.building_zone = raw_data.building_zone || defaultBuildingZone;
        this.body = raw_data.body || '';
        // Provided booking type
        this.booking_type = raw_data.booking_type || defaultBookingType;
        // Attendees provided in API booking data
        this.attendees = (raw_data.attendees || []).map((i) => new User(i));
        // Provided organiser, active user, or empty user?
        this.organiser = raw_data.organiser ? new User(raw_data.organiser) : defaultOrgainiser;
        // Creator is provided or defaults to the organizer
        this.creator = (raw_data.booked_by ? new User(raw_data.booked_by) : defaultOrgainiser) || this.organiser;
        // Setup and breakdown times can conflict but not the true event start and end times
        this.setup = raw_data.setup || {};
        this.breakdown = raw_data.breakdown || {};
        this.room_setup = raw_data.room_setup;
        this.room_breakdown = raw_data.room_breakdown;
        // Notes are assigned by room but we only support a single room
        this.notes = raw_data.notes || [];
        this.equipment_codes = raw_data.equipment_codes || {};
        this.equipment_code = raw_data.equipment_code || '';
        this.expected_attendees = raw_data.expected_attendees || {};
        this.headcount = raw_data.headcount;
        this.check_ins = raw_data.check_ins || {};
        this.notify_users = raw_data.notify_users?.length ? raw_data.notify_users : [this.organiser?.name];
        this.company = raw_data.company || [];
        // retain booked by if there : helps to get correct creator above
        this.booked_by = raw_data.booked_by;
        this.booker = raw_data.booker;
        this.booker_concierge = raw_data.booker_concierge;
        this.level_zone = raw_data.level_zone;
        this.opt_out = raw_data.opt_out;

        
        /**
         * Multiroom fields
         */
        this.is_multiroom = raw_data.is_multiroom || null;
        this.multiroom_master_id = raw_data.multiroom_master_id || null;

        /**
         * Setup Defaults when raw_data values are not provided
         *
         * all room_ids and space_list inputs can be refactored into just room like an API booking
         *
         * raw_data.room can be provides as the Space class or JSON object
         */
        // this.room = raw_data.room ? new Space(raw_data.room) : new Space();
        // this.room = raw_data?.room ? (Array.isArray(raw_data?.room) && raw_data.room.length > 0) ? raw_data?.room.map((element) => new Space(element))[0]: [new Space(raw_data.room)][0] : [new Space()][0];
        // this.room = raw_data.room ? new Space(raw_data.room) : new Space();

        if (raw_data && raw_data.room) {
            if (Array.isArray(raw_data.room) && raw_data.room.length) {
                this.room = raw_data.room.map((element) => new Space(element))[0];
            } else if (Array.isArray(raw_data.room) && raw_data.room.length === 0) {
                this.room = [new Space()][0];
            } else {
                this.room = [new Space(raw_data.room)][0];
            }
        } else {
            this.room = [new Space()][0];
        }


        const room_id = raw_data.room_id || raw_data.room_ids // Not sure why we have room_id and room_ids
        if (!this.room?.id && room_id?.length) {
            this.room = findSpace(room_id[0]) || this.room;
        }

        const roomsData = raw_data?.multi_rooms || raw_data?.room;
        this.multi_rooms = roomsData
            ? Array.isArray(roomsData)
                ? roomsData.map((element) => new Space(element))
                : [new Space(roomsData)]
            : [new Space()];

        
        const room_ids = [...(raw_data?.room_id || []), ...(raw_data?.room_ids || [])];

        room_ids.forEach(roomId => {
            const newRoom = findSpace(roomId);
            // Check if the room is found and not already included in this.room
            if (newRoom && !this.multi_rooms.some(room => room.id === newRoom.id)) {
                // Add the new room to this.room
                this.multi_rooms.push(newRoom);
            }
        });

        // this.room is sometimes getting set to undefined when raw_data?.room is coming as an object
        if(this.multi_rooms.length && this.room === undefined){
            this.room = this.multi_rooms[0];
        }
        
        /**
         * Setup the booking start and end time
         *
         *
         * Booking defaults to now if start is not defined.
         * Booking durration is now a getter
         */
        // now rounded to the next 5 minute increment
        const defaultStartTz = new DateTZ({ date: nowTz.ms, is_local_tz: false, building_tz: this.timezone }).setValue({ minute: Math.ceil(nowTz.minutes / 5) * 5 });

        /**
         * When saving a booking the BaseDataClass doens't know to use the toAPIJson
         * so startDateTz isn't being convert to the epoch timestamp
         *
         * I'm not testing for the end time values because we can assume those follow the same pattern.
         */
        const startEpochProvided = !!(raw_data.start_epoch || raw_data.start);
        const startDateTzProvided = !!raw_data.startDateTz;

        /**
         * Not trying to be fancy here, just clear.
         */
        if (!startEpochProvided && startDateTzProvided) {
            /**
             * Once a booking is saved the BaseDataClass recreates the Booking
             * but doesn't use the toApiJSON method adapt the class input, mainly the
             * startDateTz isn't converted to the start_epoch timestamp.
             */
            this.startDateTz = raw_data.startDateTz;
            this.endDateTz = raw_data.endDateTz;
        } else if (startEpochProvided) {
            /**
             * A Booking created from the API responses arrives with the booking
             * start_epoch and end_epoch timestamps.
             */
            this.startDateTz = new DateTZ({
                date: ((raw_data.start_epoch || raw_data.start) * 1000),
                is_local_tz: false,
                building_tz: this.timezone
            });
            this.endDateTz = new DateTZ({
                date: ((raw_data.end_epoch || raw_data.end)* 1000),
                is_local_tz: false,
                building_tz: this.timezone
            });
        } else {
            /**
             * And there are uses of new Booking where there are no inputs
             * and defaults are necessary.
             */
            this.startDateTz = defaultStartTz;
            this.endDateTz = defaultStartTz.addValue({ minutes: 60 });;
        }

        /**
         * End time was by design ending at one minute before, IE 4:00PM is 3:59PM,
         * because of calendar and conflcit checking
         *
         * Check and finesse it.
         * Not sure why but some booking endtime added extra seconds
         */
        if ((this.endDateTz.minutes % 5) !== 0 || this.endDateTz.second > 0) {
            // Not sure of the source but some bookings do not conform.
            this.endDateTz = this.endDateTz.setValue({ second: 0, minute: Math.round(this.endDateTz.minutes / 5) * 5 });
        }



        /**
         * Setup the booking created_date
         *
         * if it exists as a key, use the value directly.
         * if we initialize without a value, switch to checking duration.
         * Ref: MCK-826
         */
        this.created_epoch = raw_data.created_epoch || nowTz.seconds;


        /**
         * Setup the booking all_day boolean
         *
         * if it exists as a key, use the value directly.
         * if we initialize without a value, switch to checking duration.
         * Ref: MCK-826
         */
        if ('all_day' in raw_data) {
            this.all_day = raw_data.all_day;
        } else {
            this.all_day = !!raw_data.all_day || this.duration > 23 * 60;
        }


        // R--- TODO Concierge will use startDateTz so i'm not sure if this is needed.
        if (!this.is_concierge && this.all_day && this.timezone) {
            /**
             * Concierge has a all_day form field, I can see it may need this for that, does it really?
             */
            this.startDateTz = this.startDateTz.startOfValue('day')
        }

        /**
         * Setup the booking approval status
         */
         let status = raw_data.status;
         // If not provided default to approved.
         if (!raw_data.status) {
            status = 'accepted';
         }
         // "show_as" is a special rule to override how the applications display the status
         if (raw_data.show_as && raw_data.show_as === 'cancelled') {
            status = 'declined';
         }
         const approvalStatus = {};
         this.multi_rooms.forEach(room => {
            approvalStatus[room.email] = status;
        });
        // Tracking new "status" and legacy "approval_status" from raw_data.status.
         this.status = status;
         this.approval_status = raw_data.approval_status || {};

        /**
         * Catering setup
         *
         * Bring in and sort the catering order by delivery time
         */

        this.catering = (raw_data.catering instanceof Array ? raw_data.catering : []).map(
            (i) => new CateringOrder(i)
        );



        /**
         * Edited fields setup
         *
         * Bring in and sort the catering order by delivery time
         *
         * cateringOrders can return the order sorted
         */
        const edited_fields = Array.isArray(raw_data.edits)
            ? raw_data.edits
            : unique(
                flatten(
                    Object.keys(raw_data.edits || {}).map((room) => {
                        return flatten(Object.values(raw_data.edits[room]));
                    })
                )
            );
        this.edits = edited_fields;

        /**
         * New Recurring booking feilds
         *
         * New fields do not match the BookingRecurrenceDetails type
         * and are simpler to manage this way
         */
        this.recurrence_count = raw_data.recurrence_count || null;
        this.recurrence_days = raw_data.recurrence_days || null;
        this.occurrence_edits = raw_data.occurrence_edits || null;
        this.recurrence_endTz = raw_data.recurrence_end ? new DateTZ({date: raw_data.recurrence_end * 1000, is_local_tz: false, building_tz: this.timezone}) : null;
        this.recurrence_exceptions = raw_data.recurrence_exceptions || null;
        this.occurrence_ids = raw_data.occurrence_ids || null;
        this.recurrence_interval = raw_data.recurrence_interval || null;
        this.recurrence_period = raw_data.recurrence_period || null;
        this.recurrence_type = raw_data.recurrence_type || null;
        this.recurrence_master_id = raw_data.recurrence_master_id
        this.occurrence_details = raw_data.occurrence_details
        this.is_recurrent = raw_data.is_recurrent || (!!this.recurrence_type && !!this.recurrence_period) || null;
        this.recurrence_starts = raw_data.recurrence_starts || []
        this.start_epoch = raw_data.start_epoch
        this.end_epoch = raw_data.end_epoch

        /**
         * I'm not clear why, but the booking body, aka description is copied into the notes.
         * R--- In concerge data description is only a key when saving, also in the note array?
         */
        if (raw_data.body && !this.notes.find((i) => i.type === 'description')) {
            this.notes = [
                ...this.notes,
                {
                    type: 'description',
                    date: 0,
                    message: raw_data.body,
                    author: this.organiser.email
                }
            ];
        }

        /**
         * In case there is a cancellation in progress the property attempted_to_delete is set to false
         * And it is persisted until the booking is gone
         */
        this.attempted_to_delete = raw_data.attempted_to_delete || null;
    }


    /** Service for managing Bookings */
    protected get _service() {
        return ServiceManager.serviceFor(Booking);
    }

    /** Alias to approval_status */
    public get auto_approve(): boolean {
        // if concierge we auto_approve always.
        if (this.is_concierge) {
            return true;
        }
        return !this.multi_rooms.some(room => room.byRequest({
            dateTz: this.startDateTz,
            duration: this.duration,
            host: this.organiser
        }));
    }

    /** Whether booking has been approved */
    public get approved(): boolean {
        return !this.declined && !this.tentative;
    }

    /** All of the booking attendees including the organizer */
    public get allAttendees(): User[] {
        return unique([this.organiser].concat(this.attendees), 'email');
    }

    /** Computer format for booking type */
    public get bookingTypeId(): string {
        return this.booking_type.toLowerCase();
    }

    /** Human format for booking type */
    public get bookingTypeLabel(): string {
        return toTitleCase(this.booking_type);
    }

    /** Get accessor for the check in object, app expect array */
    public get checkInsArray(): string[] {
        return Object.keys(this.check_ins || {})
    }

    /** Legacy getter for "class" */ // R-- TODO remove
    public get class(): string {
        return this.booking_type;
    }

    /**  */
    public get displayEndDateTz(): DateTZ {
        if ((this.endDateTz.minutes % 5) !== 0) {
            // All the bookings should end in 59 seconds
            return this.endDateTz.addValue({ seconds: 1 });
        }
        // but if not they return 00
        return this.endDateTz;
    }

    /** Whether booking has been declined */
    public get declined(): boolean {
        /**
         * I'm not sure, nor is Cam if this is ever used to decline
         * all booking for a specific room
         */
        const isAnyRoomDeclined = this.multi_rooms.some(room => room.name.toLowerCase().includes('decline'));
        if (isAnyRoomDeclined) {
            return true;
        }

        if (this.status.includes('decline')) {
            return true;
        }

        return false;
    }

    /** Description of the booking purpose */
    public get description(): string {
        const note = (this.notes || []).find((i) => i.type === 'description');
        return note ? note.message : '';
    }

    /** Catering getter */
    public get cateringOrders() {
        // Return the catering orders sorted by delivery time
        return this.catering ? this.catering.slice().sort((a, b) => {
            if (a?.delivery_time > b?.delivery_time) return 1;
            if (b?.delivery_time > a?.delivery_time) return -1;

            return 0;
        }) : [];
    }

    /** Get the created date as DateTz */
    public get creationDateTz() {
        return new DateTZ({ date: this.created_epoch * 1000, is_local_tz: false, building_tz: this.timezone })
    }

    public get tz_offset() {
        return getTimezoneOffsetString(this.timezone);
    }

    /** Display value for the date */
    public get date_string(): string {
        return this.startDateTz.formatDate('dd MMM yyyy');
    }

    /**
     * Get the booking durration based on start and end times
     */
     public get duration(): number {
        return Math.abs(this.startDateTz.startOfValue('minute').dateDiff(this.displayEndDateTz, 'minutes'));
    }

    /** Unix timestamp of the booking start */
    public get date() {  // R--- remove if not necessary
        return this.startDateTz.ms;
    }

    /** Whether booking contains external visitors in the attendee list */
    public get has_visitors(): boolean {
        return this.attendees.reduce((a, v) => a || v.external, false);
    }

    /** Does the booking have catering orders */
    public get has_catering(): boolean {
        return !!this.catering.length;
    }

    /**
     * Unique calendar event ID
     * same as this.id, used to support existing component usage
     */
    public get icaluid(): string {
        return this.id;
    }

    /**
     * Get the booking room id
     */
    public get room_id(): string {
        return this.room.id;
    }

    /** Legacy getter of room, aka space */
    public get space(): Space {
        return this.room;
    }

    /** Status of the booking */
    public get timeBasedStatusLabel(): 'future' | 'upcoming' | 'done' | 'started' | 'in_progress' | 'expired_yesterday' {
        const  buildingTzDt = DateNow(new Date()).toZone(this.timezone) ;  // building time

        if (DateNow(new Date()).isBeforeDate(this.startDateTz.subtractValue({ minutes: 15 }))) {
            return 'future';
        } else if (DateNow(new Date()).isBeforeDate(this.startDateTz)) {
            return 'upcoming';
        } else if (DateNow(new Date()).isBeforeDate(this.startDateTz.addValue({ minutes: 15 }))) {
            return 'started';
        } else if (DateNow(new Date()).isBeforeDate(this.startDateTz.addValue({ minutes: this.duration }))) {
            return 'in_progress';
        }
        // if current time is 12 AM (as per timezone) and booking expired yesterday
        else if(buildingTzDt.startOfValue('day').addValue({ minutes: 1 }).isAfterDate(this.startDateTz, 'day') ) {
            return 'expired_yesterday'
        }

        return 'done';
    }

    /** Whether booking is tentative */
    public get tentative(): boolean {
        if (
            this.status &&
            this.status.indexOf('tentative') >= 0
        ) {
            return true;
        }

        return false;
    }

    /** Display valuie for the start and end times of the booking */
    public get time_period(): string {
        return `${this.startDateTz.formatDate('h:mma')} - ${this.displayEndDateTz.formatDate('h:mma')}`;
    }

    /** Display value for the start time of the booking */
    public get start_time(): string {
        return this.startDateTz.formatDate('h:mma');
    }

    /** Display value for the end time of the booking */
    public get end_time(): string {
        return this.endDateTz.formatDate('h:mma');
    }

    /** Display value for the duration of the booking */
    public get length_string(): string {
        return humaniseDuration(this.duration);
    }

    /** Display value for the location of the booking */
    // public get local_room_name(): string {
    //     return this.room?.local_name || 'No location';
    // }
    public get local_room_name(): string {
        if (this.multi_rooms.length === 0) {
            return 'No location';
        } else {
            return this.multi_rooms.map(room => room.local_name).join(', ');
        }
    }    

    /** Display value for the level of the first space in the booking */
    public get level(): string {
        return this.space.level.name;
    }

    /**
     * Make a copy of this object
     */
    public clone(): Booking {
        return new Booking(this.toJSON());
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(isEdit = false): Booking {
        return new Booking({
            ...this.toJSON(),
            id: isEdit ? this.id : null,
        });
    }

    /**
     *
     * @param status
     * @returns void
     */
    public undo(status?: 'accept' | 'decline', opts: { series?: boolean } = {}): Promise<void> {
        return this._service.undo(
            this.id, status || 'accept',
            opts
        );
    }

    /**
     * Delete booking from the server
     */
    public delete(opts: { series?: boolean } = {}): Promise<void> {
        if (this.id) {
            return this._service.delete(this.id, { ...opts });

            // R-- ask Cam again is he's positive these extra params are not needed for anything
            // return this._service.delete(this.id, {
            //     concierge: this.is_concierge,
            //     host: this.organiser.email,
            //     room_id: this.space?.id,
            //     icaluid: this.icaluid,
            //     start: json.start,
            //     end: json.end
            // });
        }
    }

    /**
     * Convert object into plain object
     */
    public toJSON(this: Booking): HashMap {
        let data = super.toJSON();
        // Remove the description from the notes
        data.notes = Array.isArray(data.notes) ? data.notes.filter((note) => note.type !== 'description') : data.notes;
        // Encode the nested objects
        data.room = data.room.toJSON();



        data.organiser = data.organiser.toJSON();
        data.creator = data.creator?.toJSON ? data.creator.toJSON(): data.creator;
        // Map the attendees User objects
        data.attendees = data.attendees.map((i: User) => i.toJSON());

        // New recurring booking
        data = handleRecurrenceFields(data)

        if(data.recurrence_list){
            data.recurrence_starts = [...data.recurrence_list]
            delete data.recurrence_list
        }

        return data;
    }

    /**
     * Convert object into plain object
     */
    public toApiJSON(this: Booking): HashMap {
        let data = super.toJSON();

        // Update booking payload contains additional fields
        data.icaluid = "";
        if (data.id) {
            data.icaluid = data.id;
            data.location_name = this.multi_rooms.map((item)=>{return item.local_name}).join(', ');
            data.from_room = this.multi_rooms.map((item)=>{return item.email}).join(', ');
            data.building_zone = this.room?.building?.id;
        }

        /**
         * These fields are rather in flux
         * According to Cam "approve" will be the prefered field
         * and auto_approve and approval_status can be removed.
         */
        data.approve = this.auto_approve; // this value appear incorrect on update
        data.auto_approve = [this.auto_approve]; // Needed to update approval status until BE accepts "approve"
        // delete data.approval_status;

        // Booking start and end data
        delete data.startDateTz;
        delete data.endDateTz;
        data.start = this.startDateTz.seconds;
        data.end = this.endDateTz.seconds;
        data.old_start = this.startDateTz.seconds; // remove is not necessary
        data.old_end = this.endDateTz.seconds; // remove is not necessary

        // Creation date value
        delete data.created_epoch;
        data.creation_date = this.creationDateTz.seconds;

        // Catering fields
        // data.catering = data.catering.toJSON();
        data.catering = this.cateringOrders;
        data.has_catering = this.has_catering;
        delete data.cateringOrders

        // Attendees appear quite incomplete
        // Map the attendees User objects
        data.attendees = this.allAttendees.map((i: User) => i.toJSON ? i.toJSON() : i);

        // Orgainizer and Creator data
        data.organiser = data.organiser.toJSON ? data.organiser.toJSON() : data.organiser;
        data.creator = data.creator.toJSON ? data.creator.toJSON() : data.creator;

        //  Convert check_ins to checked_in array
        delete data.check_ins;
        data.checked_in = this.checkInsArray;

        // edits - edit booking is showing all fields even w/o an edit :(

        // Room data
        data.room_ids = this.multi_rooms.map((item)=>{return item.email});
        delete data.room;

        // New recurring booking
        data = handleRecurrenceFields(data)


        if(data.recurrence_list){
            data.recurrence_starts = [...data.recurrence_list]
            delete data.recurrence_list
        }

        // Not found in dev data
        delete data.name;
        delete data.email;
        delete data.is_concierge;
        // delete data.building_zone;
        // delete data.status;

        return data;
    }

    public getType(): string {
        if (!this || this.status?.includes('decline')) {
            return 'cancelled';
        }
        const booking_type = this.booking_type;
        if (booking_type === 'internal' && this.has_visitors) {
            return 'external';
        }
        return booking_type;
    }

    /** fallback for a booking when the recurrence_end is null but it's a series booking */
    public recurrenEndFromOcurrences(occurrence_details: { id: string, start_epoch: number }[]): DateTZ {
        const date = occurrence_details?.map(occurence => occurence)
        .sort((a, b) => new Date(a.start_epoch * 1000).getTime() - new Date(b.start_epoch * 1000).getTime())
        .reverse()
        .shift()
        .start_epoch * 1000
        return new DateTZ({date, is_local_tz: false, building_tz: this.timezone});
    }

    public get dateString() {
        return shorterLocalDateFormatString(this.startDateTz);
    }
    public get dateBuildingString() {
        return shorterBuildingDateFormatString(this.startDateTz);
    }

    public get endDateString(): string {
        return shorterLocalDateFormatString(this.recurrenEndFromOcurrences(this.occurrence_details));
    }
}

/**
 * Merge catering orders with same time and location
 * @param order_list List of catering orders
 */
export function mergeCateringOrders(order_list: CateringOrder[]) {
    // R--- investigate when this is used. Staff doesn't use it when adding catering orders.
    for (let i = 0; i < order_list.length; i++) {
        const orders = order_list.filter(
            (order) =>
                order.location_id === order_list[i].location_id &&
                order.delivery_time === order_list[i].delivery_time
        );
        if (orders.length > 1) {
            const new_list = order_list.filter(
                (order) =>
                    !(order.location_id === order_list[i].location_id &&
                        order.delivery_time === order_list[i].delivery_time)
            );
            new_list.push(new CateringOrder({
                ...order_list[i],
                items: flatten(orders.map(order => order.items)),
                booking_date: this.startDateTz.ms,
                booking_timezone: this.booking_timezone,
            }));
            order_list = new_list;
            i = 0;
        }
    }
    return order_list;
}
