import { Component, HostListener, ViewEncapsulation } from '@angular/core';
import { UploadManager, Amazon, Md5Workers } from '@acaprojects/ngx-uploads';
import { ComposerService } from '@placeos/composer';

import { detectIE } from '@mckinsey-converge/base';
import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { MatDialog } from '@angular/material/dialog';
import { ZoomWarningComponent } from '@mckinsey-converge/ui';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: [
        './styles/app.component.scss',
        './styles/custom-element.styles.scss',
        './styles/native-element.styles.scss',
    ],
    encapsulation: ViewEncapsulation.None,
})
export class AppComponent extends BaseDirective {
    constructor(
        private _composer: ComposerService,
        private _uploads: UploadManager,
        private _md5_workers: Md5Workers,
        private _service: ApplicationService,
        private _dialog: MatDialog
    ) {
        super();
        /* istanbul ignore if */
        if (detectIE() && detectIE() < 12) {
            location.href = `${location.origin}${location.pathname}assets/not-supported.html`;
            return;
        }
        // CONCIERGE.date: If invalid, reset the localstorage date and timezone when reloading pages.
        const timezoneString = localStorage.getItem('CONCIERGE.timezone') || null;
        localStorage.removeItem('CONCIERGE.date');
        if (!timezoneString) {
            localStorage.removeItem('CONCIERGE.timezone');            
        }

        this._md5_workers.setup('assets/md5_worker.js');
        this.initUploads();
    }

    ngOnInit() {
        this.adjustZoom();
    }

    private adjustZoom() {
        const clientWidth = document.body.clientWidth;
        if (this.isMobileOrTablet()) { // Do not show the warning if it's a mobile or tablet device
            return;
        }
        if (clientWidth < 1300) {
            this._dialog.open(ZoomWarningComponent);
        }
    }
    private isMobileOrTablet(): boolean {
        const userAgent = navigator.userAgent || navigator.vendor;
        const maxTouchPoints = navigator.maxTouchPoints || 0; // Check for touch points 
        if (maxTouchPoints > 0 && window.innerWidth <= 1024) {
            return true;
        } // Check for common mobile/tablet user agent strings 
        const mobileAgents = ['Android', 'webOS', 'iPhone', 'iPad', 'iPod', 'BlackBerry', 'IEMobile', 'Opera Mini'];
        for (const agent of mobileAgents) {
            if (userAgent.indexOf(agent) > -1) {
                return true;
            }
        }
        return false;
    }

    private initUploads() {
        this.subscription(
            'composer',
            this._service.initialised.subscribe((state) => {
                /* istanbul ignore else */
                if (state) {
                    this.timeout('init_uploads', () => {
                        const token = this._composer.auth.token;
                        if (!token) {
                            return setTimeout(() => this.initUploads(), 300);
                        }
                        this._uploads.token = token;
                        this._uploads.autoStart = true;
                        this._uploads.endpoint = '/api/staff/uploads';
                        UploadManager.addProvider(Amazon);
                    });
                    this.unsub('composer');
                }
            })
        );
    }
}
