import { Type } from '@angular/core';
import { Observable } from 'rxjs';

import { HashMap } from '@mckinsey-converge/base';


export interface ServiceLike<T = any> extends HashMap {
    parent: any;
    add: (_: HashMap) => Promise<T>;
    update: (id: string, _: HashMap) => Promise<T>;
    delete: (id: string, params?: HashMap) => Promise<void>;
    task: (id: string, name: string, data: HashMap) => Promise<void>;
    listen: <U = any>(prop: string) => Observable<U>;
}

export interface ServiceProvider<T extends ServiceLike> {
    provideFor: Type<any>;
    useValue: T;
}

export class ServiceManager {
    /** Map of available services for child classes */
    private static _service_list: ServiceProvider<any>[] = [];

    /** Set the services used to handle data model requests */
    public static setService(type: Type<any>, service: any): void {
        if (window.debug) {
            (window as any).ServiceManager = this._service_list;
        }
        const index = ServiceManager._service_list.findIndex(provider => provider.provideFor === type);
        if (index >= 0) {
            ServiceManager._service_list.splice(index, 1, { provideFor: type, useValue: service });
        } else {
            ServiceManager._service_list.push({ provideFor: type, useValue: service });
        }
    }

    /** Get the services used to handle data model requests */
    public static serviceFor(type: Type<any>): ServiceLike<any> {
        const provider = ServiceManager._service_list.find(provider => provider.provideFor === type) || { useValue: null };
        return provider.useValue;
    }

    constructor() {
        throw new Error('ServiceMananger is static class');
    }
}
