import {
    AfterViewInit,
    OnDestroy,
    OnInit,
    TemplateRef,
    ViewChild,
    ViewContainerRef,
} from '@angular/core';
import { BaseDirective } from '@mckinsey-converge/base';

/**
 * A helper class for rendering embedded views (ng-template) as standalone components.
 *
 * If the view is lazy and does not render upon initialization,
 * it attempts embedding view again later.
 *
 * When testing a component directly that extends this class, its best to wrap the component
 * in a wrapper component for testing so that it can render itself.
 */
export class BaseTemplateComponent extends BaseDirective implements AfterViewInit, OnInit, OnDestroy {
    @ViewChild(TemplateRef, { static: true })
    public template: TemplateRef<any>;

    private isRendered = false;

    constructor(private vcRef: ViewContainerRef) {
        super();
    }

    public ngOnInit(): void {
        // try early rendering
        this.attemptViewEmbed();
    }

    public attemptViewEmbed() {
        if (this.template && !this.isRendered) {
            this.isRendered = true;
            this.vcRef.createEmbeddedView(this.template);
            return true;
        }
        return false;
    }

    public ngAfterViewInit(): void {
        if (!this.isRendered) {
            console.log('View not rendered yet, attempting to post initialization');
            setTimeout(() => {
                console.log('Timeout ended, trying again', this.template);
                if (!this.attemptViewEmbed()) {
                    console.log('Attempting render in after view initialization');
                    setTimeout(() => {
                        if (!this.attemptViewEmbed()) {
                            console.error('FAILED TO LOAD TEMPLATE');
                        }
                    }, 1000);
                }
            });
        }
    }

    public ngOnDestroy(): void {
        super.ngOnDestroy();
        this.isRendered = false;
    }
}
