import { createLoadingAction } from '../../../loading/src/lib/loading.actions';
import { Space } from '../../../data-common/src/lib/spaces/space.class';
import {
    RoomQueryOptions,
    RoomState,
    RoomStoreState
} from './rooms.types';
import {
    createAction,
    createSelector,
    props
} from '@ngrx/store';

export const roomStateSelector = (state: RoomStoreState) => state.rooms;

export const loadRoomsWithQuery = createAction('[Rooms] Load Rooms with query', props<RoomQueryOptions>());

export const loadRoomsForResults = createLoadingAction<RoomState, Space[], RoomQueryOptions>('Rooms', 'query');

export const loadRoomsForResultsMap = createLoadingAction<RoomState, Space[], RoomQueryOptions>('Rooms for map', 'query');


export const loadLaterThatDayResults = createLoadingAction<RoomState, Space[], RoomQueryOptions>('Later Rooms', 'later');

export const loadNextDayResults = createLoadingAction<RoomState, Space[], RoomQueryOptions>('Next Day Rooms', 'next');

export const loadByIdResults = createLoadingAction<RoomState, Space[], RoomQueryOptions>('By Id Rooms', 'byid');

export const selectRoomResultsSuccess = createSelector(roomStateSelector,
    loadRoomsForResults.selectors.optionalSuccess);

export const loadDiffSizeResults = createLoadingAction<RoomState, Space[], RoomQueryOptions>('Different Size Rooms', 'size');
