import { DateTZ } from "@mckinsey-converge/date-tz";

export interface SpaceRules {
    auto_approve: boolean; // if false sapce is requestable else bookable
    hide: boolean;
    max_length?: number;
    min_length?: number;
    reason?: string // specially for non-bookable rooms
}

export interface BookingNote {
    /** Type of note */
    type: 'equipment' | 'catering' | 'description' | 'private' | 'other' | 'catering-private';
    /** Name of the note's author */
    author: string;
    /** Contents of the note */
    message: string;
    /** Time the note was added to the booking */
    date: number;
    /** Display value for the time */
    time?: string;
    /** ID of the space associated with the note */
    space?: string;
    /** ID of the catering order associated with the note */
    order_id?: string;
}

export interface DateDurationData {
    dateTz: DateTZ,
    duration: number,
    mobile: boolean,
    save?: boolean,
}

export enum BOOKING_STATUS {
    approved = 'approved',
    declined = 'declined',
    tentative = 'tentative',
    unavailable = 'unavailable'
}