import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { CateringOrder } from '@mckinsey-converge/data-common';
import { Booking } from '@mckinsey-converge/data-common';

import { ApplicationService } from '@mckinsey-converge/data-common';
import { UsersService, BookingNote } from '@mckinsey-converge/data-common';
import { BookingsService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'order-details-notes',
    templateUrl: './messages.component.html',
    styleUrls: ['./messages.component.scss'],
})
export class OrderDetailsNotesComponent implements OnInit {
    /** Booking details to display */
    @Input() public booking: Booking;
    /** Catering order to display notes for */
    @Input() public order: CateringOrder;
    /** Emitter for changes to the active booking */
    @Output() public bookingChange = new EventEmitter<Booking>();
    /** Contents of the new private note */
    public new_note: string;
    /** Whether a note is being added */
    public loading: boolean;

    /** List of notes for catering order space */
    public get notes(): BookingNote[] {
        const notes = this.booking.notes.filter((note) => {
            return (
                (note.type === 'catering' || note.type === 'catering-private') &&
                note.space === this.order.location_id &&
                (!note.order_id || note.order_id === this.order.id)
            );
        });
        notes.forEach((note) => {
            // Use the booking timezone to make the note date TZ aware since the native format is not.
            const date = new DateTZ({date: note.date, is_local_tz: false, building_tz: this.booking.timezone })
            note.time = date.formatDate('dd MMM, h:mma');
        });
        return notes;
    }

    constructor(
        private _service: ApplicationService,
        private _bookings: BookingsService,
        private _booking_state: BookingStateService,
        private _users: UsersService
    ) {}

    ngOnInit(): void {}

    /**
     * Add note for catering order
     */
    public async addNote() {
        /* istanbul ignore else */
        if (this.booking && this.new_note) {
            const user = this._users.current;
            const notes = this.booking.notes;
            const now = new DateTZ({ date: Date.now(), is_local_tz: false, building_tz: this.booking.timezone })
            const date = now.startOfValue('minute');
            const new_notes = [
                ...notes,
                {
                    type: 'catering-private',
                    date: date.ms,
                    author: user.name,
                    order_id: this.order.id,
                    space: this.order.location_id,
                    message: this.new_note,
                    time: date.formatDate('h:mma'),
                },
            ];
            const pending_booking = new Booking({ ...this.booking.toJSON(), notes: new_notes });
            this.loading = true;
            const new_booking: Booking = (await this._bookings
                .save(pending_booking)
                .catch((err) => {
                    this._service.notifyError(
                        `Failed to add note to order meeting. Error: ${err.message || err}`
                    );
                    this.loading = false;
                    throw err;
                })) as any;
            this.booking = new_booking;
            if (new_booking) {
                this._booking_state.replace(new_booking);
            }
            this.order.booking = new_booking;
            this.bookingChange.emit(new_booking);
            this.loading = false;
            this.new_note = '';
        }
    }
}
