import { CurrencyPipe } from '@angular/common';

import {
    csvToJson,
    downloadFile,
    HashMap,
    humaniseDuration,
    jsonToCsv,
} from '@mckinsey-converge/base';

import { ServiceManager } from '../service-manager.class';
import { Space } from '../spaces';
import { Organisation } from '../organisation';

export class Report<T extends {} = HashMap> {
    /** Type of report */
    public readonly type: string;
    /** List of data associated with the report */
    public readonly data: T[];

    constructor(raw_data: HashMap = {}) {
        this.type = raw_data.type || '';
        this.data = this.cleanData(raw_data.data);
    }

    /** Create report data structure from CSV */
    public static fromCSV(type: string, data: string): Report {
        const csv_json = csvToJson(data);
        return new Report({ type, data: csv_json });
    }

    /** Download report data as CSV format */
    public downloadCSV(name: string = 'unnamed.csv'): void {
        downloadFile(name, jsonToCsv(this.data));
    }

    /** Download report data as JSON format */
    public downloadJSON(name: string = 'unnamed.json'): void {
        downloadFile(name, JSON.stringify(this.data, undefined, 4));
    }

    public formatCancelledBy(email: string) {
        if (email !== null && email !== undefined) {
            let name = email.split('@')[0];
            name = name.split('_').join(' '); //get name from email and convert it to title case
            name = name.replace(/\w\S*/g, function (txt) {
                return `${txt
                    .charAt(0)
                    .toUpperCase()}${txt.substr(1).toLowerCase()}`;
            });
            return name;
        } else {
            return email;
        }
    }

    public timeConverter( UNIX_timestamp: number ){
        let a = new Date(UNIX_timestamp * 1000);
        let months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        let year = a.getFullYear();
        let month = months[a.getMonth()];
        let date = a.getDate();
        let hour = ((a.getHours() + 11) % 12 + 1);
        let suffix = a.getHours() >= 12 ? "PM":"AM";
        let min = a.getMinutes();
        let time = date + ' ' + month + ' ' + year + ' - ' + hour + ':' + min + ' ' + suffix;
        return time;
    }

    private cleanData(data: T[] = []) {
        if (data.length > 0 && this.type === 'day') {
            for (let i = 0; i < data.length; i++) {
                data[i]['booker'] = data[i]['booker'].name;
                data[i]['expected_attendees'] = Object.values(data[i]['expected_attendees'])[0];
            }
        } else if (data.length > 0 && this.type === 'audit') {
            for (let i = 0; i < data.length; i++) {
                data[i]['created_at'] = this.timeConverter(data[i]['created_at']);
            }
        } else if (data.length > 0 && this.type === 'catering') {
            for (let i = 0; i < data.length; i++) {
                data[i]['Cancelled By'] = this.formatCancelledBy(
                    data[i]['Cancelled By']
                );
            }
            const fields = Object.keys(data[0]);
            const room_field = fields.find(
                (key) =>
                    key.toLowerCase().includes('room') &&
                    key.toLowerCase().includes('email')
            );
            const price_field = fields.find((key) =>
                key.toLowerCase().includes('price')
            );

            for (let row of data) {
                /* istanbul ignore else */
                const space_service = ServiceManager.serviceFor(Space);
                if (room_field && price_field && space_service) {
                    const room = space_service.find(
                        row[room_field].toLowerCase()
                    );
                    const org_service = ServiceManager.serviceFor(Organisation);
                    const bld = org_service.buildings.find(
                        (bld: { id: any }) =>
                            bld.id === room?.level?.building_id
                    );
                    row[price_field] = new CurrencyPipe('en_us').transform(
                        row[price_field] / 100,
                        bld?.currency
                    );
                }
                /* istanbul ignore else */
                for (let field of fields) {
                    row[field] =
                        typeof row[field] === 'string'
                            ? row[field].replace(/\,/g, '٫')
                            : row[field];
                }
            }
        } else if (this.type === 'bookings') {
            data = data.map((i: HashMap) => {
                const booking = i;
                try {
                    if (booking.setup instanceof Object) {
                        booking.setup = booking.setup[booking.room_email];
                    }
                    if (booking.breakdown instanceof Object) {
                        booking.breakdown =
                            booking.breakdown[booking.room_email];
                    }
                    booking.setup = humaniseDuration((booking.setup || 0) / 60);
                    booking.breakdown = humaniseDuration(
                        (booking.breakdown || 0) / 60
                    );
                    booking.description = (booking.description || '')
                        .replace(/<[^>]*>?/gm, '')
                        .replace(/\,/g, '٫')
                        .replace(/\r?\n|\r/g, ' ');
                    booking['Meeting Host'] =
                        booking.organizer?.name || booking.organizer;
                    booking['Booked By'] =
                        booking.booked_by?.name ||
                        booking.booked_by?.email ||
                        '';
                    booking.charge_code =
                        booking.equipment_codes[booking.room_email] || '';
                    //    booking.expected_attendees = Object.keys(booking.expected_attendees).map(key => booking.expected_attendees[key]).join(', ');
                    booking.attendees = (booking.attendees || [])
                        .map((person) => person.name || person.email || person)
                        .join('٫ ');
                    booking.notes = (booking.notes || [])
                        .map((note) =>
                            note.author &&
                            (note.type === 'description' ||
                                note.type === 'private')
                                ? `[${note.author}|${note.type}]${note.message
                                      .replace(/<[^>]*>?/gm, '')
                                      .replace(/\,/g, '٫')
                                      .replace(/\r?\n|\r/g, ' ')}`
                                : ''
                        )
                        .join(' | ');
                    booking.cancelled = booking.isCancelled;
                    booking.status = booking.status[booking.room_email] || '';
                    booking.company = booking.company.join(', ');
                    if (booking.cancelled_by) {
                        booking.cancelled_by = this.formatCancelledBy(
                            booking.cancelled_by
                        );
                    }
                    if (booking.isCancelled) {
                        booking.cancelled_at_epoch = new Date(
                            booking.cancelled_at_epoch * 1000
                        ).toLocaleString();
                    }
                    booking.cancelled_date = booking.cancelled_at_epoch;
                } catch (e) {}
                const remove_fields = [
                    'id',
                    'icaluid',
                    'accepted_at',
                    'accepted_by',
                    'check_ins',
                    'changeKey',
                    'created',
                    'booking_type',
                    'edit_history',
                    'end_epoch',
                    'old_attendees',
                    'start_epoch',
                    'expected_attendees',
                    'isAllDay',
                    'isCancelled',
                    'body',
                    'is_free',
                    'lastModifiedDateTime',
                    'locationType',
                    'locations',
                    'organizer',
                    'booked_by',
                    'originalEndTimeZone',
                    'originalStartTimeZone',
                    'room_booking_status',
                    'room_email',
                    'room_emails',
                    'room_id',
                    'sensitivity',
                    'seriesMasterId',
                    'show_as',
                    'notes',
                    'subject',
                    'type',
                    'previous_booking',
                    'catering',
                    'responseStatus',
                    'equipment_codes',
                    'cancelled_at_epoch',
                ];
                for (const field of remove_fields) {
                    if (booking[field] !== undefined) {
                        delete booking[field];
                    }
                }
                const output = {};
                const order = [
                    'title',
                    'start',
                    'end',
                    'location',
                    'Meeting Host',
                    'Booked By',
                    'booked_at',
                    'booked_ahead_by',
                    'all_day',
                    'attendees',
                    'company',
                    'description',
                    'cancelled',
                    'cancelled_by',
                    'cancelled_date',
                ];
                const keys = Object.keys(booking);
                keys.sort((a, b) => {
                    const idx_a = order.indexOf(a);
                    const idx_b = order.indexOf(b);
                    return (
                        (idx_a === -1 ? keys.length : idx_a) -
                        (idx_b === -1 ? keys.length : idx_b)
                    );
                });
                for (const key of keys) {
                    output[key] = booking[key];
                }
                return output;
            }) as any;
        }
        return data;
    }
}
