import { Injectable } from '@angular/core';

import { ComposerService } from '@placeos/composer';

import { Report } from './report.class';
import { BaseAPIService } from '../base.service';
import { SettingsService } from '../settings.service';

@Injectable({
    providedIn: 'root',
})
export class ReportsService extends BaseAPIService<Report> {
    constructor(protected _composer: ComposerService,
                settingsService: SettingsService) {
        super(_composer, settingsService);
        this._name = 'Reports';
        this._api_route = 'reports';
    }

    protected process(raw_data: any): Report {
        return raw_data instanceof Array
            ? new Report({ data: raw_data })
            : Report.fromCSV('', raw_data);
    }
}
