import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { BaseDirective } from '@mckinsey-converge/base';

import { Booking, BookingAction, generateBookingForm } from '@mckinsey-converge/data-common';
import { humaniseDuration } from '@mckinsey-converge/base';;
import { BookingModalComponent } from '../../../bookings/booking-modal/booking-modal.component';
import { HashMap } from '@mckinsey-converge/base';
import { Space } from '@mckinsey-converge/data-common';
import { DialogEvent } from '@mckinsey-converge/base';

export interface BookingDetailInfo {
    bookedBy: {
        display:boolean,
        value: string
    }
}
@Component({
    selector: 'meeting-details',
    templateUrl: './details.component.html',
    styleUrls: ['./details.component.scss'],
})
export class MeetingDetailsDisplayComponent extends BaseDirective implements OnInit {
    /** Booking to display details */
    @Input() public booking: Booking;
    /** Space to display details */
    @Input() public space: Space;
    /** Mapping of edited fields */

    @Output() updateBooking = new EventEmitter();

    @Output() public event = new EventEmitter();

    /** Whether to show all locations */
    public show_locations: boolean;

    /** Whether the meeting is approved or not */
    public isApproved = false;

    public edited: HashMap<boolean> = {};

    /** Display string for the locations in the booking */
    public get location(): string {
        // Ensure both this.booking.room and this.space are treated as arrays
        let rooms: Space[] = [];
        
        if (Array.isArray(this.booking.multi_rooms) && this.booking.multi_rooms.length > 0) {
            rooms = this.booking.multi_rooms;
        } else if (Array.isArray(this.space) && this.space.length > 0) {
            rooms = this.space;
        }
        
        // Map each room to its local_name and join these names with a comma
        return rooms.map(room => room.local_name).join(', ');
    }

    /** Display string for the booking duration */
    public get duration() {
        return this.booking ? humaniseDuration(this.booking.duration) : '1 hour';
    }

    /** Display string for attendees in booking */
    public get start_and_duration_time() {
        const end_ = this.booking.startDateTz.addValue({minutes: this.booking.duration});
        return `${this.start_time} - ${end_.formatDate('h:mma').toLocaleLowerCase()}`;
    }

    /** Display string for attendees in booking */
    public get start_time() {
        return `${this.booking.startDateTz.formatDate('dd MMM yyyy')} ${this.booking.startDateTz.formatDate('h:mma').toLocaleLowerCase()}`;
    }
    /** Display string for attendees in booking */
    public get end_time() {
        const end_ = this.booking.startDateTz.addValue({minutes: this.booking.duration})
        return `${end_.formatDate('dd MMM yyyy,')} ${end_.formatDate('h:mma').toLocaleLowerCase()}`;
    }
    /** Display string for attendees in booking */
    public get attendees() {
        return this.booking.attendees.map((person) => person.name).join('\n');
    }

    /** Whether booking has been declined or ended */
    public get expired(): boolean {
        return this.booking.timeBasedStatusLabel === 'done';
    }

    public bookingDetails: BookingDetailInfo;
    constructor(private _dialog: MatDialog) {
        super();
    }

    public ngOnInit(): void {
        Object.values(this.booking.edits || []).forEach(i => {
            this.edited[i] = true;
        });
        this.setDetails();
        this.isApproved = this.booking.status === 'accepted' || this.booking.status === 'approved'; 
    }

    /**
     * Function to set details of the booking, can take advantage to one time assignment and set tmeplate properties to avoid template conditions and function calls
     */
    setDetails() {
        const {creator, organiser, booker_concierge} = this.booking;
        this.bookingDetails = {
            bookedBy: {
                display: creator && ( creator.email !== organiser.email || (creator.email === organiser.email && booker_concierge !== null && organiser.email !==booker_concierge['email']) ),
                value: (creator.email === organiser.email && booker_concierge !== null && organiser.email !==booker_concierge['email']) ?  booker_concierge['name'] || booker_concierge['email']  :  creator?.name || creator.email
            }
        }
    }

    public editMeeting() {
        const dialogRef = this._dialog.open<BookingModalComponent>(BookingModalComponent, {
            data: {
                timezone: this.booking.timezone,
                booking: this.booking,
                action: BookingAction.EDIT
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        this.subscription(
            'modal_events',
            dialogRef.componentInstance.event.subscribe(async (event: DialogEvent) => {
                const form = generateBookingForm(this.booking, ['space_list'], true);
                if (event.reason === 'done') {
                    dialogRef.close();
                    this.updateBooking.emit(event.metadata);
                }
            })
        );
    }
}
