
import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';
import { BaseAPIService } from '../base.service';
import { Booking, IBookingQueryOptions } from './booking.class';
import { bookingOptionsToQuery } from './booking.utilities';
import {
    HashMap
} from '@mckinsey-converge/base';
import { ServiceManager } from '../service-manager.class';
import { SettingsService } from '../settings.service';
import { BookingCheckinParams } from './space.types';
import { DateNow } from "@mckinsey-converge/date-tz";
import { Observable } from 'rxjs/internal/Observable';
import { forkJoin } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class BookingsService extends BaseAPIService<Booking> {
    constructor(protected _composer: ComposerService,
        settingsService: SettingsService,
    ) {
        super(_composer, settingsService);
        ServiceManager.setService(Booking, this);
        this._name = 'Bookings';
        this._api_route = 'bookings';
        this._compare = (a, b) => !(a.id || '').localeCompare(b.id) || !(a.icaluid || '').localeCompare(b.icaluid);
    }

    /**
 * Get user bookings
 * @param options
 */
    public userBookings(options: IBookingQueryOptions,
        uniqueId?: string): Promise<Booking[]> {
        if (!options) {
            throw new Error('Booking avilability requires request options');
        }
        const now = DateNow(new Date())
        if (!options.from) {
            options.from = now
                .startOfValue('day')
                .subtractValue({ days: 2 })
                .ms;
        }
        if (!options.until) {
            options.until = null;
        }

        const key = `bookings|${options.id ? options.id : ''}|${uniqueId ?? ''}`;
        if (!this._promises[key]) {
            this._promises[key] = new Promise((resolve, reject) => {
                const respond = (list: Booking[]) => {
                    delete this._promises[key];
                    resolve(list);
                };
                const error = e => {
                    reject(e);
                    delete this._promises[key];
                };
                const query = bookingOptionsToQuery(options);
                if (options.id) {
                    this.show(options.id, query).then(i => respond([i]), error);
                } else {
                    this.query(query).then(respond, error);
                }
            });
        }
        return this._promises[key];
    }


    /**
     * Save changes to the booking
     * @param booking Booking update or add to the database
     */
    public save(booking: Booking, series?: boolean): Promise<Booking> {
        const body = booking.toApiJSON()
        if(series){
            body.series = true
        }
        return booking.id ? this.update(booking.id, body) : this.add(body);
    }

    /**
     * Checkin atteendee of a booking
     * @param id ID of the booking
     * @param fields Fields associated with the booking and attendee
     */
    public checkin(id: string, fields: BookingCheckinParams) {
        return  this.task(id, 'checkin', fields);
    }
    public checkinIndividualVisitor(id: string, fields: BookingCheckinParams) {
        return  this.taskObsr(id, 'checkin', fields); 
    }

    public accept(id: string, fields?: HashMap) {
        return this.task(id, 'accept', fields);
    }

    public undo(id: string, status: 'accept' | 'decline', fields?: HashMap) {
        return this.task(id, status, fields);
    }

    public decline(id: string, fields?: HashMap) {
        return this.task(id, this.settingsService.concierge ? 'concierge_decline' : 'decline', fields);
    }

    protected process(raw_data: HashMap): Booking {
        return new Booking(raw_data);
    }

    public declineMultiple(bookings: Booking[]): Observable<any[]> {
        const requests = [];
        bookings.forEach(bkg => {
            const booking = bkg.toJSON();
            const id = booking.id;
            const fields = {
                booking_id: booking.id,
                organiser: booking.organiser.email,
                room_email: booking.space?.email,
                icaluid: booking.icaluid,
                start: booking.startDateTz.formatDate('h:mm a').toLocaleLowerCase(),
                end: booking.endDateTz.formatDate('h:mm a').toLocaleLowerCase(),
            };
            if (bkg.is_recurrent && bkg.recurrence_type === 'master') {
                // Add series=true for recurrent bookings
                fields['series'] = true;
            } 
            requests.push(this.taskObsr(id, 'concierge_decline', fields) ); 
        });
        return forkJoin(requests);
    }
}
