import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { FormGroup, FormControl } from '@angular/forms';

import { UsersService } from '@mckinsey-converge/data-common';
import { Booking } from '@mckinsey-converge/data-common';
import { Identity, DialogEvent } from '@mckinsey-converge/base';
import { humaniseDuration } from '@mckinsey-converge/base';
import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationService, BookingNote } from '@mckinsey-converge/data-common';

import { BookingsService } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { DateTZ } from '@mckinsey-converge/date-tz';

export interface PrivateDetailsModalData {
    space: Space;
    booking: Booking;
}

@Component({
    selector: 'a-private-details-modal',
    templateUrl: './private-details-modal.component.html',
    styleUrls: ['./private-details-modal.component.scss'],
})
export class PrivateDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form Fields for private details */
    public form: FormGroup;
    /** Whether changes are being saved */
    public loading: boolean;
    /** List of available durations for setup and breakdown */
    public available_durations: Identity[] = new Array(5)
        .fill(0)
        .map((_, i) => ({ id: i * 15 * 60, name: humaniseDuration(i * 15) || '0 minutes' }));

    public get booking(): Booking {
        return this._data.booking;
    }

    constructor(
        private _service: ApplicationService,
        private _users: UsersService,
        private _bookings: BookingsService,
        private _booking_state_service: BookingStateService,
        private _dialog_ref: MatDialogRef<PrivateDetailsModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: PrivateDetailsModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        const note = this.booking.notes.find(
            (note) => note.type === 'private'//&& note.space === this._data.space.email
        ) || { message: '' };
        this.form = new FormGroup({
            setup: new FormControl(this.booking.room_setup || this.booking.setup[this._data.space.email] || 0),
            breakdown: new FormControl( this.booking.room_breakdown || this.booking.breakdown[this._data.space.email] || 0),
            notes: new FormControl(note.message),
        });
    }

    public saveChanges() {
        const now = new DateTZ();
        if (this.form.dirty) {
            const old_notes = this.booking.notes.filter(
                (note) => !(note.type === 'private') // && note.space === this._data.space.email
            );
            const notes = [
                ...old_notes,
                {
                    type: 'private',
                    date: now.ms,
                    message: this.form.value.notes,
                    author: this._users.current.name,
                    space: this._data.space.email,
                } as BookingNote,
            ];
            const value = this.form.value;
            const setup = { ...this.booking.setup };
            Object.keys(setup).forEach((k) => setup[k]= value.setup );
            setup[this._data.space.email] = value.setup;
            const breakdown = { ...this.booking.breakdown };
            Object.keys(breakdown).forEach((k) => breakdown[k]= value.breakdown );
            breakdown[this._data.space.email] = value.breakdown;
            const room_setup =  value.setup;
            const room_breakdown = value.breakdown;

            const new_booking = new Booking({
                ...this.booking.toJSON(),
                setup,
                breakdown,
                room_setup,
                room_breakdown,
                notes,
            });
            this.loading = true;
            this._dialog_ref.disableClose = true;
            this._bookings.save(new_booking).then(
                (booking) => {

                    const bkn = new Booking({ ...this.booking, ...booking.toApiJSON() });
                    this._data.booking = bkn;
                    this._booking_state_service.replace(bkn);

                    this.event.emit({ reason: 'done', metadata: {setup, breakdown, notes} });
                    this._dialog_ref.close();
                },
                (err) => {
                    this._dialog_ref.disableClose = false;
                    this.loading = false;
                }
            );
        } else {
            this._dialog_ref.close();
        }
    }
}
