import {Component, EventEmitter, Inject, Input, OnInit, Output} from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import {DialogEvent} from '@mckinsey-converge/base';
import {DateTZ, isToday} from '@mckinsey-converge/date-tz';

export interface ConflictsModalComponentData {
  room_name: string;
  conflicts: number[];
  copy_conflicts?: boolean;
  timezone: string;
  status?: string;
}

@Component({
  selector: 'app-conflicts-modal',
  templateUrl: './conflicts-modal.component.html',
  styleUrls: ['./conflicts-modal.component.scss']
})
export class ConflictsModalComponent implements OnInit{
  public room_name: string;
  public conflicts: number[];
  public copy_conflicts: boolean;
  public timezone;
  public status: string;


  @Output() public event = new EventEmitter<DialogEvent>();

  constructor(@Inject(MAT_DIALOG_DATA) private _data: ConflictsModalComponentData){

  }

  public ngOnInit(){
    this.timezone = this._data.timezone;
    this.room_name = this._data.room_name
    this.conflicts = this._data.conflicts
    this.copy_conflicts = this._data.copy_conflicts
    this.status = this._data.status
  }

  public formatDate(date: number) {
    const dateTZ = new DateTZ({date: date*1000,  is_local_tz: false, building_tz: this._data.timezone});
    const dateIsToday = isToday(dateTZ, this.timezone);
    const display = dateTZ.formatDate('ccc dd MMM yyyy');
    return `${display}${dateIsToday ? ' (Today)' : ''}`;
  }

  public continue(): void {
    this.event.emit({ reason: 'done' });
  }

  public close(): void {
    this.event.emit({ reason: 'close' });
  }

  public copy(): void {
    this.event.emit({ reason: 'action' });
  }


}
