import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationService, Building, CateringMenuService, CateringCategory } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { first } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';
import { CateringStorage } from 'src/app/catering/catering.storage';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'app-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss']
})
export class CateringComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    public dateTz: DateTZ = DateNow(new Date());
    /** Name of the page to display */
    public page: string;
    /** Filter string for searching */
    public search_str: string;
    /** Toggle for new category modal */
    public category: number = 0;
    /** Toggle for new package modal */
    public package: number = 0;
    /** Number of categories being displayed */
    public length: number;
    /** Order type to filter by */
    public order_type: string = '';

    public orderDetailsExpanded = new BehaviorSubject<boolean>(false);

    public has_access: boolean;
    menuWithOthers: any[]=[];
    allCategories: any[] = [
        { display: 'All orders', value: '', meta: { childItems: [] } },
        { display: 'Pantry Only', value: 'pantry', meta: { childItems: [] } },
        { display: 'Kitchen only', value: 'kitchen', meta: { childItems: [] } }
    ];
    otherCategories: any[];
    public isClosed: boolean = false;

    public selectedSort: { display: string; value: string; meta: {}; } = { display: 'All orders', value: '', meta: { childItems: [] } };

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _service: ApplicationService,
        private _org: OrganisationService,
        private cateringStorage: CateringStorage,
        private _menu: CateringMenuService,
    ) {
        super();
        this.orderDetailsExpanded.next(cateringStorage.orderDetails);
    }


    public ngOnInit(): void {



        this.subscription('orderDetailsExpand', this.orderDetailsExpanded.subscribe(value => {
            this.cateringStorage.orderDetails = value;
        }));
        this._org.initialised.pipe(first(_ => _)).subscribe(() => {
            // Init dateTz here once the organization data is loaded.
            this.dateTz = new DateTZ({ date: this.dateTz.ms, is_local_tz: false, building_tz: this.timezone });
            this.category = 0;
            this._service.title = 'Catering';
            this.subscription(
                'route.params',
                this._route.paramMap.subscribe((params) => {
                    if (params.has('page')) {
                        this.page = params.get('page');
                    }
                })
            );
            this.subscription(
                'route.query_params',
                this._route.queryParamMap.subscribe((params) => {
                    if (params.has('zone') && params.get('zone') !== this._org.building.id) {
                        const id = params.get('zone');
                        const bld = this._org.buildings.find((building) => building.id === id);
                        if (bld) {
                            this._org.building = bld;
														this.allCategories = [
															{ display: 'All orders', value: '', meta: { childItems: [] } },
															{ display: 'Pantry Only', value: 'pantry', meta: { childItems: [] } },
															{ display: 'Kitchen only', value: 'kitchen', meta: { childItems: [] } }
														];
                            this.loadAllCategory();
                        }
                    }
                })
            );
            this.subscription(
                'building',
                this._org.listen('active_building').subscribe(() => {
                    // Update dateTz here when the building is changed.
                    this.dateTz = new DateTZ({ date: this.dateTz.ms, is_local_tz: false, building_tz: this.timezone });
                    this.has_access = this._org.building ? true : false;
                    if(!this.has_access) return
										this.allCategories = [
											{ display: 'All orders', value: '', meta: { childItems: [] } },
											{ display: 'Pantry Only', value: 'pantry', meta: { childItems: [] } },
											{ display: 'Kitchen only', value: 'kitchen', meta: { childItems: [] } }
										];
                    this.loadAllCategory();
                    this._router.navigate([], {
                        relativeTo: this._route,
                        queryParams: { zone: this._org.building.id }
                    });
                })
            );
        });



    }


    /** List of top level catering categories */
    public catering_menu: CateringCategory[] = [];

    /** Currently active building */
    public get building(): Building {
        return this._org.building;
    }
     /** Filter the list of catering items */
     private filter(list: CateringCategory[]) {
        return list;
    }


    public mainCategorySelected(event, sortingValue) {
        if(!event.target.classList.contains('child')) {
            this.selectedSort = sortingValue;
            this.order_type = this.selectedSort.value;
        }
	}
    public selectSubcategory(event, sortingValue) {
		this.selectedSort = sortingValue;
        this.order_type = this.selectedSort.value;
	}

    public loadAllCategory() {

        // this.loading = true;
        this._menu.query({ zone_id: this.building.id }).then(
            (menu: any[]) => {
                this.catering_menu = (menu || []).map((item) => new CateringCategory(item));
                // this.length.emit(this.catering_menu.length);
                const filtered_menu = this.filter(this.catering_menu);

                 this.otherCategories = filtered_menu.filter(mailEl => {

                    var isThere = mailEl.items.filter(subEl =>  {
                        if(subEl.catering_type !== "kitchen" && subEl.catering_type !== "pantry") {

                            this.menuWithOthers.push({ value: subEl.name.toLowerCase(), display: subEl.name, meta: { parent: mailEl.name.toLowerCase() } })
                        }
                        return subEl.catering_type !== "kitchen" && subEl.catering_type !== "pantry";
                    } );
                    return !!isThere.length;
                });
                this.otherCategories.map(el =>  this.allCategories.push({ display: el.name, value: el.name.toLowerCase(), meta: { childItems: [] } }) );

                 this.menuWithOthers.forEach(el => {
                    this.allCategories.forEach(e => {
                        if(e.value === el.meta.parent){
                            e.meta['childItems'].push(el);
                        }
                    	})
                 });


                // console.log(this.allCategories);
            },
            (err) => {
                this._service.notifyError(
                    `Error loading menu for ${this.building.name}. Error: ${err.message | err}`,
                    'Retry',
                    () => this.loadAllCategory()
                );
                // this.loading = false;
            }
        );
    }




    public get timezone() {
        return this._org.building.timezone;
    }

}
