import {
    Inject,
    Injectable
} from '@angular/core';
import { EnvironmentData } from './types';

export const ENVIRONMENT_TOKEN = 'environments_service_data';

@Injectable({
    providedIn: 'root'
})
export class EnvironmentService {

    public get production(): boolean {
        return this.environment.production;
    }

    public get heap_io_id(): number {
        return this.environment.heap_io_id;
    }

    constructor(@Inject(ENVIRONMENT_TOKEN) private environment: EnvironmentData) {
    }
}
