import {
    Component,
    forwardRef,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation,
} from '@angular/core';
import {
    ControlValueAccessor,
    FormControl,
    NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { MatSelect } from '@angular/material/select';

import {
    BaseDirective,
    closestToTimeSlotTz,
    nearestStepToInput,
    timeWithGmtOffsetFormatString,
} from '@mckinsey-converge/base';

import { SelectOption } from '@mckinsey-converge/ui';
import {
    generateAvailableTimes,
    generateAvailableTimesTz,
} from '../utils/time.utils';
import { BehaviorSubject, combineLatest } from 'rxjs';
import { filter, map, withLatestFrom } from 'rxjs/operators';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { DateTime } from 'luxon';

@Component({
    selector: 'date-tz-time-field',
    template: `
        <div class="mck-column mck-form-input">
            <mck-label *ngIf="label" [required]="required">{{
                label
            }}</mck-label>
            <mat-form-field appearance="outline" class="mck-center-suffix">
                <mat-select
                    #select
                    [placeholder]="(placeHolderOption | async)?.display"
                    panelClass="mck-select-panel"
                    [disabled]="all_day"
                    [value]="selectedTimeString | async"
                    (valueChange)="setValue($event)"
                    disableOptionCentering
                >
                    <mat-option>
                        <ngx-mat-select-search
                            noEntriesFoundLabel="No timeslots found for current search. Try selecting one in the dropdown."
                            [placeholderLabel]="
                                (selectedOption | async)?.display ||
                                (placeHolderOption | async)?.display ||
                                ''
                            "
                            [formControl]="filterFormControl"
                        ></ngx-mat-select-search>
                    </mat-option>
                    <mat-option
                        *ngFor="
                            let option of options
                                | async
                                | filter: (nearestStep | async):'shortDisplay'
                        "
                        [value]="option.display"
                    >
                        <mck-select-option
                            [option]="option"
                            [selected]="isSelected(option)"
                        ></mck-select-option>
                    </mat-option>
                </mat-select>
                <div class="mck-column">
                    <mck-chevron-icon
                        [up]="isOpen"
                        matSuffix
                        fillClass="mck-icon-fill-black"
                    ></mck-chevron-icon>
                </div>
            </mat-form-field>
        </div>
    `,
    styleUrls: ['./date-tz-time-field.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => DateTzTimeFieldComponent),
            multi: true,
        },
    ],
    encapsulation: ViewEncapsulation.None,
})
export class DateTzTimeFieldComponent
    extends BaseDirective
    implements OnInit, OnChanges, ControlValueAccessor
{
    @Input() public label?: string;
    @Input() public required: boolean;
    /** Time step between each allowed time option */
    @Input() public step = 15;
    /** Whether form field is disabled */
    // @Input() public disabled: boolean;
    /** Whether past times are allowed */
    @Input() public noPastTimes = true;
    /** Representing the DateTZ object datetime to start time options from **/
    @Input() public fromDateTz: DateTZ = DateNow(new Date());
    // Disables this input and displays R--- "All Day" for the value
    @Input() public all_day: boolean = false;
    @Input() public show_current: boolean = false;
    @Input() public startFromDateTz: boolean = false;

    /** Form control on change handler */
    private _onChange: (_: DateTZ) => void;
    /** Form control on touch handler */
    private _onTouch: (_: DateTZ) => void;

    /** Select field for selecting the time */
    @ViewChild('select') private select_field: MatSelect;
    /** DateTZ object representing the currently set datetime */
    public selectedDateTz = new BehaviorSubject(
        closestToTimeSlotTz(DateNow(new Date()), this.step)
    );

    /** string representing the currently set datetime display value, needed for the input to display the slected value */
    public selectedTimeString = new BehaviorSubject(
        timeWithGmtOffsetFormatString(this.selectedDateTz.value)
    );

    public filterFormControl: FormControl = new FormControl();

    /** Available time blocks for the selected date */
    public options = new BehaviorSubject<SelectOption<any>[]>(
        generateAvailableTimesTz(
            DateNow(),
            !this.noPastTimes,
            this.step,
            this.timezone,
            this.all_day,
            this.show_current
                ? [generateOptionForDateTz(DateNow())]
                : [],
            this.startFromDateTz
        )
    );

    public ngOnInit(): void {
        // Generate the time input options for the booking date
        this.options.next(
            generateAvailableTimesTz(
                this.selectedDateTz.value,
                !this.noPastTimes,
                this.step,
                this.timezone,
                this.all_day,
                this.show_current
                    ? [generateOptionForDateTz(this.selectedDateTz.value)]
                    : [],
                this.startFromDateTz
            )
        );
        // Subscribe to selected option changes
        this.subscription(
            'dateTimeChanges',
            combineLatest([this.selectedDateTz])
                .pipe(
                    map(([selectedDateTz]) => {
                        /**
                         * The selectedDateTz should already be pegged to the closest time slot, but
                         * if the selected time rolls into the past this can correct it.  // R--- verify this statement
                         */
                        return closestToTimeSlotTz(selectedDateTz, this.step);
                    }),
                    withLatestFrom(this.options),
                    filter(
                        ([date, timeOptions]: [
                            DateTZ,
                            SelectOption<any>[]
                        ]) => {
                            // if new time slot opened up when date / time changes, add it here.
                            return !timeOptions.find(
                                (t) => t.value === date.formatDate('HH:mm')
                            );
                        }
                    ),
                    map(([dateTz, timeOptions]) => {
                        // TODO probably an issue here with going from a future date and time earlier than now back to day showing null
                        // Push the current date option in
                        timeOptions.push({
                            display: timeWithGmtOffsetFormatString(dateTz),
                            shortDisplay: dateTz.formatDate('hh:mma'),
                            value: dateTz,
                        });

                        // Sort the time options
                        timeOptions.sort((a, b) =>
                            `${a.value}`.localeCompare(`${b.value}`)
                        );
                        return timeOptions;
                    })
                )
                .subscribe((next) => {
                    return this.options.next(next);
                })
        );
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (
            changes.no_past_times ||
            changes.step ||
            changes.fromDateTz ||
            changes.all_day
        ) {
            // If there's a new date value then update the time options
            this.options.next(
                generateAvailableTimesTz(
                    this.selectedDateTz.value,
                    !this.noPastTimes,
                    this.step,
                    this.timezone,
                    this.all_day,
                    this.show_current
                        ? [generateOptionForDateTz(this.selectedDateTz.value)]
                        : [],
                    this.startFromDateTz
                )
            );
            // If today and all day goes from on to off the selected value isn't going to be available.
            if (
                changes.all_day &&
                !changes.all_day.currentValue &&
                changes.all_day.previousValue
            ) {
                this.setValue(this.options.value[0].display);
            }
        }
    }

    /**
     * Getter if the time picker options are visiable or not
     */
    public get isOpen() {
        return this.select_field?.panelOpen ?? false;
    }

    /**
     * Getter to return the current form timestamp from the from date object
     */
    public get timezone(): string {
        return this.fromDateTz.building_tz;
    }

    /**
     * A filter for finding a matching value when using the input text search
     */
    public nearestStep = combineLatest([
        this.filterFormControl.valueChanges,
        this.selectedDateTz,
    ]).pipe(
        map(([value, selectedDateTz]) => {
            return nearestStepToInput(
                this.step,
                value,
                DateTime.now().valueOf()
            );
        })
    );

    /**
     * Default text displayed in the search field when the component initializes
     */
    public placeHolderOption = this.options.pipe(
        map((os) => os.length > 0 && os[0])
    );

    /**
     * Returns the selected option object by matching the selectedDateTz formated string with the option.display string
     */
    public selectedOption = combineLatest([
        this.options,
        this.selectedDateTz,
    ]).pipe(
        map(([options, selectedDateTz]) =>
            options.find(
                (o) =>
                    o.display === timeWithGmtOffsetFormatString(selectedDateTz)
            )
        )
    );

    /**
     * Determines the visually selected value when the options list is visible.
     *
     * @param option DateTZ
     * @returns boolean
     */
    public isSelected(option: SelectOption<DateTZ>) {
        return this.selectedTimeString.value === option.display;
    }

    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: DateTZ) => void): void {
        this._onChange = fn;
    }

    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: DateTZ) => void): void {
        this._onTouch = fn;
    }

    /**
     * Method just used in unit tests to test disabling the input
     * @param disabled
     */
    // public setDisabledState(disabled: boolean) {
    //     this.disabled = disabled;
    // }

    /**
     * Update the selected values
     * @param dateTz the DateTz object for the new time
     */
    public setSelectedValues(dateTz: DateTZ) {
        // Update the selected DateTz and string representations
        this.selectedTimeString.next(timeWithGmtOffsetFormatString(dateTz));
        this.selectedDateTz.next(dateTz);
        if (this._onChange) {
            this._onChange(dateTz);
        }
    }

    /**
     * Update the form field value
     * @param displayString The "display" value of this.options set by the time picker
     */
    public setValue(displayString: string): void {
        // Convert the display time string to the actual option object via an array find.
        const newTimeTz = this.options.value.find(
            (opt) => opt.display === displayString
        ).value;
        // Update the selected DateTz and string representations
        this.setSelectedValues(newTimeTz);
        // Send the
        if (newTimeTz) {
            if (this._onChange) {
                this._onChange(newTimeTz);
            }
        }
    }

    /**
     * Fires when the form control value is changed
     * @param dateTz The new value for the component
     */
    public writeValue(dateTz: DateTZ | null) {
        // Init is null ?
        if (dateTz != null) {
            // Update the selected DateTz and string representations
            this.setSelectedValues(dateTz);
            // Update the available options
            this.options.next(
                generateAvailableTimesTz(
                    dateTz,
                    !this.noPastTimes,
                    this.step,
                    dateTz.building_tz,
                    this.all_day,
                    this.show_current
                        ? [generateOptionForDateTz(dateTz)]
                        : [],
                    this.startFromDateTz
                )
            );
        }
    }
}

function generateOptionForDateTz(dateTz: DateTZ): SelectOption<DateTZ> {
    return {
        display: timeWithGmtOffsetFormatString(dateTz),
        shortDisplay: dateTz.formatDate('hh:mma'),
        value: dateTz, 
    }
}
