import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { CateringOrder, CateringOrderStatus } from '@mckinsey-converge/data-common';
import { Booking } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { DialogEvent, Identity } from '@mckinsey-converge/base';
import { BaseDirective } from '@mckinsey-converge/base';
import { SpacesService } from '@mckinsey-converge/data-common';

import { BookingsService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { DateTZ, getTimezoneOffsetString } from '@mckinsey-converge/date-tz';

export interface OrderDetailsModalData {
    /** Order to display details for */
    order: CateringOrder;
    /** Booking associated with catering order */
    booking: Booking;
}

@Component({
    selector: 'a-order-details-modal',
    templateUrl: './order-details-modal.component.html',
    styleUrls: ['./order-details-modal.component.scss'],
})
export class OrderDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether status is being updated */
    public loading: boolean;
    /** Actively selected status */
    public active_status: CateringOrderStatus;
    /** List of available order statuses */
    public statuses: Identity[] = [
        { id: 'accepted', name: 'Accepted', icon: { class: 'custom-accepted' }, colour: '#3996B6' },
        {
            id: 'preparing',
            name: 'Preparing',
            icon: { class: 'custom-preparing' },
            colour: '#E7536B',
        },
        { id: 'ready', name: 'Ready', icon: { class: 'custom-ready' }, colour: '#FFD028' },
        {
            id: 'delivered',
            name: 'Delivered',
            icon: { class: 'custom-delivered' },
            colour: '#75BB43',
        },
        {
            id: 'cancelled',
            name: 'Cancelled',
            icon: { class: 'custom-cancelled' },
            colour: '#747474',
        },
    ];
    public booking: Booking;

    /** Order to display */
    public get order(): CateringOrder {
        return this._data.order;
    }

    /** Details for the active status */
    public get status_details(): Identity {
        return this.statuses.find((status) => status.id === this.active_status);
    }

    /** Space associated with the order */
    public get space(): Space {
        return this._spaces.find(this._data.order.location_id);
    }

    /** Details about the delivery time */
    public get delivery_date(): DateTZ {
        return this.booking.startDateTz.addValue({ minutes: this._data.order.delivery_time });
    }

    /** Details about the delivery_at time */
    public get delivery_at(): string {
        return this.booking.startDateTz.addValue({ minutes: this._data.order.delivery_time }).formatDate('h:mm a');
    }

    public get timezone() {
        return getTimezoneOffsetString(localStorage.getItem('CONCIERGE.timezone'));
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _bookings: BookingsService,
        private _booking_state_service: BookingStateService,
        @Inject(MAT_DIALOG_DATA) private _data: OrderDetailsModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        this.active_status = this._data.order.status;
        this.booking = new Booking(this._data.booking)
    }

    /** Update the status of the order in the booking */
    public async updateStatus(): Promise<void> {
        const booking = this._data.booking;
        /* istanbul ignore else */
        if (booking) {
            this.loading = true;
            const catering = [...booking.catering];
            catering.splice(
                catering.findIndex((order) => order.id === this._data.order.id),
                1,
                new CateringOrder({ ...this._data.order, status: this.active_status })
            );
            const new_booking = new Booking({ ...booking.toJSON(), catering });
            const updated_booking: any = await this._bookings.save(new_booking).catch((err) => {
                this._service.notifyError(
                    `Error updating order status. Error: ${err.message || err}`
                );
                this.active_status = this._data.order.status;
            });
            if (updated_booking) {
                this._booking_state_service.replace(updated_booking);
                this.booking = updated_booking;
            }
            this.loading = false;

        }
    }
}
