import {
    Component,
    Output,
    TemplateRef,
    ViewChild,
    EventEmitter,
    Input,
    OnInit
  } from "@angular/core";
  import { DropdownPanel, DropdownOpion } from "./dropdown-panel";
  
  @Component({
    selector: "cng-select",
    templateUrl: "./cng-select.component.html",
    styleUrls: ["./cng-select.component.scss"]
  })
  export class CngSelectComponent implements OnInit {
    @Input() data: any;
    public isClosed: boolean = false;
    public selected: DropdownOpion;
    constructor() {

    }
    ngOnInit(): void {
      this.selected = this.data.defaultSelected;
    }

    public optionSelected(item: DropdownOpion) {
      // this.selected = item;
      this.data.defaultSelected = item;
      this.data?.onChangeCallback && this.data?.onChangeCallback(item);
    }
  }
  