import { AbstractControl } from '@angular/forms';

import { User } from './users';

import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';


const HAS_ERROR = true;

/**
 * Build start time validation function
 * @param date Date of the time to check
 */
export function buildValidateDate(date?: number) {
  const from = new DateTZ({ date });
  return (c) => validateDate(c, date);
}

/**
 * Check that control value is a date that is the same or after from date
 * @param control Angular form control
 * @param is_from Comparison date(ms from UTC epoch). Defaults to the current time
 */
export function validateDate(control: AbstractControl, is_from?: number) {
  const from = new DateTZ({date: is_from})
  const date = new DateTZ({date: control.value})
  return date.isBeforeDate(from) ? { date: HAS_ERROR, message: 'Booking needs to be made in the future' } : null;
}

/**
 * Build start time validation function
 * @param date Date of the time to check
 * @param is_from Comparison date(ms from UTC epoch). Defaults to the current time
 */
export function buildValidateStartTime(date: number, is_from?: number) {
  const from = new DateTZ({date: is_from})
  return (c) => validateStartTime(c, date, from.ms);
}

/**
 * Check that control value time is after the comparison date time
 * @param control Angular form control
 * @param date Date of the time to check
 * @param is_from Comparison date(ms from UTC epoch). Defaults to the current time
 */
export function validateStartTime(control: AbstractControl, date: number, is_from?: number) {
  const now = new DateTZ({date: is_from});
  const parts = control.value.split(':');
  const date_obj = new DateTZ({date});
  return date_obj.setValue({ hour: +parts[0], minute: +parts[1] }).isBeforeDate(now) ? {
    start: HAS_ERROR,
    message: 'Booking time needs to be made in the future'
  } : null;
}

export function buildValidateDuration(min: number = 30, max: number = 240) {
  return (c) => validateDuration(c, min, max);
}

export function validateDuration(control: AbstractControl, min: number = 30, max: number = 240) {
  if (control.value < (min || 30)) {
    return { duration: HAS_ERROR, message: `Minimum booking duration is ${min} minute${min === 1 ? 's' : ''}` };
  } else if (control.value > (max || 480)) {
    return { length: HAS_ERROR, message: `Maximum booking duration is ${max} minute${max === 1 ? 's' : ''}` };
  }
  return null;
}

export function buildValidateAttendees(host: User, min: number = 0) {
  return (c) => validateAttendees(c, host, min);
}

export function validateAttendees(control: AbstractControl, host: User, min: number = 0) {
  if (!control || !(control.value instanceof Array)) {
    return min > 0
      ? { invalid: true, message: `Minimum of ${min} attendee${min === 1 ? ' is' : 's are'} required other than you` }
      : null;
  }
  const list: User[] = control.value;
  if (host) {
    const result = list.find(a => a.email === host.email);
    if (result) {
      list.splice(list.indexOf(result), 1);
    }
  }
  return list.length >= min ? null : {
    count: true,
    message: `Minimum of ${min} attendee${min === 1 ? ' is' : 's are'} required other than you`
  };
}

export function validateEndTime(dateControlTz: AbstractControl) {
  return (control: AbstractControl) => {
    // const date = new DateTZ({date: date_control ? date_control.value : undefined})
    const now = DateNow(new Date())
    return dateControlTz.value.addValue({ minutes: control.value || 60 }).isBeforeDate(now)? { end_time: true } : null;
  };
}

export function noWhitespaceValidator(control: AbstractControl) {
  const value = control?.value;
  if ( value === '') {
      return null; // Return null if control value is undefined or null
  }
  const isWhitespace = value.trim().length === 0;
  // const isValid = !isWhitespace;
  return !isWhitespace ? null : { whitespace: true };
}

