
import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';
import { CateringCategory } from './catering-category.class';
import { BaseAPIService } from '../base.service';
import { SettingsService } from '../settings.service';
import { ServiceManager } from '../service-manager.class';

@Injectable({
    providedIn: 'root'
})
export class CateringCategoriesService extends BaseAPIService<CateringCategory> {

    constructor(protected _composer: ComposerService,
                settingsService: SettingsService) {
        super(_composer, settingsService);
        ServiceManager.setService(CateringCategory, this);
        this._name = 'catering category/group';
        this._api_route = 'catering/category';
    }

    public query(): never {
        throw Error('No index endpoint for catering categories. Use catering menu service.');
    }

    public show(): never {
        throw Error('No show endpoint for catering categories. Use catering menu service.');
    }

    public processItem(raw_item: any) {
        return new CateringCategory(raw_item);
    }

    public format(item: CateringCategory) {
        return { ...item };
    }
}
