import {
    Component,
    forwardRef,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import {
    BaseDirective,
    nearestDurationToInput,
    DurationGroup,
} from '@mckinsey-converge/base';
import {
    ControlValueAccessor,
    FormControl,
    NG_VALUE_ACCESSOR
} from '@angular/forms';
import {
    BehaviorSubject,
    combineLatest
} from 'rxjs';

import { SelectOption } from '@mckinsey-converge/ui';
import {
    map
} from 'rxjs/operators';
import {
    DEFAULT_DURATION,
    defaultDurationGroups,
    generateDurationOptionsFromGroups
} from '../utils/time.utils';
import { MatSelect } from '@angular/material/select';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'mck-duration-field',
    template: `
        <div class="mck-column mck-form-input">
            <mck-label [required]="required">{{label}}</mck-label>
            <mat-form-field appearance="outline" class="mck-center-suffix">
                <mat-select
                    #select
                    [placeholder]="(placeHolderOption | async)?.display"
                    panelClass="mck-select-panel"
                    [value]="value | async"
                    [disabled]="disabled"
                    (valueChange)="setValue($event)"
                    disableOptionCentering>
                    <mat-option>
                        <ngx-mat-select-search
                            noEntriesFoundLabel="No timeslots found for current search. Try selecting one in the dropdown."
                            [placeholderLabel]="(selectedOption | async)?.display"
                            [formControl]="filterFormControl"></ngx-mat-select-search>
                    </mat-option>
                    <mat-option
                        *ngFor="let option of options | async | filter: (nearestStep | async): 'display'"
                        [value]="option.value"
                        [disabled]="isDisabled(option)">
                        <mck-select-option
                            [option]="option"
                            [selected]="isSelected(option) | async"
                        ></mck-select-option>
                    </mat-option>
                </mat-select>
                <div class="mck-column">
                    <mck-chevron-icon [up]="isOpen" matSuffix fillClass="mck-icon-fill-black"></mck-chevron-icon>
                </div>
            </mat-form-field>
        </div>
    `,
    styleUrls: ['./duration-field.component.css'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => DurationFieldComponent),
            multi: true
        }
    ],
    encapsulation: ViewEncapsulation.None
})
export class DurationFieldComponent extends BaseDirective implements OnInit, ControlValueAccessor, OnChanges {
    @Input()
    public label: string;
    @Input()
    public required: boolean;
    @Input() max_length?: number;

    /** Whether form field is disabled */
    @Input() public disabled: boolean;
    /** Start date value to count from */
    @Input() public startDate: number;
    /** Set to limit the duration options */
    @Input() public extended?: boolean;
    /** Representing desired timezone **/
    @Input() public timezone?: string = new DateTZ({ date: Date.now() }).local_tz;

    /**
     * Specify the slice of durations to use when generating options.
     */
    @Input() public durationGroups: DurationGroup[] = defaultDurationGroups;

    /** Form control on change handler */
    private _onChange: (_: number) => void;
    /** Form control on touch handler */
    private _onTouch: (_: number) => void;

    /** Select field for selecting the time */
    @ViewChild('select') private select_field: MatSelect;

    public value = new BehaviorSubject<number>(DEFAULT_DURATION);

    public currentStartDate = new BehaviorSubject(DateNow(new Date()));

    public filterFormControl: FormControl = new FormControl();

    public nearestStep = combineLatest([this.filterFormControl.valueChanges, this.currentStartDate]).pipe(
        map(([value, currentStartDate]) => nearestDurationToInput(this.durationGroups, currentStartDate.ms, value)
    ));

    public options = new BehaviorSubject<SelectOption<any>[]>([]);

    public placeHolderOption = this.options.pipe(
        map(options => options.find(o => o.value === DEFAULT_DURATION))
    );

    public selectedOption = combineLatest([this.options, this.value]).pipe(
        map(([options, value]) => options.find(o => o.value === value))
    );

    public isSelected(option: SelectOption<any>) {
        return this.value.pipe(map(t => t === option.value));
    }

    public isDisabled(option: SelectOption<any>) {
        return this.max_length ? option.value > this.max_length : false;
    }

    public get isOpen() {
        return this.select_field?.panelOpen ?? false;
    }

    ngOnInit(): void {
        this.subscription('OptionsCalculation',
            this.currentStartDate.pipe(
                map(d => generateDurationOptionsFromGroups(d.toZone(this.timezone), this.durationGroups, this.extended))
            ).subscribe(next => {
                this.options.next(next);
            }));
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.timezone) {
            const dopt = generateDurationOptionsFromGroups(this.currentStartDate.value.toZone(this.timezone), this.durationGroups, this.extended);
            this.options.next(dopt);
        }
        if (changes.startDate) {
            // R--- refactor input param
            const date = new DateTZ({date: this.startDate})
            this.currentStartDate.next(date);
        }
    }

    /**
     * Update the form field value
     * @param new_value New value to set on the form field
     */
    public setValue(new_value: number): void {
        this.writeValue(new_value);
    }

    /**
     * Update local value when form control value is changed
     * @param value The new value for the component
     */
    writeValue(obj: number): void {
        this.value.next(obj);
        if (this._onChange) {
            this._onChange(obj);
        }
    }

    public setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    /**
     * Registers a callback function that is called when the control's value changes in the UI.
     * @param fn The callback function to register
     */
    public registerOnChange(fn: (_: number) => void): void {
        this._onChange = fn;
    }

    /**
     * Registers a callback function is called by the forms API on initialization to update the form model on blur.
     * @param fn The callback function to register
     */
    public registerOnTouched(fn: (_: number) => void): void {
        this._onTouch = fn;
    }
}
