import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { first } from 'rxjs/operators';

import { BaseDirective } from '@mckinsey-converge/base';
import { Space } from '@mckinsey-converge/data-common';
import { SpacesService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';

@Component({
    selector: 'week-view-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class WeekViewTimelineComponent extends BaseDirective implements OnInit {
    /** Timestamp of the active date */
    @Input() public dateTz: DateTZ = DateNow(new Date());
    /** ID of the active level */
    @Input() public level: string;
    /** ID of the room type to filter displayed spaces by */
    @Input() public space_type: string;
    /** Whether weekends are displayed */
    @Input() public weekends: boolean;
    /** Whether space bookings are being loaded */
    @Input() public loading: boolean;
    /** List of dates to display */
    public dateListTz: DateTZ[] = [];
    /** List of spaces to display bookings for */
    public space_list: Space[] = [];

    constructor(private _spaces: SpacesService, private _booking_state_service: BookingStateService) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._booking_state_service.startPollingWeek();
            this.dateListTz = this.generateDatesTz(this.dateTz, this.weekends);
            this.space_list = this._spaces.filter(
                (space) =>
                    space.zones.includes(this.level) &&
                    (!this.space_type || space.zones.includes(this.space_type))
            );
        });

        this.subscription(
            'spaces',
            this._spaces.listen('list').subscribe((spaces) => {
                this.space_list = this._spaces.filter(
                    (space) =>
                        space.zones.includes(this.level) &&
                        (!this.space_type || space.zones.includes(this.space_type))
                );
            })
        );
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.dateTz || changes.weekends || changes.level || changes.space_type) {
            this.dateListTz = this.generateDatesTz(this.dateTz, this.weekends);
            this.space_list = this._spaces.filter(
                (space) =>
                    space.zones.includes(this.level) &&
                    (!this.space_type || space.zones.includes(this.space_type))
            );
        }
    }

    public ngOnDestroy() {
        if (this._booking_state_service?.stopPolling) this._booking_state_service?.stopPolling();
    }

    /**
     * Generate list of dates to display
     * @param dateTz Currently selected date
     * @param weekends Whether weekends should be included
     */
    private generateDatesTz(dateTz: DateTZ, weekends: boolean = false): DateTZ[] {
        const list: DateTZ[] = [];
        // start of day takes midnight to the previous day issue
        let day = dateTz.addValue({ minutes: 5 }).startOfValue('day');
        let start = day.startOfValue('week');
        let end = start.endOfValue('week').addValue({ minutes: 15 });
        while (start.isBeforeDate(end)) {
            if ((start.dateWeekday !== 6 && start.dateWeekday !== 7) || weekends) {
                list.push(start);
            }
            start = start.addValue({ days: 1 });
        }

        return list;
    }
    
}
